<template>
  <div class="mainCover">
    <HeaderBar :needm="needm" :needb="needb" :tit="titleName"></HeaderBar>
    <router-link to="/news">
      <div class="find-box">
        <p class="find">查找群</p>
        <div class="next-box">
          <img :src="next" alt class="next-img" />
        </div>
      </div>
    </router-link>

    <div class="mine-box">
      <p class="mine">我的群</p>
    </div>

    <div class="mainContent">
      <ul>
        <li v-for="(team, index) in teamList" @click="handleChatJump(team)">
          <div class="friend-list">
            <div class="head-portrait-box">
              <img :src="debug" alt class="head-portrait" />
            </div>
            <div class="name-box">
              <p class="name">{{team.name}}</p>
            </div>
          </div>
        </li>
        <li v-if="!teamList || teamList.length<1">暂无内容</li>
      </ul>
    </div>
  </div>
</template>
<script>
export default {
  name: "news_friends",
  props: {},
  data() {
    return {
      titleName: "群聊",
      needb: true,
      needm: false,
      next: "",
      debug: "",
      teamType: this.$route.params.teamType
    };
  },
  computed: { 
    teamList: function () {
      return this.$store.state.teamlist && this.$store.state.teamlist.filter(team => {
        return team.type === this.teamType && team.validToCurrentUser
      })
    },
  },
  created() {
    this.debug = require("images/debug.png");
  },
  mounted() {},
  watch: {},
  methods: {
    handleChatJump({teamId}){
      const sessionId = `team-${teamId}`;
      this.$router.push({
        name: 'chat',
        params: {
          sessionId
        }  
      });
      this.$store.dispatch('setCurrSession', sessionId)
    }
  },
  components: {}
};
</script>
<style scoped lang="css">
a {
  text-decoration: none;
}

.find-box {
  margin: 0px 10px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: #333;
  font-size: 14px;
  /* border-bottom: 1px solid #eaeaea; */
}

.next-img {
  width: 12px;
  height: 12px;
}

.mine-box {
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  /* border-bottom: 1px solid #eaeaea; */
  background: #f6f7f9;
}

.mine {
  color: #333;
  margin-left: 10px;
  font-size: 12px;
}

/* ********************************************************** */
.friend-list {
  width: 100%;
  display: flex;
  height: 50px;
}

.head-portrait-box {
  width: 15%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.head-portrait {
  width: 36px;
  height: 36px;
  border-radius: 50%;
}

.name-box {
  width: 85%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid #eaeaea;
}

.name {
  font-size: 14px;
  color: #383839;
}
</style>



