    goToCreateclubs() {
      this.$router.push({
        path: "/createclubs"
      });
      this.commentlist = [];
    },
    async setbrowsing(item) {
      try {
        let params = {
          ask_id: item.ask_id
        };
        let res = await this.$api.all.POSTfn(params, "browsing");
        console.log("设置浏览量" + JSON.stringify(res));
      } catch (e) {
        console.log(e);
      }
    },
    goaskdetail(item) {
      window.sessionStorage.setItem("askdata", JSON.stringify(item));
      this.setbrowsing(item);
      this.$router.push({
        name: "commune_f"
      });
      this.commentlist = [];
    },
    // 公社数据
    async getdate(direct, hot) {
      if (window.hasOwnProperty("webkit")) {
        this.isShow = false;
      } else {
        this.isShow = true;
      }
      try {
        let params = {
          count: 6,
          target_id: hot,
          direction: direct
        };
        let res = await this.$api.home.GETfn(params, "commune_list");
        if (direct == "bigger") {
          this.commentlist = res.result.data.concat(this.commentlist);
        } else if (direct == "litter") {
          this.commentlist = this.commentlist.concat(res.result.data);
        } else {
          this.commentlist = res.result.data;
        }
        this.iddata = res.result;
        console.log(res);
      } catch (e) {}
    },
