<template>
  <div class="mainCover">
    <div class="mainContent">
      <input
        id="msg-text"
        class="discuss-txt"
        placeholder="请发表您的评论"
        v-model="commentvalue"
        type="text"
      />
   
        <button class="buttons" @click="sendcomment()">发布</button>

    </div>
  </div>
</template>

<script>
import { Toast } from "mint-ui";
export default {
  props: {},
  data() {
    return {
      needb: true,
      needm: false,
      write: "",
      commentvalue: "",
      fid: ""
    };
  },
  computed: {},
  // created() {
  //   this.getParams();
  // },
  activated(){
  this.getParams();
  },
  mounted() {},
  watch: {
    // 监测路由变化,只要变化了就调用获取路由参数方法将数据存储本组件即可
    // $route: "getParams",
    commentvalue: {
      deep: true,
      handler(newVal, oldVal) {
        if (newVal.charAt(newVal.length-1)=='@') {
          console.log(newVal);
          this.$router.push({
            path: "/friendList",
            name: "friendList"
          });
        }
      }
    }
  },
  methods: {
    getParams() {
      // 将数据放在当前组件的数据内
      var routerdata = localStorage.getItem("homedata");
      this.fid = localStorage.getItem("fd");
      // console.log(frid)
      this.write = JSON.parse(routerdata);
     if (localStorage.getItem("fn") && this.commentvalue.charAt(this.commentvalue.length-1)=='@') {
        this.commentvalue =this.commentvalue +localStorage.getItem("fn")+"   ";
      }
    },
    // @好友
    async atfriend() {
      try {
        let params = {
          notice_id: this.fid,
          project_id: this.write.id?this.write.id:this.write.target_id,
          type: "1"
        };
        let res = await this.$api.all.POSTfn(params, "atfriend");
        console.log("@" + JSON.stringify(res));
      } catch (e) {
        console.log(e);
      }
    },

    //发表评论
    async sendcomment() {
      if (this.commentvalue == "") {
        Toast("请输入评论信息");
      }
      try {
        //   console.log("接过来的id"+this.$route.params.data.id)
        let params = {
          target: this.write.id?this.write.id:this.write.target_id,
          title: this.write.title,
          text: this.commentvalue,
          _: "RFs0",
          type: "article"
        };
        // ;
        let res = await this.$api.all.POSTfn(params, "send_comment");
        console.log("pinglun" + JSON.stringify(res));
        if(res.result=='ok'){
          Toast("评论成功")
        }
          
        // this.commentvalue = "";
        // this.$router.push({
        //   path: "/homeDetail",
        //   name: "homeDetail"
        // });
      } catch (e) {
        console.log(e);
      }
      if(this.commentvalue.indexOf("@"+ localStorage.getItem("fn"))!=-1){
          this.atfriend();
      }
        this.commentvalue = ""
    }
  },
  components: {}
};
</script>

<style scoped lang="css">
.buttons {
  text-align: center;
  background: #fff;
  margin-left: 10px;
  font-size: 14px
}
>>> .mint-button-text{
  color: #3966a7 !important;
}
.discuss-txt {
  font-size: 14px;
  border-radius: 20px;
  background: #f6f7f9;
  padding: 5px;
  width: 70%;
}
.mainContent {
  padding: 10px;
}
</style>



