<template>
  <div class="mainCover">
    <HeaderBar @fatherbackcall="backIntegral" :needcsb="needb" :tit="titleName"></HeaderBar>
    <div class="mainContent">
      <div class="list_th">
        <div class="list_th_2" v-for="item in orderList" :key="item.user_id">
          <div class="list_th_2_1">
            <img :src="smallimage" />
          </div>
          <div class="list_th_2_2">
            <div>{{item.content}}</div>

            <p>
              {{item.integral}}积分
              <span>x {{item.number}}</span>
            </p>
          </div>
          <div class="clearfix"></div>
          <div class="list_th_2_3">
            <span>{{item.name}}</span>
            <span>{{item.created}}</span>
            <span>
              <img :src="address" />
            </span>
            <span>{{item.address}}</span>
          </div>
        </div>
        <div class="clearfix"></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "intergral_list",
  props: {},
  data() {
    return {
      titleName: "订单填写",
      needb: true,
      smallimage: "",
      orderList: []
    };
  },
  computed: {},
  created() {
    this.address = require("images/address.png");
    this.addEvent();
  },
  mounted() {},
  watch: {},
  methods: {
    backIntegral() {
      this.$router.push("me_integral");
    },
    async addEvent() {
      try {
        let res = await this.$api.all.GETfn({}, "integral_order_user_list");
        res = res.result;
        if (res) {
          this.orderList = res;
        } else {
          this.$toast(res);
        }
      } catch (error) {}
    }
  },
  components: {}
};
</script>

<style scoped lang="css">
.payment_th_1_1 {
  width: 12%;
  float: left;
  padding: 20px 10px 0 10px;
}
/* 重置 */
ul {
  list-style: none;
  margin: 0;
  padding-inline-start: 0;
}
li {
  margin: 0;
}
img {
  display: block;
  width: 100%;
  height: 100%;
}

.list_th {
  background-color: #f5f6f9;
  position: relative;
  height: 620px;
}
.list_th_2 {
  width: 355px;
  margin: 10px;
  color: #023249;
  background: white;
  float: left;
}
.list_th_2 span {
  font-size: 14px;
  text-align: left;
}
.list_th_2 p {
  text-align: left;
  line-height: 36px;
}
.list_th_2 p span {
  float: right;
}
.list_th_2_1 {
  width: 80px;
  height: 72px;
  padding: 10px;
  float: left;
}
.list_th_2_2 {
  text-align: left;
  font-size: 14px;
  padding: 10px 10px 0 10px;
  float: left;
  width: 275px;
}

.list_th_2_3 {
  border-top: 1px #c8c7cc solid;
  text-align: left;
  font-size: 14px;
  height: 24px;
  line-height: 24px;
}
.list_th_2_3 span:nth-child(3) {
  width: 24px;
  height: 24px;
  display: block;
  padding-top: 14px;
}
.list_th_2_3 span {
  padding: 10px 0 10px 10px;
  float: left;
  color: #676767;
}
.clearfix {
  clear: both;
}
</style>



