<template>
  <div class="mainCover">
    <HeaderBar :needm="needm" :needb="needb" :tit="titleName"></HeaderBar>
    <div class="mainContent">
      <div class="selectbtn">
        <mt-cell :title="listdatas[0].name">
          <mt-button size="small" type="primary" @click.native="sheetVisible1 = true">行业类型</mt-button>
        </mt-cell>
      </div>
      <div class="selectbtn">
        <mt-cell :title="listdatas[1].name">
          <mt-button size="small" type="primary" @click.native="sheetVisible2 = true">所在区域</mt-button>
        </mt-cell>
      </div>
      <div class="selectbtn">
        <mt-cell :title="listdatas[2].name">
          <mt-button size="small" type="primary" @click.native="sheetVisible3 = true">回报机制</mt-button>
        </mt-cell>
      </div>
      <div class="selectbtn">
        <mt-cell :title="listdatas[3].name">
          <mt-button size="small" type="primary" @click.native="sheetVisible4 = true">投资金额</mt-button>
        </mt-cell>
      </div>
    </div>

    <ul class="bottom-but-ul">
      <li class="bottom-li">
        <mt-button size="small" type="primary" @click.native="resetFn">重置</mt-button>
      </li>
      <li class="bottom-li">
        <mt-button size="small" type="primary" @click.native="searchFn">搜索</mt-button>
      </li>
    </ul>

    <mt-actionsheet :actions="actions1" v-model="sheetVisible1"></mt-actionsheet>
    <mt-actionsheet :actions="actions2" v-model="sheetVisible2"></mt-actionsheet>
    <mt-actionsheet :actions="actions3" v-model="sheetVisible3"></mt-actionsheet>
    <mt-actionsheet :actions="actions4" v-model="sheetVisible4"></mt-actionsheet>
  </div>
</template>

<script>
export default {
  name: "senior",
  props: {},
  data() {
    return {
      titleName: "高级搜索",
      needb: true,
      needm: false,
      sheetVisible1: false,
      sheetVisible2: false,
      sheetVisible3: false,
      sheetVisible4: false,
      actions1: [],
      actions2: [],
      actions3: [],
      actions4: [],
      hangyeValue: "未选择行业类型",
      quyuValue: "未选择所在区域",
      huibaoValue: "未选择回报机制",
      touziValue: "未选择投资金额",
      listdatas: [
        { name: "未选择行业类型", id: "" },
        { name: "未选择所在区域", id: "" },
        { name: "未选择回报机制", id: "" },
        { name: "未选择投资金额", id: "" }
      ],
      itemarrary: [
        "occupation_id[]",
        "address_id[]",
        "mechanism_id[]",
        "investment_amount_id[]"
      ],
      UserInfo: this.$getUser
    };
  },
  computed: {},
  created() {
    // this.getDatalist();
    this.getactions1();
    this.getactions2();
    this.getactions3();
    this.getactions4();
  },
  mounted() {},
  watch: {},
  methods: {
    searchFn() {
      try {
        let that = this;
        let params = {};
        if (this.listdatas[0].id) {
          params["occupation_id[]"] = this.listdatas[0].id;
        }
        if (this.listdatas[1].id) {
          params["address_id[]"] = this.listdatas[1].id;
        }
        if (this.listdatas[2].id) {
          params["mechanism_id[]"] = this.listdatas[2].id;
        }
        if (this.listdatas[3].id) {
          params["investment_amount_id[]"] = this.listdatas[3].id;
        }

        if (Object.keys(params).length === 0) {
          this.$toast("您还未做出选择");
          return;
        }
        window.sessionStorage.setItem("SEARCHITEM", JSON.stringify(params));
        this.resetFn();
        this.$router.push({
          name: "profinder"
        });
      } catch (error) {
        console.log(error);
      }
    },
    selectactions1(e, index) {
      let param = {
        name: e.name,
        type: e.type,
        id: e.id
      };
      this.listdatas[0] = param;
    },
    selectactions2(e, index) {
      let param = {
        name: e.name,
        type: e.type,
        id: e.id
      };
      this.listdatas[1] = param;
    },
    selectactions3(e, index) {
      let param = {
        name: e.name,
        type: e.type,
        id: e.id
      };
      this.listdatas[2] = param;
    },
    selectactions4(e, index) {
      let param = {
        name: e.name,
        type: e.type,
        id: e.id
      };
      this.listdatas[3] = param;
    },
    async getDatalist() {
      try {
        let param = {};
        let res = await this.$api.all.GETfn(param, "height_find");
        res = res.result;
      } catch (error) {
        console.log(error);
      }
    },
    resetFn() {
      this.listdatas = [
        { name: "未选择行业类型", id: "" },
        { name: "未选择所在区域", id: "" },
        { name: "未选择回报机制", id: "" },
        { name: "未选择投资金额", id: "" }
      ];
    },
    async getactions1() {
      try {
        let that = this;
        let params = {};
        let res = await this.$api.all.POSTfn(params, "person_type");
        res = res.result;
        res.forEach((v, i, arr) => {
          let param = {
            name: v.name,
            id: v.id,
            type: v.type,
            method: that.selectactions1
          };
          this.actions1.push(param);
        });
        console.log(this.actions1);
      } catch (error) {
        console.log(error);
      }
    },
    async getactions2() {
      try {
        let that = this;
        let params = {};
        let res = await this.$api.all.POSTfn(params, "person_area");
        res = res.result;
        res.forEach((v, i, arr) => {
          let param = {
            name: v.name,
            id: v.id,
            type: v.type,
            method: that.selectactions2
          };
          this.actions2.push(param);
        });
        console.log(this.actions2);
      } catch (error) {
        console.log(error);
      }
    },
    async getactions3() {
      try {
        let that = this;
        let params = {};
        let res = await this.$api.all.POSTfn(params, "person_reward");
        res = res.result;
        res.forEach((v, i, arr) => {
          let param = {
            name: v.name,
            id: v.id,
            type: v.type,
            method: that.selectactions3
          };
          this.actions3.push(param);
        });
        console.log(this.actions3);
      } catch (error) {
        console.log(error);
      }
    },
    async getactions4() {
      try {
        let that = this;
        let params = {};
        let res = await this.$api.all.POSTfn(params, "person_money");
        res = res.result;
        res.forEach((v, i, arr) => {
          let param = {
            name: v.name,
            id: v.id,
            type: v.type,
            method: that.selectactions4
          };
          this.actions4.push(param);
        });
        console.log(this.actions4);
      } catch (error) {
        console.log(error);
      }
    }
  },
  components: {}
};
</script>

<style scoped lang="css">
ul.bottom-but-ul {
  width: 100%;
  height: 60px;
  line-height: 60px;
  display: flex;
}

li.bottom-li {
  list-style: none;
  flex: 1;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  text-align: center;
}

li.bottom-li .mint-button--primary {
  width: 150px;
}

.selectbtn {
  margin: 0px 12px;
  border-bottom: 1px solid #eee;
}

.selectbtn .mint-button--normal,
.selectbtn .mint-button--small {
  padding: 0px 10px;
}

.mint-cell {
  min-height: 44px;
}

</style>



