<template>
  <div class="mainCover">
    <HeaderBar :needb="needb" :tit="titleName"></HeaderBar>
    <div class="mainContent">
      <ul class="personal-ul">
        <li @click.prevent="goToPage('personal_head')" class="person-li">
          <span>头像</span>
          <!-- <div class="content">&nbsp;</div> -->
          <div class="touxiang">
            <img v-lazy="UserInfo.avatar" class="page-default-image" />
          </div>
          <div class="backicon">
            <img :src="backimage" />
          </div>
        </li>
        <li @click.prevent="goToPage('personal_name')" class="person-li">
          <span>昵称</span>
          <div class="username content">{{UserInfo.name}}</div>
          <div class="backicon">
            <img :src="backimage" />
          </div>
        </li>
        <li class="person-li">
          <span>账号</span>
          <div class="rightcontent content">{{UserInfo.account}}</div>
        </li>
        <li class="person-li">
          <span>手机号</span>
          <div class="rightcontent content">{{UserInfo.mobile}}</div>
        </li>
        <li @click.prevent="goToPage('personal_gender')" class="person-li">
          <span>性别</span>
          <div class="content">{{sexStr}}</div>
          <div class="backicon">
            <img :src="backimage" />
          </div>
        </li>
        <li @click.prevent="goToPage('personal_autograph')" class="person-li">
          <span>个性签名</span>
          <div class="signclass content">{{UserInfo.sign}}</div>
          <div class="backicon">
            <img :src="backimage" />
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "me_personal",
  props: {},
  data() {
    return {
      titleName: "编辑资料",
      needb: true,
      search: "",
      sign: "",
      UserInfo: this.initParam(),
      backimage: "",
      sexStr: ""
    };
  },
  computed: {},
  created() {
    this.backimage = require("images/right_arrow.png");
    this.initParam();
  },
  mounted() {},
  watch: {
    "$route.path": function() {
      this.initParam();
    }
  },
  methods: {
    initParam() {
      this.UserInfo = this.$getUser;
      let sex = this.UserInfo.sex;
      if (sex == "0") this.sexStr = "男";
      if (sex == "1") this.sexStr = "女";
      if (sex == "2") this.sexStr = "保密";
    },
    goToPage(name) {
      this.$router.push({
        name
      });
    }
  },
  components: {}
};
</script>

<style scoped lang="css">
.personal-ul {
  width: 100%;
  padding: 10px 12px;
}

.personal-ul li {
  /* padding: 5px 10px; */
  width: 100%;
  border-bottom: 1px solid #eee;
}

li.person-li {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 50;
  height: 50px;
  line-height: 50px;
}

.person-li span {
  display: inline-block;
  width: 70px;
  height: 50;
  font-size: 14px;
  color: #333;
}

.person-li .content {
  flex: 1;
  height: 100%;
  font-size: 14px;
  color: #999;
  text-align: right;
  /* padding-right: 5px; */
}

.person-li div.touxiang {
  width: 40px;
  height: 40px;
  padding-left: 140px;
}

.touxiang img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}

.person-li div.backicon {
  width: 30;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.backicon img {
  width: 24;
  height: 24px;
}

.username {
  word-break: break-all;
  display: block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.signclass {
  line-height: 50px;
  max-height: 90px;
  display: block;
  word-break: break-all;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
</style>



