<template>
  <div class="mainCover">
    <HeaderBar :needb="needb" :tit="titleName"></HeaderBar>
    <div class="content">
      <div class="club-name">
        <input type="text" placeholder="请填写标题" v-model="formItem.commname" name="name" />
      </div>
      <div class="mt-cell-item">
        <mt-cell :title="'[ ' + check + ' ]'">
          <mt-button
            size="small"
            type="primary"
            @click.native="sheetVisible = true"
            class="tag-btn"
          >标签(必选)</mt-button>
        </mt-cell>
      </div>
      <div class="mt-cell-item">
        <mt-cell :title="'[ ' + check1 + ' ]'">
          <mt-button size="small" type="primary" @click.native="checkClub" class="tag-btn">标签(必选)</mt-button>
        </mt-cell>
      </div>
      <div class="createclubs_tq" id="describe">
        <textarea
          placeholder="请输入内容,建议200字....."
          style="height:200px;width:100%;color:#333"
          id="question"
          name="info"
          v-model="formItem.introduction"
        ></textarea>
      </div>
    </div>
    <div class="createclubs_tm">
      <div class="logo-box">
        <Photoclipcom
          v-if="showClipflag"
          viewdom="fasImg"
          outputSizeWidth="600"
          outputSizeHeight="300"
          :inSize="INSIZE"
          @fatherfromcall="getchilfromdata"
          @fatherimgcall="getchildimgcall"
        ></Photoclipcom>
        <div class="imgBorder">
          <img :src="formItem.imgi" alt class="face-img" id="fasImg" />
        </div>
      </div>
    </div>
    <div class="btn">
      <button type="button" class="c-ok-btn" id="yBtn" @click="commune_cj()">确认</button>
      <button type="button" class="c-cancel-btn nBtn" @click="$router.back(-1)">取消</button>
    </div>
    <div>
      <mt-actionsheet :actions="actions1" v-model="sheetVisible" ref="actions"></mt-actionsheet>
    </div>
  </div>
  <!-- </div> -->
</template>

<script>
export default {
  name: "pubdy",
  props: {},
  data() {
    return {
      titleName: "发布动态",
      needb: true,
      bgImage: "",
      communecj: [],
      actions1: [],
      formItem: {
        commname: "",
        imgi: require("images/upload.jpg"),
        introduction: "",
        file: ""
      },
      check: "未选择渠道",
      check1: "未选择公社",
      sheetVisible: false,
      type: "",
      param: new FormData(),
      // rdata: [],
      commue_id: "",
      INSIZE: [300, 150],
      showClipflag: false
    };
  },
  computed: {},
  activated() {
    this.bgImage = require("images/lvse.jpg");
    this.param = new FormData();
    this.getLabelData();
    let CommueInfo = JSON.parse(window.sessionStorage.getItem("commueinfo"));
    if (CommueInfo) {
      this.check1 = CommueInfo.name;
      this.commue_id = CommueInfo.id;
    }
    this.showClipflag = false;
    this.$nextTick(() => {
      this.showClipflag = true;
    });
  },
  mounted() {},
  watch: {},
  methods: {
    checkClub() {
      this.$router.push("searchClub");
    },
    getchildimgcall(data) {
      this.formItem.imgi = data;
    },
    getchilfromdata(data) {
      this.param.append("file", data.get("file"));
    },
    //发布社问
    async commune_cj() {
      // let ast = window.sessionStorage.getItem("asktype");
      if (
        this.formItem.commname == "" ||
        this.formItem.imgi == require("images/upload.jpg") ||
        this.formItem.introduction == "" ||
        this.check == "" ||
        this.check1 == ""
      ) {
        this.$toast("还未完成选择");
        return;
      }
      let params = this.param;
      params.append("title", this.formItem.commname);
      params.append("content", this.formItem.introduction);
      params.append("tag", this.check);
      // params.append("file", this.formItem.imgi);
      params.append("type", 3);
      params.append("communeid", this.commue_id);

      try {
        let res = await this.$api.all.POSTfn(params, "createAsk");
        console.log(res);
        if (res.result == "ok") {
          this.formItem.commname == "";
          this.formItem.imgi == require("images/upload.jpg");
          this.formItem.introduction == "";
          this.check == "未选择";
          this.$toast("发布成功");
          this.$router.push({
            name: "find"
          });
        } else {
          this.$toast("发布失败");
        }
      } catch (e) {
        console.log(e);
      }
    },
    async getLabelData() {
      try {
        let params = {};
        let res = await this.$api.all.GETfn(params, "find_ask_tag_list");
        console.log(res);
        for (let i in res.result) {
          res.result[i].name = res.result[i].tag_name;
          res.result[i].method = this.tagActive;
        }
        this.actions1 = res.result;
      } catch (e) {
        console.log(e);
      }
    },

    tagActive(call) {
      this.check = call.name;
    }
  },
  components: {},
  destoryed() {
    window.sessionStorage.removeItem("commueinfo");
    this.$destory();
  }
};
</script>

<style scoped lang="css">
.content {
  padding: 0px 12px;
}

.club-name {
  height: 36px;
  display: flex;
  align-items: center;
  border: 1px solid #eee;
  border-radius: 3px;
  margin-top: 10px;
}

.club-name input {
  width: 100%;
}

.logo-box >>> #imagefile {
  position: absolute;
  opacity: 0;
  background: greenyellow;
  width: 100px;
  height: 100px;
  position: absolute !important;
}
.tag-btn {
  font-size: 12px;
  float: left;
  margin-left: 10px;
  background: #4a8dff;
  color: #fff;
  padding: 0px 10px;
}
input {
  font-size: 12px;
  line-height: 30px;
  padding: 0 10px;
}
.createclubs_tm {
  width: 100%;
  padding: 10px 10px;
}
.createclubs_tq {
  width: 100%;
  /* height: 36px; */
  /* display: flex; */
  /* align-items: center; */
  border: 1px solid #eee;
  border-radius: 3px;
}
.weixuanze {
  margin-left: 10px;
}
textarea {
  border: 0px;
  padding: 10px;
}
.btn {
  text-align: center;
  margin-top: 10px;
  display: flex;
  justify-content: space-between;
}
.c-ok-btn,
.c-cancel-btn {
  width: 150px;
  height: 36px;
  background: #4a8dff;
  color: #fff;
  font-size: 14px;
  padding: 5px 40px;
  border-radius: 3px;
  margin: 0px 12px;
}
.c-cancel-btn {
  background: #ccc;
}

.face-img {
  height: 100px;
  width: 100px;
}

.imgBorder {
  height: 115px;
  width: 115px;
  /* border: 2px solid #e2e3e3;  */
  padding: 5px;
}
.tag-btn[data-v-0f4b0c0e] {
  margin-right: 210px;
}
>>> .mint-cell {
  width: 100%;
  height: 40px;
}
#hh-commune {
  color: #333;
}
</style>




