<template>
  <div class="mainCover">
    <div class="homeheader" v-show="isShow">
      <img :src="require('images/logo_blue.png')" />
      <input type="text" @click.stop="gotoSearch('seatch')" placeholder="请输入关键词" />
    </div> 
     <div class="nav" >
      <swiper :options="swiperOption">
        <swiper-slide
          v-for="(v,i) in tab"
          :key="i"
          :class="{'nav-swiper-slide' : true,'nav-longer' : v.name.length==4,'nav-short' : v.name.length<=3,'nav-biglonger' : v.name.length>4 }"
        >
          <div
            :class="active == 'tab'+i ? 'nav-li active' : 'nav-li'"
            @click.prevent="changeactivewang(v,i,$event)"
          >{{v.name}}</div>
        </swiper-slide>
      </swiper>
    </div>
    <div class="mainContent">
      <keep-alive>
        <!-- <components :is="currcomponent" :type="typevalue" v-show="tansitionflag"></components> -->
        <components :is="currcomponent" :hotPro="allHot"  v-show="tansitionflag"></components>
      </keep-alive>
      <newList :is="currcomponent1" :type="typevalue" v-if="tansitionflag1"></newList>
    </div>
  </div>
</template>
<script>
import pictureNew from "pages/home/pictureNew.vue";
import zhuanti from "pages/home/zhuanti.vue";
import vadioNew from "pages/home/vadioNew.vue";
import newList from "pages/home/newslist.vue";
import { mapState, mapGetters, mapMutations } from "vuex";
import vueSeamless from "vue-seamless-scroll";
import Bscroll from "better-scroll";
import { Toast } from "mint-ui";
export default {
  props: {},
  mounted() {},
  data() {
    return {
      allHot:[],
      isShow: true,
      refresh: 1,
      logoimg: "",
      active: "tab1",
      tab: [],
      hasTabs: false,
      currcomponent: "HomeCom",
      currcomponent1: "newslist",
      typevalue: "",
      tansitionflag: true,
      tansitionflag1: false,
      swiperOption: {
        slidesPerView: 4,
        // spaceBetween: 1,
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        }
      },
      titleName: "首页"
    };
  },
  created() {
   this.allData()
  },
  active(){
     this.allData()
  },
  activated() {
    this.brige();
    // this.getnav();
    let that = this;
    this.isLogin();
    this.allData();
  },
  components: {
    pictureNew,
    vadioNew,
    zhuanti,
    newList,
    vueSeamless
  },
  computed: {
    ...mapState(["homedetailid"]),
    optionCustomer() {
      return {
        direction: 3,
        limitMoveNum: 20,
        step: 0
      };
    }
  },
  watch: {},
  methods: {
    brige() {
      let that = this;
      window.changeType = function(typeId) {
     
        that.changeactive(typeId);
        that.isShow=false;
      };
    },
    isLogin() {
      let that = this;
      let isLogin = window.localStorage.getItem("isLogin");
      let loginstauts;
      if (isLogin == true || isLogin == "true") {
        loginstauts = "2";
      } else {
        loginstauts = "3";
      }
      var str = location.href;
      str = str.match(/(\S*)\#/)[1];
      let localUrl = str + "#/";
      let param = {
        route: localUrl + "login",
        loginstating: loginstauts,
        searchUrl: localUrl + "search"
      };

      if (window.hasOwnProperty("webkit")) {
        window.webkit.messageHandlers.openWebView.postMessage(param);
      } else {
      }
    },
    changeactive(typeId) {
      if (typeId == "222") {
        this.allData();
        this.typevalue = "";
        this.tansitionflag = true;
        this.tansitionflag1 = false;
        // location.reload() 
        // return 
      } else {
        this.tansitionflag = false;
        this.tansitionflag1 = false;
        this.currcomponent1 = "";
        this.typevalue = "";
        this.tansitionflag1 = true;
        if (typeId == "111") {
          this.currcomponent1 = "zhuanti";
        } else if (typeId == "333") {
          this.currcomponent1 = "vadioNew";
        } else if (typeId == "444") {
          this.currcomponent1 = "vadioNew";
        } else {
          this.currcomponent1 = "newslist";
        }
        this.typevalue = typeId;
      }
    },
    changeactivewang(v, i) {
      this.active = "tab" + i;
      if (i == 1) {
        this.typevalue = "";
        this.tansitionflag = true;
        this.tansitionflag1 = false;
      } else {
        this.tansitionflag = false;
        this.tansitionflag1 = false;
        this.currcomponent1 = "";
        this.typevalue = "";
        this.$nextTick(() => {
          this.tansitionflag1 = true;
          // if (i == 2) {
          //   this.currcomponent1 = "vadioNew";
          // } else 
          if (i == 0) {
             this.currcomponent1 = "zhuanti";
          } else {
            this.currcomponent1 = "newslist";
          }
          this.typevalue = v.id;
        });
      }
    },
    gotoSearch(pathname) {
      this.$router.push({
        name: "search",
        params: {
          pathname
        }
      });
    },
        //  全部热点数据
    async allData() {
      console.log("用户信息"+JSON.stringify(this.$getUser) )
      // if (window.hasOwnProperty("webkit")) {
      //   this.isShow = false;
      // } else {
      //   this.isShow = true;
      // }
      try {
         this.tab =[]
        let params = {};
        let res = await this.$api.all.GETfn(params, "index_hot");
        // res.recommendedUser.length=3;
         res.hotInformation.length=3
        for(let i in res.information){
          res.information[i].date=this.timeago(res.information[i].date*1000)
           if (res.information[i].image_ids.length > 3) {
            res.information[i].image_ids.length = 3;
          }
        }
         for(let i in res.banner){
          res.banner[i].date=this.timeago(res.banner[i].date*1000)
        }
        for(let t in res.hotInformation){
          res.hotInformation[t].date=this.timeago(res.hotInformation[t].date*1000)
        }
        this.allHot=res;
        let param = {
          name: "关注",
          id: 0
        };
        this.tab = this.tab
          .concat(param)
          .concat({
            name: "热点",
            id: 1
          })
          // .concat({
          //   name: "视频新闻",
          //   id: 13
          // })
          .concat(res.menu);
      } catch (e) {
        console.log(e)
      }
    },
    timeago(dateTimeStamp) {
      //dateTimeStamp是一个时间毫秒，注意时间戳是秒的形式，在这个毫秒的基础上除以1000，就是十位数的时间戳。13位数的都是时间毫秒。
      var result;
      var minute = 1000 * 60; //把分，时，天，周，半个月，一个月用毫秒表示
      var hour = minute * 60;
      var day = hour * 24;
      var week = day * 7;
      var halfamonth = day * 15;
      var month = day * 30;
      var now = new Date().getTime(); //获取当前时间毫秒
      var diffValue = now - dateTimeStamp; //时间差

      if (diffValue < 0) {
        return;
      }
      var minC = diffValue / minute; //计算时间差的分，时，天，周，月
      var hourC = diffValue / hour;
      var dayC = diffValue / day;
      // var weekC = diffValue / week;
      // var monthC = diffValue / month;
      // if (monthC >= 1 && monthC <= 3) {
      // 	result = " " + parseInt(monthC) + "月前";
      // } else if (weekC >= 1 && weekC <= 3) {
      // 	result = " " + parseInt(weekC) + "周前";
      // } else
      if (dayC >= 1 && dayC <= 3) {
        result = " " + parseInt(dayC) + "天前";
      } else if (hourC >= 1 && hourC <= 23) {
        result = " " + parseInt(hourC) + "小时前";
      } else if (minC >= 1 && minC <= 59) {
        result = " " + parseInt(minC) + "分钟前";
      } else if (diffValue >= 0 && diffValue <= minute) {
        result = "刚刚";
      } else {
        var datetime = new Date();
        datetime.setTime(dateTimeStamp);
        var Nyear = datetime.getFullYear();
        var Nmonth =
          datetime.getMonth() + 1 < 10
            ? "0" + (datetime.getMonth() + 1)
            : datetime.getMonth() + 1;
        var Ndate =
          datetime.getDate() < 10
            ? "0" + datetime.getDate()
            : datetime.getDate();
        var Nhour =
          datetime.getHours() < 10
            ? "0" + datetime.getHours()
            : datetime.getHours();
        var Nminute =
          datetime.getMinutes() < 10
            ? "0" + datetime.getMinutes()
            : datetime.getMinutes();
        var Nsecond =
          datetime.getSeconds() < 10
            ? "0" + datetime.getSeconds()
            : datetime.getSeconds();
        result = Nyear + "-" + Nmonth + "-" + Ndate;
      }
      return result;
    },
  }
};
</script>



<style lang="css" scoped>
/* .mint-tab-container-wrap,
.mint-tab-container,
.page-tab-container {
  min-height: 533px !important;
  height: 533px !important;
} */

/* .mint-tab-container-item {
  min-height: 533px !important;
  height: 533px !important;
} */

/* .newsCover {
  min-height: 533px !important;
  height: 533px !important;
} */

.mainContent {
  min-height: calc(100vh- 134px) !important;
  height: calc(100vh- 134px) !important;
 
}

.seamless-warp {
  height: 40px;
  width: 375px;
  overflow: hidden;
}
.comm-name p {
  /* height: 40px; */
  /* line-height: 40px; */
  margin: 15px 12px 10px 12px;
  font-weight: 600;
}

.hh-face-img img {
  min-height: 200px;
  max-height: 250px;
}
.swiper-slide {
  /* padding: 0px 8px; */
}
.homeheader {
  min-height: 44px;
  height: 44px;
  line-height: 44px;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 7px 0px;
}
.homeheader img {
  display: inline-block;
  width: 40px;
  height: 40px;
  margin: 0px 7px;
}
.homeheader input {
  flex: 1;
  margin-left: 10px;
  margin-right: 16px;
  height: 30px;
  line-height: 30px;

  text-decoration: none;
  border-radius: 20px;
 
  text-indent: 2em;
  font-size: 12px;
  background:#eee
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


.c-part-block {
  margin-top: 8px;
}

.comm-body img {
  width: 100%;
}
.comm-keyword {
  display: flex;
  justify-content: space-between;
  /* padding: 15px; */
  /* font-size: 14px; */
  margin: 0px 12px;
}
/* tab*/

.nav {
  width: 380px;
  /* width: 100%; */
  min-height: 40px;
  line-height: 40px;

  /* padding: 5px 0px 0px 0px; */
}

.nav .swiper-container {
  border-bottom: 1px solid #eee;
}

>>> .nav-swiper-slide {
  /* display: inline-block; */
  height: 40px;
  font-size: 14px;
  color: #222;
  /* min-width: 50px !important; */
  /* width: 50px !important; */
  background: #fff;
  text-align: center;
}

.nav-longer {
  min-width: 75px !important;
  max-width: 105px !important;
}

.nav-biglonger {
  min-width: 103px !important;
  max-width: 110px !important;
}

.nav-short {
  min-width: 45px !important;
  max-width: 55px !important;
}

.nav-li {
  display: inline-block;
  height: 40px;
  font-size: 14px;
  color: #222;
  /* min-width: 50px !important; */
  background: #fff;
  text-align: center;
  width: 100%;
}

.nav-li.active {
  font-size: 16px;
  color: #4a8dff;
  min-width: 35px;
  width: 100%;
  max-width: 80px !important;
}


input[type="button" i],
input[type="submit" i],
input[type="reset" i],
input[type="file" i]::-webkit-file-upload-button,
button {
  border-color: #ffffff;
}

.swiperbox .swiper-container {
  margin-top: 30px !important;
}
</style>


