            <h2>中国经济导报社简介</h2>
            <p>
              中国经济导报社是经中央机构编制委员会批准成立的国家发展和改革委员会直属司(局)级事业单位，《中国经济导报》是经国家新闻出版广电总局(原国家新闻出版署)批准出版。1995年创办，经过20年的发展，《中国经济导报》的发行量和国内外社会上的影响力不断扩大，被专家和读者评价为国内含金量最高的经济类报纸之一。
              <br />中国经济导报社下设办公室、人事部、通联部、出版部、广告策划部、新闻中心、新闻专题部、新媒体与发展部、经济研究部、《中国战略新兴产业》编辑部等十个部门，报社拥有《中国战略新兴产业》杂志社、中国发展网、北京计鹏信息咨询公司三家下属机构，在全国建立了驻地方记者站和工作机构。
              <br />中国经济导报社主要业务工作：
              <br />一、《中国经济导报》
              <br />《中国经济导报》是国家发展改革委主管，中国经济导报社主办的全国性经济类报纸。她依托国家宏观调控部门特别是国家发展和改革委员会的信息、网络优势，以及与社会各界的广泛联系，力求在国民经济和社会发展的战略规划、总量平衡、结构调整、地区布局、经济政策、重大建设项目投资、价格调控、区域经济、经济运行调节、经济体制改革、重点行业部门、企业经营发展等方面为中外读者提供权威性、前瞻性、实用型的独家信息，成为联系国家宏观调控部门与微观经济主体的桥梁和纽带。
              <br />《中国经济导报》是国家指定的发布依法必须招标的投资建设项目招标公告的报纸;是国家指定的发布企业债券公告的报纸;是国家指定发布价格公报的报纸。是为全国人大政协两会、夏季达沃斯、深圳高技术交易会等重要会议提供专刊、特刊的指定报纸。
              <br />报社举办经济发展高端论坛、峰会及亚洲品牌盛典等大会，承办国家经济发展规划、丝绸之路经济带和21世纪海上丝绸之路、长江经济带、京津冀协同发展、全国应对气候变化、循环经济节能减排等大型活动及展览。
              <br />二、《中国战略新兴产业》杂志
              <br />《中国战略新兴产业》杂志是经国家新闻出版广电总局批复，由国家发改委主管、中国经济导报社主办的周刊，是国家宏观经济调控部门根据经济发展战略目标、产业布局、实施高新技术产业化和节能减排重要对策而进行导向性、系统性、权威性的宣传报道，开展有效便利的信息服务、进行交流示范的重要手段。它是我国第一份主要面向国内外战略性新兴产业市场，以新闻报道、信息交流、咨询服务、论坛活动、搭建市场评级认证为核心内容的期刊，具有宏观政府部门政策指导性、实用服务性及学术探讨性。通过权威、及时、准确、周到的全过程服务，为各级政府部门制定产业发展规划提供依据、为产业、企业和行业机构、学界提供全方位的政策指导、产品技术信息，为国家投资项目建设单位搭建平台和桥梁，为企业产品占领市场进行投资决策和发展指引方向、当好参谋。
              <br />三、中国发展网(www.chinadevelopment.com.cn)
              <br />中国发展网(www.chinadevelopment.com.cn)是2012年5月经国家互联网信息办公室批准，国家发展改革委主管、中国经济导报社主办的新闻信息服务网站。从事互联网新闻信息服务业务，刊登播发新闻信息，提供电子公告等互联网新闻信息服务。它是全国发展改革系统、物价等相关系统唯一具有新闻资质的网站，将基于国务院新闻办的批准，播发新闻信息，提供电子公告等新闻信息服务;基于国家发改委所赋予导报社职能，提供更符合新闻传播规律的全国发展改革及物价系统的新闻信息发布;基于国家发改委所赋予的招标公告、企业债券公告和价格公告等特许政务信息发布职能，建设相关信息数据库及与此相关的行业信息和企业信息等数据库，提供更加符合互联网发展规律，也更加利于政府监管的信息收集和服务平台。中国发展网将建成新闻性与服务性相结合的专业化的网络新闻信息平台，成为全国发展改革委系统，包括国家能源局、国家粮食局、物质储备局信息交流、咨询服务的窗口，竭诚为部门、行业、产业、企业客户提供网络及移动互联网正面、全面、权威的信息服务。
              <br />中国经济导报社围绕国家发展改革委中心工作，坚持正确舆论导向，全面呈现国家经济社会发展的方针、政策和措施，深入报道国内外经济形势、重大活动、思想理论及评论观点，围绕经济社会发展中的亮点、热点、疑点、难点问题，及时、准确、多角度、全方位进行新闻报道，为各级政府机构、企事业单位、经济学界、教学科研单位及外国驻华机构、外商提供高质量的信息服务，为部门行业、战略新兴产业、国有企业、民营企业，搭建可靠、高效的交流信息、沟通供求的互动平台，对国家的政策进行深度解读，与企业发展的需求紧密结合，赢得更多的合作与发展机会。
            </p>
