<template>
  <div class="mainCover">
    <HeaderBar :needb="needb" :tit="titleName"></HeaderBar>
    <div class="mainContent">
      <vue-scroll
        ref="scroll"
        :ops="opsconfig"
        @refresh-start="handleRS"
        @load-before-deactivate="handleLBD"
        @refresh-before-deactivate="handleRBD"
        @load-start="handleLoadStart"
      >
        <div class="coml">
          <div class="commune" v-for="(i,d) in asklist" :key="d" @click="goaskdetail(i)">
            <div class="tit">
              <p class="titname">
                <span class="titn">{{i.title}}</span>
                <span v-if="i.status=='1'" style="color:rgb(240, 164, 0)">(待审核)</span>
                <span v-else-if="i.status=='2'" style="color:rgb(0, 160, 35)">(已通过)</span>
                <span v-else-if="i.status=='3'" style="color:rgb(200, 0, 0)">(未通过)</span>
              </p>
              <p class="content">{{i.content}}</p>
              <div class="commune_b">
                <span>来自:{{i.commune_name}}</span>
                <!-- <span>浏览量:{{i.browse_number}}</span> -->
                <!-- <span>评论:{{i.comment_number}}</span> -->
                <span>{{i.create_time}}</span>
              </div>
            </div>
            <img :src="i.image_url" alt />
          </div>
        </div>
        <div class="nodata" v-if="asklist.length == 0">暂无数据</div>
      </vue-scroll>
    </div>
  </div>
</template>

<script>
import { ScrollConfig } from "common/data.js";
import { Toast } from "mint-ui";
export default {
  name: "examination",
  props: {},
  data() {
    return {
      opsconfig: ScrollConfig.opsconfig,
      rdata: [],
      asklist: [],
      titleName: "审公告",
      needb: true,
      iddata: []
    };
  },
  computed: {},

  activated() {
    this.getParams();
    this.exlist();
  },
  mounted() {},
  watch: {
    $route: {
      handler(route) {
        console.log("路由" + route.name);
        if (route.name != "examination") {
          this.asklist = [];
        }
      }
    },
    asklist: function() {
      this.$nextTick(function() {
        document.querySelector(".coml").click();
      });
    }
  },
  methods: {
    getParams() {
      var routerdata = window.sessionStorage.getItem("commitydata");
      this.rdata = JSON.parse(routerdata);
    },
    //
    async setbrowsing(item) {
      try {
        let params = {
          ask_id: item.id
        };
        let res = await this.$api.all.POSTfn(params, "browsing");
        console.log("设置浏览量" + JSON.stringify(res));
      } catch (e) {
        console.log(e);
      }
    },
    goaskdetail(item) {
      window.sessionStorage.setItem("askdata", JSON.stringify(item));

      this.setbrowsing(item);
      this.$router.push({
        name: "commune_f"
        // params: {
        //   data: item
        // }
      });
      this.asklist = [];
    },
    async exlist(direct, hot) {
      let params = {
        communeid: this.rdata.id,
        count: 10,
        target_id: hot,
        direction: direct
      };
      try {
        let res = await this.$api.all.GETfn(params, "ask_list");
        console.log(res);
        if (direct == "litter") {
          this.asklist = this.asklist.concat(res.result.data);
        } else {
          this.asklist = res.result.data.concat(this.asklist);
        }
        this.iddata = res.result;
      } catch (e) {
        console.log(e);
      }
    },
    handleRS(vsInstance, refreshDom, done) {
      // alert("这是执行下拉刷新开始做啥事");
      done();
    },
    handleLoadStart(vm, dom, done) {
      setTimeout(() => {
        // alert("这是执行下拉加载之前需要执行什么操作");

        done();
      }, 600);
    },
    handleLBD(vm, loadDom, done) {
      let direction = "litter";
      let hot = this.iddata.min_id;
      if (this.iddata.more == 0) {
        //  this.noSscrollData = true;
        Toast("无更多数据");
        done();
      } else {
        this.exlist(direction, hot);
        done();
      }
    },
    handleRBD(vm, loadDom, done) {
      setTimeout(() => {
        // alert("这是执行下拉加载完毕需要执行的事件");
        done();
      }, 500);
    }
  },
  components: {}
};
</script>

<style scoped lang="css">
>>> .__vuescroll {
  padding: 0px 12px !important;
}

.commune {
  border-bottom: 1px solid #eee;
  min-height: 86px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.tit {
  width: 251px;
  padding: 5px 5px 5px 0px;
}

.titname {
  color: #333333;
  font-size: 14px;
  font-weight: 600;
  width: 100%;
  height: 22px;
  /* overflow: hidden;
text-overflow: ellipsis;
white-space: nowrap; */
}
.titn {
  display: inline-block;
  width: 180px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.content {
  font-size: 12px;
  color: #666;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  display: -webkit-box !important;
  color: #666666;
  width: 100%;
  height: 34px;
}

.commune_b {
  font-size: 12px;
  color: #bbbbbb;
  min-height: 20px;
  line-height: 20px;
}

.commune_b span {
  margin-right: 5px;
}

.commune img {
  /* height: 70px; */
  min-height: 60px;
  width: 100px;
  border-radius: 3px;
  border: none;
}

.nodata {
  text-align: center;
  padding-top: 30px;
  color: #666666;
}
</style>



