
//其他公社详情

<template>
  <div class="mainCover">
    <HeaderBar :needb="needb" :tit="asdata.commune_name" v-if="showHeaderflag"></HeaderBar>
    <div class="details_th">
      <img :src="asdata.image_url?asdata.image_url:'../../../static/imgs/shemorentu.jpg'" alt />
      <!-- <img :src="asdata.image_url" alt /> -->
    </div>
    <div class="datails_tt">
      <button class="datails_qw" @click.stop="iscollect()">{{collect}}</button>
      <button class="datails_qe" @click="gocommune()">评论</button>
      <button class v-if="asdata.create_commune_user_id==$getUser.id" @click="popaudui()">{{audit}}</button>
      <button class v-if="asdata.create_commune_user_id==$getUser.id" @click="setdu()">{{du}}</button>
      <button
        class="hot"
        v-if="asdata.create_commune_user_id==$getUser.id"
        @click="sethOrc($event)"
      >{{hot}}</button>
      <button
        class="rec"
        v-if="asdata.create_commune_user_id==$getUser.id"
        @click="sethOrc($event)"
      >{{rec}}</button>
    </div>
    <div class="mainContent">
        <div class="datails_tm">
          <div class="datails_qa" @click="privateinfo(asdata.user_id)">
            <img :src="asdata.avatar?asdata.avatar:'../../../static/imgs/morentu.jpg'" alt />
            <span style="margin-left:5px;">{{asdata.user_nickname}}</span>
          </div>
          <p class="tit">{{asdata.title}}</p>
          <p v-if="asdata.target_caption">{{asdata.target_caption}}</p>
          <p v-else>{{asdata.content?asdata.content:asdata.text}}</p>
        </div>
        <div class="c-part-block"></div>
        <div class="datails_te" v-if="teamlist.length!=0">
          <p class="swiperTitle c-font-special">
            <span class="c-title-vline"></span> 组队列表
          </p>
          <div class="datails_qr" v-for="(i,d) in teamlist" :key="d" @click="goteamdetail(i)">
            <div class="tt">
              <img :src="i.image_url?i.image_url:'../../../static/imgs/morentu.jpg'" alt />
              <span>{{i.name}}</span>
            </div>

            <span
              class="joining max"
              v-if="parseInt(i.user_count) >= parseInt(i.num)"
              style="color: red;"
            >(满)</span>
            <span class="joining" v-else-if="i.is_join =='0'" @click.stop="isjoint(i)">+</span>
            <span class="joining" v-else-if="i.is_join=='1'" @click.stop="isjoint(i)">-</span>
          </div>
        </div>
        <div class="datails_tr" @click="gocreateteam()">
          <div>发起组队</div>
        </div>
        <div class="c-part-block"></div>
        <div class="datails_tp">
          <ul v-if="replylist.length!=0">
            <p class="swiperTitle c-font-special">
              <span class="c-title-vline"></span> 评论列表
            </p>
            <li v-for="(i,n) in replylist" :key="n" class="rbox">
              <div></div>
              <div class="datails_qz">
                <img
                  :src="i.user_avatar?i.user_avatar:'../../../static/imgs/activity.png'"
                  alt
                  @click.stop="privateinfo(i.user_id)"
                />
                <div class="datails_tx">
                  <div class="datails_tx_name">{{i.user_name}}</div>
                  <div class="datails_tx_date">{{i.time}}</div>
                </div>
                <div class="caina" @click.stop.once="setcaina(i,$event)">{{i.iscai}}</div>
              </div>
              <p class="replytext">{{i.text}}</p>
            </li>
            <div @click="more()" class="more" v-if="iddata.more==1">点击加载更多</div>
          </ul>
          <p v-else class="nodata">----暂无评论----</p>
        </div>
      </div>
    </div>
  
</template>
<script>
import { MessageBox } from "mint-ui";
import { Toast } from "mint-ui";
import { ScrollConfig } from "common/data.js";
// import { Toast } from "mint-ui";
export default {
  name: "commune_th",
  props: {},
  data() {
    return {
      opsconfig: ScrollConfig.opsconfig,
      refresh: 1,
      noSscrollData: false,
      triggerType: "load",
      showHeaderflag: true,
      needb: true,
      needm: false,
      search: "",
      comment_create: [],
      asdata: [],
      create: true,
      audit: "通过",
      du: "监督",
      hot: "最热",
      rec: "推介",
      iddata: [],
      replylist: [],
      counthas: "",
      teamlist: [],
      showcreate: true,
      iscombig: true,
      collect: "关注",
      isc: [],
      rdata: [],
      isj: ""
    };
  },
  computed: {},
  activated() {
    this.getParams();
    this.search = require("images/search.png");
    this.getpinglun();
    this.getteamlist();
    this.issupervies();
    this.ishot();
    this.iscaina();
    this.isjoin();
    this.iscollects();
    // this.iscommbig();
  },
  mounted() {},
  watch: {
    $route: {
      handler(route) {
        // console.log("路由" + route.name);

        if (route.name != "commune_f") {
          this.replylist = [];
        }
      }
    }
  },
  methods: {
    getParams() {
      let self = this;

      let routerdata = window.sessionStorage.getItem("askdata");

      this.asdata = JSON.parse(routerdata);
      this.showHeaderflag = false;
      this.$nextTick(() => {
        this.showHeaderflag = true;
      });
      let commdata = window.sessionStorage.getItem("commitydata");
      console.log("luyou陆游的" + routerdata);
      // console.log("路由的数据" + commdata);
      this.rdata = JSON.parse(commdata);

      if (this.asdata.status == 1) {
        this.audit = "待审核";
      } else if (this.asdata.status == 2) {
        this.audit = "已通过";
      } else if (this.asdata.status == 3) {
        this.audit = "未通过";
      }
    },
    // 设置加入组队
    isjoint(i, e) {
      // console.log(JSON.stringify(i));
      let params = {
        teamid: i.team_id,
        // askid: this.asdata.id?this.asdata.id:this.asdata.ask_id
        askid: ""
      };
      if (this.asdata.id) {
        params.askid = this.asdata.id;
      } else if (this.asdata.ask_id) {
        params.askid = this.asdata.ask_id;
      } else if (this.asdata.target) {
        params.askid = this.asdata.target;
      } else {
        params.askid = this.asdata.target_id;
      }
      if (i.is_join == 0) {
        this.addjoint(params);
        console.log("加入");
      } else{
        this.deljoint(params);
      }
    },
    // 加入
    async addjoint(params) {
      try {
        let res = await this.$api.all.POSTfn(params, "join_team");
        console.log(res);
        if (res.result == true) {
          console.log("加入");
        } else {
          Toast(res.result);
        }
         this.getteamlist();
      } catch (e) {
        console.log(e);
      }
    },
    // 离开
    async deljoint(params) {
      try {
        let res = await this.$api.all.POSTfn(params, "quit_team");
        console.log(res);
        if (res.result == "ok") {
          console.log("离开");
        }
         this.getteamlist();
      } catch (e) {
        console.log(e);
      }
    },
    // 设置关注
    iscollect() {
      let params = {
        // target: this.asdata.id?this.asdata.id:this.asdata.ask_id
        target: ""
      };
      if (this.asdata.id) {
        params.target = this.asdata.id;
      } else if (this.asdata.ask_id) {
        params.target = this.asdata.ask_id;
      } else if (this.asdata.target) {
        params.target = this.asdata.target;
      } else {
        params.target = this.asdata.target_id;
      }
      if (this.collect == "关注") {
        this.addcollect(params);
      } else {
        this.delcollect(params);
      }
    },
    //是否采纳
    async iscollects() {
      try {
        let params = {
          // askid: this.asdata.id?this.asdata.id:this.asdata.ask_id
          target: ""
        };
        if (this.asdata.id) {
          params.target = this.asdata.id;
        } else if (this.asdata.ask_id) {
          params.target = this.asdata.ask_id;
        } else if (this.asdata.target) {
          params.target = this.asdata.target;
        } else {
          params.target = this.asdata.target_id;
        }
        let res = await this.$api.all.GETfn(params, "collect_has");
        console.log("关注" + JSON.stringify(res));
        if (res.data == "1") {
          this.collect = "已关注";
        } else {
          this.collect = "关注";
        }
      } catch (e) {
        console.log(e);
      }
    },
    // 关注公告
    async addcollect(params) {
      try {
        let res = await this.$api.all.POSTfn(params, "collect");
        console.log(res);
        if (res.result == "ok") {
          this.collect = "已关注";
        }
      } catch (e) {
        console.log(e);
      }
    },
    // 关注公告
    async delcollect(params) {
      try {
        let res = await this.$api.all.POSTfn(params, "collect_delete");
        console.log(res);
        if (res.result == "ok") {
          this.collect = "关注";
        }
      } catch (e) {
        console.log(e);
      }
    },
    // 去评论页
    gocommune() {
      this.isjoing("commity_commune");
      // this.$router.push({
      //   name: "commity_commune"
      // });
    },
    //去个人资料页
    privateinfo(v) {
      window.sessionStorage.setItem("S-person", v);

      this.$router.push({
        name: "commune_information"
      });
    },
    goteamdetail(item) {
      window.sessionStorage.setItem("teamdetail", JSON.stringify(item));
      this.$router.push({
        name: "commune_personal"
      });
    },
    // // 是否是社长
    // async iscommbig() {
    //   try {
    //     let params = {};
    //     let res = await this.$api.all.GETfn(params, "commune_creator");
    //     if (res.result == false) {
    //       this.iscombig = false;
    //     } else {
    //       this.iscombig = true;
    //     }
    //   } catch (e) {
    //     console.log(e);
    //   }
    // },
    popaudui() {
      MessageBox({
        $type: "prompt",
        title: "PPP",
        message: "是否通过",
        showCancelButton: true,
        confirmButtonText: "通过",
        cancelButtonText: "不通过",
        showInput: true
      })
        .then(({ value, action }) => {
          let data = {
            id: this.asdata.id ? this.asdata.id : this.asdata.ask_id,
            type: 1,
            communeid: this.asdata.commune_id
          };
          if (action == "confirm") {
            data.status = 2;
            data.text = value;
            this.seteximne(data);
            this.audit = "已通过";
            console.log("通过");
          }
        })
        .catch(err => {
          let data = {
            // id: this.asdata.id?this.asdata.id:this.asdata.ask_id,
            id: "",
            type: 1,
            communeid: this.asdata.commune_id,
            text: "不通过"
          };
          if (this.asdata.id) {
            data.id = this.asdata.id;
          } else if (this.asdata.ask_id) {
            data.id = this.asdata.ask_id;
          } else {
            data.id = this.asdata.target_id;
          }
          if (err == "cancel") {
            data.status = 3;
            //  data.text=value
            this.seteximne(data);
            //取消的回调
            this.audit = "未通过";
            console.log("不通过");
          }
        });
    },
    // 审核
    async seteximne(params) {
      try {
        // console.log(this.$api.all.eximane)
        let res = await this.$api.all.POSTfn(params, "examine");
        console.log(res);
      } catch (e) {
        console.log(e);
      }
    },

    //是否监督了
    async issupervies() {
      try {
        let params = {
          // id: this.asdata.id?this.asdata.id:this.asdata.ask_id
          id: ""
        };
        if (this.asdata.id) {
          params.id = this.asdata.id;
        } else if (this.asdata.ask_id) {
          params.id = this.asdata.ask_id;
        } else if (this.asdata.target) {
          params.id = this.asdata.target;
        } else {
          params.id = this.asdata.target_id;
        }
        let res = await this.$api.all.GETfn(params, "is_supervise");
        console.log(res);
        if (res.result == false) {
          this.du = "监督";
        } else {
          this.du = "已监督";
        }
      } catch (e) {
        console.log(e);
      }
    },
    // 设置监督
    setdu() {
      if (this.du == "已监督") {
        return;
      } else {
        console.log("监督");
        this.adddu();
      }
    },
    //去监督
    async adddu() {
      let data = {
        // askid: this.asdata.id?this.asdata.id:this.asdata.ask_id
        askid: ""
      };
      if (this.asdata.id) {
        data.askid = this.asdata.id;
      } else if (this.asdata.ask_id) {
        data.askid = this.asdata.ask_id;
      } else if (this.asdata.target) {
        data.askid = this.asdata.target;
      } else {
        data.askid = this.asdata.target_id;
      }
      try {
        let res = await this.$api.all.POSTfn(data, "sn_ask");
        if (res.result == "ok") {
          this.du = "已监督";
        }
        console.log(res);
      } catch (e) {
        console.log(e);
      }
    },

    // 是否推介和最热
    async ishot() {
      try {
        let params = {
          // askid: this.asdata.id?this.asdata.id:this.asdata.ask_id
          askid: ""
        };
        if (this.asdata.id) {
          params.askid = this.asdata.id;
        } else if (this.asdata.ask_id) {
          params.askid = this.asdata.ask_id;
        } else if (this.asdata.target) {
          params.askid = this.asdata.target;
        } else {
          params.askid = this.asdata.target_id;
        }
        let res = await this.$api.all.GETfn(params, "hot_rec");
        console.log(res);
        if (res.result.is_hot == 1) {
          this.hot = "最热";
        } else {
          this.hot = "未最热";
        }
        if (res.result.is_recommend == 1) {
          this.rec = "推介";
        } else {
          this.rec = "未推介";
        }
      } catch (e) {
        console.log(e);
      }
    },
    //设置推荐和最热
    sethOrc(e) {
      var target = e.target.className;
      let data = {
        // askid: this.asdata.id?this.asdata.id:this.asdata.ask_id
        askid: ""
      };
      if (this.asdata.id) {
        data.askid = this.asdata.id;
      } else if (this.asdata.ask_id) {
        data.askid = this.asdata.ask_id;
      } else if (this.asdata.target) {
        params.askid = this.asdata.target;
      } else {
        data.askid = this.asdata.target_id;
      }
      if (this.rec == "未推介" || this.rec == "未最热") {
        data.op = "set";
      } else {
        data.op = "cancel";
      }
      if (target == "hot") {
        data.extendtag = this.hot;
        data.extendtype = "hot";
        this.addhot(data);
        if (this.hot == "未最热") {
          this.hot = "最热";
        } else if (this.hot == "最热") {
          this.hot = "未最热";
        }
        Toast("成功设置" + this.hot);
      } else if (target == "rec") {
        data.extendtag = this.rec;
        data.extendtype = "rec";
        this.addhot(data);
        if (this.rec == "未推介") {
          this.rec = "推介";
        } else if (this.rec == "推介") {
          this.rec = "未推介";
        }
        Toast("成功设置" + this.rec);
      }
    },
    //监督最热
    async addhot(data, ev) {
      try {
        let res = await this.$api.all.POSTfn(data, "commune_edit");
        if (res.result == "ok") {
        }
        console.log(res);
      } catch (e) {
        console.log(e);
      }
    },

    more() {
      let dictir = "litter";
      let hot = this.iddata.min_id;
      if (this.iddata.more == 0) {
        Toast("无更多数据");
      } else {
        this.getpinglun(dictir, hot);
      }
    },

    // 评论列表
    async getpinglun(direct, hot) {
      // debugger;
      try {
        let params = {
          // target: this.asdata.id?this.asdata.id:this.asdata.ask_id,
          target: "",
          // ask_id:"",
          count: 5,
          target_id: hot,
          direction: direct
        };
        if (this.asdata.id) {
          params.target = this.asdata.id;
        } else if (this.asdata.ask_id) {
          params.target = this.asdata.ask_id;
        } else if (this.asdata.target) {
          params.target = this.asdata.target;
        } else {
          params.target = this.asdata.target_id;
        }
        let res = await this.$api.all.GETfn(params, "reply_list");
        console.log(res);
        res.result.data.forEach((item, index) => {
          item.iscai = "采纳此评论";
        });
        this.replylist = this.replylist.concat(res.result.data);
        this.iddata = res.result;
      } catch (e) {
        console.log(e);
      }
    },
    //是否采纳
    async iscaina() {
      try {
        let params = {
          // askid: this.asdata.id?this.asdata.id:this.asdata.ask_id
          askid: ""
        };
        if (this.asdata.id) {
          params.askid = this.asdata.id;
        } else if (this.asdata.ask_id) {
          params.askid = this.asdata.ask_id;
        } else if (this.asdata.target) {
          params.askid = this.asdata.target;
        } else {
          params.askid = this.asdata.target_id;
        }
        let res = await this.$api.all.GETfn(params, "isaccept");
        console.log("采纳" + JSON.stringify(res));
        this.replylist.forEach((item, index) => {
          if (item.comment_id != res.result.comment_id) {
            this.$set(item, "iscai", "采纳此评论");
          } else {
            this.$set(item, "iscai", "已采纳");
          }
        });
        this.isc = res.result;
      } catch (e) {
        console.log(e);
      }
    },
    // 设置采纳
    async setcaina(i, e) {
      this.iscaina();
      if (this.isc.length != 0) {
        Toast("只能采纳一条哦");
        return;
      }
      let data = {
        ask_id: "",
        // ask_id: this.asdata.id?this.asdata.id:this.asdata.ask_id,
        uid: this.$getUser.id,
        aid: i.comment_id,
        auid: i.user_id
      };
      if (this.asdata.id) {
        data.ask_id = this.asdata.id;
      } else if (this.asdata.ask_id) {
        data.ask_id = this.asdata.ask_id;
      } else if (this.asdata.target) {
        data.ask_id = this.asdata.target;
      } else {
        data.ask_id = this.asdata.target_id;
      }
      // debugger;
      try {
        let res = await this.$api.all.POSTfn(data, "accept");
        if (res.result == "ok") {
          e.target.innerText = "已采纳";
        }
        console.log(res);
      } catch (e) {
        console.log(e);
      }
    },

    // 组队列表
    async getteamlist() {
      try {
        let params = {
          ask_id: "",
          // ask_id: this.asdata.id?this.asdata.id:this.asdata.ask_id,
          count: 10
        };
        if (this.asdata.id) {
          params.ask_id = this.asdata.id;
        } else if (this.asdata.ask_id) {
          params.ask_id = this.asdata.ask_id;
        } else if (this.asdata.target) {
          params.ask_id = this.asdata.target;
        } else {
          params.ask_id = this.asdata.target_id;
        }
        let res = await this.$api.all.GETfn(params, "project_team_list");
        console.log("team" + JSON.stringify(res));
        this.teamlist = res.result;
        for (let i in res.result) {
          res.result[i].user_id = this.$getUser.id;
          this.showcreate = false;
        }
      } catch (e) {}
    },
    //是否加入公社
    async isjoin() {
      try {
        let params = {
          communeid: this.rdata.id
        };
        let res = await this.$api.all.GETfn(params, "isjoin");
        this.isj = res.result;
      } catch (e) {
        console.log(e);
      }
    },
    isjoing(r) {
      if (this.isj == 1) {
        this.$router.push({
          path: r
        });
      } else {
        Toast("请先加入公社");
        return;
      }
    },
    gocreateteam() {
      this.isjoing("/commune_team");
    }
  },
  components: {}
};
</script>
<style scoped>
.mint-header {
  position: fixed;
  top: 0;
}
.rbox {
  padding: 10px 0 0 0;
  position: relative;
}
.pass {
  position: absolute;
}
.nopass {
  position: absolute;
  right: 0;
}
.details_th {
  height: 190px;
  width: 100%;
  padding: 10px;
  margin-top: 40px;
}
.details_th img {
  height: 100%;
  width: 100%;
  border-radius: 3px;
}
.datails_tt {
  /* border-bottom: 1px solid #d8d8d8; */
  padding: 0px 12px;
  /* margin: 14px 10px; */
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  /* height: 44px;
  min-height: 44px; */
}

.datails_tt button {
  height: 34px;
  width: 48%;
  border-radius: 3px;
  background: #4a8dff;
  font-size: 14px;
  color: #fff;
  /* margin: 10px 6px; */
  margin-bottom: 8px;
}

.datails_tm {
  min-height: 100px;
  margin: 5px 12px 16px 12px;
}

.datails_tm .datails_qa {
  height: 50px;
  line-height: 50px;
