<template>
  <div class="mainCover">
    <div class="changeCover">
      <div class="headerCover">
        <HeaderBar :needm="needm" :needb="needb" :tit="titleName" v-show="isShow"></HeaderBar>
      </div>

      <div class="topUserInfo">
        <div class="headerPortrait">
          <ul class="headerP-ul">
            <img v-lazy="alldata.avatar" class="page-default-image" :src="alldata.avatar" />
            <div class="accountInfo">
              <p class="name-class">{{alldata.name}}</p>
              <div class="account-meta-li">
                <p>
                  <span class="title">
                    {{arr[item.type -1]}}：
                    <span class="content">{{item.name || '无'}}</span>
                  </span>
                </p>
              </div>
            </div>
          </ul>
          <div class="contactbutton">
            <button @click.prevent="toPageChatRoom">
              <span>+</span> 添加好友
            </button>
          </div>
        </div>
        <div class="sign">{{alldata.sign}}</div>
      </div>
    </div>
    <div class="mainContent">
      <div class="page-navbar">
        <mt-navbar class="page-part" v-model="selected">
          <mt-tab-item id="0">文章</mt-tab-item>
          <mt-tab-item id="1">视频</mt-tab-item>
          <mt-tab-item id="2">话题</mt-tab-item>
          <mt-tab-item id="3">项目</mt-tab-item>
        </mt-navbar>
        <mt-tab-container class="page-content" v-model="selected">
          <components :is="currcomponent"></components>
        </mt-tab-container>
      </div>
    </div>
  </div>
</template>

<script>
const AddInProjects = () =>
  import(
    /* webpackChunkName: 'ImportFuncDemo' */ "pages/find/addInProjects.vue"
  ); //参与项目
const BigVAttention = () =>
  import(
    /* webpackChunkName: 'ImportFuncDemo' */ "pages/find/bigVAttention.vue"
  ); //大V关注
const BigVreview = () =>
  import(/* webpackChunkName: 'ImportFuncDemo' */ "pages/find/bigVreview.vue"); //大V评论
const BigVProblem = () =>
  import(/* webpackChunkName: 'ImportFuncDemo' */ "pages/find/bigVProblem.vue"); //大V问题

export default {
  name: "bigvonline",
  props: {},
  data() {
    return {
      isShow:true,
      rdataid: "",
      titleName: "个人资料",
      needb: true,
      needm: false,
      UserInfo: this.$getUser,
      alldata: [],
      UserParam: {},
      selected: "0",
      currcomponent: "BigVProblem",
      componentarr: [
        "BigVProblem",
        "BigVreview",
        "BigVAttention",
        "AddInProjects"
      ],
      arr: ["行业类型", "所在区域"]
    };
  },
  computed: {},
  activated() {
    this.rdataid = localStorage.getItem("S-person");
    this.getData();
  },
  created() {
    // this.UserParam = JSON.parse(window.sessionStorage.getItem("S-itemv"));
    // this.getData();
  },
  mounted() {},
  watch: {
    selected(newv, oldv) {
      this.currcomponent = this.componentarr[newv];
    }
  },
  methods: {
    toPageChatRoom() {
      this.$toast("功能暂未开放,敬请期待");
    },
    gotoPath(pathname) {
      this.$router.push({
        path: pathname
      });
    },

    async getData() {
       if (window.hasOwnProperty("webkit")) {
        this.isShow = false;
      } else {
        this.isShow = true;
      }
      try {
        let params = {
          s: this.rdataid
        };
        let res = await this.$api.all.GETfn(params, "xiaoxi_find");
        this.alldata = res.user;
      } catch (error) {
        console.log(error);
      }
    },
    gobackfn() {
      this.$router.go(-1); //返回上一页
    }
  },
  components: {
    AddInProjects,
    BigVProblem,
    BigVreview,
    BigVAttention
  },
  destroyed() {
    this.$destory();
  }
};
</script>

<style scoped lang="css">
.mint-header {
  background-color: #fff !important;
  color: #3966a7 !important;
}

.mint-tab-item-label {
  height: 44px;
  line-height: 44px;
  font-size: 16px;
  color: #333;
}
.sign {
  color: #666;
  margin-top: 5px;
  margin-left: 80px
}
.mint-tab-item {
  padding: 0px !important;
  height: 99%;
  color: #333;
  display: flex;
  align-items: center;
  justify-content: center;
}

.mint-navbar .mint-tab-item.is-selected {
  border-bottom: 2px solid #3966a7;
  color: #3966a7 !important;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 600;
}

.is-selected .mint-tab-item-label {
  color: #3966a7 !important;
}

.page-navbar {
  width: 100%;
  height: 100%;
}
.page-part {
  height: 44px;
  line-height: 44px;
  border-bottom: 1px solid #eee;
}

.topUserInfo {
  width: 100%;
  border-top: 1px solid  #f1f2f3;
border-bottom: 15px solid #f5f5f5;padding-bottom: 20px
  /* background: -webkit-linear-gradient(#4a8dff, #fff); Safari 5.1 - 6.0 */
  /* background: -o-linear-gradient(#4a8dff, #fff); Opera 11.1 - 12.0 */
  /* background: -moz-linear-gradient(#4a8dff, #fff); Firefox 3.6 - 15 */
  /* background: linear-gradient(#4a8dff, #fff); 标准的语法 */
}

.headerPortrait {
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 10px;
 
}

.accountInfo {
  font-size: 15px;
  height: 50px;
  margin-left: 10px;
  margin-right: 60px;
}

.headerP-ul {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
}

.headerP-ul li {
  height: 100%;
}

.headerP-li-left {
  width: 60%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  align-items: center;
}
.page-default-image {
  width: 60px;
  height: 60px;
  border-radius: 50%;
}
.name-class {
  font-size: 16px;
}

.headerP-li-right {
  width: 40%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  align-items: center;
  /* padding-left: 40px; */
  padding: 10px 0px 26px;
}

.contactbutton {
  height: 30px;
  line-height: 30px;
}

.contactbutton button {
  height: 30px;
  line-height: 30px;
  width: 100px;
  background: #3966a7;
  color: #fff;
  font-size: 14px;
  border-radius: 50px;
}

.account-ul {
  width: 100%;
  height: 60px;
}

.account-ul li {
  height: 35px;
  line-height: 35px;
  list-style: none;
}

.account-li {
  font-size: 16px;
  font-weight: 300;
}

.account-meta-li {
  color: #3966a7;
  margin-top: 5px;
  
}

ul.bottom-but-ul {
  width: 100%;
  height: 60px;
  line-height: 60px;
  display: flex;
  /* background: #eee */
}

li.bottom-li {
  list-style: none;
  flex: 1;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.mint-button--normal,
.mint-button--small {
  padding: 0px 10px;
  font-size: 15px;
}

.selectbtn {
  border-bottom: 1px solid #eee;
}

.selectbtn .mint-button--normal,
.selectbtn .mint-button--small {
  padding: 0px 10px;
}

.largeV-option-ul {
  width: 90%;
  height: 80px;
  display: flex;
  margin: 20px 5%;
}
li.largeV-option-li {
  list-style: none;
  flex: 1;
  text-align: center;
  border: 1px solid #eeeeee;
  border-radius: 50%;
  border-left: none;
  border-bottom: none;
  border-right: none;
  list-style: none;
}

li.largeV-option-li span {
  margin: 5px 0px 2px 0px;
  display: block;
  text-align: center;
  font-size: 15px;
}
li.largeV-option-li span img {
  width: 25px;
  height: 25px;
  margin: 0 auto;
}

.page-content {
  height: 441px;
  min-height: 441px;
}

.mint-tab-container {
  height: calc(100vh-208px) !important;
  min-height: calc(100vh-208px) !important;
}

.mint-tab-container-wrap {
  height: calc(100vh-208px) !important;
  min-height: calc(100vh-208px) !important;
}

.changeCover {
  /* background: linear-gradient(#ace0f9 0%, #fff1eb 100%); */
  z-index: 100000000;
}

.mint-button--default,
.mint-button--small {
  background: transparent;
  color: #3966a7;
}
</style>



