<template>
  <div class="mainCover">
    <!-- <HeaderBar :needb="true" :tit="tit"></HeaderBar> -->
    <div class="bottom" v-show="isShow">
      <div class="detailbottom">
        <!-- @click.stop="gotocomment(propdata)" -->
        <input
          type="text"
          :placeholder="placehoderInfo?'我 回复 '+ placehoderInfo: '我来讲讲....'"
          class="inp"
          id="sendcomment"
          v-model="commentvalue"
          @keyup.13="sendcomment(propdata.id?propdata.id:propdata.target_id,commentid)"
          @blur="inpblur()"
          @focus="inputfouse()"
        />
        <collectOrlike 
          :targetid="propdata.id?propdata.id:propdata.target_id"
          :pro="propdata"
          ref="child"
          :loginid="useridaz"
          @fatherMethod="fatherfouse"
          v-show="commShow"
        ></collectOrlike>
      </div>
      <div class="sendbtn"  @touchstart.stop="sendcomment()">发送</div>
    </div>
    <div class="mainContent">
      <!-- <p class="fenxiang">分享</p>  -->
      <div class="article" id="article">
        <div class="artictitle">
          <p class="artictitle-title">{{detaildata.title}}</p>
          <p class="artictitle-content">
            <span
              class="from"
              @click.stop="goFrom($event)"
              v-show="detaildata.source"
            >{{detaildata.source?detaildata.source:""}}</span>
            <span>&nbsp;浏览量:{{detaildata.clicks}} &nbsp;&nbsp;</span>
            {{detaildata.publish_time}}
          </p>
        </div>
        <div class="content">
          <p class="content_html kk_content_html" ref="content_html" v-html="detaildata.content"></p>
        </div>
      </div>
      <!-- 标签 -->
      <!-- <div class="page-part">
          <mt-badge v-for="(item,index) in taglist" :key="index" :type="randomRgb()">{{item.tag}}</mt-badge>
          <span class="gotag" @click="gotag()">添加标签</span>
      </div>-->
      <!-- 推荐 -->
      <div v-if="detailsRecommend.length!=0" class="tuijianactive" id="tujianheight">
        <p class="swiperTitle">
          <span class="c-title-vline"></span> 推荐文章
        </p>
        <ul class="tuijianbox">
          <li v-for="(d,k) in detailsRecommend" :key="k" @click.stop="godetail(d)">
            <div class="tuijian" v-if="d.image_id_array.length!=0">
              <div class="hasimg">
                <p class="titlename">{{d.title}}</p>
                <p class="footBottm">
                  <span class="from" v-if="d.source">{{d.source?d.source:" "}}</span>
                  <span v-if="d.clicks">浏览量:{{d.clicks}}</span>
                  <span>{{ d.date?timeago(d.date*1000):timeago(d.published*1000)}}</span>
                </p>
              </div>
              <img :src="d.image_id_array[1]" alt />
            </div>
            <div class="noimg" v-if="d.image_id_array.length==0">
              <p class="titlename">{{d.title}}</p>
              <p class="footBottm">
                <span class="from" v-if="d.source">{{d.source?d.source:" "}}</span>
                <span>浏览量:{{d.clicks }}</span>
                <span>{{ d.date?timeago(d.date*1000):timeago(d.published*1000)}}</span>
              </p>
            </div>
          </li>
        </ul>
      </div>
      <!-- 评论 -->
      <div>
        <ul v-if="comment.length!=0" class="hot-reviews">
          <div class="sub-title">
            <div class="c-part-block"></div>
            <p class="tit">
              <span class="c-title-vline"></span> 热门评论
            </p>
          </div>
          <div class="childcomme" id="pinglunid">
            <li v-for="(c,k) in comment" :key="k" class="por" @click.stop="gocommedetail(c)">
              <div class="commentbox">
              
                 <div class="ingbox">
                     <img
                  :src="c.avatar?c.avatar:'../../../static/imgs/morentu.jpg'"
                  @click.stop="privateinfo(c.user_id)"
                />
                 <img :src="bigv" class="bigv" v-if="c.is_vip==1"/>
                 </div>
               
           
                <!-- @click.stop="privateinfo(c.user_id)" -->
                <div class="commentcon">
                  <div>
                    <p class="review-nick">
                      {{c.nickname}}
                      <span class="color" >
                        <span class="hh-fab-value">{{c.flowers}}</span>
                        <i
                          v-if="c.is_flower==1"
                          class="iconfont icon-bigGood"
                          @click.stop="removelike(c,c.comment_id)"
                        ></i>
                        <i v-else class="iconfont icon-good" @click.stop="addlike(c,c.comment_id)" style="margin-top:2px"></i>
                        <!-- <i class="iconfont icon-good" @click="setlike(c)"></i> -->
                      </span>
                    </p>
                    <p class="review-area">{{c.text}}</p>
                  </div>
                  <div class="review-button">
                    <p :data-time="c.timestamp" class="review-time hh-time">
                      {{c.timestamp?c.timestamp:"刚刚"}} ·
                      <span class="replyBtn">&nbsp;回复</span>
                    </p>
                    <div v-if="c.user_id==useridaz" class="del" @click.stop="del(c,k)">删除</div>
                  </div>
                </div>
              </div>
              <div class="son" >
                <div class="sonbox">
                  <div
                    v-for="(s,h) in c.secondComment"
                    :key="h"
                    class="sonitem"
                    @click.stop="gocommedetail(s,'isself')"
                  >
                  <div class="imgbox" style="width:40px">
                      <img :src="s.avatar?s.avatar:'../../../static/imgs/morentu.jpg'" alt @click.stop="privateinfo(s.user_id)" />
                    <img :src="bigv" class="bigv" v-if="s.is_vip=='1'"/>  
                  </div>
                  
                    <div>
                      <p class="sonname">
                        <span class="namebox">{{s.nickname}}
                        <span class="tobrige">回复</span>
                        {{s.tu_nickname}}</span>
                        <span class="color">
                          <span class="hh-fab-value">{{s.flowers}}</span>
                          <i
                            v-if="s.is_flower==1 && useridaz"
                            class="iconfont icon-bigGood"
                            @click.stop="removelike(s,s.id)"
                          ></i>
                          <i v-else class="iconfont icon-good" @click.stop="addlike(s,s.id)"></i>
                        </span>
                      </p>
                      <!-- <span class="sonReply" v-if="s.secondComment.length>0">{{s.secondComment.length}}回复</span> -->
                      <p class="sontext">{{s.text}}</p>
                      <div class="review-button">
                        <p :data-time="s.timestamp" class="review-time hh-time">
                          {{s.timestamp?s.timestamp:"刚刚"}} ·
                          <span class="replyBtn">&nbsp;回复</span>
                        </p>
                        <div
                          v-if="s.user_id==useridaz"
                          class="sondel"
                          @click.stop="delson(c,s,h)"
                        >删除</div>
                      </div>
                    </div>
                  </div>
                </div>
                <p
                  v-if="c.comments>2"
                  @click.stop="propShow(c.comment_id)"
                  class="comlength"
                >全部 {{c.comments}} 条评论>>></p>
              </div>
            </li>
            <div @click="more()" class="more" v-if="iddata.more==1">点击加载更多</div>
          </div>
        </ul>
        <p v-else class="hh-comment">-----暂无评论------</p>
      </div>
    </div>
    <mt-popup v-model="popupVisible" position="bottom">
      <div class="tanchu">
        <div class="close" @click="propShow()">
          <p class="swiperTitle">
            <span class="c-title-vline"></span> 全部回复
          </p>
          <p style="font-size:30px;padding-right:16px;">×</p>
        </div>
        <commedetail :propid="commid" ref="comme" :loginid="useridaz"></commedetail>
      </div>
    </mt-popup>
  </div>
</template>

<script>
import commedetail from "pages/home/commedetail.vue";
import collectOrlike from "com/collectOrlike.vue";
import { MessageBox } from 'mint-ui';
import { ComUtils } from "common/utils";
import { Toast } from "mint-ui";
import { async } from 'q';
export default {
  data() {
    return {
      bigv:require("images/bigv.png"),
      alertdata: "确定取消吗",
      propdata: [],
      tit: "PPP",
      detaildata: [],
      comment: [],
      typelist: ["primary", "error", "success", "warning"],
      taglist: [],
      content: "",
      isShow: false,
      iddata: [],
      actid: "",
      popupVisible: false,
      commid: "",
      commentid: "",
      commentvalue: "",
      like: "icon-bigGood",
      likeNum: 1,
      detailsRecommend: [],
      placehoderInfo: "",
      useridaz: "",
      keycode: true,
      commShow:true,
    };
  },
  computed: {},
  activated() {
      this.getParams();
      this.getdetaildate(
        this.propdata.id ? this.propdata.id : this.propdata.target_id
      );
      this.onetuisong(
        this.propdata.id ? this.propdata.id : this.propdata.target_id
      );

  },
  created() {
    this.brige();
    this.getParams();
  },
  mounted() {
    // 轮播数据
  },
  watch: {
    // 监测路由变化,只要变化了就调用获取路由参数方法将数据存储本组件即可
    // $route: "getParams"
  },

  methods: {
    getParams() {
      this.useridaz='419286589156950409'
      if (window.hasOwnProperty("webkit")) {
        this.isShow = false;
      } else {
        this.isShow = true;
      }
      var routerdata = localStorage.getItem("homedata");
      var type = localStorage.getItem("atype");
      this.propdata = JSON.parse(routerdata);
    },
    brige() {
      let that = this;
      window.getArticleInfo = function(data, getuserid) {
        //  Toast(data.id)
        that.propdata = data;
        that.useridaz = getuserid;
        that.getdetaildate(
          that.propdata.id ? that.propdata.id : that.propdata.target_id
        );
        // that.onetuisong(data.id)
      };
      // window.iosGetcommune = function() {
      //   that.getcommentdate(
      //     that.propdata.id ? that.propdata.id : that.propdata.target_id
      //   );
      // };
    },
    //ios获取评论
    propShow(pro) {
      if (pro) {
        this.commid = pro;
        let that = this;
        setTimeout(function() {
          that.$refs.comme.getcommedetail();
        });
      } else {
        this.comment = [];
        this.getcommentdate(
          this.propdata.id ? this.propdata.id : this.propdata.target_id
        );
      }
      this.popupVisible = !this.popupVisible;
    },

    //去个人资料页
    privateinfo(v) {
      if (!this.useridaz) {
        app.goLogin();
      } else {
        app.gouserInfo(v);
      }
    },
    goFrom(el) {
      //  this.$router.push({
      //   name: "newFrom"
      // });
      let goUrl = "newFrom";
      var str = location.href.slice(0, 20);
      if (window.hasOwnProperty("webkit")) {
        window.webkit.messageHandlers.openSourceArticles.postMessage(
          str + "/#/" + goUrl
        );
      } else {
        app.gotoDetail(str, goUrl, el.target.innerHTML);
      }
    },
  
    // 获取详情页数据
    async getdetaildate(activeId) {
    
      this.comment = [];
      try {
        let res = await this.$api.all.GETfn(
          {},
          "detail",
          // a
          this.propdata.id ? this.propdata.id : this.propdata.target_id
        );
        if(app !=undefined && app.hasOwnProperty('sendClick')){
          app.sendClick(res.id,res.clicks)
        }else{

        }
          //  app.sendCli ck(res.id,res.clicks)
        let dom = document.createElement("div");
        
        dom.innerHTML = res.content;
       
        dom.querySelectorAll("p").forEach(function(e) {
          e.setAttribute(
            "style",
            " word-break:break-all; display:block;width:94%;font-family: -apple-system, 'PingFang SC', 'Helvetica Neue', Helvetica, STHeiTi, sans-serif; line-height:27px!important;color:#333 !important;font-size:18px!important; font-weight:normal; letter-spacing:1px; text-align: justify!important;margin:0 auto;padding-bottom:20px"
          );
        });
         
        dom.querySelectorAll("span").forEach(function(e) {
          e.setAttribute(
            "style",
            " font-family: -apple-system, 'PingFang SC', 'Helvetica Neue', Helvetica, STHeiTi, sans-serif; line-height:27px!important;color:#333 !important;font-size:18px!important; font-weight:normal; letter-spacing:1px; text-align: justify!important;"
          );
        });
    
        dom.querySelectorAll("div").forEach(function(e) {
          e.setAttribute(
            "style",
            " word-break: break-all;display:block; width:94%;font-family: -apple-system, 'PingFang SC', 'Helvetica Neue', Helvetica, STHeiTi, sans-serif; line-height:27px!important;color:#333 !important;font-size:18px!important; font-weight:normal; letter-spacing:1px; text-align: justify!important;"
          );
        });
           
        dom.querySelectorAll("a").forEach(function(e) {
				e.setAttribute(
					"href",
					"javascript:void(0)"
				);
		
      });
      
         dom.querySelectorAll("br").forEach(function(e) {
          //  console.log(e)
           var brdom=document.createElement("br")
           var parent=e.parentNode
           parent.insertBefore(brdom,e);
        });
        //  Toast(dom.innerHTML)
        dom.querySelectorAll("img").forEach(function(e) {
          e.setAttribute(
            "style",
            "width:98% !important;height:auto;margin:0 auto"
          );
          

          // debugger;
          let imgDomain = "http://img2.ceh.com.cn/show/";
          let imgUrl = e.getAttribute("src");
          // e.style.marginRight="-100px"
          // debugger;
          if (imgUrl.substr(0, 7) == "http://") {
          } else {
            e.setAttribute("src", imgDomain + imgUrl + "/medium");
          }
        });
        
        res.content = dom.innerHTML;
      
        // console.log("文章图片" + contentImg);
        this.detaildata = res;

        if (window.hasOwnProperty("webkit")) {
        } else {
          this.$refs.child.iscollect();
          this.$refs.child.islike();
          this.$refs.child.likenum();
          this.$refs.child.getcomment();
        }
      } catch (e) {
        // Toast(e);
        // console.log(e);
      }
      this.getcommentdate(
        this.propdata.id ? this.propdata.id : this.propdata.target_id
      );
     
      // this.onetuisong(a)
    },
    // 单文章推送
    async onetuisong(id) {
      var atype = localStorage.getItem("atype");
      try {
        let params = {
          id: this.propdata.id ? this.propdata.id : this.propdata.target_id,
          count: 1
        };

        let res = await this.$api.all.GETfn(params, "onetuisong");
        this.detailsRecommend = res.result.data;
      } catch (e) {
        console.log(e);
      }
    },
    // 跳转到推荐详情
    godetail(item) {
     app.gotoNext(item.type_id,item.id)
      // let params = {
      //   id: item.id
      // };
      // if (window.hasOwnProperty("webkit")) {
      //   window.webkit.messageHandlers.go2RecommendDetailWithID.postMessage(
      //     params
      //   );
      // } else {
      //   console.log(JSON.stringify(item));
      //   this.detaildata = item;
      //   if (item.detailsRecommend != undefined) {
      //     this.propdata = item;
      //     this.onetuisong(item);
      //   }
      //   this.getdetaildate();
      //   this.getcommentdate();
      // }
    },
    //去点赞
    async addlike(data,id) {
     
      if (!this.useridaz) {
        app.goLogin();
      }
      let params = {
        target:id,
        userid: this.useridaz,
        user_id: this.useridaz
      };
      try {
        if(this.keycode==true){
          this.keycode=false
        let res = await this.$api.all.POSTfn(params, "good");
        if (res.result == "ok") {
          this.$set(data, `is_flower`, "1");
          let num = Number(data.flowers);
          num = num += 1;
          this.$set(data, `flowers`, num);
          console.log("点赞数量" + num);
            if(app !=undefined && app.hasOwnProperty('backList')){
          app.backList()
        }else{

        }
        }
        console.log(res);
        this.keycode=true;
          }
      } catch (e) {
         this.keycode=true;
        console.log(e);
      }
    },
    // 取消点赞
    async removelike(data,id) {
      
      let params = {
        target: data.id,
        userid: this.useridaz,
        user_id: this.useridaz
      };
      try {
        let res = await this.$api.all.POSTfn(params, "good_delete");
        if (res.result == "ok") {
          this.$set(data, `is_flower`, 0);
          let num = Number(data.flowers);
          num = num -= 1;
          this.$set(data, `flowers`, num);
          console.log("点赞数量" + num);
            if(app !=undefined && app.hasOwnProperty('backList')){
          app.backList()
        }else{

        }
        }
      } catch (e) {
        console.log(e);
      }
    },

    // 获取评论数据
    async getcommentdate(id, direct, hot) {
      if (!hot) {
        this.comment = [];
      }
      try {
        let params = {
          target: id,
          target_id: hot,
          direction: direct,
          count: 10,
          userid: this.useridaz?this.useridaz:2,
          user_id: this.useridaz?this.useridaz:2
        };
        let res = await this.$api.all.GETfn(params, "comment");
        // Toast(JSON.stringify(res.result.data) )
        for (let i in res.result.data) {
          res.result.data[i].timestamp = this.timeago(
            res.result.data[i].timestamp * 1000
          );
          // console.log(res.result.data[i].avatar.endsWith(".com/"))
          if(res.result.data[i].avatar){
            if( res.result.data[i].avatar.endsWith(".com/")){
            res.result.data[i].avatar='../../../static/imgs/morentu.jpg'
          }
            
          }
          
          for (let t in res.result.data[i].secondComment) {
            res.result.data[i].secondComment[t].timestamp = this.timeago(
              res.result.data[i].secondComment[t].timestamp * 1000
            );
             if(res.result.data[i].secondComment[t].avatar){
             if( res.result.data[i].secondComment[t].avatar.endsWith(".com/")){
            res.result.data[i].secondComment[t].avatar='../../../static/imgs/morentu.jpg'
          }
             }
          }
        }

        this.comment = this.comment.concat(res.result.data);
        this.iddata = res.result;
        console.log(res);
      } catch (e) {
        console.log(e);
      }
    },
    more() {
      let dictir = "litter";
      let hot = this.iddata.min_id;
      if (this.iddata.more == 0) {
        Toast("无更多数据");
      } else {
        this.getcommentdate(
          this.propdata.id ? this.propdata.id : this.propdata.target_id,
          dictir,
          hot
        );
      }
    },
    // 随机背景色
    randomRgb() {
      return this.typelist[Math.floor(Math.random() * this.typelist.length)];
    },
    gotag() {
      this.$router.push({
        path: "/tag",
        name: "tag"
      });
    },
    //发表评论
    async sendcomment() {
      if (!this.useridaz) {
        app.goLogin();
      }
      
      if (this.commentvalue.trim() == "") {
        Toast("评论信息不能为空");
        return;
      }
      try {
        if (this.keycode == true) {
          this.keycode = false;

          let params = {
            target: this.commentid
              ? this.commentid
              : this.propdata.id
              ? this.propdata.id
              : this.propdata.target_id,
            text: this.commentvalue,
            _: "RFs0",
            type: "article",
            userid: this.useridaz,
            
          };
          // ;
          let res = await this.$api.all.POSTfn(params, "send_comment");
          if(res.result=="ok"){
             Toast("发布成功");
          }else{
             Toast(res.result);
          }
         
          this.commentid = "";
          this.placehoderInfo = "";
          this.commentvalue = "";
          this.getcommentdate(
            this.propdata.id ? this.propdata.id : this.propdata.target_id
          );
          this.$refs.child.getcomment();
          this.keycode = true;
          // this.getcomment()
            if(app !=undefined && app.hasOwnProperty('backList')){
          app.backList()
        }else{

        }
        }
      } catch (e) {
        console.log(e);
        this.keycode = true;
      }

      if (this.commentvalue.indexOf("@" + localStorage.getItem("fn")) != -1) {
        this.atfriend();
      }
    },
    gocommedetail(item, isself) {
      localStorage.setItem("commid", item);
      if (window.hasOwnProperty("webkit")) {
        let params = {
          id: item.id
        };
        window.webkit.messageHandlers.fouse.postMessage(params);
      } else {
        if (!this.useridaz) {
          app.goLogin();
        }
        if (item.user_id == this.useridaz) {
          Toast("不能自己回复自己");
          return;
        }
        document.getElementById("sendcomment").focus();
        if (!item) {
          return;
        }
        this.commentid = item.id;
        this.placehoderInfo = item.nickname ? item.nickname : item.user_name;
      }
    },
    fatherfouse() {
      var articleheight = document.getElementById("article").offsetHeight;
      document.querySelector(".mainContent").scrollTo(0, articleheight + 20);
      //  document.getElementById("sendcomment").focus();
    },
    inputfouse(){
      this.commShow=false
      document.querySelector(".sendbtn").style.display="block"
    },
    inpblur() {
       this.commShow=true
      this.commentid = "";
      this.placehoderInfo = "";
       document.querySelector(".sendbtn").style.display="none"
    },
    //删除评论
     del(item, index) {
      MessageBox.confirm('确定删除此条评论?').then(async action => {
          try {
        let params = {
          // userid: "327195464540160441",
          comment_id: item.comment_id,
          userid: this.useridaz,
          user_id: this.useridaz
        };
        let res = await this.$api.all.POSTfn(params, "delcomme");
        if (res.result == "ok") {
          console.log(index);
          this.comment.splice(index, 1);
          this.$refs.child.getcomment();
        }
      } catch (e) {
        console.log(e);
      };
        
 
});
    
    },
    //删除评论
   delson(s, item, index) {
       MessageBox.confirm('确定删除此条评论?').then(async action => {
      try {
        let params = {
          // userid: "327195464540160441",
          comment_id: item.id,
           userid: this.useridaz,
          user_id: this.useridaz
        };
        let res = await this.$api.all.POSTfn(params, "delcomme");
        console.log(res);
        if (res.result == "ok") {
          s.secondComment.splice(index, 1);
          this.$refs.child.getcomment();
           this.getcommentdate(this.propdata.id ? this.propdata.id : this.propdata.target_id);
        }
      } catch (e) {
        console.log(e);
      }
      });
    },
    timeago(dateTimeStamp) {
      //dateTimeStamp是一个时间毫秒，注意时间戳是秒的形式，在这个毫秒的基础上除以1000，就是十位数的时间戳。13位数的都是时间毫秒。
      var result;
      var minute = 1000 * 60; //把分，时，天，周，半个月，一个月用毫秒表示
      var hour = minute * 60;
      var day = hour * 24;
      var week = day * 7;
      var halfamonth = day * 15;
      var month = day * 30;
      var now = new Date().getTime(); //获取当前时间毫秒
      var diffValue = now - dateTimeStamp; //时间差

      if (diffValue < 0) {
        return;
      }
      var minC = diffValue / minute; //计算时间差的分，时，天，周，月
      var hourC = diffValue / hour;
      var dayC = diffValue / day;
      var weekC = diffValue / week;
      var monthC = diffValue / month;
      if (dayC >= 1 && dayC <= 6) {
        result = " " + parseInt(dayC) + "天前";
      } else if (hourC >= 1 && hourC <= 23) {
        result = " " + parseInt(hourC) + "小时前";
      } else if (minC >= 1 && minC <= 59) {
        result = " " + parseInt(minC) + "分钟前";
      } else if (diffValue >= 0 && diffValue <= minute) {
        result = "刚刚";
      } else {
        var datetime = new Date();
        datetime.setTime(dateTimeStamp);
        var Nyear = datetime.getFullYear();
        var Nmonth =
          datetime.getMonth() + 1 < 10
            ? "0" + (datetime.getMonth() + 1)
            : datetime.getMonth() + 1;
        var Ndate =
          datetime.getDate() < 10
            ? "0" + datetime.getDate()
            : datetime.getDate();
        var Nhour =
          datetime.getHours() < 10
            ? "0" + datetime.getHours()
            : datetime.getHours();
        var Nminute =
          datetime.getMinutes() < 10
            ? "0" + datetime.getMinutes()
            : datetime.getMinutes();
        var Nsecond =
          datetime.getSeconds() < 10
            ? "0" + datetime.getSeconds()
            : datetime.getSeconds();
        result = Nyear + "-" + Nmonth + "-" + Ndate;
      }
      return result;
    }
  },
  components: {
    collectOrlike,
    commedetail
  }
};
</script>

