<template>
  <div>
    <div class="mainCover">
      <HeaderBar :needb="true"  :tit="titleName"></HeaderBar>
      <div class="mainContent">
          <ul class="listbox">
              <li v-for="(item,index) in friends" :key="index" class="listitem" @click="atfriend(item)">
                  <img :src="item.user_image?item.user_image:'../../../static/imgs/activity.png'" alt="">
                  <p>{{item.name}}</p>
              </li>
          </ul>
      </div>
    </div>
  </div>
</template>

<script>
// import HeaderBar from "com/Header-bar.vue";
export default {
  props: {},
  data() {
    return {
        titleName: "请选择好友",
      needb: true,
      needm: false,
      friends:[],
      at:[]
    };
  },
  computed: {},
  activated() {
      this.getParams()
      this.getfriend()
  },
  mounted() {},
  watch: {},
  methods: {
       getParams() {
      // 将数据放在当前组件的数据内
      var routerdata = localStorage.getItem("homedata");
       
      this.at = JSON.parse(routerdata);
    },
    //    获取好友列表
    async getfriend() {
      try {
        let params = {
        };
        let res = await this.$api.all.GETfn(params, "minFriend");
        console.log(res)
        this.friends=res.result
      } catch (e) {
        console.log(e);
      }
    },
     atfriend(item) {
        localStorage.setItem("fn",item.name)
         localStorage.setItem("fd",item.id)
        this.$router.go(-1)
    },  
  },
  components: {}
};
</script>

<style scoped lang="css">
.listbox{
    padding:5px 10px;
}
.listitem{
    padding: 5px 0;
    display: flex;
    align-items: center

}
.listitem img{
    height: 40px;
    width: 40px;
    border-radius: 50%
}
.listitem p{
    border-bottom: 1px solid #ccc;
    margin-left: 5px;
    height:50px;
    line-height: 45px;
    width: 100%;
    font-size: 14px;
    color: #333
}

</style>



