<template>
  <div class="mainCover">
    <HeaderBar :needm="needm" :needb="needb" :tit="titleName"></HeaderBar>
    <div class="interest_th">
      <ul class="interest_th_1">
        <li
          v-for="(item,index) in targettag"
          :key="index"
          :class="['list',item.cssname]"
          @click="deltag(targettag,item,index)"
        >{{item.name}}</li>
        <input
          type="text"
          class="tag-input"
          placeholder="添加标签"
          @keyup.enter="addtag(tagvalue)"
          v-model="tagvalue"
        />
      </ul>
      <div></div>
      <p>所有标签</p>
      <ul class="interest_th_2">
        <li
          v-for="(item,index) in alltag"
          :key="index"
          :class="['list',item.cssname]"
          @click="tagon(alltag,item,index)"
        >{{item.tag}}</li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "me_interest",
  props: {},
  data() {
    return {
      titleName: "兴趣爱好",
      needb: true,
      needm: false,
      search: "",
      alltag: [],
      targettag: [],
      active: "active",
      tagvalue: "",
      tagdata: []
    };
  },
  computed: {},
  activated() {
    var routerdata = window.sessionStorage.getItem("homedata");
    this.tagdata = JSON.parse(routerdata);
    console.log("评论接过来的" + routerdata);
    this.search = require("images/search.png");
    this.usertag();
    this.utargettag();
    // this.addcss()
  },
  mounted() {},
  watch: {},
  methods: {
    //用户的所有标签
    async usertag() {
      console.log("所有标签");
      try {
        let params = {
          // userid: "327195464540160441",
          // token:
          //   "6g475hc3c2ce55cab1209998c8ecf04a0e4125a05d3806c448a6e8a9c0901b93840",
          count: 1000
        };
        // ;
        let res = await this.$api.all.GETfn(params, "user_tages");
        console.log("all" + JSON.stringify(res));
        const can = new Map();
        console.log("shuju" + JSON.stringify(res.result.data));
        this.alltag = res.result.data.filter(
          a => !can.has(a.tag) && can.set(a.tag, 1)
        );
        // console.log("all" + JSON.stringify(this.alltag));
      } catch (e) {
        console.log(e);
      }
    },
    //用户对文章的标签
    async utargettag() {
      try {
        let params = {
          // userid: "327195464540160441",
          // token:
          //   "6g475hc3c2ce55cab1209998c8ecf04a0e4125a05d3806c448a6e8a9c0901b93840",
          target: this.tagdata.id
        };
        // ;
        let res = await this.$api.all.GETfn(params, "user_mutag");
        console.log("utag" + JSON.stringify(res));
        if (res.result == "ok") {
          this.targettag = Array.from(new Set(res.data));

          this.targettag.forEach((item, index) => {
            for (let i in this.alltag) {
              if (item.name == this.alltag[i].tag) {
                this.$set(item, "cssname", "active");
                this.$set(this.alltag[i], "cssname", "active");
              } else {
              }
            }
          });
          const data = new Map();
          this.targettag = res.data.filter(
            a => !data.has(a.name) && data.set(a.name, 1)
          );
        }
      } catch (e) {
        console.log(e);
      }
    },
    // 添加标签
    async addtag(tagname) {
      if (tagname != "") {
        let data = {
          target: this.tagdata.id,
          tag: tagname
        };
        try {
          let res = await this.$api.all.POSTfn(data, "add_tag");
          if (res.result == "ok") {
            let item = {
              name: tagname,
              cssname: "active"
            };
            this.targettag.push(item);
            this.tagvalue = "";
          }
          console.log(res);
        } catch (e) {
          console.log(e);
        }
      }
    },

    //点击去掉
    async deltag(target, item, index) {
      let that = this;
      let mu = target.find(n => n.name == item.name);
      if (mu && item.cssname == "active") {
        this.$set(item, "name", item.name + " " + " X");
        this.$set(item, "cssname", "del");
        return;
      } else if (mu && item.cssname == "del") {
        let data = {
          userid: "327195464540160441",
          target: this.tagdata.id,
          tag: item.name
        };
        try {
          let res = await this.$api.all.POSTfn(data, "del_tag");
          console.log(res);
          if (res.result == "ok") {
            target.splice(index, 1);
          }
        } catch (e) {
          console.log(e);
        }
      } else {
        this.$set(item, "cssname", "active");
        // this.navArr.push(name);
      }
    },
    // 点击把上面的放上去
    async tagon(alltagn, item, index) {
      // 点击的和上面相同的下标；
      var delchong = this.targettag.findIndex((value, index, arr) => {
        return value.name == item.tag;
      });
      // 点击的和上面相同的内容；
      var chongitem = this.targettag.find((value, index, arr) => {
        return value.name == item.tag;
      });
      if (chongitem && item.cssname == "active") {
        // 删除上面的
        this.$set(item, "cssname", "");
        let data = {
          userid: "327195464540160441",
          target: this.tagdata.id,
          tag: chongitem.name
        };
        try {
          let res = await this.$api.all.POSTfn(data, "del_tag");
          if (res.result == "ok") {
            this.targettag.splice(delchong, 1);
          }
        } catch (e) {
          console.log(e);
        }
      } else {
        this.$set(item, "cssname", "active");
        this.addtag(item.tag);
      }
    }
  },
  components: {}
};
</script>

<style scoped lang="css">
/* 重置 */
ul {
  list-style: none;
  margin: 0;
}
img {
  display: block;
}

.interest_th {
  background-color: #f5f6f9;
  font-size: 14px;
  height: 100%;
}
.interest_th_1 {
  margin: 10px;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}
ul .active {
  padding: 2px 10px;
  background: white;
  border-radius: 20px;
  margin: 4px;
  color: #023249;
  border: 1px solid #023249;
}
ul .del {
  padding: 2px 10px;
  background: #023249;
  border-radius: 20px;
  margin: 4px;
  color: #ffff;
  border: 1px solid #023249;
}

.interest_th div {
  width: 100%;
  height: 1px;
  background-color: #c7c8cc;
  margin: 0px;
}

.interest_th_2 {
  margin: 10px;
}
.list {
  padding: 2px 10px;
  background: white;
  border-radius: 20px;
  float: left;
  margin: 4px;
  color: #000;
  border: 1px solid #c8c7cc;
}
p {
  margin: 10px;
  color: #676767;
  text-align: left;
}
.tag-input {
  border: none;
  padding: 3px 10px;
  font-size: 14px;
  width: 100px;
  border-radius: 20px;
  text-align: center;
  margin: 4px;
  border: 1px dashed #ccc;
}
</style>



