<template>
  <div>
    <HeaderBar :needb="needb" :tit="titleName"></HeaderBar>
    <div class="mainContent">
      <vue-scroll
        ref="scroll"
        :ops="opsconfig"
        @refresh-start="handleRS"
        @load-before-deactivate="handleLBD"
        @refresh-before-deactivate="handleRBD"
        @load-start="handleLoadStart"
      >
        <div v-for="(item,index) in askcommlist" :key="index" class="contentmain" @click="goaskdetail(item)">
          <p class="text">{{ item.text }}</p>
          <div class="conetentbox">
            <div class="boxtop">
              <div class="tit">
                <h5>{{item.title}}[{{item.commune_name}}]</h5>
                <p>{{item.content}}</p>
              </div>
              <img :src="item.image_url" alt />
            </div>
            <p class="boxbottom">
              <span>
                类型:
                <span v-if="item.type==1">社问</span>
                <span v-if="item.type==2">公告</span>
                <span v-if="item.type==3">动态</span>
              </span>
              <span>
                来自:
                <span>{{ item.commune_name }}</span>
              </span>
            </p>
          </div>
        </div>
      </vue-scroll>
    </div>
  </div>
</template>

<script>
import { ScrollConfig } from "common/data.js";
import { Toast } from "mint-ui";
export default {
  props: {},
  data() {
    return {
      titleName: "评论公告",
      askcommlist: [],
      needb: true,
      opsconfig: ScrollConfig.opsconfig,
      refresh: 1,
      noSscrollData: false,
      iddata: []
    };
  },
  computed: {},
  activated() {
    this.getaskcommedate();
  },
  mounted() {},
  watch: {},
  methods: {
     //
    async setbrowsing(item) {
      try {
        let params = {
          ask_id: item.id
        };
        let res = await this.$api.all.POSTfn(params, "browsing");
        console.log("设置浏览量" + JSON.stringify(res));
      } catch (e) {
        console.log(e);
      }
    },
    goaskdetail(item) {
      window.sessionStorage.setItem("askdata", JSON.stringify(item));

      this.setbrowsing(item);
      this.$router.push({
        name: "commune_f"
        // params: {
        //   data: item
        // }
      });
      this.askcommlist == [];
    },
    // 新闻数据
    async getaskcommedate(direct, hot) {
      let that = this;
      
      try {
        let params = {
          liststype: "askcomment",
          count: 10,
          req_id: hot,
          direction: direct
          // disable_cache: 0
        };

        let res = await this.$api.all.GETfn(params, "my_commune");
         if ( direct == "litter") {
          that.askcommlist = that.askcommlist.concat(res.result.data);
        }else{
           that.askcommlist= res.result.data.concat(that.askcommlist);
        }
        // that.askcommlist = res.result.data;
        this.iddata = res.result;
      } catch (e) {
        console.log(e);
      }
    },

    handleRS(vsInstance, refreshDom, done) {
      // alert("这是执行下拉刷新开始做啥事");
      done();
    },
    handleLoadStart(vm, dom, done) {
      setTimeout(() => {
        // alert("这是执行下拉加载之前需要执行什么操作");
        done();
      }, 600);
    },
    handleLBD(vm, loadDom, done) {
      let dictir = "litter";
      let hot = this.iddata.min_id;
      if (this.iddata.more == 0) {
        Toast("无更多数据");
        done();
        return
      }
      this.getaskcommedate(dictir, hot);
      done();
    },
    handleRBD(vm, loadDom, done) {
      let dictir = "bigger";
      let hot = this.iddata.max_id;
      if (this.iddata.more == 0) {
        Toast("无更多数据");
        done();
      }
      this.getaskcommedate(dictir, hot);
      done();
    }
  },
  components: {}
};
</script>

<style scoped lang="css">
.mainContent{
    height: 623px;
}
.text {
  color: #333;
  padding: 3px;
  font-size: 14px
}
.tit{
  width:65%;
  
}
.tit h5,.tit p{
overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.conetentbox {
  background: #f6f7f9 !important;
  font-size: 12px;
  padding: 5px;
}
.conetentbox p {
  color: #666;
}
h5 {
  font-size: 14px;
  color: #333;
}
.boxtop img {
  height: 60px;
  width: 100px;
}
.contentmain {
  background: #ffff;
  padding: 5px 10px;
  border-bottom: #cccccc;
}
.boxtop {
  display: flex;
  justify-content: space-between;
  line-height: 25px;
}
.boxbottom {
  font-size: 12px;
  color: #999 !important;
}
</style>



