<template>
  <div class="mainCover">
    <HeaderBar :needm="needm" :needb="needb" :tit="titleName"></HeaderBar>
    <div class="mainContent">
      <div class="top-box">
        <div class="login-box">
          <span class="login-text">登录</span>
          <span class="sign">
            <span class="nbsp">&nbsp;</span>
          </span>
        </div>
        <div class="login-box" @click="gotoregistration">
          <span class="login-text">注册</span>
          <span class="sign">&nbsp;</span>
        </div>
      </div>

      <div>
        <p class="message">短信登录</p>
        <div class="input-box">
          <span class="iconfont icon-tel" slot="icon"></span>

          <input
            type="tel"
            class="input-default"
            placeholder="请输入手机号"
            maxlength="13"
            pattern="\d*"
            v-model.number="Telphone"
            oninput="value=value.replace(/[^\d]/g,'')"
          />
        </div>

        <div class="input-box">
          <span class="iconfont icon-vcode" slot="icon"></span>
          <input
            type="tel"
            class="input-default"
            placeholder="请输入图形验证码"
            maxlength="6"
            v-model="GVCodeValue"
            pattern="\d*"
            oninput="value=value.replace(/[^\d]/g,'')"
          />
          <img :src="imgyanzen" alt class="imageyanz" />
        </div>

        <div class="input-box input-box-last">
          <span class="iconfont icon-ivcode" slot="icon"></span>
          <input
            type="tel"
            class="input-default"
            placeholder="请输入验证码"
            maxlength="6"
            v-model="VCodevalue"
            pattern="\d*"
            oninput="value=value.replace(/[^\d]/g,'')"
          />
          <span class="sendMsg" @click.prevent="sendMsg">{{codeText}}</span>
        </div>

        <div class="button-login">
          <input type="button" value="登录" class="input-button-login" @click="loginConfirm" />
        </div>

        <div class="button-other">
          <router-link to="/login">账号密码登录</router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ComUtils } from "common/utils";

export default {
  name: "messagelogin",
  props: {},
  data() {
    return {
      titleName: "登录",
      needb: true,
      needm: false,
      imgyanzen: "",
      Telphone: "",
      GVCodeValue: "",
      VCodevalue: "",
      codeText: "发送验证码",
      sendMsgFlag: true,
      hassendMsg: false
    };
  },
  computed: {},
  created() {
    this.getGVCode();
  },
  mounted() {},
  watch: {},
  methods: {
    gotoregistration() {
      this.$router.push({
        name: "registration"
      });
    },
    async getGVCode() {
      let timestamp = new Date().getTime();
      this.imgyanzen =
        "http://api2.ceh.com.cn/captcha/image/get?size=18&len=6&_=AWMT&uuid=111&r" +
        timestamp;
    },
    checkFn() {
      let mobile = this.Telphone;
      let gvcodevalue = this.GVCodeValue;
      let vcodevalue = this.VCodevalue;
      if (mobile == "" || mobile == null) {
        this.$toast("您还未输入手机号码");
        return false;
      }

      if (gvcodevalue == "" || gvcodevalue == null) {
        this.$toast("您还未输入图形验证码");
        return false;
      }

      let regExp = /^(86)?((13\d{9})|(15[0,1,2,3,5,6,7,8,9]\d{8})|(18[0,5,6,7,8,9]\d{8}))$/;
      if (!regExp.test(mobile)) {
        this.$toast("您输入的手机号码有误");
        return false;
      }

      if (gvcodevalue == "" || gvcodevalue == null || gvcodevalue == 0) {
        this.$toast("您还未输入图形验证码");
        return false;
      }

      if (gvcodevalue.length < 4) {
        this.$toast("您输入图形验证码最少输入4位数");
        return false;
      }

      return true;
    },
    sendMsg() {
      if (!this.sendMsgFlag) {
        return;
      }

      if (!this.checkFn()) {
        return;
      }

      this.hassendMsg = true;

      this.SendTelMsg();
    },
    async loginConfirm() {
      try {
        if (!this.checkFn()) {
          return false;
        }

        if (!this.hassendMsg) {
          this.$toast("您还未发送验证码");
          return false;
        }

        let param = {
          tel: this.Telphone,
          code: this.VCodevalue,
          _device_uuid: "111"
        };
        let res = await this.$api.all.POSTfn(param, "msg_login");
        if (res.result == "ok") {
          this.$toast("登录成功!");

          //保存用户信息
          ComUtils.saveParams(res);
          setTimeout(() => {
            this.$router.push({
              name: "homePage"
            });
          }, 1000);
        } else {
          this.$toast(res.result);
        }
      } catch (error) {
        console.log(error);
      }
    },
    async SendTelMsg() {
      try {
        let param = {
          tel: this.Telphone,
          uuid: "111",
          image_code: this.GVCodeValue,
          type: "login"
        };
        let res = await this.$api.all.GETfn(param, "send_code");
        res = res.result;
        if (res == "ok") {
          this.sendMsgFlag = false;
          let times = 60;
          let setMsgInterval = setInterval(() => {
            this.codeText = times + "S";
            times--;
            if (times == -1) {
              this.codeText = "发送验证码";
              this.sendMsgFlag = true;
              clearInterval(setMsgInterval);
            }
          }, 1000);
        } else {
          this.$toast(res);
        }
        this.getGVCode();
        console.log(res);
      } catch (error) {
        this.getGVCode();
        console.log(error);
      }
    }
  },
  components: {}
};
</script>

<style scoped lang="css">
a {
  color: #333333 !important;
  text-decoration: none;
}

.top-box {
  min-height: 110px;
  background-color: #4a8dff;
  display: flex;
  width: 100%;
  align-items: flex-end;
  text-align: center;
  color: #fff;
}

.login-box {
  flex: 1;
  min-height: 60px;
  line-height: 60px;
  text-align: center;
  clear: both;
  overflow: hidden;
}

.login-box span {
  display: block;
}

.login-text {
  height: 40px;
  line-height: 40px;
  font-size: 17px;
  padding: 5px 0px 0px 0px;
}

.sign {
  min-height: 22px;
  line-height: 22px;
  min-width: 22px;
  position: relative;
  left: 50%;
  margin-left: -10px;
}

.nbsp {
  width: 0px;
  height: 0px;
  border-left: 11px solid transparent;
  border-right: 11px solid transparent;
  border-bottom: 11px solid #fff;
  border-top: 11px solid transparent;
}

.message {
  height: 44px;
  line-height: 44px;
  text-align: center;
  color: #4a8dff;
  font-size: 17px;
  font-weight: 500;
}

.input-box .sendMsg {
  min-width: 100px;
  display: inline-block;
  height: 30px;
  margin: 5px;
  line-height: 30px;
  font-size: 14px;
  text-align: center;
  color: #fff;
  background: #4a8dff;
  border-radius: 3px;
}

.input-box {
  height: 44px;
  line-height: 44px;
  display: flex;
}

.input-box span {
  display: inline-block;
  width: 26px;
  height: 26px;
  margin: 0px 5px 0px 15px;
  color: #4a8dff;
}
.input-box img.imageyanz {
  height: 30px;
  line-height: 30px;
  margin: 7px 10px;
  width: 80px;
}

.input-box input {
  height: 26px;
  margin: 9px 5px;
  flex: 1;
  line-height: 26px;
  font-size: 15px;
}

.button-login {
  /* margin-top: 20px; */
  height: 40px;
  line-height: 40px;
  width: 100%;
  font-size: 16px;
  color: #fff;
  text-align: center;
  padding: 0px 12px;
}

.input-button-login {
  width: 100%;
  height: 100%;
  background-color: #4a8dff;
  color: #fff;
  font-size: 16px;
  border-radius: 3px;
}

.button-other {
  height: 44px;
  line-height: 44px;
  display: flex;
}

.button-other a {
  display: inline-block;
  flex: 1;
  text-align: left;
  padding-left: 15px;
  color: #4a8dff !important;
}

.input-box-last {
  margin-bottom: 20px;
}
</style>



