<template>
  <div class="mainCover">
    <!-- <HeaderBar :needcsb="needb" @fatherbackcall="goBackPage()" :tit="titleName"></HeaderBar> -->
    <!-- <div class="marginClass">&nbsp;</div> -->
    <div class="headersearch">
      <input type="text" placeholder="请输入关键字" v-model="resultSet" class="searchinput"    @keyup.13="appkeyHide"/>
      <i class="mintui mintui-search"></i>
      <!-- <mt-search placeholder="请输入项目名关键字" v-model="resultSet" cancel-text="取消"></mt-search> -->
    </div>

    <ul class="headertab-ul">
      <li class="headertab-li" @click.prevent="sheetVisible1 = true" size="large">
        <span class="headertab-span c-font-list">
          {{cityfield}}
          <span class="all-tab-span all-tab-left">
            <span class="all-tab-nbsp">&nbsp;</span>
          </span>
        </span>
      </li>
      <li class="headertab-li" @click.prevent="sheetVisible2 = true" size="large">
        <span class="headertab-span c-font-list">
          {{industryfield}}
          <span class="all-tab-span all-tab-left">
            <span class="all-tab-nbsp">&nbsp;</span>
          </span>
        </span>
      </li>
      <li class="headertab-li headertab-li-active" @click.prevent="sheetVisible3 = true">
        <span class="headertab-span c-font-list">
          {{huibaoValue}}
          <span class="all-tab-span all-tab-right">
            <span class="all-tab-nbsp">&nbsp;</span>
          </span>
        </span>
      </li>
    </ul>

    <div class="mainContent">
      <ul class="personalinfo-ul">
        <!-- <div > -->
        <vue-scroll
          ref="scroll"
          :ops="opsconfig"
          @refresh-start="handleRS"
          @load-before-deactivate="handleLBD"
        >
         <div class="nodata" v-show="alldata.length <=0">----- 暂无数据 -----</div>
          <li v-for="(v,i) in alldata" :key="i" class="personalinfo-li">
            <ul class="detailsinfo-ul">
              <li class="detailsinfo-left" @click.stop="gotoInfo(v)">
                <img v-lazy="v.avatar==''?touxiang:v.avatar" alt="icon" class="page-default-image" />
                <img :src="bigv" class="bigv" v-if="v.is_vip=='1'"/>  
                <span class="detailsinfo-span">
                  <span class="c-font-name">{{v.nickname}}</span>
                  <span class="c-font-content">{{v.o_name }} {{ v.j_name}}</span>
                </span>
              </li>
              <li class="detailsinfo-right">
                <!-- <mt-button type="default" size="small" @click="goToChatRoom(v.id)">急速联系</mt-button> -->
                <span class="btn" @click.stop="goToChatRoom(v)">急速联系</span>
              </li>
            </ul>
          </li>
        </vue-scroll>

        <!-- </div> -->
      </ul>
     

      <!-- <div class="placeholder">&nbsp;</div> -->
    </div>

    <mt-actionsheet :actions="actions1" v-model="sheetVisible1"></mt-actionsheet>
    <mt-actionsheet :actions="actions2" v-model="sheetVisible2"></mt-actionsheet>
    <mt-actionsheet :actions="actions3" v-model="sheetVisible3"></mt-actionsheet>
  </div>
</template>

<script>
import { ScrollConfig } from "common/data.js";
import { ComUtils } from "common/utils";
import { Toast } from "mint-ui";
export default {
  name: "profinder",
  props: {},
  data() {
    return {
       bigv:require("images/bigv.png"),
        touxiang:require("images/touxiangz.png"),
      opsconfig: ScrollConfig.opsconfig,
      titleName: "项目找人",
      needb: true,
      resultSet: "",
      sheetVisible1: false,
      sheetVisible2: false,
      sheetVisible3: false,
      actions1: [],
      actions2: [],
      actions3: [],
      alldata: [],
      UserInfo: this.$getUser,
      itemarrary: {
        "address_id[]": "",
        "occupation_id[]": "",
        "job_id[]": ""
      },
      max_id: "",
      min_id: "",
      cityfield: "城市",
      industryfield: "行业",
      huibaoValue: "职务"
    };
  },
  computed: {},
  created() {
    this.getactions1();
    this.getactions2();
    this.getactions3();
  },
  mounted() {},
  activated() {
    let data = {
      id: "319430217930703154",
      token:
        "5cec187gbd89e56deb4ff3aa102fd4ad128d7ae45d8c3c6546ed8173e4901323840"
    };
    ComUtils.saveParams(data);
    this.pullactionstate();
  },
  watch: {
    resultSet: function() {
      if (document.querySelector("input") == document.activeElement) {
        this.pullactionstate();
      } else {
        this.pullactionstate();
      }
    }
  },

  methods: {
    clearParamfn(e) {
      console.log("点击" + JSON.stringify(e));
      if (e.name == "全部城市") {
        this.cityfield = "城市";
        this.itemarrary["address_id[]"] = "";
        this.pullactionstate();
      } else if (e.name == "全部行业") {
        this.industryfield = "行业";
        this.itemarrary["occupation_id[]"] = "";
        this.pullactionstate();
      } else {
        this.huibaoValue = "职务";
        this.itemarrary["job_id[]"] = "";
        this.pullactionstate();
      }
      // this.itemarrary = { "address_id[]": "", "occupation_id[]": """omechanism_id[]" };
      // window.sessionStorage.removeItem("SEARCHITEM");
    },
    goBackPage() {
      this.$router.push("find");
    },
    async pullactionstate(v, i) {
  
      try {
        // window.sessionStorage.removeItem("SEARCHITEM");
        let params = {
          key: this.resultSet,
          count: 10,
          direction: v,
          target_id: i
        };
        Object.assign(params, this.itemarrary);
        let res = await this.$api.all.GETfn(params, "user_recommend");
        res = res.result;
        this.max_id = res.max_id;
        this.min_id = res.min_id;
          if (v) {
             console.log("有下拉")
            if (v == "bigger") {
              res.data.forEach((item, index, arr) => {
                this.alldata.unshift(item);
              });
            } else {
              res.data.forEach((item, index, arr) => {
                this.alldata.push(item);
              });
            }
          } else{
             if(res.length<=0){
          this.alldata = [];
           return false; 
        }
          this.alldata = res.data;

          }
      } catch (error) {
        console.log(error);
      }
    },
    selectactions1(e, index) {
      this.cityfield = e.name;
      this.itemarrary["address_id[]"] = e.id;
      this.pullactionstate();
    },
    selectactions2(e, index) {
      this.itemarrary["occupation_id[]"] = e.id;
      this.industryfield = e.name;
      this.pullactionstate();
    },
    selectactions3(e, index) {
      this.itemarrary["job_id[]"] = e.id;
      this.huibaoValue = e.name;
      this.pullactionstate();
    },
    gotoInfo(item) {
      app.goUserInfo(JSON.stringify(item));
    },
    goToChatRoom(item) {
      app.goChatRoom(JSON.stringify(item));
    },
    appkeyHide(){
      app.keyHide()
    },
    async getactions1() {
      try {
        let that = this;
        let params = {};
        let res = await this.$api.all.POSTfn(params, "person_area");
        res = res.result;
        let _param = {
          name: "全部城市",
          method: that.clearParamfn
        };
        this.actions1.push(_param);
        res.forEach((v, i, arr) => {
          let param = {
            name: v.name,
            id: v.id,
            type: v.type,
            method: that.selectactions1
          };
          this.actions1.push(param);
        });

        console.log(this.actions1);
      } catch (error) {
        console.log(error);
      }
    },
    async getactions2() {
      try {
        let that = this;
        let params = {};
        let res = await this.$api.all.POSTfn(params, "person_type");
        res = res.result;
        let _param = {
          name: "全部行业",
          method: that.clearParamfn
        };
        this.actions2.push(_param);
        res.forEach((v, i, arr) => {
          let param = {
            name: v.name,
            id: v.id,
            type: v.type,
            method: that.selectactions2
          };
          this.actions2.push(param);
        });
        console.log(this.actions2);
      } catch (error) {
        console.log(error);
      }
    },
    async getactions3() {
      try {
        let that = this;
        let params = {};
        let res = await this.$api.all.POSTfn(params, "jobList");
        console.log(res)
        res = res.result.job;
       
        let _param = {
          name: "全部职务",
          method: that.clearParamfn
        };
        this.actions3.push(_param);
        res.forEach((v, i, arr) => {
         

              let param = {
            name: v.name,
            id: v.id,
            type: v.type,
            method: that.selectactions3
          };
          this.actions3.push(param);
        
        
          
        });
        console.log(this.actions3);
      } catch (error) {
        console.log(error);
      }
    },
    async handleRS(vsInstance, refreshDom, done) {
      // this.pullactionstate("bigger", this.max_id);
      // this.$forceUpdate();
      /************************************* */
      done();
    },
    async handleLBD(vm, loadDom, done) {
      this.pullactionstate("litter", this.min_id);
      this.$forceUpdate();
      /************************************* */
      done();
    }
  },
  components: {},
  destoryed() {
    window.sessionStorage.removeItem("SEARCHITEM");
    this.$destory();
  }
};
</script>

<style scoped lang="css">
.mainContent {
  height: calc(100vh -80px) !important;
  min-height: calc(100vh -80px) !important;
}

.placeholder {
  width: 100%;
  height: 2px;
  line-height: 2px;
}
.btn {
  font-size: 12px;
  color: #ffffff;
  background: #3966a7;
  border-radius: 3px;
  padding: 5px 7px;
}
>>> .mint-actionsheet-list{
  max-height: 400px !important;
  overflow-y: auto !important;

}

.mint-button--small {
  height: 28px;
  min-width: 80px;
  line-height: 28px;
}
.headersearch >>> .mint-searchbar-cancel {
  color: #3966a7 !important;
}
.headertab-span {
  display: block;
  width: 100%;
  height: 20px;
}
.headertab-span * {
  vertical-align: middle;
}
.all-tab-span {
  display: inline-block;
  width: 8px;
  height: 8px;
  /* position: relative;
  left: 76%; */
  /* margin-left: -8px; */
  border-right: 6px solid transparent;
  border-left: 6px solid transparent;
  border-top: 6px solid #ddd;
}

.all-tab-nbsp {
  width: 0px;
  height: 0px;
}

.headersearch {
  height: 46px;
  line-height: 46px;
  font-size: 15px;
  background: #f6f6f7;
  padding: 0 10px;
}

.headertab-ul {
  width: 100%;
   border-bottom: 1px solid #eee;
      padding-left: 2px
}

.headertab-li {
  list-style: none;
  flex: 1;
  text-align: center;
  min-height: 44px;
  line-height: 44px;
 
  vertical-align: bottom;
  /* padding-right: 8px; */
  float: left;
  min-width: 30.9%;
}

.personalinfo-ul {
  display: block;
  width: 100%;
  line-height: 60px;
  height: calc(100vh -80px) !important;
  min-height: calc(100vh -80px) !important;
}

.personalinfo-li {
  display: block;
  width: 100%;
  height: 60px;
  position: relative
  /* margin: 6px 0px; */
}

.detailsinfo-ul {
  width: 100%;
  display: flex;
  height: 60px;
  list-style: none;
}

.detailsinfo-left {
  width: 280px;
  display: flex;
  align-items: center;
  height: 60px;
}

.detailsinfo-left img {
  width: 40px;
  height: 40px;
  margin: 8px 0px 8px 12px;
  border-radius: 50%;
}

.detailsinfo-left span.detailsinfo-span {
  flex: 1;
  margin-left: 8px;
}

span.detailsinfo-span span {
  display: block;
  height: 22px;
  line-height: 22px;
  /* margin: 5px; */
}

span.detailsinfo-name {
  font-size: 14px;
}
span.detailsinfo-phone {
  font-size: 12px;
}

.detailsinfo-right {
  flex: 1;
  height: 60px;
}
.c-font-name {
  color: #3966a7;
}
.searchinput {
  border-radius: 20px;
  padding: 5px 10px 5px 30px;
  width: 100%;
  height: 30px;
}
.searchinput:focus {
  background: #ffffff;
}
>>> .mintui-search {
  position: absolute;
  top: 1px;
  left: 20px;
  color: #3966a7;
}
.personalinfo-li .bigv{
  height: 16px;
  width: 16px;
  position:absolute;
  top: 30px;
  left: 25px;
}
.nodata{
  color: #666;
  text-align: center;
  margin-top: 30px
}
</style>



