<template>
  <div class="mainCover">
    <HeaderBar :needm="needm" :needb="needb" :tit="titleName"></HeaderBar>
    <div class="mainContent">
      <ul class="gender-ul">
        <li class="gender-li">
          <span class="gender-span gender-span-left">男</span>
          <span class="gender-span gender-span-right">
            <mt-switch v-model="MAN" @click.native.prevent="setSexFn(0)"></mt-switch>
          </span>
        </li>
        <li class="gender-li">
          <span class="gender-span gender-span-left">女</span>
          <span class="gender-span gender-span-right">
            <mt-switch v-model="WOMEN" @click.native.prevent="setSexFn(1)"></mt-switch>
          </span>
        </li>
        <li class="gender-li">
          <span class="gender-span gender-span-left">保密</span>
          <span class="gender-span gender-span-right">
            <mt-switch v-model="DONTWALK" @click.native.prevent="setSexFn(2)"></mt-switch>
          </span>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { ComUtils } from "common/utils";
export default {
  name: "personal_gender",
  props: {},
  data() {
    return {
      titleName: "更改性别",
      needb: true,
      needm: false,
      search: "",
      MAN: false,
      WOMEN: false,
      DONTWALK: false,
      sexvalue: "",
      UserInfo: this.initParam()
    };
  },
  computed: {},
  created() {
    this.initParam();
  },
  mounted() {},
  watch: {
    "$route.path": function() {
      this.initParam();
    }
  },
  activated() {
    this.initParam();
  },
  methods: {
    initParam() {
      this.$nextTick(() => {
        this.sexvalue = this.$getUser.sex;
        this.UserInfo = this.$getUser;
        this.getSexInfo();
      });
    },
    async setSexFn(index) {
      try {
        if (index == "0") {
          if (!this.WOMEN && !this.DONTWALK && this.MAN) {
            this.$toast("至少选择一项");
            setTimeout(() => {
              this.getSexInfo();
            }, 1000);
            return;
          }
          this.WOMEN = false;
          this.DONTWALK = false;
        } else if (index == "1") {
          if (!this.MAN && !this.DONTWALK && this.WOMEN) {
            this.$toast("至少选择一项");
            setTimeout(() => {
              this.getSexInfo();
            }, 1000);
            return;
          }
          this.MAN = false;
          this.DONTWALK = false;
        } else if (index == "2") {
          if (!this.WOMEN && !this.MAN && this.DONTWALK) {
            this.$toast("至少选择一项");
            setTimeout(() => {
              this.getSexInfo();
            }, 1000);
            return;
          }
          this.WOMEN = false;
          this.MAN = false;
        }
        let params = {
          sex: index,
          name: this.UserInfo.name,
          sign: this.UserInfo.sign
        };
        let res = await this.$api.all.GETfn(params, "set_user");
        if (res.result == "ok") {
          this.$toast("修改成功");
          this.UserInfo.sex = index;
          ComUtils.saveParams(this.UserInfo);
          this.$router.go(-1);
        } else {
          this.$toast(res.result);
        }
        console.log(res.result);
      } catch (e) {
        console.log(e);
      }
    },
    getSexInfo() {
      if (this.sexvalue == "0") {
        this.MAN = true;
        this.WOMEN = false;
        this.DONTWALK = false;
      } else if (this.sexvalue == "1") {
        this.WOMEN = true;
        this.MAN = false;
        this.DONTWALK = false;
      } else if (this.sexvalue == "2") {
        this.DONTWALK = true;
        this.MAN = false;
        this.WOMEN = false;
      }
    }
  },
  components: {}
};
</script>

<style scoped lang="css">
.gender-ul {
  padding: 0px 12px;
}

.gender-li {
  list-style: none;
  height: 50px;
  line-height: 50px;
  font-size: 14px;
  display: flex;
  border-bottom: 1px solid #eee;
}

.gender-li span {
  display: inline-block;
  height: 50px;
  line-height: 50px;
}

span.gender-span-left {
  padding-left: 4%;
  width: 75%;
}

span.gender-span-right {
  flex: 1;
  padding: 15px 0px;
}
</style>



