<template>
  <div class="mainCover">
    <HeaderBar
      :needcsb="needb"
      @fatherbackcall="backIntegral"
      :needj="needj"
      :tit="titleName"
    >
    </HeaderBar>
    <mt-navbar v-model="selected">
      <mt-tab-item id="1"><span><img :src="integral" alt="" srcset="" v-show="show_img"> <img :src="integralActive" alt="" srcset="" v-show="img_show">赚积分</span></mt-tab-item>
      <mt-tab-item id="2"><span><img :src="gift" alt="" srcset="" v-show="img_show"><img :src="giftActive" alt="" srcset="" v-show="show_img">几分兑换(600)</span></mt-tab-item>
    </mt-navbar>

    <div class="mainContent">
      <!-- <p v-show="allList.length==0" class="nothing">——— 暂无商品 ———</p> -->
      <ul class="scroll-ul">
        <vue-scroll
          ref="scroll"
          :ops="opsconfig"
          @refresh-start="handleRS"
          @load-before-deactivate="handleLBD"
        >
         <mt-tab-container v-model="selected">
        <mt-tab-container-item id="1">
            <div class="task">
              <div >
                <p>用户登录</p>
                <p class="task_plan">用户每天登陆(1/1) 可获得20积分奖励</p>
              </div>
              <p>去完成</p>
            </div>
            <div class="task">
              <div >
                <p>发布动态</p>
                <p class="task_plan">发布动态(1/1) 可获得20积分奖励</p>
              </div>
              <p class="completed">已完成</p>
            </div>
        </mt-tab-container-item>

        <mt-tab-container-item id="2">
          <div class="nodata">--- 敬请期待 ---</div>
        </mt-tab-container-item>
      </mt-tab-container>
        </vue-scroll>
      </ul>
    </div>
  </div>
</template>

<script>
import { ScrollConfig } from "common/data.js";

export default {
  name: "me_intergral",
  props: {},
  data() {
    return {
      selected: "1",
      opsconfig: ScrollConfig.opsconfig,
      titleName: "我的积分",
      needb: true,
      UserInfo: this.$getUser,
      needj: true,
      integral:require("images/integral.png"),
      integralActive:require("images/integralActive.png"),
       gift:require("images/gift.png"),
      giftActive:require("images/gitActive.png"),
      img_show:true,
      show_img:false
    };
  },
  computed: {},
  created() {
    this.debug = require("images/small.jpg");
    this.getDate("bigger", "0");
  },
  mounted() {},
  activated () {
    this.getDate("bigger", "0");
    this.brige()
  },
  watch: {
      selected(val, old) {
     this.img_show=!this.img_show;
     this.show_img=!this.show_img;
    },
  },
  methods: {
        brige() {   
      let that = this;
      window.getUserInfo = function(data) {
      };
    
    },
    backIntegral() {
      this.$router.push("me");
    },
    async getDate(type, numsid) {
      try {
        let param = {
         user_id: "319430217930703154"
        };
        let res = await this.$api.all.POSTfn(param, "integral/info");
      } catch (error) {}
    },
    async handleRS(vsInstance, refreshDom, done) {
        done();
    },
    async handleLBD(vm, loadDom, done) {
        done();
    }
  },
  components: {}
};
</script>

<style scoped lang="css">
/* .mint-navbar{
 margin: 5px 100px;
width: 300px !important
} */
>>>.mint-tab-item-label{
line-height: 2 !important;
}
.mint-navbar {
  padding-top: 4px;
   margin: 5px auto;
width: 380px !important
}
.mint-navbar .mint-tab-item.is-selected {
  border: none
}
.mint-navbar .mint-tab-item.is-selected span{
  padding-bottom: 5px;
  display: inline-block;
   color: #3966a7;
  border-bottom: 3px solid #3966a7;
  
}
.mint-navbar .mint-tab-item:nth-child(1){
  border-right: 1px solid #eee 
}
.mint-tab-item {
  color: #999;
  line-height: 40px;
  min-height: 40px;
}
.mint-tab-item *{
  vertical-align: middle
}
.mint-tab-item img{
  height: 35px;
  width: 35px;
}
.mint-tab-container-item {
 
}
.task{
  display: flex;
  justify-content:space-between;
  border-bottom: 1px solid #eee;
  padding: 8px 10px 
}
.task div{
  color: #333;
}
.task_plan{
  color: #999;
  font-size: 14px
}
.task>p{
  height: 34px;
  line-height: 30px;
  width: 90px;
  text-align: center;
  border: 2px solid #666;
  color: #333;
  border-radius: 20px;
  margin-top: 5px
}
.task .completed{
  color: #3966a7;
  border: 2px solid #3966a7
}
.nodata{
  text-align: center;
  color: #666;
  margin-top: 100px;
  font-size: 20px
}
</style>



