<template>
  <div class="mainCover">
    <HeaderBar :needm="needm" :needb="needb" :tit="titleName"></HeaderBar>
    <div class="mainContent"></div>
    <div class="footer-input">
      <ul class="footer-input-ul">
        <li class="footer-input-li footer-input-li-left">
          <input type="text" class="footer-input-input" />
        </li>
        <li class="footer-input-li footer-input-li-right" @click.prevent="alertFn">发送</li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "chatroom",
  props: {},
  data() {
    return {
      titleName: "聊天室",
      needb: true,
      needm: false
    };
  },
  computed: {},
  created() {},
  mounted() {},
  watch: {},
  methods: {
    alertFn() {}
  },
  components: {}
};
</script>

<style scoped lang="css">
.footer-input {
  min-height: 44px;
  line-height: 44px;
  width: 100%;
}

.footer-input-ul {
  min-height: 44px;
  line-height: 44px;
  width: 100%;
  display: flex;
  background: #4a8dff;
  color: #fff;
}

.footer-input-li-left {
  width: 310px;
  min-height: 44px;
  line-height: 44px;
  padding: 0 0 0 13px;
}

.footer-input-li-right {
  flex: 1;
  text-align: center;
}

.footer-input-input {
  width: 100%;
  min-height: 30px;
  line-height: 30px;
  /* margin: 3px 0; */
  border-radius: 3px;
  text-indent: 3px;
  font-size: 14px;
  white-space: normal;
  word-wrap: break-word;
  word-break: break-all;
  background:#fff;
}
</style>



