<template>
  <div class="mainCover">
    <mt-search
      cancel-text="取消"
      placeholder="请输入搜索信息"
      v-model="searchValue"
      ref="findcancel"
      v-show="isShow"
    ></mt-search>
    <div class="canceldiv" @click.prevent="$router.back(-1)">&nbsp;</div>
    <div class="nav" ref="nav">
      <swiper :options="swiperOption">
        <swiper-slide
          v-for="(v,i) in tab"
          :key="i"
          :class="{'nav-swiper-slide' : true,'nav-longer' : v.name.length==4,'nav-short' : v.name.length<=3,'nav-biglonger' : v.name.length>4 }"
        >
          <div
            :class="active == 'tab'+i ? 'nav-li active' : 'nav-li'"
            @click.prevent="changeactivewang(v,i,$event)"
          >{{v.name}}</div>
        </swiper-slide>
      </swiper>
    </div>

    <div class="mainContent">
      <allSearch ref="zonghe" v-show="blockShow"></allSearch>
      <!-- <zhuanti v-show="blockShow1"></zhuanti> -->
      <vadioNew v-show="blockShow2"></vadioNew>
      <huati v-show="blockShow3"></huati>
      <person v-show="blockShow4"></person>
      <vue-scroll
        ref="scroll"
        :ops="opsconfig"
        @refresh-start="handleRS"
        @load-before-deactivate="handleLBD"
        @refresh-before-deactivate="handleRBD"
        @load-start="handleLoadStart"
        v-show="blockShow1"
      >
        <ul id="v-article" class="ull" v-cloak ref="text">
          <li v-for="(item, key) in newList" :key="key" @click.stop="godetail(item)">
            <div class="itemlist" v-if="item.type_id!='3' && (parseInt(item.coverType)==1 || parseInt(item.coverType)==2 || parseInt(item.coverType)==4)">
              <img :src="item.cover" :data-src="item.cover" alt class="project-img" />
              <div class="iteml">
                <div class="hot-name">
                  <p v-html="item.title"></p>
                </div>
                <div class="hot-footer">
                  <p class="hot-keyword clearfix">
                    <span class="com-span-title" >{{item.source_category?item.source_category:"来源"}}</span>
                    <span class="date">{{item.date}}</span>
                  </p>
                </div>
              </div>
            </div>
            <!-- 多图详情-->
            <div class="com-text" v-if="parseInt(item.coverType)==3">
              <ul class="com-ul">
                <li class="sign-li-title" v-html="item.title"></li>
                <ul class="com-last-ul">
                  <li class="more-li-img" v-for="(s,y) in item.image_ids" :key="y">
                    <img v-lazy="s" class="page-lazyload-image1" />
                  </li>
                </ul>
                <li class="com-li-info">
                  <span class="com-span-title">{{item.source_category?item.source_category:"来源"}}</span>
                  <span class="com-span-pre">浏览量:{{item.clicks}}</span>
                  <span class="com-span-time">{{item.date}}</span>
                </li>
              </ul>
            </div>
            <!-- 视频新闻 -->
            <!-- <div v-if="item.type_id=='3'"> -->
            <div class="comm-body" v-if="item.type_id=='3'">
              <div class="hh-face-img">
                <img class="page-lazyload-image1 bg" v-lazy="item.cover" alt />
                <img class="bofang" src="../../../static/imgs/bo1.png" alt />
                <p class="clickNum">
                  {{item.clicks}}次播放
                  <span>51:20</span>
                </p>
                <div class="masking">
                  <p v-html="item.title"></p>
                </div>
              </div>
              <div class="ciomm-footer clearfix">
                <div class="comm-keyword">
                  <span>{{item.date}}</span>
                  <div class="iconbox">
                    <i class="iconfont icon-good">
                      <span>{{item.flowers}}</span>
                    </i>
                    <i class="commueicon com-icon iconfont icon-commue">
                      <span class="hh-fab-value">{{item.comments}}</span>
                    </i>
                    <i class="shareicon com-icon iconfont icon-share">
                      <span>{{item.shares}}</span>
                    </i>
                  </div>
                </div>
              </div>
            </div>
          </li>
        </ul>
        <div slot="load-beforeDeactive" v-if="noSscrollData">暂无更多</div>
      </vue-scroll>
    </div>
  </div>
</template>

<script>
import { ScrollConfig } from "common/data.js";
// import AutoScroll from "assets/script/autoScroll";
import { Toast } from "mint-ui";
import zhuanti from "pages/home/zhuanti.vue";
import vadioNew from "pages/home/vadioNew.vue";
import person from "com/person.vue";
import newList from "pages/home/newslist.vue";
import allSearch from "com/allSearch.vue";
import huati from "com/huati.vue";
let autoScrollInstance = null;
export default {
  name: "search",
  props: {},
  data() {
    return {
      blockShow: true,
      blockShow1: false,
      blockShow2: false,
      blockShow3: false,
      blockShow4: false,
      active: "tab0",
      searchValue: "",
      routerPage: "",
      newList: [],
      opsconfig: ScrollConfig.opsconfig,
      refresh: 1,
      noSscrollData: false,
      triggerType: "load",
      iddata: [],
      isShow: false,
      navActiveIndex: 0, //当前高亮的tab选项卡index
      swiperOption: {
        slidesPerView: 7,
        // spaceBetween: 1,
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        }
      },
      allSaerchD: [],
      tab: [
        {
          name: "综合",
          id: "0"
        },
        {
          name: "咨讯",
          id: "1"
        },
        {
          name: "公社",
          id: "2"
        },
        {
          name: "视频",
          id: "3"
        },
        {
          name: "图片",
          id: "4"
        },
        {
          name: "用户",
          id: "5"
        },
        {
          name: "项目",
          id: "6"
        },
        {
          name: "话题",
          id: "7"
        }
      ]
    };
  },
  computed: {},
  created() {
    this.routerPage = this.$route.params.pathname;

    // this.getnewdate();
  },
  mounted() {},
  activated() {
    this.newList=[]
    if (window.hasOwnProperty("webkit")) {
      this.isShow = false;
    } else {
      this.isShow = true;
    }
    this.brige();
    let that = this;
    setTimeout(function() {
      that.$refs.zonghe.allData();
    });
  },
  watch: {
    searchValue: function() {
      this.focus();
    },
    $route: function() {
      this.$nextTick(function() {
        this.$refs.text.click();
      });
    }
  },
  methods: {
    getIos(item, gourl) {
      let that = this;
      let isLogin = window.localStorage.getItem("isLogin");
      let loginstauts;
      if (isLogin == true || isLogin == "true") {
        loginstauts = "1";
      } else {
        loginstauts = "0";
      }
      var str = location.href;
      str = str.match(/(\S*)\#/)[1];
      let localUrl = str + "#/";
      let param = {
        articleId: item.id + "",
        articleUrl: localUrl + gourl,
        // 'articleType':type.category+"",
        loginstating: loginstauts,
        userId: this.$getUser.id + "",
        userToken: this.$getUser.token + "",
        route: localUrl + "login"
      };
      if (window.hasOwnProperty("webkit")) {
        window.webkit.messageHandlers.openWebView.postMessage(param);
      } else {
      }
    },
    brige() {
      let that = this;
      //  Toast("ios传过来的参数1")
      window.searchvalue = function(data) {
        //  Toast("ios传过来的参数"+JSON.stringify(data))
        if (data != undefined && data.hasOwnProperty("text")) {
          that.getnewdate(data.text);
        } else {
        }
      };
    },
    focus: function() {
      if (document.querySelector("input") == document.activeElement) {
        this.fuzzy_search(this.searchValue.trim());
      }
    },
    fuzzy_search: function(value) {
      var that = this;
      if (!value) {
        that.newList = [];
        return;
      }
      this.getnewdate();
    },
    changeactivewang(v, i) {
      this.active = "tab" + i;
      if (i == 0) {
        this.blockShow = true;
        this.blockShow1 = false;
        this.blockShow2 = false;
        this.blockShow3 = false;
        this.blockShow4 = false;
      } else if (i == 1 || i == 2 || i == 4 || i == 6) {
        this.blockShow = false;
        this.blockShow1 = true;
        this.blockShow2 = false;
        this.blockShow3 = false;
        this.blockShow4 = false;
      } else if (i == 7) {
        this.blockShow = false;
        this.blockShow1 = false;
        this.blockShow2 = false;
        this.blockShow3 = true;
        this.blockShow4 = false;
      } else if (i == 5) {
        this.blockShow = false;
        this.blockShow1 = false;
        this.blockShow2 = false;
        this.blockShow3 = false;
        this.blockShow4 = true;
      } else {
        this.blockShow = false;
        this.blockShow1 = true;
        this.blockShow2 = false;
        this.blockShow3 = false;
        this.blockShow4 = false;
        this.newList=[]
         this.getnewdate("13")

      }
    },
    // 新闻数据
    async getnewdate(type,text, direct, hot) {
      try {
        let params = {
          s: this.searchValue,
          // s: text,
          category: type,
          count: 10,
          hot_id: hot,
          direction: direct,
          disable_cache: 0
        };
        let res = await this.$api.home.GETfn(params, "search");
        if (res.data.length == 0) {
          Toast("无搜索内容");
        }
         for(let i in res.data){
          res.data[i].date=this.timeago(res.data[i].date*1000)
        }
        // this.newList = res.data;
        if (direct == "bigger") {
          res.data = res.data.concat(this.newList);
        } else {
          res.data = this.newList.concat(res.data);
        }
        this.iddata = res;
        this.changeColor(res.data);
      } catch (e) {
        console.log(e);
      }
    },
    changeColor(resultsList) {
      resultsList.map((item, index) => {
        if (this.searchValue && this.searchValue.length > 0) {
          // 匹配关键字正则
          let replaceReg = new RegExp(this.searchValue, "g");
          // 高亮替换v-html值
          let replaceString =
            '<span style="color:red">' + this.searchValue + "</span>";
          resultsList[index].title = item.title.replace(
            replaceReg,
            replaceString
          );
        }
      });
      this.newList = [];
      this.newList = resultsList;
    },
    handleRS(vsInstance, refreshDom, done) {
      // alert("这是执行下拉刷新开始做啥事");
      done();
    },
    handleLoadStart(vm, dom, done) {
      setTimeout(() => {
        // alert("这是执行下拉加载之前需要执行什么操作");

        done();
      }, 600);
    },
    handleLBD(vm, loadDom, done) {
      let dictir = "litter";
      let hot = this.iddata.min_id;
      console.log(this.iddata.has_more);
      if (this.iddata.has_more == false) {
        this.noSscrollData = true;
        Toast("无搜索内容");
        done();
        return;
      }
      this.getnewdate(dictir, hot);
      done();
    },
    handleRBD(vm, loadDom, done) {
      let dictir = "bigger";
      let hot = this.iddata.max_id;
      console.log(this.iddata.has_more);
      if (this.iddata.has_more == false) {
        this.noSscrollData = true;
        Toast("无搜索内容");
        done();
        return;
      }
      this.getnewdate(dictir, hot);
      done();
    },
    //点击浏览
    async setclick(item) {
      try {
        let params = {
          // userid: "327195464540160441",
          target: item.id,
          action: 1,
          value: 1
        };
        let res = await this.$api.all.GETfn(params, "setclick");
        console.log(res);
        if (res.result.result == "ok") {
        }
      } catch (e) {
        console.log(e);
      }
    },
    // 跳转到详情页
    godetail(item) {
      let gourl = "";
      if (item.type_id == "2") {
        localStorage.setItem("homedata", JSON.stringify(item));

        gourl = "pictureDetail";
      } else if (item.type_id == "4") {
        localStorage.setItem("zhuantidata", JSON.stringify(item));
        gourl = "subjectDetail";
      } else {
        localStorage.setItem("homedata", JSON.stringify(item));
        localStorage.setItem("atype", item.category_id);
        gourl = "homeDetail";
      }
      this.setclick(item);
      if (window.hasOwnProperty("webkit")) {
        this.getIos();
      } else {
        window.sessionStorage.setItem("homedata", JSON.stringify(item));
        localStorage.setItem("homedata", JSON.stringify(item));
        this.$router.push({
          // path: "/homeDetail",
          name: gourl
        });
      }
    },
    timeago(dateTimeStamp) {
      //dateTimeStamp是一个时间毫秒，注意时间戳是秒的形式，在这个毫秒的基础上除以1000，就是十位数的时间戳。13位数的都是时间毫秒。
      var result;
      var minute = 1000 * 60; //把分，时，天，周，半个月，一个月用毫秒表示
      var hour = minute * 60;
      var day = hour * 24;
      var week = day * 7;
      var halfamonth = day * 15;
      var month = day * 30;
      var now = new Date().getTime(); //获取当前时间毫秒
      var diffValue = now - dateTimeStamp; //时间差

      if (diffValue < 0) {
        return;
      }
      var minC = diffValue / minute; //计算时间差的分，时，天，周，月
      var hourC = diffValue / hour;
      var dayC = diffValue / day;
      // var weekC = diffValue / week;
      // var monthC = diffValue / month;
      // if (monthC >= 1 && monthC <= 3) {
      // 	result = " " + parseInt(monthC) + "月前";
      // } else if (weekC >= 1 && weekC <= 3) {
      // 	result = " " + parseInt(weekC) + "周前";
      // } else
      if (dayC >= 1 && dayC <= 3) {
        result = " " + parseInt(dayC) + "天前";
      } else if (hourC >= 1 && hourC <= 23) {
        result = " " + parseInt(hourC) + "小时前";
      } else if (minC >= 1 && minC <= 59) {
        result = " " + parseInt(minC) + "分钟前";
      } else if (diffValue >= 0 && diffValue <= minute) {
        result = "刚刚";
      } else {
        var datetime = new Date();
        datetime.setTime(dateTimeStamp);
        var Nyear = datetime.getFullYear();
        var Nmonth =
          datetime.getMonth() + 1 < 10
            ? "0" + (datetime.getMonth() + 1)
            : datetime.getMonth() + 1;
        var Ndate =
          datetime.getDate() < 10
            ? "0" + datetime.getDate()
            : datetime.getDate();
        var Nhour =
          datetime.getHours() < 10
            ? "0" + datetime.getHours()
            : datetime.getHours();
        var Nminute =
          datetime.getMinutes() < 10
            ? "0" + datetime.getMinutes()
            : datetime.getMinutes();
        var Nsecond =
          datetime.getSeconds() < 10
            ? "0" + datetime.getSeconds()
            : datetime.getSeconds();
        result = Nyear + "-" + Nmonth + "-" + Ndate;
      }
      return result;
    }
  },
  components: {
    allSearch,
    vadioNew,
    zhuanti,
    huati,
    person
  }
};
</script>

