  /* min-height: 80px; */
  display: flex;
  justify-content: space-between;
}
.content {
  min-height: calc(100vh- 40px) !important;
  height: calc(100vh- 40px) !important;
}
.com-left {
  width: 80%;
  padding-right: 10px;
  padding-bottom: 4px;
  display: flex;
  flex-wrap: wrap;
  align-content: space-between;
}

.more-li-img {
  width: 33%;
}

.more-li-img img {
  width: 100%;
  padding: 0px 2px;
  height: 70px;
  border-radius: 3px;
}

.sign-li-title {
  font-size: 14px;
  color: #333;
  min-height: 22px;
  max-height: 44px;
  line-height: 22px;
  margin-bottom: 5px;
  width: 100%;
  flex: 1;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
}
/* graph-li-title */
.graph-li-title {
  /* flex: 1; */
  font-size: 14px;
  color: #333;
  min-height: 22px;
  max-height: 44px;
  line-height: 22px;
  width: 100%;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
}

.sign-li-img {
  width: 100%;
}

.sign-li-img img {
  width: 100%;
  max-height: 180px;
  border-radius: 3px;
}

.graph-li-img {
  width: 40%;
}

.graph-li-img img {
  width: 100%;
  height: 75px;
  max-height: 180px;
  border-radius: 3px;
}

.com-li-info {
  width: 100%;
  display: flex;
  height: 16px;
  line-height: 16px;
  font-size: 12px;
}

.com-li-info span {
  display: inline-block;
  height: 16px;
  line-height: 16px;
}

.com-span-title {
  min-width: 70px;
  color: #3966a7 !important;
}

.com-span-pre {
  flex: 0.9;
  color: #999;
}

.com-span-time {
  min-width: 40px;
  color: #999;
}

/****
    新增文章详情
  **/

.comment-ul {
  display: flex;
  /* flex-wrap: nowrap; */
  justify-content: space-around;
}
</style>





