<template>
  <div class="iconbox">
    <i class="color iconfont icon-commue" slot="icon" v-if="!actType" @click="getparants()">
      <span class="hh-fab-value">{{num}}</span>
    </i>
    <i :class="['iconfont',like,'color']" @click="setlike()">
      <span class="hh-fab-value">{{likeNum}}</span>
    </i>

    <i :class="['iconfont',collect,'color']" @click="setcollect()"></i>
    <i class="color iconfont icon-share" @click="share()"></i>
  </div>
</template>

<script>
import pictwite from "pages/home/pictwite.vue";
import { Toast } from "mint-ui";
export default {
  name: "child",
  props: ["targetid", "target", "pro", "actType", "loginid"],
  data() {
    return {
      collect: "icon-coller",
      like: "icon-good",
      num: 1,
      likeNum: 1,
      isShow: true
    };
  },
  computed: {},
  created() {},
  activated() {},
  mounted() {},
  watch: {},
  methods: {
    // 设置收藏
    setcollect() {
      if (!this.loginid) {
          if (!this.useridaz) {
        app.goLogin()
      }
      }
      let data = {
        user_id: this.loginid,
        userid: this.loginid,
        target: this.targetid,
        _: "9jNy"
      };
      // 如果不是收藏状态点击的时候就去收藏
      if (this.collect != "icon-bigceller") {
        this.addcollect(data);
      } else {
        this.removeCollect(data);
      }
    },
    //去收藏
    async addcollect(data) {
      try {
        let res = await this.$api.all.POSTfn(data, "collect");

        if (res.result == "ok") {
          Toast("收藏成功");
          this.collect = "icon-bigceller";
            if(app !=undefined && app.hasOwnProperty('backList')){
          app.backList()
        }else{

        }
        }
        console.log(res);
      } catch (e) {
        console.log(e);
      }
    },
    //是否收藏
    async iscollect() {
      if (!this.loginid) {
        return;
      }
      try {
        let params = {
          target: this.targetid,
           userid: this.loginid,
        target: this.targetid,
        };
        let res = await this.$api.all.GETfn(params, "collect_has");
      
        if (res.result == "ok") {
          if (res.data == 1) {
            this.collect = "icon-bigceller";
          } else {
            this.collect = "icon-coller";
          }
        }
      } catch (e) {
        console.log(e);
      }
    },
    // 取消收藏
    async removeCollect(data) {
      try {
        let res = await this.$api.all.POSTfn(data, "collect_delete");
        if (res.result == "ok") {
          Toast("取消收藏成功");
          this.collect = "icon-coller";
            if(app !=undefined && app.hasOwnProperty('backList')){
          app.backList()
        }else{

        }
        }
        console.log(res);
      } catch (e) {
        console.log(e);
      }
    },
    //是否点赞
    async islike() {
      if(!this.loginid){
        return;
      }
      try {
        let params = {
          target: this.targetid,
           userid: this.loginid,
        target: this.targetid,
        };
        let res = await this.$api.all.GETfn(params, "good_has");
        if (res.result == "ok") {
         
          if (res.data == 1) {
            this.like = "icon-bigGood";
          } else {
            this.like = "icon-good";
          }
        }
      } catch (e) {
        console.log(e);
      }
    },
    // 获取评论数据
    async getcomment() {
      try {
        let params = {
            userid: this.loginid?this.loginid:2,
        target: this.targetid
        };
        let res = await this.$api.all.GETfn(params, "comment");
        this.num = res.result.total;
      } catch (e) {
        console.log(e);
      }
    },
    // 获取文章总赞数
    async likenum() {
      try {
        let params = {
          target: this.targetid,
           userid: this.loginid,
        target: this.targetid,
        };
        let res = await this.$api.all.GETfn(params, "good_count");
        if (res.result == "ok") {
          this.likeNum = res.count;
        }
      } catch (e) {
        console.log(e);
      }
    },
    // 设置点赞
    setlike() {
       if(!this.loginid){
         if (!this.useridaz) {
        app.goLogin()
      }
      }
      let params = {
        target: this.targetid,
        type: "2003",
        user_id: this.loginid,
        userid: this.loginid
      };
      // 如果不是点赞状态点击的时候就去点赞
      if (this.like != "icon-bigGood") {
        this.addlike(params);
      } else {
        this.removelike(params);
      }
    },
    //去点赞
    async addlike(data) {
      try {
        let res = await this.$api.all.POSTfn(data, "good");
      
        if (res.result == "ok") {
          Toast("点赞成功")
          this.like = "icon-bigGood";
          this.likeNum++;
            if(app !=undefined && app.hasOwnProperty('backList')){
          app.backList()
        }else{

        }
        }
        console.log(res);
      } catch (e) {
        console.log(e);
      }
    },
    // 取消点赞
    async removelike(data) {
      try {
        let res = await this.$api.all.POSTfn(data, "good_delete");
        if (res.result == "ok") {
          Toast("取消点赞成功")
          this.like = "icon-good";
          this.likeNum--;
            if(app !=undefined && app.hasOwnProperty('backList')){
          app.backList()
        }else{

        }
        }
      } catch (e) {
        console.log(e);
      }
    },
    jumpcommdetail(item, type) {
      window.sessionStorage.setItem("homedata", JSON.stringify(item));
      this.$router.push({
        path: "/pictwite",
        name: "pictwite"
      });
    },
getparants(){
  this.$emit('fatherMethod');
},
    share() {
      if (!this.loginid) {
          
        app.goLogin()
  
      }else{
        app.articleShare(JSON.stringify(this.pro));
      }
      
    }
  },
  components: { pictwite }
};
</script>

<style scoped lang="css">
.tanchu {
  height: 500px;
  width: 101%;
  overflow-y: scroll;
}
.close {
  font-size: 20px;
  padding: 10px 10px 10px 0;
  color: #333;
  text-align: right;
  position: fixed;
  bottom: 500px;
  background: #fff;
  width: 100%;
}
.iconbox {
  display: flex;
  align-items: center;
  width: 170px;
  justify-content: space-around;
  /* padding: 0px 5px 2px; */
  margin-left: 6px;
}
.iconbox i {
  display: flex;
  align-items: center;
}

.iconfont {
  font-size: 18px;
}

.icon-share {
  font-size: 15px;
}
.icon-commue {
  font-size: 16px;
}

.hh-fab-value {
  display: inline-block;
  height: 16px;
  width: 16px;
  line-height: 16px;
  text-align: center;
  font-size: 12px;
}

.commueicon {
  margin-top: 3px;
  font-size: 21px;
}
.color {
  color: #333;
}
</style>


