<template>
  <div class="mainCover">
    <div class="homeheader">
      <img v-lazy="logoimg" />
      <input type="text" @click.stop="gotoSearch('homePage')" placeholder="请输入关键词" />
    </div>
    <div class="nav">
      <swiper :options="swiperOption">
        <swiper-slide
          v-for="(v,i) in tab"
          :key="i"
          :class="{'nav-swiper-slide' : true,'nav-longer' : v.name.length==4,'nav-short' : v.name.length<=3,'nav-biglonger' : v.name.length>4 }"
        >
          <div
            :class="active == 'tab'+i ? 'nav-li active' : 'nav-li'"
            @click.prevent="changeactive(v,i,$event)"
          >{{v.name}}</div>
        </swiper-slide>
      </swiper>
    </div>
    <div class="mainContent">
      <keep-alive>
        <!-- <components :is="currcomponent" :type="typevalue" v-show="tansitionflag"></components> -->
        <components :is="currcomponent" :type="typevalue" v-show="tansitionflag"></components>
      </keep-alive>
      <components :is="currcomponent1" :type="typevalue" v-if="tansitionflag1"></components>
    </div>
  </div>
</template>
<script>
import pictureNew from "pages/home/pictureNew.vue";
import zhuanti from "pages/home/zhuanti.vue";
import vadioNew from "pages/home/vadioNew.vue";
import { mapState, mapGetters, mapMutations } from "vuex";
import vueSeamless from "vue-seamless-scroll";
import Bscroll from "better-scroll";

export default {
  props: {},
  mounted() {},
  data() {
    return {
      refresh: 1,
      logoimg: "",
      active: "tab0",
      tab: [],
      hasTabs: false,
      currcomponent: "HomeCom",
      currcomponent1: "newslist",
      typevalue: "",
      tansitionflag: true,
      tansitionflag1: false,
      swiperOption: {
        slidesPerView: 5,
        // spaceBetween: 1,
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        }
      },
      titleName: "首页"

      // optionCustomer: { direction: 3, limitMoveNum: 15, step: 0 }
    };
  },
  created() {
    if (window.plus) {
      plus.navigator.setStatusBarBackground("#4a8dff");
    }
    this.logoimg = require("images/logo.png");
        let that = this;
    this.getios(function(bridge) {
      bridge.registerHandler("getUserInfo", function(data, responseCallback) {
       
        that.changeactive(data.tiaoID);
      }); 
    });
        
  },  
  activated() {
    this.getnav()
    let that = this;
    this.getios(function(bridge) {
      bridge.registerHandler("getUserInfo", function(data, responseCallback) {
        //  alert("hhahahahhah"+JSON.stringify(data))
        //  alert(that.titleName)
        that.changeactive(data.tiaoID);
      });
    });
  },
  components: { 
    pictureNew,
    vadioNew,
    zhuanti,
    vueSeamless
  },
  computed: {
    ...mapState(["homedetailid"]),
    optionCustomer() {
      return {
        direction: 3,
        limitMoveNum: 20,
        step: 0
      };
    }
  },
  watch: {},
  methods: {
    getios(callback) { 
      if (window.GCWebviewJSBridge) {
        return callback(GCWebviewJSBridge);
      }

      // alert(window.KBWVJSBCallBacks);
      if (window.KBWVJSBCallBacks) {
        return window.KBWVJSBCallBacks.push(callback);
      }
      window.KBWVJSBCallBacks = [callback];
      var GCWVJSBIframe = document.createElement("iframe");
      GCWVJSBIframe.style.display = "none";
      GCWVJSBIframe.src = "gcwvjsbscheme://__GC_BRIDGE_LOADED__";
      document.documentElement.appendChild(GCWVJSBIframe);
      setTimeout(function() {
        document.documentElement.removeChild(GCWVJSBIframe);
      }, 0);
    },
    changeactive(v, i) {
      this.active = "tab" + i;
      if (i == 0) {
        this.typevalue = "";
        this.tansitionflag = true;
        this.tansitionflag1 = false;
      } else {
        this.tansitionflag = false;
        this.tansitionflag1 = false;
        this.currcomponent1 = "";
        this.typevalue = "";
        this.$nextTick(() => {
          this.tansitionflag1 = true;
          if (i == 1) {
            this.currcomponent1 = "vadioNew";
          } else {
            this.currcomponent1 = "newslist";
          }
          this.typevalue = v.id;
        });
      }
    },
    // changeactive(i) {
    //   this.currcomponent1 = "";
    //   this.typevalue = "";
    //   this.$nextTick(() => {
    //     this.tansitionflag1 = true;
    //     this.currcomponent1 = "newslist";
    //     this.typevalue = i;
    //     // alert("typeval" + this.typevalue);
    //   });
    // },
    gotoSearch(pathname) {
      this.$router.push({
        name: "search",
        params: {
          pathname
        }
      });
    },
    // 顶部导航
    async getnav() {
      try {
        let params = {};
        let res = await this.$api.home.GETfn(params, "nav");
        let param = {
          name: "热点",
          id: 0
        };

        this.tab = this.tab
          .concat(param)
          .concat({
            name: "视频新闻",
            id: 13
          })
          .concat(res.data);
        console.log(
          "哈哈哈哈哈哈啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊"
        );
      } catch (e) {}
    }
  }
};
</script>



<style lang="css" scoped>
/* .mint-tab-container-wrap,
.mint-tab-container,
.page-tab-container {
  min-height: 533px !important;
  height: 533px !important;
} */

/* .mint-tab-container-item {
  min-height: 533px !important;
  height: 533px !important;
} */

/* .newsCover {
  min-height: 533px !important;
  height: 533px !important;
} */

.mainContent {
  min-height: calc(100vh- 134px) !important;
  height: calc(100vh- 134px) !important;
}

.seamless-warp {
  height: 40px;
  width: 375px;
  overflow: hidden;
}
.comm-name p {
  /* height: 40px; */
  /* line-height: 40px; */
  margin: 15px 12px 10px 12px;
  font-weight: 600;
}

.hh-face-img img {
  min-height: 200px;
  max-height: 250px;
}
.swiper-slide {
  /* padding: 0px 8px; */
}
.homeheader {
  min-height: 44px;
  height: 44px;
  line-height: 44px;
  background: #4a8dff;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 7px 0px;
}
.homeheader img {
  display: inline-block;
  width: 30px;
  height: 30px;
  margin: 0px 7px;
}
.homeheader input {
  flex: 1;
  margin-left: 10px;
  margin-right: 16px;
  height: 30px;
  line-height: 30px;
  border: none;
  text-decoration: none;
  border-radius: 3px;
  text-indent: 2em;
  font-size: 12px;
  background: #fff;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.swiperTitle {
  /* height: 20px;
  line-height: 20px; */
  /* font-size: 19px; */

  /* padding-left: 5px; */
  /* box-sizing: border-box; */
  /* font-weight: 700; */
  /* border-left: 5px solid #1b81cb; */
  /* margin: 20px 12px 0px 12px; */
  /* display: block; */
}

.c-part-block {
  margin-top: 8px;
}

.comm-body img {
  width: 100%;
}
.comm-keyword {
  display: flex;
  justify-content: space-between;
  /* padding: 15px; */
  /* font-size: 14px; */
  margin: 0px 12px;
}
/* tab*/
.mint-tab-item {
  color: black;
  -webkit-box-flex: 1;
  -webkit-flex: 1 0 auto;
  -ms-flex: 1 0 auto;
  flex: 1 0 auto;
  padding: 3px 5px;
}
/* .nav::-webkit-scrollbar {
  display: none;
} */
.nav {
  width: 380px;
  /* width: 100%; */
  min-height: 40px;
  line-height: 40px;

  /* padding: 5px 0px 0px 0px; */
}

.nav-ul {
  height: 40px;
  line-height: 40px;
}
.nav .swiper-container {
  border-bottom: 1px solid #eee;
}

>>> .nav-swiper-slide {
  /* display: inline-block; */
  height: 40px;
  font-size: 14px;
  color: #222;
  /* min-width: 50px !important; */
  /* width: 50px !important; */
  background: #fff;
  text-align: center;
}

.nav-longer {
  min-width: 75px !important;
  max-width: 105px !important;
}

.nav-biglonger {
  min-width: 103px !important;
  max-width: 110px !important;
}

.nav-short {
  min-width: 45px !important;
  max-width: 55px !important;
}

.nav-li {
  display: inline-block;
  height: 40px;
  font-size: 14px;
  color: #222;
  /* min-width: 50px !important; */
  background: #fff;
  text-align: center;
  width: 100%;
}

.nav-li.active {
  font-size: 16px;
  color: #4a8dff;
  min-width: 35px;
  width: 100%;
  max-width: 80px !important;
}
/* .swiper-container{
  width: 400px
} */
a.mint-tab-item {
  display: inline-block;
  height: 30px;
  line-height: 30px;
}

div.mint-tab-item-label {
  display: inline-block;
  height: 50px !important;
  line-height: 50px !important;
  font-size: 15px;
}

/* .mint-navbar .mint-tab-item.is-selected {
  border-bottom: 3px solid red;
  color: red;
  margin-bottom: -3px;
} */
.mint-tab-item {
  flex-basis: 10px;
  flex-shrink: 10;
  white-space: nowrap;
  /* padding: 10px 0 */
}

input[type="button" i],
input[type="submit" i],
input[type="reset" i],
input[type="file" i]::-webkit-file-upload-button,
button {
  border-color: #ffffff;
}

.swiperbox .swiper-container {
  margin-top: 30px !important;
}
</style>


