<template>
  <div class="commMain">
    <p class="swiperTitle">
      <span class="c-title-vline"></span> 热门评论
    </p>
    <div class="childcomme">
      <li v-for="(i,t) in comment" :key="t" class="por" @click.stop="gocommedetail(i)">
        <div class="commentbox">
          <img :src="i.avatar?i.avatar:'../../../static/imgs/activity.png'" />
          <!-- @click.stop="privateinfo(i.user_id)" -->
          <div class="commentcon">
            <div>
              <p class="review-nick">
                {{i.user_name}}
                <span class="color">
                  <span class="hh-fab-value">{{i.flowers}}</span>
                  <i
                    v-if="i.is_flower==1"
                    class="iconfont icon-bigGood"
                    @click.stop="removelike(i)"
                  ></i>
                  <i v-else class="iconfont icon-good" @click.stop="addlike(i)"></i>
                </span>
              </p>
              <p class="review-area">{{i.text}}</p>
            </div>

            <div class="review-button">
              <p :data-time="i.timestamp" class="review-time hh-time">
                {{i.timestamp?i.timestamp:"刚刚"}} ·
                <span class="replyBtn">回复</span>
              </p>
              <div v-if="i.user_id==$getUser.id" class="del" @click.stop="del(i,t)">删除</div>
            </div>
          </div>
        </div>
        <!-- v-if="i.secondComment.length!=0" -->
        <div class="son">
          <div class="sonbox">
            <div
              v-for="(s,h) in i.secondComment"
              :key="h"
              class="sonitem"
              @click.stop="gocommedetail(s)"
            >
              <img :src="s.avatar?s.avatar:touxiang" alt />
              <div>
                <p class="sonname">
                  {{s.nickname}}
                  <span class="color">
                    <span class="hh-fab-value">{{s.flowers}}</span>
                    <i
                      v-if="s.is_flower==1"
                      class="iconfont icon-bigGood"
                      @click.stop="removelike(s)"
                    ></i>
                    <i v-else class="iconfont icon-good" @click.stop="addlike(s)"></i>
                  </span>
                </p>
                <!-- <span class="sonReply" v-if="s.secondComment.length>0">{{s.secondComment.length}}回复</span> -->
                <p class="sontext">{{s.text}}</p>
                <p :data-time="s.timestamp" class="review-time hh-time">
                  {{s.timestamp?s.timestamp:"刚刚"}} ·
                  <span class="replyBtn">回复</span>
                </p>
              </div>
            </div>
          </div>
          <p
            v-if="i.comments>2"
            @click.stop="propShow(i.comment_id)"
            class="comlength"
          >全部 {{i.comments}} 条评论>>></p>
        </div>
      </li>
      <div @click="more()" class="more" v-if="iddata.more==1">点击加载更多</div>
    </div>
    <mt-popup v-model="popupVisible" position="bottom">
      <div class="tanchu">
        <div class="close" @click="propShow()">
          <p class="swiperTitle">
            <span class="c-title-vline"></span> 全部回复
          </p>
          <p style="font-size:30px;padding-right:16px;">×</p>
        </div>
        <commedetail :propid="commid" ref="comme"></commedetail>
      </div>
    </mt-popup>
  </div>
</template>

<script>
import { Toast } from "mint-ui";
export default {
  props: ["vadiodata"],
  data() {
    return {
      tit: "评论详情",
      propid: "",
      comment: [],
      commeval: "",
      isshow: false,
      ed: "",
      resdata: [],
      write: [],
      fid: "",
      iddata: [],
      touxiang: require("images/morentu.jpg"),
      commid: ""
    };
  },
  computed: {},
  activated() {
    // this.getParams();
    // this.getcommentdate();
  },
  mounted() {},
  watch: {
    commeval: {
      deep: true,
      handler(newVal, oldVal) {
        if (newVal.charAt(newVal.length - 1) == "@") {
          console.log(newVal);
          this.$router.push({
            path: "/friendList",
            name: "friendList"
          });
        }
      }
    },
    $route: {
      handler(route) {
        console.log("路由" + route.name);
        if (route.name != "pictwite") {
          this.comment = [];
        }
      }
    }
  },
  methods: {
    gocommedetail(item) {
      localStorage.setItem("commid", item);
      if (window.hasOwnProperty("webkit")) {
        let params = {
          id: item.id
        };
        window.webkit.messageHandlers.replyCommentByID.postMessage(params);
      } else {
        if (item) {
          this.commid = item.id;
        }
      }
    },
    // 获取评论数据
    async getcommentdate(direct, hot) {
      try {
        let params = {
          target: this.vadiodata,
          target_id: hot,
          direction: direct,
          count: 5
        };
        let res = await this.$api.all.GETfn(params, "comment");
        this.comment = [];
        for (let i in res.result.data) {
          res.result.data[i].timestamp = this.timeago(
            res.result.data[i].timestamp * 1000
          );
        }
        // this.comment=res.result.data

        this.comment = this.comment.concat(res.result.data);
        this.iddata = res.result;
        console.log(res);
      } catch (e) {
        console.log(e);
      }
    },

    more() {
      let dictir = "litter";
      let hot = this.iddata.min_id;
      if (this.iddata.more == 0) {
        Toast("无更多数据");
      } else {
        this.getcommentdate(dictir, hot);
      }
    },

    //删除评论
    async del(item, index) {
      try {
        let params = {
          // userid: "327195464540160441",
          comment_id: item.comment_id
        };
        let res = await this.$api.all.POSTfn(params, "delcomme");
        console.log(res);
        if (res.result.result == "ok") {
          this.comment.splice(index, 1);
        }
      } catch (e) {
        console.log(e);
      }
    },
    //去个人资料页
    privateinfo(v) {
      localStorage.setItem("S-person", v);

      this.$router.push({
        name: "commune_information"
      });
    },

    timeago(dateTimeStamp) {
      //dateTimeStamp是一个时间毫秒，注意时间戳是秒的形式，在这个毫秒的基础上除以1000，就是十位数的时间戳。13位数的都是时间毫秒。
      var result;
      var minute = 1000 * 60; //把分，时，天，周，半个月，一个月用毫秒表示
      var hour = minute * 60;
      var day = hour * 24;
      var week = day * 7;
      var halfamonth = day * 15;
      var month = day * 30;
      var now = new Date().getTime(); //获取当前时间毫秒
      var diffValue = now - dateTimeStamp; //时间差

      if (diffValue < 0) {
        return;
      }
      var minC = diffValue / minute; //计算时间差的分，时，天，周，月
      var hourC = diffValue / hour;
      var dayC = diffValue / day;
      // var weekC = diffValue / week;
      // var monthC = diffValue / month;
      // if (monthC >= 1 && monthC <= 3) {
      // 	result = " " + parseInt(monthC) + "月前";
      // } else if (weekC >= 1 && weekC <= 3) {
      // 	result = " " + parseInt(weekC) + "周前";
      // } else
      if (dayC >= 1 && dayC <= 3) {
        result = " " + parseInt(dayC) + "天前";
      } else if (hourC >= 1 && hourC <= 23) {
        result = " " + parseInt(hourC) + "小时前";
      } else if (minC >= 1 && minC <= 59) {
        result = " " + parseInt(minC) + "分钟前";
      } else if (diffValue >= 0 && diffValue <= minute) {
        result = "刚刚";
      } else {
        var datetime = new Date();
        datetime.setTime(dateTimeStamp);
        var Nyear = datetime.getFullYear();
        var Nmonth =
          datetime.getMonth() + 1 < 10
            ? "0" + (datetime.getMonth() + 1)
            : datetime.getMonth() + 1;
        var Ndate =
          datetime.getDate() < 10
            ? "0" + datetime.getDate()
            : datetime.getDate();
        var Nhour =
          datetime.getHours() < 10
            ? "0" + datetime.getHours()
            : datetime.getHours();
        var Nminute =
          datetime.getMinutes() < 10
            ? "0" + datetime.getMinutes()
            : datetime.getMinutes();
        var Nsecond =
          datetime.getSeconds() < 10
            ? "0" + datetime.getSeconds()
            : datetime.getSeconds();
        result = Nyear + "-" + Nmonth + "-" + Ndate;
      }
      return result;
    }
  },
  components: {}
};
</script>

<style scoped lang="css">
/* pinglun */
.tanchu {
  height: 500px;
  width: 101%;
  overflow-y: scroll;
}
.close {
  font-size: 20px;
  /* padding:10px; */
  color: #333;
  text-align: right;
  position: fixed;
  bottom: 500px;
  background: #fff;
  width: 100%;;
  display:flex;justify-content: space-between;
  border-radius: 6px 6px 0px 0px;
}

.close .swiperTitle{
    padding: 5px 0px 0px 12px;
}

.close *{
  vertical-align: middle
}
.review-button {
  margin-top: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 300px;
}

.review-button div {
  font-size: 12px;
  color: #999;
  font-size: 12px;

  /* display: flex;
  justify-content: center;
  align-items: center; */
}

.review-time {
  height: 30px;
  font-size: 12px;
  color: #999;
  display: flex;
  align-items: center;
  width: 200px;
}

.more {
  padding: 20px 0 0 0;
  text-align: center;
  font-size: 14px;
  color: #333;
}
.review-nick {
  color: #3966a7;
  font-size: 14px;
  font-weight: 700;
  /* line-height: 24px; */
  padding-bottom: 4px;
}

.review-area {
  font-size: 14px;
  line-height: 16px;
  color: #333;
  text-align: justify;
  word-wrap: break-word;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
.commentbox {
  display: flex;
  align-items: inherit;
  width: 100%;
  padding: 12px 0px;
}

.por {
  line-height: 20px;

  border-bottom: 1px solid #eee;
}
.commentbox img {
  display: inline-block;
  height: 44px;
  width: 44px;
  border-radius: 50%;
}

.commentcon {
  text-align: start;
  width: 100%;

  margin-left: 10px;
}
/* 子评论的样式*/
.childcomme {
  padding: 0px 12px;
  background: #ffffff;
}
.childitem {
  display: flex;
  justify-content: space-between;
}
.detailbottom * {
  vertical-align: middle;
}
.detailbottom p {
  height: 30px;
  line-height: 30px;
  padding-left: 20px;
}
.inp {
  width: 60%;
  height: 30px;
  line-height: 30px;
  font-size: 14px;
  /* padding: 10px; */
  padding-left: 10px;
  border-radius: 22px;
}

input:focus {
  background-color: #fff;
}

.bottom {
  width: 100%;
  position: fixed;
  bottom: 0;
  left: 0;
}
.sontext {
  overflow: hidden;
  width: 250px;
  min-height: 18px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.sonbox {
  max-height: 140px;
  overflow: hidden;
  line-height: 16px;
}
.sonitem {
  margin-left: 50px;
  font-size: 14px;
  line-height: 20px;
  min-height: 30px;
  display: flex;
  justify-content: start;
  box-sizing: border-box;
  max-height: 75px;
}
.sonitem img {
  height: 34px;
  width: 34px;
  border-radius: 50%;
}
.sonitem div {
  margin-left: 10px;
  color: #333;
  width: 100%;
}
.childitem {
  display: flex;
  justify-content: space-between;
}

.sonname {
  color: #3966a7;
  font-weight: 700;
  width: 100%;
  font-size: 14px;
}
.comlength {
  padding: 0px 35px 10px 50px;
  color: #3966a7;
  font-size: 12px;
}
/* 评论新增 */
.color {
  font-size: 14px;
  float: right;
  color: #666;
}
.iconfont {
  font-size: 20px;
}
.sonReply {
  display: inline-block;
  background: #eee;
  color: #333;
  padding: 2px 10px;
  margin-left: 10px;
  border-radius: 20px;
}
.replyBtn {
  color: #3966a7;
}
</style>



