<template>
  <div class="mainCover">
    <HeaderBar :needm="needm" :needb="needb" :tit="titleName"></HeaderBar>
    <div class="mainContent">
      <p v-show="allList.length==0" class="nothing">——— 您还没有提问过 ———</p>
      <ul class="scroll-ul">
        <vue-scroll
          ref="scroll"
          :ops="opsconfig"
          @refresh-start="handleRS"
          @load-before-deactivate="handleLBD"
        >
          <li v-for="(v,i) in allList" :key="i" class="scroll-li" @click="gocommune(v)">
            <div class="content">
              <div class="content-left">
                <p class="title">
                  <b>{{v.title}}</b>
                </p>
                <p class="word">{{v.content}}</p>
                <p class="from">来自：{{v.commune_name}}</p>
              </div>
              <div class="content-right">
                <span class="span-x" @click.prevent="spanxfn(v,i)">×</span>
                <img v-lazy="v.image_url" class="page-lazyload-image" />
              </div>
            </div>
          </li>
        </vue-scroll>
      </ul>
    </div>
  </div>
</template>

<script>
import { ScrollConfig } from "common/data.js";
import moment from "moment";
export default {
  name: "me_questions",
  props: {},
  data() {
    return {
      opsconfig: ScrollConfig.opsconfig,
      titleName: "提问",
      needb: true,
      needm: false,
      allList: [],
      UserInfo: this.$getUser,
      max_id: "",
      min_id: ""
    };
  },
  computed: {},
  created() {
    moment.locale("zh-cn");
    this.getDate("bigger", "0");
  },
  mounted() {},
  watch: {},
  methods: {
    gocommune(v) {
      window.sessionStorage.setItem("askdata", JSON.stringify(v));
      this.$router.push("commune_f");
    },
    spanxfn(v, i) {
      let that = this;
      this.$message.confirm("确定删除本条提问?", "提示").then(action => {
        if (action == "confirm") {
          that.deletefn(v, i);
        } else {
          console.log("取消操作");
        }
      });
    },
    async deletefn(v, i) {
      this.allList.splice(i, 1);
      let params = {
        ask_id: v.id
      };
      let res = await this.$api.all.GETfn(params, "commune_ask_del");
      this.$forceUpdate();
    },
    async getDate(type, numsid) {
      try {
        let timestamp = new Date().getTime();
        console.log(timestamp);
        let params = {
          count: 10,
          timestamp: timestamp,
          direction: type,
          target_id: numsid,
          liststype: "ask",
          type: "1"
        };
        let res = await this.$api.all.GETfn(params, "my_commune");

        /************************** */
        res = res.result;
        this.allList = res.data;
        this.max_id = res.max_id;
        this.min_id = res.min_id;
        /************************** */
      } catch (error) {}
    },
    async handleRS(vsInstance, refreshDom, done) {
      // alert("这是执行下拉刷新开始做啥事");
      try {
        let timestamp = new Date().getTime();
        let param = {
          count: 10,
          timestamp: timestamp,
          direction: "bigger",
          target_id: this.max_id,
          liststype: "ask",
          type: "1"
        };
        let res = await this.$api.all.GETfn(param, "my_commune");
        /************************************* */

        res = res.result;
        if (res.data.length > 0) {
          this.max_id = res.max_id;
          this.min_id = res.min_id;
          res.data.forEach((item, index, arr) => {
            this.allList.unshift(item);
          });
          this.$forceUpdate();
        } else if (res.more == "0" && res.data.length == 0) {
          this.$toast("暂无最新数据");
          done();
          return;
        }
        /************************************* */
        done();
      } catch (error) {}
    },
    async handleLBD(vm, loadDom, done) {
      // alert("这是执行下拉加载之后需要执行什么操作");

      try {
        let timestamp = new Date().getTime();
        let param = {
          count: 10,
          timestamp: timestamp,
          direction: "litter",
          target_id: this.min_id,
          liststype: "ask",
          type: "1"
        };
        let res = await this.$api.all.GETfn(param, "my_commune");
        /************************************* */
        res = res.result;
        if (res.data.length > 0) {
          this.dataxiaflag = false;
          this.max_id = res.max_id;
          this.min_id = res.min_id;
          res.data.forEach((item, index, arr) => {
            this.allList.push(item);
          });
          this.$forceUpdate();
        } else if (res.more == "0" && res.data.length == 0) {
          this.$toast("没有更多数据");
          this.dataxiaflag = true;
          done();
          return;
        }
        /************************************* */
        done();
      } catch (error) {}
    }
  },
  components: {}
};
</script>

<style scoped lang="css">
.mainContent {
  width: 100%;
  height: 623px;
  min-height: 623px;
}
.scroll-ul {
  width: 100%;
  min-height: calc(100vh-44px);
  height: calc(100vh-44px);
  padding: 0px 12px;
}

.scroll-li {
  width: 100%;
  /* height: 100px; */
  /* min-height: 80px; */
}

.content {
  width: 100%;
  height: 100px;
  display: flex;
  border-bottom: 1px solid #eee;
  padding: 6px 0px;
}

.content-left {
  width: 65%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.title {
  font-size: 14px;
  color: #333;
  text-align: justify;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  display: -webkit-box !important;
  padding-right: 10px;
}

.word {
  /* height: 40%; */
  font-size: 12px;
  line-height: 15px;
  color: #666;
  text-align: justify;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  display: -webkit-box !important;
  padding-right: 10px;
}

.from {
  height: 25%;
  font-size: 12px;
  color: #999;
  display: inherit;
  align-items: center;
  text-align: justify;
}

.content-right {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row-reverse;
}
.span-x {
  display: block;
  width: 18px;
  height: 18px;
  /* line-height: 25px; */
  text-align: center;
  font-size: 12px;
  position: absolute;
  /* top: -10px; */
  z-index: 88888888;
  /* right: 2px; */
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.7);
  color: #000;
  margin: -28px 0px 0px 50px;
}

.content-right img {
  width: 90%;
  height: 90%;
  flex: 1;
  display: block;
}

.nothing {
  margin-top: 15px;
  font-size: 12px;
  color: #999;
  height: 40px;
  line-height: 40px;
  width: 100%;
  text-align: center;
}
</style>



