<<template>
<div>
      <swiper :options="swiperOption">
        <swiper-slide v-for="(n,i) in swiperdata" :key="i">
          <div class="cover-full"  @click.prevent="jumpnewdetail(n,swiper,$event)">
           <!-- <img :src="n.cover" class="page-lazyload-image1" v-if="i==0||i==(swiperdata.length-1)">  -->
           <img :src="n.cover" class="page-lazyload-image1 hotb"> 
            <span class="hot" v-if="n.type_id=='0'||item.type_id=='1'">图文</span>
          <span class="hot" v-else-if="n.type_id=='2'">图集</span>
          <span class="hot" v-else-if="n.type_id=='3'">视频</span>
          <span class="hot" v-else-if="n.type_id=='4'">专题</span>
           <div class="title"><p >{{n.title}}</p></div> </div>
         </swiper-slide>
               <!-- <div v-if="false" class="swiper-pagination1" slot="pagination"></div> -->
      </swiper>
      <div class="nbsp-class">&nbsp;</div>
</div>
  
</template>

<script>
export default {
  name: "swiperg",
  props: ["itemg"],
  data() {
    return {
      swiperOption: {
        // initialSlide: "1",
        effect: "coverflow",
        grabCursor: true,
        centeredSlides: true,
        slidesPerView: "auto",
        coverflowEffect: {
          rotate: 50,
          stretch: 0,
          depth: 100,
          modifier: 1,
          slideShadows: true
        },
        pagination: {
          el: ".swiper-pagination1"
        },
        loop: true,
        observer: true, //修改swiper自己或子元素时，自动初始化swiper
        observeParents: true
      },
      swiper: [],
      swiperdata: []
    };
  },
  created() {
    let that = this;
    this.swiper = this.itemg;
    let swiperimg = this.swiper.data;
    swiperimg.forEach((item, index, arr) => {
      this.swiperdata.push(item);
    });
  },
  methods: {
      //点击浏览
    async setclick(item) {
      try {
        let params = {
          target: item.id,
          action:1,
          value:1
        };
        let res = await this.$api.all.GETfn(params, "setclick");
        console.log(res);
        if (res.result.result == "ok") { }
      } catch (e) {
        console.log(e);
      }
    },
    jumpnewdetail(item, type, e) {
     localStorage.setItem("homedata", JSON.stringify(item));
     localStorage.setItem("atype", type.category);


//todo todo
      // //要传入的参数
      // let param = ["articleId:articleType:articleUrl:userId:userToken:" , item.id+'',type.category+'',"homeDetail",this.$getUser.id+"",this.$getUser.token+''];
      // //保存到缓存
      // localStorage.setItem("plusData", JSON.stringify(param));

      // this.$router.push({
      //   path: "/homeDetail?plusJump",
      //   name: "homeDetail"
      // });
       this.$router.push({
        path: "/homeDetail",
        name: "homeDetail"
      });


      this.setclick(item)
    }
  }
};
</script>

<style scoped>
.hotb {
  position: relative;
}
.hot {
  padding: 2px 5px;
  background: #0000007f;
  color: #fff;
  z-index: 100000000;
  position: absolute;
  left: 3px;
  bottom: 50px;
  border-radius: 3px;
  font-size: 12px;
}
.swiper-inner {
  width: 100%;
  /* height: 400px; */
  /* padding-top: 50px; */
  /* padding-bottom: 50px; */
  margin-top: 100px;
  margin-left: -250px;
}
.title {
  font-size: 12px;
  width: 100%;
  height: 30px;
  line-height: 15px;
  background: #ffff;
  box-sizing: border-box;
  padding: 0 6px;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
  text-align: justify;
}
.swiper-slide {
  background-position: center;
  background-size: cover;
  width: 70%;

  /* height: 400px; */

  /* -webkit-box-shadow: -2px -1px 16px -2px #888;  */
  /* box-shadow: -2px -1px 16px -2px #888; */
  padding-bottom: 6px;
  border-style: solid;
  border-color: #eee;
  border-width: 1px;
  /* border-radius: 3px; */
}

.cover-full {
  width: 100%;
}
.swiper-container {
  /* width: 100%; */
  overflow: hidden;
}
.swiper-slide img {
  height: 150px;
  width: 100%;
  /* border-radius: 5px 5px 0px 0px; */
}
.list-block /deep/ .swiper-wrapper {
  transition-timing-function: linear !important;
}

.nbsp-class {
  height: 10px;
  background: transparent;
  width: 100%;
}
</style>


