<template>
  <mt-header class="c-color header-top-ppp" :title="titleName">
    <mt-button icon="back" @click.native="$router.back(-1)" slot="left" v-show="needback"></mt-button>
    <mt-button icon="back" @click.native="backcustomfn" slot="left" v-show="needcustomback"></mt-button>
    <mt-button icon="more" slot="right" v-show="needmore" @click="morefn"></mt-button>
    <mt-button slot="right" v-show="needshare">分享</mt-button>
    <mt-button slot="right" v-show="needwrite" @click.native="writesubmit">编写</mt-button>
    <mt-button slot="right" v-show="needsubmit" @click.native="childsubmit">提交</mt-button>
    <mt-button slot="right" v-show="needcreate" @click.native="createfn">创建</mt-button>
    <mt-button slot="right" v-show="needchange" @click.native="changefn">更改</mt-button>
    <mt-button slot="right" v-show="needjoin" @click.native="joinfn">{{jiontit}}</mt-button>

    <slot name="search"></slot>
  </mt-header>
</template>

<script>
export default {
  props: [
    "tit",
    "needm",
    "needb",
    "needf",
    "needw",
    "needs",
    "needc",
    "needch",
    "needcsb",
    "needj",
    "jointit"
  ],
  data() {
    return {
      titleName: "",
      needmore: false,
      needback: false,
      needshare: false,
      needwrite: false,
      needcreate: false,
      needchange: false,
      needcustomback: false,
      needjoin: false,
      jiontit: ""
    };
  },
  computed: {},
  created() {
    this.titleName = this.tit;
    this.needmore = this.needm;
    this.needback = this.needb;
    this.needshare = this.needf;
    this.needwrite = this.needw;
    this.needsubmit = this.needs;
    this.needcreate = this.needc;
    this.needchange = this.needch;
    this.needcustomback = this.needcsb;
    this.needjoin = this.needj;
    this.jiontit = this.jointit;
  },
  mounted() {},
  watch: {
    tit: {
      handler(newv, oldv) {
        this.titleName = newv;
      },
      deep: true
    }
  },
  methods: {
    writesubmit() {
      this.$emit("fatherwrite");
    },
    childsubmit() {
      this.$emit("fathersubmit");
    },
    createfn() {
      this.$emit("fathercreate");
    },
    morefn() {
      this.$emit("fathermorefn");
    },
    changefn() {
      this.$emit("fatherchange");
    },
    backcustomfn() {
      this.$emit("fatherback");
    },
    joinfn() {
      this.$emit("fatherjoin");
    }
  },
  components: {}
};
</script>

<style>
.mint-header {
  width: 100% !important;
  height: 44px;
  line-height: 44px;
  min-height: 44px;
  font-size: 16px;
}

.header-top-ppp {
  width: 100% !important;
  height: 44px !important;
  line-height: 44px !important;
  min-height: 44px !important;
  font-size: 16px !important;
}
</style>



