<template>
  <div class="mainCover">
    <div class="back">
      <div class="header-top">
        <mt-button class="left-back" icon="back" @click.native="$router.back(-1)" slot="left"></mt-button>
        <mt-button class="right-create" slot="right" @click.native="createMeeting">创建会议</mt-button>
      </div>
    </div>

    <div class="mainContent">
      <div class="joinbox">
        <!-- 账号输入框 -->
        <p>
          <span class="c-title-vline"></span> 加入会议
        </p>
        <div class="input-box">
          <input
            type="tel"
            class="input-default"
            maxlength="13"
            onkeyup="value=value.replace(/[^\w]/g,'') "
            onbeforepaste="clipboardData.setData('text',clipboardData.getData('text').replace(/[^\w]/g,''))"
            v-model="Account"
            placeholder="请输入会议id"
          />
        </div>
        <button class="join" @click.prevent="joinMeetings()">加入会议</button>
      </div>
    </div>
  </div>
</template>

<script>
import { TEST } from "common/test.js";
import { Request } from "common/http.js";

import { MessageBox } from "mint-ui";
export default {
  props: {},
  data() {
    return {
      needc: true,
      titleName: "",
      Account: "",
      name: "keke",
      _BARCODE: "zoom"
      // B: window.plus.bridge
    };
  },
  computed: {},
  activated() {
    TEST();
    // Request();
  },
  mounted() {},
  watch: {},
  methods: {
    callbackfunction(data) {
      data = JSON.parse(data);
      if (data.error) {
        this.$toast(data.error.message + "  code:" + data.error.code);
        return;
      }

      console.log(data.start_url);
      data.zak = data.start_url.split("zak=")[1];
      delete data.start_url;
      MessageBox({
        $type: "prompt",
        title: "PPP",
        message: "会议号(其他用户可根据此会议号加入)：" + data.id,
        showCancelButton: true
      }).then(res => {
        if (res === "confirm") {
          window.plus.zoom.startMeeting(data);
        } else {
        }
      });
    },
    joinMeetings() {
      window.plus.zoom.joinMeeting({
        meetNo: this.Account
      });
    },
    methodGetUrl(url, params) {
      let newUrl = url;
      if (params) {
        const paramsArray = [];
        Object.keys(params).forEach(key =>
          paramsArray.push(`${key}=${params[key]}`)
        );
        if (!~url.search(/\?/)) {
          newUrl += `?${paramsArray.join("&")}`;
        } else {
          newUrl += `&${paramsArray.join("&")}`;
        }
      }
      return newUrl;
    },
    createMeeting() {
      const userInfo = this.$getUser;
      const meetingCreate = "https://www.zoomus.cn/v1/meeting/create";

      const api_key = "aKAUlxr6SCmj79sr1ZWM-w";
      const api_secret = "96bTGilfOuN2iM4NNyQYQivqRMoBf4T0BGIf";
      let that = this;
      Request(
        that.methodGetUrl(meetingCreate, {
          api_key: api_key,
          api_secret: api_secret,
          host_id: "wz9Bh2rnR--gVjUN_-N_BQ",
          topic: "会议",
          type: 2
        }),
        data => {
          that.callbackfunction(data);
        }
      );
    }
  },
  components: {}
};
</script>

<style scoped lang="css">
element.style {
  font-size: 12px !important;
}
.back { 
  height: 250px;
  width: 100%;
  background: url(../../assets/imgs/meeting.jpg) no-repeat center;
  background-size: 100% 100%;
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  /* padding: 0 2px; */
}
.icon-fanhui {
  font-weight: 900;
  color: #fff;
}

.iconfont {
  font-size: 16px;
  margin-left: 12px;
}

.input-box input {
  /* padding: 0 0 0 30px; */
  flex: 1;
  line-height: 26px;
  font-size: 14px;
  color: #1b81cb;
}
.input-box {
  margin-top: 10px;
  width: 100%;
  height: 35px;
  border: 1px solid #ddd;
  line-height: 35px;
  display: flex;
  /* margin: 15px 0 0 0; */
  font-size: 14px;
  border-radius: 3px;
  padding: 0 10px;
  /* font-weight: 900; */
  color: #999;
}
.joinbox {
  padding: 16px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.joinbox p {
  height: 44px;
  line-height: 44px;
  font-size: 15px;
  font-weight: 900;
  width: 100%;
}

.create {
  background: #fff;
  color: #4a8dff;
  width: 100px;
  height: 30px;
  /* line-height: 30px; */
  border-radius: 3px;
  font-weight: 700;
  border: none;
  /* border: 1px solid #4a8dff; */
  margin: 10px 10px 0px 0px;
}

.join {
  width: 120px;
  height: 34px;
  margin-top: 20px;
  color: #fff;
  font-size: 14px;
  background-color: #4a8dff;
  border-radius: 3px;
}

.header-top {
  width: 100%;
  height: 44px;
  line-height: 44px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  clear: both;
  padding-right: 5px;
}

.left-back {
  float: left;
  border: none;
  background: transparent !important;
}

.right-create {
  border: none;
  width: 100px;
  height: 32px !important;
  color: #4a8dff;
  background: #fff !important;
  font-weight: 600;
}

>>> .mint-button-text {
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
}

>>> .mint-button {
  height: 44px;
  line-height: 44px;
  border: none;
  font-size: 15px;
}
</style>



