<template>
  <div class="mainCover">
    <router-link to="/commune_f">
      <HeaderBar :needm="needm" :needb="needb" :tit="titleName"></HeaderBar>
    </router-link>
    <div class="team_q">
      <input type="text" placeholder="*我来说两句" id />
    </div>
  </div>
</template>
<script>
export default {
  name: "commune_th",
  props: {},
  data() {
    return {
      titleName: "评论",
      needb: true,
      needm: true,
      search: ""
    };
  },
  computed: {},
  activated() {
    this.search = require("images/search.png");
  },
  mounted() {},
  watch: {},
  methods: {},
  components: {}
};
</script>
<style>
.team_q input {
    width: 100%;
    height: 100%;
  padding: 5px;
  border: none;
  text-align: justify;
}

.team_q {
    height: 36px;
    margin: 12px 12px;
    border: 1px solid #eee;
    border-radius: 3px;
}
</style>



