        <div class="nbsp">&nbsp;</div>
        <swipergun :itemg="n.style_2"></swipergun>
    <!-- </keep-alive>-->
    <!-- </div>
      <div class="c-part-block"></div>
    </div>-->
    <!-- </vue-scroll> -->
    <!-- 公社 -->
    <!-- <div class="commentlist">
      <p class="swiperTitle">
        <span class="c-title-vline"></span> 公社
      </p>
      <ul class="comment-ul">
        <li
          v-for="(item,index) in commmentlist"
          :key="index"
          @click="jumpcommdetail(item)"
          class="comment-li"
        >
          <div class="comm-name c-font-list">
            <p class>{{item.name}}</p>
          </div>
          <div class="comm-body">
            <div class="hh-face-img">
              <img v-lazy="item.image_url" class="page-lazyload-image1" />
            </div>
