          <div class="c-part-block"></div>
        </li>
      </ul>
    </div>-->
    <div>
      <ul>
        <li v-for="(i,t) in indexNew" :key="t" class="newitem" @click="goAdetail(i)">
          <p>{{i.title}}</p>
          <img :src="i.cover" alt />
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { ScrollConfig } from "common/data.js";
import swiperfang from "com/swiperfang.vue";
import swipergun from "com/swipergun.vue";
import moment from "moment";

export default {
  name: "homecom",
  props: {},
  data() {
    return {
      opsconfig: ScrollConfig.opsconfig,
      commmentlist: [],
      datalist: [],
      indexNew: []
    };
  },
  computed: {},
  created() {
    // this.getdate();
    this.getswiper();
    moment.locale("zh-cn");
    this.getnewdate();
  },
  mounted() {},
  watch: {},
  filters: {
    changeTime(val) {
      return moment(val)
        .startOf("minute")
        .fromNow();
    }
  },
  methods: {
    goAdetail(item) {
      let gourl = "";
      if (item.type_id == "2") {
        localStorage.setItem("homedata", JSON.stringify(item));
        gourl = "pictureDetail";
      } else if (item.type_id == "13") {
        localStorage.setItem("homedata", JSON.stringify(item));
        gourl = "vadiodetail";
      } else if (item.type_id == "4") {
        localStorage.setItem("zhuantidata", JSON.stringify(item));
        gourl = "subjectDetail";
      } else {
        localStorage.setItem("homedata", JSON.stringify(item));
        localStorage.setItem("atype", item.category_id);
        gourl = "homeDetail";
      }
      // localStorage.setItem("homedata", JSON.stringify(item));
      let param = [
        "articleId:articleType:articleUrl:userId:userToken:",
        item.id + "",
        item.category_id + "",
        gourl,
        this.$getUser.id + "",
        this.$getUser.token + ""
      ];
      //保存到缓存
      localStorage.setItem("plusData", JSON.stringify(param));
      this.$router.push({
        path: "/" + gourl + "?plusJump",
        name: gourl
      });
    },
    // 新闻数据
    async getnewdate() {
      try {
        let self = this;
        let params = {
          s: "",
          category: 1001,
          count: 10,
          disable_cache: 0
        };
        let res = await this.$api.all.GETfn(params, "list");
        this.indexNew = res.data;

        console.log(res);
      } catch (e) {
        console.log(e);
      }
    },
    jumpcommdetail(item) {
      window.sessionStorage.setItem("commitydata", JSON.stringify(item));
      this.$router.push({
        name: "commune_meeting"
      });
    },
    // 公社数据
    async getdate(type, numid) {
      try {
        let params = {
          count: 6,
          direction: type,
          target_id: numid,
          recommend: 1
        };
        let res = await this.$api.all.GETfn(params, "commune_list");
        res = res.result;
        this.commmentlist = res.data;
      } catch (e) {
        console.log(e);
      }
    },
    // 轮播数据
    async getswiper() {
      try {
        let params = {};
        let res = await this.$api.all.GETfn(params, "home");
        // console.log(res);
        let datal = [];
        for (let i in res.data) {
          if (res.data[i].column) {
            datal.push(res.data[i]);
          }
        }
        this.datalist = datal;
        this.getdate("bigger", "0");
      } catch (e) {
        // console.log(e);
      }
    }

    //   handleRS(vsInstance, refreshDom, done) {
    //     // alert("这是执行下拉刷新开始做啥事handleRS ");
    //     // this.getswiper()
    //     done();
    //   },
    //   handleLoadStart(vm, dom, done) {
    //     setTimeout(() => {
    //       // alert("这是执行shang拉加载之前需要执行什么操作 handleLoadStart");
    //       done();
    //     }, 600);
    //   },
    //   handleLBD(vm, loadDom, done) {
    //     this.getdate("litter", this.min_id);
    //     // alert("这是执行shnag拉加载之后需要执行什么操作  handleLBD");
    //     done();
    //   },
    //   handleRBD(vm, loadDom, done) {
    //     console.log();
    //     // xialadeshihou
    //     this.getswiper();
    //     done();
    //   }
  },
  components: {
    swiperfang,
    swipergun
  }
};
</script>

<style scoped lang="css">
.newitem {
  padding: 5px;
  display: flex;
  justify-content: space-between;
}
.newitem img {
  height: 70px;
  width: 100px;
}
.homecomcover {
  min-height: 532px !important;
  height: 532px !important;
}

.swiperTitle {
  height: 36px;
  line-height: 36px;
  text-align: left;
  box-sizing: border-box;
  font-weight: 700;
  display: block;
  width: 100%;
  padding: 0px 12px;
}

.comment-li {
  height: 295px;
  min-height: 295px;
}

.comm-name {
  display: block;
  width: 100%;
  height: 30px;
  line-height: 30px;
}

.comm-name p {
  width: 100%;
  height: 30px;
  line-height: 30px;
  font-weight: 600;
  display: block;
  padding: 0px 12px;
}

.hh-face-img {
  width: 100%;
  max-height: 220px;
  min-height: 220px;
}
.hh-face-img img {
  width: 100%;
  max-height: 220px;
  min-height: 220px;
  border-radius: 3px;
}

.comm-body {
  /* width: 100%; */
  max-height: 220px;
  min-height: 220px;
  margin: 0px 12px;
}

.comm-keyword {
  width: 100%;
  max-height: 30px;
  min-height: 30px;
  line-height: 30px;
  padding: 0px 12px;
}

.comm-footer {
  width: 100%;
  max-height: 30px;
  min-height: 30px;
  line-height: 30px;
  /* padding: 0px 12px; */
}

.comm-keyword span {
  display: inline-block;
  /* margin: 0 10px; */
  margin-right: 10px;
}

.nbsp {
  height: 10px;
  width: 100%;
  background: transparent;
}
</style>



