<template>
  <div class="content">
    <!-- swiper -->

    <!-- <div v-for="(n,i) in swipesList" :key="i" @click="jumpdetail(n)">
        <img :src="n.cover" alt />
        <div class="masking">
          <p>{{n.title}}</p>
        </div>
    </div>-->
    <!-- 公社 -->
<vue-scroll
      ref="scroll"
      :ops="opsconfig"
      @refresh-start="handleRS"
      @load-before-deactivate="handleLBD"
      @refresh-before-deactivate="handleRBD"
      @load-start="handleLoadStart"
    >
    <ul class="comment-ul">
      <li @click="jumppicture()" v-for="(v,i) in 5" :key="i">
        <div class="comm-body">
          <div class="hh-face-img">
            <img :src="imgarr[i]" />
          </div>
          <div class="comm-footer clearfix">
            <p class="title">我是标题</p>
            <p class="comm-keyword">
              <span>评论量:10</span>
              <span>时间:2019-10-10 12:20</span>
              <span>浏览量:10</span>
            </p>
          </div>
        </div>
        <div class="c-part-block"></div>
      </li>
    </ul>
     </vue-scroll>
  </div>
</template>

<script>
import { ScrollConfig } from "common/data.js";
import { Toast } from "mint-ui";
export default {
  name: "swiperf",
  props: ["itemf"],
  data() {
    return {
        opsconfig: ScrollConfig.opsconfig,
      refresh: 1,
      noSscrollData: false,
      triggerType: "load",
      imgarr: []
    };
  },
  created() {
    for (let i = 1; i < 6; i++) {
      let imgsrc = require(`@/assets/imgs/${i}.png`);
      this.imgarr.push(imgsrc);
    }
  },
  methods: {
    jumppicture() {
      this.$router.push({
        path: "/pictureDetail",
        name: "pictureDetail"
      });
    },
      handleRS(vsInstance, refreshDom, done) {
      // alert("这是执行下拉刷新开始做啥事");
      done();
    },
    handleLoadStart(vm, dom, done) {
      setTimeout(() => {
        // alert("这是执行下拉加载之前需要执行什么操作");
        done();
      }, 600);
    },
    handleLBD(vm, loadDom, done) {
      done();
    },
    handleRBD(vm, loadDom, done) {
      done();
    },
  }
};
</script>

<style scoped lang="css">
* {
  margin: 0;
  padding: 0;
}
.content {
  height: 100%;
  text-align: start;
}
.hh-face-img {
  width: 100%;
  height: 200px;
  padding: 0px 12px;
}

.hh-face-img img {
  width: 100%;
  height: 200px;
  border-radius: 3px;
}
.comment-ul {
  height: 100%;
}

.comment-ul li{
    padding-bottom: 10px;
}

.title{
    color: #333;
    line-height: 20px;
    padding: 5px 0px;
    text-align: justify;
    font-size: 14px;
}

.comm-keyword {
  display: flex;
  justify-content: space-between;
  color: #999;
  font-size: 12px;
  line-height: 16px;
    padding-bottom: 10px;
}
.comm-footer {
  /* height: 60px; */
  padding: 0px 12px;
  line-height: 28px;
  box-sizing: border-box;
}
</style>


