<template>
  <div class="mainCover">
    <HeaderBar :needb="needb" :tit="titleName"></HeaderBar>
    <div class="mainContent">
      <p v-show="allList.length==0" class="nothing">——— 您还没有过浏览记录 ———</p>
      <ul class="scroll-ul">
        <vue-scroll
          ref="scroll"
          :ops="opsconfig"
          @refresh-start="handleRS"
          @load-before-deactivate="handleLBD"
        >
          <li
            v-for="(v,i) in allList"
            :key="i"
            @click.prevent="enterCollection(v)"
            class="scroll-li"
          >
            <div class="content">
              <div class="content-left">
                <p class="title">{{v.target_caption}}</p>
                <div class="date">
                  <img :src="like" />
                  <p>
                    点赞
                    <span>{{v.clicks}}</span>
                  </p>
                  <img :src="com" />
                  <p>
                    评论
                    <span>{{v.comments}}</span>
                  </p>
                </div>
              </div>
              <div class="content-right">
                <img v-lazy="v.cover" class="page-lazyload-image1" />
              </div>
            </div>
          </li>
        </vue-scroll>
      </ul>
    </div>
  </div>
</template>

<script>
import { ScrollConfig } from "common/data.js";
import moment from "moment";
import { ComUtils } from "common/utils.js";

export default {
  name: "me_history",
  props: {},
  data() {
    return {
      opsconfig: ScrollConfig.opsconfig,
      titleName: "浏览历史",
      needb: true,
      allList: [],
      UserInfo: this.$getUser,
      max_id: "",
      min_id: "",
      like: "",
      com: ""
    };
  },
  computed: {},
  created() {
    this.like = require("images/like.png");
    this.com = require("images/comment.png");
    moment.locale("zh-cn");
    // this.getDate("bigger", "0");
  },
  mounted() {},
  watch: {},
  activated() {
    this.getDate("bigger", "0");
  },
  methods: {
    enterCollection(v) {
      if (v.target_type == "2003") {
        window.sessionStorage.setItem("askdata", JSON.stringify(v));
        this.$router.push({
          name: "commune_f"
        });
      } else if (v.target_type == "2001") {
        window.sessionStorage.setItem("homedata", JSON.stringify(v));
        // this.$router.push({
        //   name: "homeDetail"
        // });
        let that = this;
        ComUtils.skipPage(v, that);
      } else {
        this.$toast("数据异常,请联系管理员");
      }
    },
    async getDate(type, numsid) {
      try {
        let timestamp = new Date().getTime();
        console.log(timestamp);
        let params = {
          count: 10,
          timestamp: timestamp,
          direction: type,
          target_id: numsid
        };
        let res = await this.$api.all.GETfn(params, "scan_history");
        res = res.data;
        this.allList = res.data;
        this.max_id = res.max_id;
        this.min_id = res.min_id;
        console.log(res.data);
      } catch (error) {
        console.log(error);
      }
    },
    async handleRS(vsInstance, refreshDom, done) {
      // alert("这是执行下拉刷新开始做啥事");
      try {
        let timestamp = new Date().getTime();
        let param = {
          count: 10,
          timestamp: timestamp,
          direction: "bigger",
          target_id: this.max_id
        };
        let res = await this.$api.all.GETfn(param, "scan_history");
        res = res.data;
        if (res.more == "0" && res.data.length == 0) {
          this.$toast("暂无最新数据");
          done();
          return;
        } else if (res.data.length > 0) {
          this.max_id = res.max_id;
          // this.min_id = res.min_id;
          res.data.forEach((item, index, arr) => {
            this.allList.unshift(item);
          });
          this.$forceUpdate();
          console.log(this.allList);
          done();
        }
        console.log(this.allList);
        done();
      } catch (error) {}
    },
    async handleLBD(vm, loadDom, done) {
      // alert("这是执行下拉加载之后需要执行什么操作");

      try {
        let timestamp = new Date().getTime();
        let param = {
          count: 10,
          timestamp: timestamp,
          direction: "litter",
          target_id: this.min_id
        };
        let res = await this.$api.all.GETfn(param, "scan_history");
        res = res.data;
        if (res.more == "0" && res.data.length == 0) {
          this.$toast("没有更多数据");
          done();
          return;
        } else if (res.data.length > 0) {
          // this.max_id = res.max_id;
          this.min_id = res.min_id;
          res.data.forEach((item, index, arr) => {
            this.allList.push(item);
          });
          this.$forceUpdate();
          done();
          console.log(this.allList);
        }
        done();

        console.log(this.allList);
      } catch (error) {}
    }
  },
  components: {}
};
</script>

<style scoped lang="css">
.mainContent {
  width: 100%;
  min-height: calc(100vh- 44px) !important;
  height: calc(100vh- 44px) !important;
}

.scroll-ul {
  width: 100%;
  min-height: calc(100vh- 44px) !important;
  height: calc(100vh- 44px) !important;
}

.scroll-li {
  width: 100%;
  height: 100px;
  min-height: 100px;
  /* line-height: 90px; */
  /* margin: 5px 0px 10px 0px; */
  padding: 6px 12px;
}

.content {
  height: 90px;
  min-height: 90px;
  /* margin: 5px 2%; */
  display: flex;
  border-bottom: 1px solid #eee;
  /* padding: 5px 0px; */
}

.content-left {
  width: 65%;
  /* height: 100%; */
  line-height: 20px;
  /* min-height: 90px; */
  display: flex;
  flex-wrap: wrap;
  align-content: space-between;
}
.content-right {
  flex: 1;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.content-right img {
  width: 90%;
  height: 90%;
}

.title {
  width: 100%;
  font-size: 14px;
  color: #333;
  /* height: 60px; */
  line-height: 20px;
  /* padding: 3px 0px; */
  text-align: justify;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
}

.date {
  height: 30px;
  /* line-height: 20px; */
  font-size: 12px;
  color: #023249;
  display: flex;
  align-items: center;
}

.date img {
  display: inline-block;
  width: 20px;
  height: 20px;
}

.date p {
  height: 20px;
  line-height: 20px;
  margin-right: 5px;
}

.nothing {
  margin-top: 15px;
  font-size: 12px;
  color: #999;
  height: 40px;
  line-height: 40px;
  width: 100%;
  text-align: center;
}
</style>



