<template>
  <div class="Cover">
    <p v-show="allList.length==0" class="nothing">——— Ta还没有评论过 ———</p>
    <ul class="comment-ul">
      <vue-scroll
        ref="scroll"
        :ops="opsconfig"
        @refresh-start="handleRS"
        @load-before-deactivate="handleLBD"
      >
        <li v-for="(v,i) in allList" :key="i" class="comment-li" @click.prevent="gotoAsk(v)">
          <p class="comments">{{v.text}}</p>
          <div class="content">
            <div class="content-left">
              <p class="title">{{v.title}}</p>
              <p class="date">{{v.time | changeTime}}</p>
            </div>
            <div class="content-right">
              <img v-lazy="v.cover" class="page-lazyload-image1" />
            </div>
          </div>
        </li>
      </vue-scroll>
    </ul>
  </div>
</template>

<script>
import { ScrollConfig } from "common/data.js";
import moment from "moment";
export default {
  name: "bigVreview",
  props: {},
  data() {
    return {
      opsconfig: ScrollConfig.opsconfig,
      titleName: "大V评论",
      needb: true,
      UserParam: {},
      allList: [],
      UserInfo: this.$getUser,
      max_id: "",
      min_id: ""
    };
  },
  computed: {},
  created() {
    this.UserParam = JSON.parse(window.sessionStorage.getItem("S-itemv"));
    moment.locale("zh-cn");
    this.getDate("bigger", "0");
  },
  mounted() {},
  watch: {
    $route() {
      this.UserParam = JSON.parse(window.sessionStorage.getItem("S-itemv"));
      this.getDate("bigger", "0");
    }
  },
  filters: {
    changeTime(val) {
      return moment(val)
        .startOf("minute")
        .fromNow();
    }
  },
  methods: {
    gotoAsk(v) {
      console.log(v);
      window.sessionStorage.setItem("askdata", JSON.stringify(v));
      this.$router.push({
        name: "commune_f"
      });
    },
    async getDate(type, numsid) {
      try {
        let timestamp = new Date().getTime();
        console.log(timestamp);
        let param = {
          count: 10,
          timestamp: timestamp,
          direction: type,
          target_id: numsid,
          userid: this.UserParam.user_id,
          user_id: this.UserParam.user_id,
          token: this.UserParam._token
        };
        let res = await this.$api.all.GETfn(param, "user_comment");
        res = res.result;
        this.max_id = res.max_id;
        this.min_id = res.min_id;
        this.allList = res.data;

        console.log(this.allList);
      } catch (error) {
        console.log(error);
      }
    },
    async handleRS(vsInstance, refreshDom, done) {
      // alert("这是执行下拉刷新开始做啥事");
      try {
        let timestamp = new Date().getTime();
        let param = {
          count: 10,
          timestamp: timestamp,
          direction: "bigger",
          target_id: this.max_id,
          userid: this.UserParam.user_id,
          user_id: this.UserParam.user_id,
          token: this.UserParam._token
        };
        let res = await this.$api.all.GETfn(param, "user_comment");
        res = res.result;

        if (res.more == "0" && res.data.length == 0) {
          this.$toast("暂无最新数据");
          done();
          return;
        }
        if (res.data.length > 0) {
          this.max_id = res.max_id;
          this.min_id = res.min_id;
          res.data.forEach((item, index, arr) => {
            this.allList.unshift(item);
          });
          this.$forceUpdate();
        }
        console.log(this.allList);
        done();
      } catch (error) {}
    },
    async handleLBD(vm, loadDom, done) {
      // alert("这是执行下拉加载之后需要执行什么操作");
      try {
        let timestamp = new Date().getTime();
        let param = {
          count: 10,
          timestamp: timestamp,
          direction: "litter",
          target_id: this.min_id,
          userid: this.UserParam.user_id,
          user_id: this.UserParam.user_id,
          token: this.UserParam._token
        };
        let res = await this.$api.all.GETfn(param, "user_comment");
        res = res.result;
        if (res.more == "0" && res.data.length == 0) {
          this.$toast("没有更多数据");
          this.dataxiaflag = true;
          done();
          return;
        }
        if (res.data.length > 0) {
          this.dataxiaflag = false;
          this.max_id = res.max_id;
          this.min_id = res.min_id;
          res.data.forEach((item, index, arr) => {
            this.allList.push(item);
          });
          this.$forceUpdate();
        }
        done();

        console.log(this.allList);
      } catch (error) {}
    }

    // async getDataList() {
    //   try {
    //     let param = {
    //       userid: this.UserParam.user_id,
    //       user_id: this.UserParam.user_id,
    //       token: this.UserInfo._token,
    //       target: this.UserParam.user_id
    //     };
    //     let res = await this.$api.all.POSTfn(param, "user_resume"); //likeimg
    //     this.alldata = res.result;
    //   } catch (error) {
    //     console.log(error);
    //   }
    // }
  },
  components: {}
};
</script>

<style scoped lang="css">
.Cover {
  width: 100%;
  height: calc(100vh-208px) !important;
  min-height: calc(100vh-208px) !important;
}

.mint-tab-container-wrap {
  height: calc(100vh-208px) !important;
  min-height: calc(100vh-208px) !important;
}

.comment-ul {
  width: 100%;
  height: calc(100vh-208px) !important;
  min-height: calc(100vh-208px) !important;
  padding: 0px 12px;
}

.comment-li {
  border-bottom: 1px solid #eee;
  /* height: 120px; */
  min-height: 120px;
  display: block;
  padding: 8px 0px 6px;
}

.comments {
  font-size: 12px;
  min-height: 20px;
  color: #333;
  /* text-align: justify; */
}

.content {
  height: 86px;
  min-height: 86px;
  padding: 8px 0px;
  display: flex;
  background-color: #f6f7f9;
}

.content-left {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-left: 10px;
}

.title {
  font-size: 12px;
  min-height: 20px;
  color: #333;
  text-align: justify;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  /* display: -webkit-box !important; */
}

.date {
  min-height: 20px;
  font-size: 10px;
  color: #999;
  display: inherit;
  align-items: center;
  text-align: justify;
}

.content-right {
  width: 35%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row-reverse;
  margin-left: 10px;
}

img {
  width: 98%;
  height: 100%;
  display: block;
}

.nothing {
  margin-top: 15px;
  font-size: 12px;
  color: #999;
  height: 40px;
  line-height: 40px;
  width: 100%;
  text-align: center;
  background: transparent;
  margin-top: 15px;
}
</style>



