<template>
  <div class="mainCover">
    <HeaderBar :needb="needb" :tit="titleName"></HeaderBar>
    <div class="mainContent">
      <div class="address_bg">
        <ul class="address">
          <li>
            <label>收货人:</label>
            <input type="text" placeholder="请输入收货人姓名" maxlength="10" v-model="param.Receiver" />
          </li>
          <li>
            <label>手机号码:</label>
            <input
              type="tel"
              placeholder="请输入手机号"
              maxlength="13"
              pattern="\d*"
              v-model.number="param.Telphone"
              oninput="value=value.replace(/[^\d]/g,'')"
            />
          </li>
          <li>
            <label>配送地址:</label>
            <input type="text" placeholder="请输入详细地址" maxlength="40" v-model="param.Address" />
          </li>
          <li>
            <label>邮政编码:</label>
            <input type="tel" placeholder="请输入邮政编码" maxlength="15" v-model="param.EmailCode" />
          </li>
        </ul>
        <!-- <router-link to="/integral_payment"> -->
        <button @click="Preservation">保存</button>
        <!-- </router-link> -->
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "intergral_address",
  props: {},
  data() {
    return {
      titleName: "收货地址",
      needb: true,
      param: {
        Telphone: "",
        Address: "",
        EmailCode: "",
        Receiver: ""
      }
    };
  },
  computed: {},
  created() {
    this.param = {};
  },
  mounted() {},
  watch: {
    Preservation() {
      let param = {};
    }
  },
  methods: {
    Preservation() {
      if (
        !this.param.Receiver ||
        !this.param.Telphone ||
        !this.param.Address ||
        !this.param.EmailCode
      ) {
        this.$toast("请填写信息");
        return false;
      }
      window.sessionStorage.setItem("addParam", JSON.stringify(this.param));
      this.$router.push({
        name: "integral_fill"
      });
    }
  },
  components: {}
};
</script>

<style scoped lang="css">
/* 重置 */
ul {
  list-style: none;
  margin: 0;
  padding-inline-start: 0;
}
li {
  margin: 0;
}
img {
  display: block;
  width: 100%;
}

.address_bg {
  background-color: #f5f6f9;
}
.address {
  margin: 10px 0px 20px 0px;
  text-align: left;
  background-color: white;
}
.address li {
  padding-left: 15px;
  height: 44px;
  line-height: 44px;
  font-size: 15px;
  border-bottom: 1px #c8c7cc solid;
}

.address li label {
  display: inline-block;
  min-width: 60px;
}
.address li input {
  color: #676767;
  font-size: 14px;
  padding-left: 10px;
}
button {
  width: 100%;
  height: 40px;
  line-height: 40px;
  color: #fff;
  background: #1b81cb;
  border-radius: 4px;
  font-size: 16px;
  padding: 0px 10px;
}
</style>



