          <div>
            <h2 class="titlea">改变你的生活方式 向绿色未来招手</h2>
            <span>2019-06-06</span>
            <span>中国经济导报社</span>
          </div>
          <div>
            <!-- <img src="../../../static/imgs/activity.png" /> -->
          </div>
          <p>
            蚂蚁森林/供图
            <br />中国经济导报记者|白雪
            <br />在今年的《政府工作报告》中提到，“绿色发展人人有责，贵在行动、成在坚持。”多种一棵树、选择绿色出行方式、践行垃圾分类……绿色生活方式不是一种能力，而是一种态度，更是一种责任，需要付诸于行动。
            <br />我们都在期待蓝天白云、绿水青山，但这样的生活是等不来的，需要我们共同努力去创造。走绿色发展道路、创造良好的生态环境，既需要顶层设计，也离不开一家一户的努力；既需要政府部门的科学管理，也离不开企业、个人的积极参与。
            <br />数字技术自下而上推动绿色金融
            <br />积极借助绿色金融的力量，支持可持续发展、低碳及可抵御气候变化带来负面影响的产业与项目，来减少粗放的发展模式曾给环境带来的伤害，这一直是我国在绿色金融道路上在探寻的模式。全国政协委员魏明德在今年两会上建议，绿色产品需要多样化，不仅有绿色信贷、绿色债券、绿色资产证券化、绿色发展基金及PPP项目等，还要包括绿色租赁、绿色信托、绿色理财、碳金融等新兴产品。
            <br />说起蚂蚁森林，大家应该都不陌生，这并不是一款可以简单种树的应用，更是绿色金融的国际标杆。
            <br />2017年5月，二十国集团（以下简称“G20”）框架下的绿色投资大会在德国柏林举行。这是G20框架下第一次把绿色和金融科技结合起来的专题会议。绿色数字金融联盟（GDFA）发布了这次会议上唯一一份专题报告，称赞蚂蚁森林的中国实践是世界标杆。
            <br />“数字技术让人们可以用自下而上的方式推动绿色金融。”蚂蚁金服首席执行官井贤栋说，数亿人通过一克克能量积累，一点点微小的改变，产生巨大的影响，这是对《巴黎协定》和2030年可持续发展议程等自上而下方式的重要补充。
            <br />蚂蚁森林工作人员告诉中国经济导报记者，“蚂蚁森林调动的不是公众募款，而是行动力。”这个虚拟与真实深度结合的主意，前所未有地调动了社会公众的行动力，数亿人坚持着原来每个个体都难以坚持的低碳行为。
            <br />近日，NASA公布的一张卫星资料图片更是让我们倍感欣喜。照片显示，地球新增了5%的绿化面积，相当于多出一整块亚马逊森林，而其中的1/4都要归功于中国。
            <br />中国网友的自豪感油然而生，因为很可能中国地图上多出的一点绿色，与他们的努力有关：“这里面有我的一份功劳，我在蚂蚁森林里已经种下了好多棵树。”“这片绿色里应该有我每天收集能量而种下的两棵沙柳。”
            <br />——一位杭州的270斤男生，坚持每天走路，减肥了70斤。快坚持不下来的时候，他就鼓励自己，“我可是要在荒漠边上种一片森林的人，我要坚持。”
            <br />——一位来自福建的男士，在老婆怀孕期间，天天陪老婆散步，给宝宝买东西，自己也在蚂蚁森林攒了很多能量。宝宝出生那天，他的梭梭树也种成了。
            <br />当然，不只是这些，蚂蚁森林和盒马的联手又为拒绝使用塑料袋多了一个理由。近日盒马宣布，在全国门店消费不购买塑料袋，可获得蚂蚁森林绿色能量。据盒马预计，此举全年有望减少使用1277万只塑料袋，创造的绿色能量可种植约1.5万棵梭梭树。
            <br />新的生活方式正在悄然改变。“某种程度上，跟种下亿万棵真树相比，亿万人的真实行动更令人震撼。”蚂蚁森林工作人员在给中国经济导报记者讲述这些故事的时候非常振奋。
            <br />据了解，2016年8月支付宝上线蚂蚁森林。用户步行替代开车、在线缴纳水电煤、网络购票等行为节省的碳排放量，将被计算为虚拟的“绿色能量”，用来在手机里养大一棵棵虚拟树。虚拟树长成后，蚂蚁金服和公益合作伙伴就会在地球上种下一棵真树，或守护相应面积的保护地，以培养和激励用户的低碳环保行为。
            <br />上述工作人员告诉中国经济导报记者，“截至2018年12月底，蚂蚁森林全球用户近4亿，累计减排308万吨，守护公益保护地6.9万亩，累计种植真树5552万棵，总面积超76万亩，控沙面积超100万亩，累计创造超过18万人次的绿色就业岗位，实现劳务收入超过2700万元。”其中，2018年通过蚂蚁森林项目创造超过13万人次绿色就业岗位，相关劳务收入超过1950万元。保护地6.9万亩，低碳场景19个。仅在春节集五福期间的11天里，用户们贡献的能量一共种成了近400万棵树，折合种植面积达到约3.6万亩。
            <br />当然，如果你觉得蚂蚁森林只是在改变年轻人的生活方式那就大错特错了，蚂蚁森林可不只是年轻人的专属。据上述工作人员介绍，“在蚂蚁森林里种树的不只是年轻人，还有60岁以上的老人，相较而言，他们更为偏爱合种家庭树和爱情树。”数据显示，60岁以上的老人种的爱情树，存活率要明显高于35岁以下的年轻人。他们平均种成一颗树要比年轻人慢上20天左右，但是更为执着。
            <br />低速电动车差异化发展推动绿色出行
            一次充电可行驶约100公里，百公里耗电10~15度，也就是说每公里约8分钱，整车售价约3~5万元，无需政府补贴。这种低速电动车你会选择吗？
            <br />交通出行是人们日常生活必不可少的组成部分，绿色出行已渐渐成为首选。在全国人大代表张天任看来，低速电动车作为新型短途交通工具，具有“绿色环保、节能减排、经济实惠、便捷安全”的购买和乘用体验，深受市场青睐。
            <br />据了解，这类车可供4人以内乘坐，用12千瓦时左右的铅蓄电池，一次充电可行驶约100公里，百公里耗电10~15度（约每公里8分钱），整车售价约3~5万元，无需政府补贴，如以中小城市居民日均机动出行30公里左右来计算，每月行驶900公里，电费约70元。
            <br />谈及此类低速电动车的使用范围，张天任告诉中国经济导报记者，“在三四线城市、城乡结合部，以及广大城镇、农村地区，其消费群体包括上班族、退休人员、商贩、村民等，主要用于上下班、接送小孩、上街买菜、进城办事等日常小范围活动；在大中城市传统燃油车未涉足的细分市场也有一小部分需求，如老年人和残障人代步、物流‘最后一公里’，以及环保、邮政等市政和公务专用车辆。”
            <br />正是广大百姓绿色出行的刚需充足，低速电动车产业发展势不可挡，但是由于国家没有出台相关标准，这类车不能上牌，无法纳入现行的道路交通管理体系，一定程度上阻碍了产业健康有序发展。
            <br />因此，张天任建议加强低速电动车区域差别化管理，呼吁国家对于低速电动车加强管理，鼓励绿色出行。“科学制定相关技术标准，实行差别化的监管政策。”张天任解释说，立足当前“低速电动车标准和管理办法尚未健全，而群众绿色出行需求得不到满足”的现状，根据区域特点，在道路资源安全可控范围内，将低速电动车纳入现行道路交通管理体系，实行差别化监管政策。除高速公路和大中城市人群密集的繁华街道重点限行外，所有乡村公路和中小城市、城镇的部分道路应向低速电动车开放。
            <br />同时，张天任也建议下放管理权限开展试点，让地方政府因地制宜，开展上路管理试点工作。在此基础上，及时总结好的经验和做法，积极推广应用，成功一个开放一个，更好地促进整个产业健康、有序、可持续发展。
            <br />当然，除了低速电动车未来的发展，围绕绿色的出行方式，关于新能源汽车等话题在两会上也是备受关注。全国人大代表尹同跃指出，氢燃料作为一种零排放、无污染的新能源，是国家实现节能减排和促进汽车产业的发展必然选择。目前美国、日本等发达国家已将氢能规划上升到国家能源战略高度，但我国在氢能生产、储存、运输方面目前还缺少统一规划与布局，相关配套检测与检验机构尚不完善，氢燃料产业发展进程相对缓慢。因此，尹同跃建议由国家层面统筹规划，将氢能从国家发展重点方向升级为国家发展战略的层面，做好顶层设计，促进氢能产业健康发展。
            <br />取消焚烧发电补贴推动垃圾分类
            <br />你在家开始垃圾分类了吗？全国人大代表潘向黎表示，“除了大家都认同的理由之外，垃圾分类体现了一座城市的品质。”
            <br />由国家发展改革委、住房城乡建设部发布的《生活垃圾分类制度实施方案》要求：2020年底前，在46个重点城市城区范围内先行实施生活垃圾强制分类。离最终期限仅有不到一年时间，中国经济导报记者却发现垃圾分类的实施效果“惨不忍睹”。在北京市大兴区西红门附近的某小区门口，4个垃圾桶清楚地表明了种类，但是居民投放的垃圾都是“一锅烩”。随后记者来到朝阳区一个较高端小区，情况有所好转，居民将各自分类好的垃圾投放到不同的垃圾桶内，但是垃圾车一来，却将干湿垃圾混杂在一起清运。
            <br />一项调查显示，80%的人知道垃圾分类回收很重要，但只有20%的人在认真分类。这反映出当前公众对垃圾回收认知的普遍情况：认可垃圾回收并认为自己应当承担责任，但对垃圾分类回收知识了解不够，对分类回收设施认可不足，尚未养成习惯。
            <br />知名经济学者、财经评论家郭凡礼告诉中国经济导报记者，“垃圾分类实质上是一项典型的社会治理工作，其成效高低与进度快慢主要取决于我国社会治理的法治化水平和全民参与的普遍程度。”他认为，目前，我们最为突出的短板是全民参与，即全民日复一日、年复一年地主动自觉参与。同时，我国尚未为生活垃圾分类处理提供坚实的法治基础，通过法律对个人形成有效的约束。
            <br />今年两会上，全国人大代表印萍就建议加快生活垃圾分类立法。对于垃圾分类处理的议案，印萍建议，加快强制性生活垃圾分类国家立法工作，推进差别化垃圾处理收费和信用制度的法律环境建设，加快生活垃圾分类标准体系建设，同时还应加强垃圾回收处置技术研发，最终构建节约型社会。
            <br />全国政协委员陈晓红则建议完善城市生活垃圾分类处置收费制度。一是完善垃圾分类体系建设，制定完善配套政策，培养公众环保意识；二是优化相关收费措施，明确费用征收主体，扶持垃圾分类处理产业链；三是建立阶段收费机制，完善责任追究机制，促进垃圾分类处置收费制度可持续发展。
            <br />针对垃圾分类落实难的问题，全国人大代表里赞建议取消焚烧发电补贴。里赞认为，在垃圾分类问题上，还得通过终端改革倒逼前端。为此，他呼吁将“发电”从“垃圾焚烧”中剥离出来。从实际情况看，补贴政策刺激了垃圾焚烧厂建设，变相鼓励垃圾源头放纵、焚烧可回收物等，不利于绿色循环经济发展。
          </p>
        </li>
