<template>
  <div class="mainCover">
    <HeaderBar :needs="true" :needb="needb" @fathersubmit="submitfnc" :tit="titleName"></HeaderBar>

    <div class="mainContent">
      <form action method="get" class="opinion_th">
        <div>
          <p>意见</p>
          <textarea
            name="内容"
            cols="24"
            rows="2"
            maxlength="120"
            placeholder="请输入您的意见"
            v-model="Comment"
          ></textarea>
        </div>
        <div class="inputBox">
          <p>姓名（选填）</p>
          <input type="text" name id placeholder="请填写您的姓名" />
        </div>
        <div class="inputBoxb">
          <p>联系方式（选填）</p>
          <input type="text" name id placeholder="请填写您的电话或邮箱" />
        </div>
        <div class="subBtn">提交</div>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  name: "me_opinion",
  props: {},
  data() {
    return {
      titleName: "意见反馈",
      needb: true,
      Comment: ""
    };
  },
  computed: {},
  created() {},
  mounted() {},
  watch: {},
  methods: {
    async submitfnc() {
      try {
        let Comment = this.Comment;
        if (Comment.length < 6) {
          this.$toast("评论字符不得少于6个字符");
          return;
        }
        if (Comment.length > 120) {
          this.$toast("评论字符不得大于120个字符");
          return;
        }
        let param = {
          proposal: this.Comment
        };
        let res = await this.$api.all.POSTfn(param, "user_setProposal");
        if (res.result == "ok") {
          this.$toast("发布成功！");
          this.$router.go(-1);
        } else {
          this.$toast(res.result);
        }
      } catch (error) {
        console.log(error);
      }
    }
  },
  components: {},
  beforeRouteLeave(to, from, next) {
    this.Comment = "";
    next();
  }
};
</script>

<style scoped lang="css">
.mainContent {
  background: #f5f6fa;
}
.opinion_th {
  width: 100%;
  /* background-color: #f5f6f9; */

  line-height: 2;
  padding: 12px;
}

.opinion_th textarea {
  width: 100%;
  padding: 12px;
  height: 200px;
  /* line-height: 40px; */
  font-size: 14px;
  color: #333;
  text-align: justify;
  border-radius: 3px;
  border: none;
}
input {
  padding: 10px;
  width: 100%;
}
.inputBox {
  margin-top: 10px;
}
.inputBoxb {
  margin-top: 20px;
}
.subBtn {
  padding:5px;
  background: #fff;
  text-align: center;
  margin-top: 20px;color: #3966a7
}
</style>



