<template>
  <div class="mainCover">
    <HeaderBar :needb="needb" :tit="titleName"></HeaderBar>
    <div class="mainContent">
      <ul class="push-ul">
        <vue-scroll
          ref="scroll"
          :ops="opsconfig"
          @refresh-start="handleRS"
          @load-before-deactivate="handleLBD"
        >
          <li
            class="push-li"
            v-for="(v,i) in noticelist"
            :key="i"
            ref="pushli"
            @click.prevent="changeX(i)"
          >
            <div class="box">
              <div class="push-span content">{{v.title}}</div>
              <div class="state-box">
                <span
                  class="push-span state fontState1"
                  v-show="v.state==0"
                >{{v.state | filterstatefn}}</span>
                <span
                  class="push-span state fontState2"
                  v-show="v.state==1"
                >{{v.state | filterstatefn}}</span>
                <span class="push-span date">{{v.c_time | changeTime}}</span>
              </div>
            </div>
          </li>
        </vue-scroll>
      </ul>
    </div>
  </div>
</template>

<script>
import { ScrollConfig } from "common/data.js";
import moment from "moment";
export default {
  name: "me_history",
  props: {},
  data() {
    return {
      opsconfig: ScrollConfig.opsconfig,
      titleName: "我的通知",
      needb: true,
      noticelist: [],
      UserInfo: this.$getUser,
      max_id: "",
      min_id: ""
    };
  },
  created() {
    moment.locale("zh-cn");
    // this.getDatelist("bigger", "0");
  },
  filters: {
    filterstatefn(val) {
      if (val == "1") {
        return "已读";
      } else if (val == "0") {
        return "未读";
      }
    },
    changeTime(val) {
      return moment(val)
        .startOf("minute")
        .fromNow();
    }
  },
  computed: {},
  activated() {
    this.getDatelist("bigger", "0");
  },
  mounted() {},
  watch: {},
  methods: {
    async getDatelist(type, numid) {
      try {
        let params = {
          count: 20,
          direction: type,
          target_id: numid
        };
        let res = await this.$api.all.GETfn(params, "notice");
        res = res.result;
        if (res.data.length > 0) {
          this.noticelist = res.data;
          this.max_id = res.max_id;
          this.min_id = res.min_id;
        }
      } catch (error) {
        console.log(error);
      }
    },
    async changeX(i) {
      try {
        let params = {
          id: this.noticelist[i].id
        };
        let res = await this.$api.all.POSTfn(params, "notice_set_state");
        if (res.result == "ok") {
          this.$nextTick(() => {
            this.noticelist[i].state = "1";
          });
        }
      } catch (error) {
        console.log(error);
      }
    },
    async handleRS(vsInstance, refreshDom, done) {
      // alert("这是执行下拉刷新开始做啥事");
      try {
        let timestamp = new Date().getTime();
        let param = {
          count: 10,
          timestamp: timestamp,
          direction: "bigger",
          target_id: this.max_id
        };
        let res = await this.$api.all.GETfn(param, "notice");
        res = res.result;
        if (res.data.length == 0) {
          this.$toast("暂无最新数据");
          done();
          return;
        } else if (res.data.length > 0) {
          this.max_id = res.max_id;
          // this.min_id = res.min_id;
          res.data.forEach((item, index, arr) => {
            this.noticelist.unshift(item);
          });
          done();
          this.$forceUpdate();
        }
        done();
      } catch (error) {}
    },
    async handleLBD(vm, loadDom, done) {
      try {
        let timestamp = new Date().getTime();
        let param = {
          count: 10,
          timestamp: timestamp,
          direction: "litter",
          target_id: this.min_id
        };
        let res = await this.$api.all.GETfn(param, "notice");
        res = res.result;
        if (res.data.length == 0) {
          this.$toast("没有更多数据");
          done();
          return;
        } else if (res.data.length > 0) {
          // this.max_id = res.max_id;
          this.min_id = res.min_id;
          res.data.forEach((item, index, arr) => {
            this.noticelist.push(item);
          });
          this.$forceUpdate();
        }
        done();
      } catch (error) {}
    }
  },
  components: {}
};
</script>
 
<style scoped lang="css">
.mainContent {
  min-height: calc(100vh- 44px) !important;
  height: calc(100vh- 44px) !important;
}
.push-ul {
  min-height: calc(100vh- 44px);
  height: calc(100vh- 44px);
  /* padding: 0px 12px; */
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.push-li {
  min-height: 66px;
  height: 66px;
  width: 100%;
  padding: 5px 12px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.box {
  width: 100%;
  height: 100%;
  background-color: #fff;
  border-radius: 5px;
  padding: 8px;
  display: flex;
  flex-wrap: wrap;
  align-content: space-between;
  box-shadow: 0 4px 15px #eee;
}

.content {
  width: 100%;
  font-size: 14px;
  color: #333;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.state-box {
  line-height: 12px;
}

.state {
  width: 50%;
  font-size: 12px;
  font-weight: 600;
}
.date {
  width: 50%;
  font-size: 12px;
  color: #999;
  margin-left: 10px;
  /* text-align: right; */
}

.fontState1 {
  color: rgb(228, 86, 43);
}

.fontState2 {
  color: rgb(93, 185, 228);
}
</style>



