<template>
  <div class="mainCover">
    <HeaderBar :needm="needm" :needb="needb" :tit="titleName"></HeaderBar>

    <div class="mainContent">
      <ul v-if="newFriendsList.length > 0">
        <li class="friend-list" v-for="friend in newFriendsList">
          <div class="head-portrait-box">
            <img :src="friend.avatar" alt class="head-portrait" />
          </div>
          <div class="name-box">
            <div class="info">
              <p class="name">{{friend.nick}}</p>
              <p class="preview">{{friend.ps}}</p>
            </div>
            <div class="add-button" @tap="handleAgree(friend)">同意</div>
          </div>
        </li>
      </ul>
      <p v-else style="text-align: center;margin-top: 20px;">暂无更多</p>
    </div>
  </div>
</template>

<script>
import mui from 'static/js/mui.min.js';

export default {
  name: "news_friends",
  props: {},
  data() {
    return {
      titleName: "好友申请",
      needb: true,
      needm: false,
      debug: ""
    };
  },
  computed: {
    newFriendsList() {
      return this.$store.state.newFriendsList;
    }
  },
  created() {
    this.debug = require("images/debug.png");
  },
  mounted() {},
  watch: {},
  methods: {
    handleAgree(item){
      const that = this;
      const { sysMsgs: { idServer }, account, } = item;
      plus.nativeUI.prompt("请填写备注信息: ", ({value})=>{
        // 假设 sysMsg 是通过回调 `onsysmsg` 收到的系统通知
        nim.passFriendApply({
            account,
            ps: value,
            idServer: idServer,
            done: passFriendApplyDone
        });
        function passFriendApplyDone(error, obj) {
            console.log(error);
            console.log(obj);
            mui.toast('通过好友申请' + (!error?'成功':'失败'));
            if (!error) {
                that.$store.commit('updateFriends', obj.friend)
            }else{
                mui.toast(error.message)
            }
            that.$store.commit('deleteNewFriend', obj);
        }
      }, "提示", "", ['确认','取消']);
    }
  },
  components: {}
};
</script>

<style scoped lang="css">
ul {
  list-style-type: none;
}

li {
  width: 100%;
  overflow: hidden;
}

.friend-list {
  width: 100%;
  display: flex;
  height: 60px;
  padding-right: 12px;
}

.head-portrait-box {
  width: 15%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.head-portrait {
  width: 36px;
  height: 36px;
  border-radius: 50%;
}

.info {
  width: 70%;
}

.name-box {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  border-bottom: 1px solid #eee;
}

.name {
  display: block;
  font-size: 14px;
  color: #222;
}

.add-button {
  width: 26%;
  height: 30px;
  font-size: 12px;
  background-color: #4a8dff;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 3px;
}

.preview {
  font-size: 12px;
  color: #666;
}
</style>



