<template>
  <div class="Cover">
    <p v-show="allList.length==0" class="nothing">——— Ta还没有关注过别人 ———</p>

    <ul class="follow1-ul">
      <vue-scroll
        ref="scroll"
        :ops="opsconfig"
        @refresh-start="handleRS"
        @load-before-deactivate="handleLBD"
      >
        <li class="follow1-li" v-for="(v,i) in allList" :key="i" @click.prevent="communedetail(v)">
          <div class="content1-left">
            <span class="span-title">{{(v.target_caption)}}</span>
            <!-- <span class="span-content">来自: {{v.target_caption}}</span> -->
            <span class="span-data">{{v.time | changeTime}}</span>
          </div>
          <div class="content1-right" v-show="v.cover!=''">
            <img v-lazy="v.cover" class="page-lazyload-image1" />
          </div>
        </li>
      </vue-scroll>
    </ul>
  </div>
</template>

<script>
import { ScrollConfig } from "common/data.js";
import moment from "moment";
export default {
  opsconfig: ScrollConfig.opsconfig,
  name: "bigVAttention",
  props: {},
  data() {
    return {
      opsconfig: ScrollConfig.opsconfig,
      titleName: "大V关注",
      needb: true,
      UserParam: {},
      alldata: [],
      max_id: "",
      min_id: "",
      allList: []
    };
  },
  filters: {
    changeTime(val) {
      return moment(val)
        .startOf("minute")
        .fromNow();
    }
  },
  computed: {},
  created() {
    this.UserParam = JSON.parse(window.sessionStorage.getItem("S-itemv"));
    moment.locale("zh-cn");
    this.smallimage = require("images/small.jpg");
    this.getDate("bigger", "0");
  },
  mounted() {},
  watch: {
    $route() {
      this.UserParam = JSON.parse(window.sessionStorage.getItem("S-itemv"));
      this.getDate("bigger", "0");
    }
  },
  methods: {
    communedetail(v) {
      console.log(v);
      if (v.target_type == "2003") {
        window.sessionStorage.setItem("askdata", JSON.stringify(v));
        this.$router.push({
          name: "commune_f"
        });
      } else if (v.target_type == "2001") {
        window.sessionStorage.setItem("homedata", JSON.stringify(v));
        this.$router.push({
          name: "homeDetail"
        });
      } else {
        this.$toast("数据异常,请联系管理员");
      }
    },
    async getDate(type, numsid) {
      try {
        let timestamp = new Date().getTime();
        let param = {
          userid: this.UserParam.user_id,
          user_id: this.UserParam.user_id,
          token: this.UserParam._token,
          target: this.UserParam.user_id,
          count: 10,
          timestamp: timestamp,
          direction: type,
          target_id: numsid
        };
        let res = await this.$api.all.GETfn(param, "user_collect"); //likeimg
        res = res.result;
        this.allList = res.data;
        console.log(this.allList);
        this.max_id = res.max_id;
        this.min_id = res.min_id;
      } catch (error) {
        console.log(error);
      }
    },
    activated() {},
    async handleRS(vsInstance, refreshDom, done) {
      // alert("这是执行下拉刷新开始做啥事");
      try {
        let timestamp = new Date().getTime();
        let param = {
          userid: this.UserParam.user_id,
          user_id: this.UserParam.user_id,
          token: "",
          target: this.UserParam.user_id,
          count: 10,
          timestamp: timestamp,
          direction: "bigger",
          target_id: this.max_id
        };
        let res = await this.$api.all.GETfn(param, "user_collect");
        res = res.result;
        if (res.more == "0" && res.data.length == 0) {
          this.$toast("暂无最新数据");
          done();
          return;
        } else if (res.data.length > 0) {
          this.max_id = res.max_id;
          this.min_id = res.min_id;
          res.data.forEach((item, index, arr) => {
            this.allList.unshift(item);
          });
          this.$forceUpdate();
          done();
        }
        console.log(this.allList);
        done();
      } catch (error) {}
    },
    async handleLBD(vm, loadDom, done) {
      // alert("这是执行下拉加载之后需要执行什么操作");
      try {
        let timestamp = new Date().getTime();
        let param = {
          userid: this.UserParam.user_id,
          user_id: this.UserParam.user_id,
          token: "",
          target: this.UserParam.user_id,
          count: 10,
          timestamp: timestamp,
          direction: "litter",
          target_id: this.min_id
        };
        let res = await this.$api.all.GETfn(param, "user_collect");
        res = res.result;
        if (res.more == "0" && res.data.length == 0) {
          this.$toast("没有更多数据");
          done();
          return;
        } else if (res.data.length > 0) {
          this.max_id = res.max_id;
          this.min_id = res.min_id;
          res.data.forEach((item, index, arr) => {
            this.allList.push(item);
          });
          done();
          this.$forceUpdate();
        }
        done();
        console.log(this.allList);
      } catch (error) {}
    }
  },
  components: {}
};
</script>

<style scoped lang="css">
.Cover {
  width: 100%;
  height: calc(100vh-208px) !important;
  min-height: calc(100vh-208px) !important;
}

.follow1-ul {
  width: 100%;
  height: calc(100vh-208px) !important;
  min-height: calc(100vh-208px) !important;
  padding: 0px 12px;
}

.nothing {
  margin-top: 15px;
  font-size: 12px;
  color: #999;
  height: 40px;
  line-height: 40px;
  width: 100%;
  text-align: center;
}
ul {
  /* margin: 0;
  padding-inline-start: 0; */
}
li {
  list-style: none;
}

.follow2-button {
  flex: 1;
  height: 30px;
}

.follow2-content {
  height: 90px;
  line-height: 25px;
  width: 50%;
  padding: 10px 10px;
}

.follow2-content span {
  display: block;
  font-size: 14px;
}

.follow2-image {
  width: 30%;
  height: 90px;
  padding: 10px 0px;
}
.follow2-image img {
  width: 100%;
  height: auto;
}

.follow2-li div,
p,
button {
  display: inline-block;
}

.follow2-li {
  font-size: 14px;
  text-align: left;
  line-height: 20px;
  height: 90px;
  min-height: 90px;
  width: 100%;
  padding: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* .follow1-ul {
  height: calc(100vh-208px) !important;
  min-height: calc(100vh-208px) !important;
  width: 100%;
} */

.follow1-li {
  font-size: 14px;
  text-align: left;
  line-height: 15px;
  height: 86px;
  min-height: 86px;
  width: 100%;
  padding: 8px 0px;
  border-bottom: 1px solid #eee;
  display: flex;
}

.content1-left {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

/* .content1-left span {
  display: block;
  width: 100%;
  height: 25px;
  line-height: 25px;
  padding: 2px 0px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
} */

.span-title {
  font-size: 12px;
  line-height: 20px;
  min-height: 20px;
  color: #333;
  text-align: justify;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  display: -webkit-box !important;
}

.span-content {
  font-size: 12px;
  min-height: 30px;
  line-height: 15px;
  color: #666;
  text-align: justify;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  display: -webkit-box !important;
}

.span-data {
  min-height: 20px;
  font-size: 10px;
  color: #999;
  display: inherit;
  align-items: center;
  text-align: justify;
}

.content1-right {
  width: 35%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row-reverse;
  margin-left: 10px;
}

.content1-right img {
  width: 100%;
  height: 98%;
  flex: 1;
  display: block;
}
</style>



