<template>
  <div class="mainCover">
    <HeaderBar :needb="needb" :tit="titleName"></HeaderBar>
    <div class="mainContent">
      <p v-show="allList.length==0" class="nothing">——— 您还没有组过队伍 ———</p>

      <ul class="scroll-ul">
        <vue-scroll
          ref="scroll"
          :ops="opsconfig"
          @refresh-start="handleRS"
          @load-before-deactivate="handleLBD"
        >
          <li v-for="(v,i) in allList" :key="i" class="scroll-li" @click.prevent="communedetail(v)">
            <div class="content">
              <p class="title">
                <b>{{v.team_name}}</b>
              </p>
              <div class="next">
                <img :src="next" alt class="next-img" />
              </div>
            </div>
            <p class="date">{{v.create_time | changeTime}}</p>
            <div class="info">
              <p>发起人：{{v.initiator_name}}</p>
              <p>来自：{{v.commune_name}}</p>
              <p>需要：{{v.num}}人</p>
            </div>
          </li>
        </vue-scroll>
      </ul>
    </div>
  </div>
</template>

<script>
import { ScrollConfig } from "common/data.js";
import moment from "moment";

export default {
  name: "me_team",
  props: {},
  data() {
    return {
      opsconfig: ScrollConfig.opsconfig,
      titleName: "组队",
      needb: true,
      next: "",
      allList: [],
      UserInfo: this.$getUser,
      max_id: "",
      min_id: ""
    };
  },
  computed: {},
  filters: {
    changeTime(val) {
      return moment(val)
        .startOf("minute")
        .fromNow();
    }
  },
  created() {
    this.next = require("images/next.png");
    moment.locale("zh-cn");
    this.getDate("bigger", "0");
  },
  mounted() {},
  watch: {},
  methods: {
    communedetail(v) {
      window.sessionStorage.setItem("teamdetail", JSON.stringify(v));
      this.$router.push("commune_personal");
    },
    async getDate(type, numsid) {
      try {
        let timestamp = new Date().getTime();
        let params = {
          count: 10,
          timestamp: timestamp,
          direction: type,
          target_id: numsid
        };
        let res = await this.$api.all.GETfn(params, "team_user_list2");
        res = res.result;
        this.allList = res.data;
        this.max_id = res.max_id;
        this.min_id = res.min_id;
      } catch (error) {}
    },
    async handleRS(vsInstance, refreshDom, done) {
      // alert("这是执行下拉刷新开始做啥事");
      try {
        let timestamp = new Date().getTime();
        let param = {
          count: 10,
          timestamp: timestamp,
          direction: "bigger",
          target_id: this.max_id
        };
        let res = await this.$api.all.GETfn(param, "team_user_list2");
        /************************************* */

        debugger;
        res = res.result;
        if (res.more == "0" && res.data.length == 0) {
          this.$toast("暂无最新数据");
          done();
          return;
        }
        if (res.data.length > 0) {
          this.max_id = res.max_id;
          this.min_id = res.min_id;
          res.data.forEach((item, index, arr) => {
            this.allList.unshift(item);
          });
          this.$forceUpdate();
        }
        /************************************* */
        done();
      } catch (error) {}
    },
    async handleLBD(vm, loadDom, done) {
      // alert("这是执行下拉加载之后需要执行什么操作");
      try {
        let timestamp = new Date().getTime();
        let param = {
          count: 10,
          timestamp: timestamp,
          direction: "litter",
          target_id: this.min_id
        };
        let res = await this.$api.all.GETfn(param, "team_user_list2");
        /************************************* */
        debugger;
        res = res.result;
        if (res.more == "0" && res.data.length == 0) {
          this.$toast("没有更多数据");
          done();
          return;
        }
        if (res.data.length > 0) {
          this.max_id = res.max_id;
          this.min_id = res.min_id;
          res.data.forEach((item, index, arr) => {
            this.allList.push(item);
          });
          this.$forceUpdate();
        }
        /************************************* */ done();
        console.log(this.allList);
      } catch (error) {}
    }
  },
  components: {}
};
</script>

<style scoped lang="css">
.mainContent {
  width: 100%;
  height: 623px;
  min-height: 623px;
}
.scroll-ul {
  width: 100%;
    min-height: calc(100vh-44px);
  height: calc(100vh-44px);
  padding: 0px 12px;
}

.scroll-li {
  width: 100%;
  height: 90px;
  min-height: 90px;
  padding: 5px 0px;
  border-bottom: 1px solid #eee;
}

.content {
  width: 100%;
  height: 25px;
  min-height: 25px;
  line-height: 25px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.title {
  width: 90%;
  font-size: 14px;
  color: #333;
  text-align: justify;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  display: -webkit-box !important;
  height: 25px;
  min-height: 25px;
}
.next {
  width: 20px;
  height: 20px;
  display: flex;
  flex: 1;
  align-items: center;
  justify-content: center;
}

.next-img {
  width: 12px;
  height: 12px;
}

.date {
  font-size: 12px;
  color: #999;
  height: 25px;
  min-height: 25px;
  line-height: 25px;
  width: 100%;
  display: block;
}

.info {
  width: 100%;
  height: 30px;
  min-height: 30px;
  font-size: 12px;
  color: #666;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.nothing {
  margin-top: 15px;
  font-size: 12px;
  color: #999;
  height: 40px;
  line-height: 40px;
  width: 100%;
  text-align: center;
}
</style>



