<template>
  <div class="mainCover">
    <HeaderBar :needm="needm" :needb="needb" :tit="titleName"></HeaderBar>
    <div class="mainContent">
      <div class="club-name">
        <input type="text" placeholder="请填写标题" v-model="formItem.commname" name="name" />
      </div>
      <div class="tagbox">
        <mt-cell :title="'[ ' + check + ' ]'">
          <mt-button
            size="small"
            type="primary"
            @click.native="sheetVisible = true"
            class="tag-btn"
          >标签(可选)</mt-button>
        </mt-cell>
      </div>
      <div class="createclubs_tq" id="describe">
        <!-- <label>*描述</label> -->
        <textarea
          placeholder="请输入内容,建议200字....."
          style="height:200px;width:100%;color:#333"
          id="question"
          name="info"
          v-model="formItem.introduction"
        ></textarea>
      </div>

      <div class="createclubs_tm">
        <div class="logo-box">
          <Photoclipcom
            viewdom="fasImg"
            outputSizeWidth="600"
            outputSizeHeight="600"
            :inSize="INSIZE"
            @fatherfromcall="getchilfromdata"
            @fatherimgcall="getchildimgcall"
          ></Photoclipcom>
          <div class="imgBorder">
            <img :src="formItem.imgi" alt class="face-img" id="fasImg" />
          </div>
        </div>
      </div>
      <div class="btn">
        <button type="button" class="c-ok-btn" id="yBtn" @click="commune_cj()">确认</button>
        <button type="button" class="c-cancel-btn nBtn" @click="$router.back(-1)">取消</button>
      </div>
      <div>
        <mt-actionsheet :actions="actions1" v-model="sheetVisible" ref="actions"></mt-actionsheet>
      </div>
    </div>
  </div>
</template>

<script>
import Thumbnail from "com/thumbnail.vue";
import { Toast } from "mint-ui";
export default {
  name: "community",
  props: {},
  data() {
    return {
      titleName: "发布",
      needb: true,
      needm: false,
      bgImage: "",
      communecj: [],
      actions1: [
        {
          name: "关键字",
          method: this.isKey
        }
      ],
      formItem: {
        commname: "",
        imgi:require("images/upload.jpg"),
        introduction: "",
        file: ""
      },
      check: "未选择",
      sheetVisible: false,
      type: "",
      param: new FormData(),
      rdata: [],
      INSIZE: [300, 150]
    };
  },
  computed: {},
  activated() {
    this.bgImage = require("images/lvse.jpg");
    this.getLabelData();
    this.getParams();
  },
  mounted() {},
  watch: {
    $route: {
      handler(route) {
        // console.log("路由" + route.name);
        const that = this;
        if (route.name == "club_asked") {
          if (window.sessionStorage.getItem("asktype") == 1) {
            console.log("typetype:" + this.titleName);
            this.titleName = "发布设问";
          } else {
            this.titleName = "发布公告   ";
          }
          this.$forceUpdate();
          console.log("typetype");
        }
        if (route.name == "commune_meeting") {
          this.formItem.commname = "";
          this.formItem.imgi = require("images/upload.jpg");
          this.formItem.introduction = "";
          this.check = "未选择";
        }
      },
      deep: true
    }
  },
  methods: {
    getParams() {
      var routerdata = window.sessionStorage.getItem("commitydata");
      // console.log(routerdata);
      this.rdata = JSON.parse(routerdata);
      if (window.sessionStorage.getItem("asktype") == 1) {
        this.titleName = "发布设问";
        console.log("tobiaoti" + this.titleName);
      } else {
        this.titleName = "发布公告";
      }
    },
    getchildimgcall(data) {
      this.formItem.imgi = data;
    },
    getchilfromdata(data) {
      this.param.append("file", data.get("file"));
    },
    //发布社问
    async commune_cj() {
      var ast = window.sessionStorage.getItem("asktype");
      if (
        this.formItem.commname == "" ||
        this.formItem.imgi == require("images/upload.jpg")||
        this.formItem.introduction == "" ||
        this.check == ""
      ) {
        return;
      }
      let params = this.param;
      params.append("title", this.formItem.commname);
      params.append("content", this.formItem.introduction);
      params.append("tag", this.check);
      // params.append("file", this.formItem.imgi);
      params.append("type", ast);
      params.append("communeid", this.rdata.id);

      try {
        let res = await this.$api.all.POSTfn(params, "createAsk");
        console.log(res);
        if (res.result == "ok") {
          if (ast == 1 || ast == 2) {
            this.$router.push({
              name: "commune_meeting"
            });
          } else {
            this.$router.push({
              name: "find"
            });
          }

          this.formItem.commname = "";
          this.formItem.imgi =require("images/upload.jpg");
          this.formItem.introduction = "";
          this.check = "未选择";
          Toast("发布成功,等待社长审核");
        }
      } catch (e) {
        console.log(e);
      }
    },
    async getLabelData() {
      try {
        let params = {};
        let res = await this.$api.all.GETfn(params, "find_ask_tag_list");
        console.log(res);
        for (let i in res.result) {
          res.result[i].name = res.result[i].tag_name;
          res.result[i].method = this.tagActive;
        }
        this.actions1 = res.result;
      } catch (e) {
        console.log(e);
      }
    },
    tagActive(call) {
      this.check = call.name;
    }
  },
  components: { Thumbnail }
};
</script>

<style scoped lang="css">
.content {
  padding: 0px 12px;
}

.club-name {
  height: 36px;
  display: flex;
  align-items: center;
  border: 1px solid #eee;
  border-radius: 3px;
  margin-top: 10px;
}
.club-name input {
  width: 100%;
}
.tagbox{
  padding: 0 10px
}
/* .mainContent{
  padding: 40px 0 0 0
}
.mint-header{
  position: fixed;
  top: 0;
  left: 0
} */
.logo-box >>> #imagefile {
  opacity: 0;
  width: 100px;
  height: 100px;
  position: absolute !important;
}
.tag-btn {
  font-size: 12px;
  float: left;
  margin-left: 10px;
  background: #4a8dff;
  color: #fff;
  padding: 0px 10px;
}
input {
  font-size: 12px;
  line-height: 30px;
  padding: 0 10px;
}
.createclubs_tm {
  width: 100%;
  padding: 10px 10px;
}
.createclubs_tq {
  width: 100%;
  /* height: 36px; */
  /* display: flex; */
  /* align-items: center; */
  border: 1px solid #eee;
  border-radius: 3px;
}
.weixuanze {
  margin-left: 10px;
}
textarea {
  border: 0px;
  padding: 10px;
}
.btn {
  text-align: center;
  margin-top: 10px;
  display: flex;
  justify-content: space-between;
}
.c-ok-btn,
.c-cancel-btn {
  width: 150px;
  height: 36px;
  background: #4a8dff;
  color: #fff;
  font-size: 14px;
  padding: 5px 40px;
  border-radius: 3px;
  margin: 0px 12px;
}
.c-cancel-btn {
  background: #ccc;
}

.face-img {
  height: 100px;
  width: 100px;
}

.imgBorder {
  height: 115px;
  width: 115px;
  /* border: 2px solid #e2e3e3;  */
  padding: 5px;
}
.tag-btn[data-v-0f4b0c0e] {
  margin-right: 210px;
}
.mint-cell {
  width: 370px;
  height: 40px;
}
#hh-commune {
  color: #333;
}
</style>




