<template>
  <div class="mainCover">
    <HeaderBar :needcsb="true" @fatherbackcall="gotoBegin()" :tit="titleName" v-show="isShow" ></HeaderBar>
    <div class="mainContent">
      <div class="top-box">
        <div class="login-box">
          <span class="login-text">登录</span>
          <span class="sign">
            <span class="nbsp">&nbsp;</span>
          </span>
        </div>
        <div class="login-box" @click="gotoregistration">
          <span class="login-text">注册</span>
          <span class="sign">&nbsp;</span>
        </div>
      </div>
      <!-- onkeyup="value=value.replace(/[^\w]/g,'') " -->
      <!-- onbeforepaste="clipboardData.setData('text',clipboardData.getData('text').replace(/[^\w]/g,''))" -->
      <div>
        <p class="message">账号密码登录</p>

        <!-- 账号输入框 -->
        <div class="input-box">
          <span class="iconfont icon-account" slot="icon"></span>

          <input
            type="text"
            class="input-default"
            placeholder="请输入账号"
            maxlength="13"
            v-model="Account"
          />
        </div>

        <div class="input-box input-box-last">
          <span class="iconfont icon-pass" slot="icon"></span>
          <input
            type="password"
            class="input-default"
            placeholder="请输入密码"
            maxlength="13"
            v-model="PassWord"
          />
        </div>

        <div class="button-login">
          <input type="button" value="登录" class="input-button-login" @click="loginConfirm" />
        </div>

        <div class="button-other">
          <router-link to="/messagelogin">短信登录</router-link>
          <router-link to="/forgetpassword">忘记密码</router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ComUtils } from "common/utils";
import { Toast } from "mint-ui";
export default {
  name: "login",
  props: {},
  data() {
    return {
      titleName: "登录",
      needb: true,
      needm: false,
      PassWord: "",
      Account: "",
      beginPage: "",
      isShow:false
    };
  },
  computed: {},
  created() {},
  mounted() {},
  watch: {},
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.beginPage = from.name;
    });
  },
  activated(){
     if (window.hasOwnProperty("webkit")) {
        this.isShow = false;
      } else {
        this.isShow = true;
      }
  },
  methods: {
    gotoBegin() {
    //  Toast ("登录返货")
      if (window.hasOwnProperty("webkit")) {
        //  Toast ("ios登录返回1")
        //  let param={}
          window.webkit.messageHandlers.closeWebView.postMessage(null);
        // window.webkit.messageHandlers.closeWebView.postMessage();
        Toast ("ios登录返回2")
      } else {
           Toast ("HTML登录返回")
        let begin = this.beginPage;
        if (begin == "" || begin == null) {
          begin = "homePage";
        }
        this.$router.push({
          name: begin
        });
      }
    },
    gotoregistration() {
      this.$router.push({
        name: "registration"
      });
    },
    async loginConfirm() {
      try {
        let passwordword = this.PassWord;
        let accountValue = this.Account;
        if (accountValue == "" || accountValue == null) {
          this.$toast("您还未输入账户信息");
          return false;
        }

        if (passwordword == "" || passwordword == null) {
          this.$toast("您还未输入密码");
          return false;
        }
        let param = {
          account: accountValue,
          password: passwordword
        };
        let res = await this.$api.all.POSTfn(param, "login");
        if (res.result == "ok") {
           let isLogin = window.localStorage.setItem("isLogin","true")
          this.$store.dispatch("pullbuttomselected", "homePage");
          //保存用户信息
          ComUtils.saveParams(res);
          // 初始化云信
          
          this.addYunXin(res);
          if (window.hasOwnProperty("webkit")) {
            window.webkit.messageHandlers.closeWebView.postMessage(null);
            this.Toast("登录成功!");
          } else {
            setTimeout(() => {
              this.$router.push({
                name: "homePage"
              });
            }, 1000);
          }
        } else {
          this.Toast(res.result);
           window.localStorage.clear();
        }
      } catch (error) {
        console.log(error);
      }
    },
    async addYunXin({ id }) {
      try {
        let param = {
          userid: id
        };
        let res = await this.$api.all.POSTfn(param, "yx_token");
        res = res.result;
        if (res.result == "false") {
          alert("聊天账号异常,请联系管理员!");
          return false;
        }
        window.localStorage.setItem(
          "YXToken",
          JSON.stringify({ token: res.token, account: res.account })
        );
        this.$store.dispatch("connect");
      } catch (error) {}
    }
  },
  components: {}
};
</script>

<style scoped lang="css">
>>>.mint-header{
  background: #4a8dff !important;
  color: #fff !important;
}
 .mintui-back{
   color: #fff
 }
a {
  color: #333333 !important;
  text-decoration: none;
}

.top-box {
  min-height: 110px;
  background-color: #4a8dff;
  display: flex;
  width: 100%;
  align-items: flex-end;
  text-align: center;
  color: #fff;
}

.login-box {
  flex: 1;
  min-height: 60px;
  line-height: 60px;
  text-align: center;
  clear: both;
  overflow: hidden;
}

.login-box span {
  display: block;
}

.login-text {
  height: 40px;
  line-height: 40px;
  font-size: 17px;
  padding: 5px 0px 0px 0px;
}

.sign {
  min-height: 22px;
  line-height: 22px;
  min-width: 22px;
  position: relative;
  left: 50%;
  margin-left: -10px;
}

.nbsp {
  width: 0px;
  height: 0px;
  border-left: 12px solid transparent;
  border-right: 12px solid transparent;
  border-bottom: 12px solid #fff;
  border-top: 12px solid transparent;
}

.message {
  height: 44px;
  line-height: 44px;
  text-align: center;
  color: #4a8dff;
  font-size: 16px;
  font-weight: 500;
}

.input-box {
  height: 44px;
  line-height: 44px;
  display: flex;
}

.input-box span {
  display: inline-block;
  width: 26px;
  height: 26px;
  margin: 0px 5px 0px 15px;
  color: #4a8dff;
}

.input-box input {
  height: 26px;
  margin: 9px 5px;
  flex: 1;
  line-height: 26px;
  font-size: 15px;
}

.button-login {
  /* margin-top: 20px; */
  height: 40px;
  line-height: 40px;
  width: 100%;
  font-size: 16px;
  color: #fff;
  text-align: center;
  padding: 0px 12px;
}

.input-button-login {
  width: 100%;
  height: 100%;
  background-color: #4a8dff;
  color: #fff;
  font-size: 16px;
  border-radius: 3px;
}

.button-other {
  height: 44px;
  line-height: 44px;
  display: flex;
}

.button-other a {
  display: inline-block;
  flex: 1;
  color: #4a8dff !important;
}

.button-other a:first-child {
  text-align: left;
  padding-left: 15px;
}

.button-other a:last-child {
  text-align: right;
  padding-right: 15px;
}

.input-box-last {
  margin-bottom: 20px;
}
</style>



