<template>
  <div class="mainCover">
    <HeaderBar :needb="needb" :needs="needs" :tit="titleName" @fathersubmit="commune()"></HeaderBar>
    <div class="mainContent team_q">
      <textarea cols="50" placeholder="*请输入您要发布的内容" class="conten" v-model="content"></textarea>
    </div>
  </div>
</template>

<script>
import { Toast } from "mint-ui";
export default {
  props: {},
  data() {
    return {
      titleName: "发布评论",
      needb: true,
      needs: "true",
      content: "",
      asdata:[],
       fid: ""
        
    };
  },
  computed: {},
  activated() {
      this.getParams()
  },
  mounted() {},
  watch: {
     content: {
      deep: true,
      handler(newVal, oldVal) {
        console.log(newVal.charAt(newVal.length-1))  
        if (newVal.charAt(newVal.length-1)=='@') {
          console.log(newVal);
          this.$router.push({
            path: "/friendList",
            name: "friendList"
          });
        }
      }
    }
  },
  methods: {
    getParams() {
      var routerdata = window.sessionStorage.getItem("askdata");
      this.asdata = JSON.parse(routerdata);
       this.fid = window.sessionStorage.getItem("fd");
       if (window.sessionStorage.getItem("fn") && this.content.charAt(this.content.length-1)=='@') {
        this.content =this.content + window.sessionStorage.getItem("fn")+"   ";
      }
    },      
      // @好友
    async atfriend() {
      try {
        let params = {
          notice_id: this.fid,
          project_id: this.asdata.id,
          type: "1"
        };
        let res = await this.$api.all.POSTfn(params, "atfriend");
        console.log("@@@@@" + JSON.stringify(res));
      } catch (e) {
        console.log(e);
      }
    },
    async commune() {
      if (this.content == "") {
        Toast("评论内容不能为空");
        return;
      };
      try {
        let data = {
        text: this.content,
        askid: this.asdata.id
      };
      console.log("公告id"+this.asdata.id)
        let res = await this.$api.all.POSTfn(data, "add_reply");
        if (res.result == "ok") {
           console.log("pinglunchenggonglema "+JSON.stringify(res));
          this.$router.push({
            path: "/commune_f"
          });
        }else{
          Toast(res.result)
        }
      } catch (e) {
        console.log(e)
      }

      if(this.content.indexOf("@"+ window.sessionStorage.getItem("fn"))!=-1){
          this.atfriend();
      }
      this.content = ""
    }
  },
  components: {}
};
</script>

<style scoped lang="css">
textarea {
    width: 100%;
    height: 100%;
  padding: 5px;
  border: none;
  text-align: justify;
    border: 1px solid #eee;
     border-radius: 3px;
}
.team_q {
  padding: 50px 5px;
    height: 300px;
    margin: 12px 12px;
    width:95%;
    box-sizing: border-box
  
   
}
.mint-header{
  position:fixed;
  top: 0;
  left: 0
}
</style>



