<template>
  <div class="Cover">
    <p v-show="allList.length==0" class="nothing">——— Ta还没有提问过 ———</p>
    <ul class="scroll-ul">
      <vue-scroll
        ref="scroll"
        :ops="opsconfig"
        @refresh-start="handleRS"
        @load-before-deactivate="handleLBD"
      >
        <li v-for="(v,i) in allList" :key="i" class="scroll-li" @click.prevent="gocommune(v)">
          <div class="content">
            <div class="content-left">
              <p class="title">{{v.title}}</p>
              <p class="word">{{v.content}}</p>
              <p class="from">来自：{{v.commune_name}}</p>
            </div>
            <div class="content-right">
              <!-- <span class="span-x" @click.prevent="spanxfn(v,i)">x</span> -->
              <img v-lazy="v.image_url" class="page-lazyload-image1" />
            </div>
          </div>
        </li>
      </vue-scroll>
    </ul>
  </div>
</template>

<script>
import { ScrollConfig } from "common/data.js";
import moment from "moment";

export default {
  name: "bigVProblem",
  props: {},
  data() {
    return {
      opsconfig: ScrollConfig.opsconfig,
      titleName: "大V问题",
      needb: true,
      bigimages: "",
      UserParam: {},
      allList: [],
      UserInfo: this.$getUser,
      max_id: "",
      min_id: ""
    };
  },
  computed: {},
  created() {
    moment.locale("zh-cn");
    this.UserParam = JSON.parse(window.sessionStorage.getItem("S-itemv"));
    this.getDate("bigger", "0");
  },
  mounted() {},
  watch: {
    $route() {
      this.UserParam = JSON.parse(window.sessionStorage.getItem("S-itemv"));
      this.getDate("bigger", "0");
    }
  },
  methods: {
    gocommune(v) {
      console.log(v);
      window.sessionStorage.setItem("askdata", JSON.stringify(v));
      this.$router.push("commune_f");
    },
    async getDate(type, numsid) {
      try {
        let timestamp = new Date().getTime();
        console.log(timestamp);
        let params = {
          count: 10,
          timestamp: timestamp,
          direction: type,
          target_id: numsid,
          liststype: "ask",
          type: "1",
          userid: this.UserParam.user_id,
          user_id: this.UserParam.user_id,
          token: ""
        };
        let res = await this.$api.all.GETfn(params, "my_commune");

        /************************** */
        res = res.result;
        this.allList = res.data;
        this.max_id = res.max_id;
        this.min_id = res.min_id;
        /************************** */
      } catch (error) {}
    },
    spanxfn(v, i) {
      let that = this;
      this.$message.confirm("确定删除本条提问?", "提示").then(action => {
        if (action == "confirm") {
          that.deletefn(v, i);
        } else {
          console.log("取消操作");
        }
      });
    },
    async deletefn(v, i) {
      this.allList.splice(i, 1);
      let params = {
        ask_id: v.id
      };
      let res = await this.$api.all.GETfn(params, "commune_ask_del");
      this.$forceUpdate();
    },
    async handleRS(vsInstance, refreshDom, done) {
      // alert("这是执行下拉刷新开始做啥事");
      try {
        let timestamp = new Date().getTime();
        let param = {
          count: 10,
          timestamp: timestamp,
          direction: "bigger",
          target_id: this.max_id,
          liststype: "ask",
          type: "1",
          userid: this.UserParam.user_id,
          user_id: this.UserParam.user_id,
          token: ""
        };
        let res = await this.$api.all.GETfn(param, "my_commune");
        /************************************* */

        res = res.result;
        if (res.more == "0" && res.data.length == 0) {
          this.$toast("暂无最新数据");
          done();
          return;
        }
        if (res.data.length > 0) {
          this.max_id = res.max_id;
          // this.min_id = res.min_id;
          res.data.forEach((item, index, arr) => {
            this.allList.unshift(item);
          });
          this.$forceUpdate();
        }
        /************************************* */
        done();
      } catch (error) {}
    },
    async handleLBD(vm, loadDom, done) {
      // alert("这是执行下拉加载之后需要执行什么操作");
      try {
        let timestamp = new Date().getTime();
        let param = {
          count: 10,
          timestamp: timestamp,
          direction: "litter",
          target_id: this.min_id,
          liststype: "ask",
          type: "1",
          userid: this.UserParam.user_id,
          user_id: this.UserParam.user_id,
          token: ""
        };
        let res = await this.$api.all.GETfn(param, "my_commune");
        /************************************* */
        res = res.result;
        if (res.more == "0" && res.data.length == 0) {
          this.$toast("没有更多数据");
          this.dataxiaflag = true;
          done();
          return;
        }
        if (res.data.length > 0) {
          this.dataxiaflag = false;
          // this.max_id = res.max_id;
          this.min_id = res.min_id;
          res.data.forEach((item, index, arr) => {
            this.allList.push(item);
          });
          this.$forceUpdate();
        }
        /************************************* */
        done();
      } catch (error) {}
    }
  },
  components: {}
};
</script>

<style scoped lang="css">
.Cover {
  width: 100%;
  height: calc(100vh-208px) !important;
  min-height: calc(100vh-208px) !important;
}

.scroll-ul {
  width: 100%;
  height: calc(100vh-208px) !important;
  min-height: calc(100vh-208px) !important;
  padding: 0px 12px;
}

.scroll-li {
  width: 100%;
  height: 86px;
  min-height: 86px;
}

.content {
  width: 100%;
  height: 86px;
  min-height: 86px;
  padding: 8px 0px;
  display: flex;
  border-bottom: 1px solid #eaeaea;
}

.content-left {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.title {
  font-size: 12px;
  min-height: 20px;
  height: 20px;
  line-height: 20px;
  color: #333;
  text-align: justify;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  /* display: -webkit-box !important; */
}

.word {
  font-size: 12px;
  min-height: 30px;
  line-height: 15px;
  color: #666;
  text-align: justify;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  display: -webkit-box !important;
}

.from {
  min-height: 20px;
  font-size: 10px;
  color: #999;
  display: inherit;
  align-items: center;
  text-align: justify;
}

.content-right {
  width: 35%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row-reverse;
  margin-left: 10px;
}
.span-x {
  display: block;
  width: 25px;
  height: 25px;
  line-height: 25px;
  text-align: center;
  font-size: 13px;
  position: relative;
  top: -25px;
  z-index: 88888888;
  right: 2px;
}

.content-right img {
  width: auto;
  height: 100%;
  flex: 1;
  display: block;
}

.nothing {
  margin-top: 15px;
  font-size: 12px;
  color: #999;
  height: 40px;
  line-height: 40px;
  width: 100%;
  text-align: center;
}
</style>



