<template>
  <div class="mainCover">
    <!-- <HeaderBar :needb="true" :tit="tit" v-show="isShow"></HeaderBar> -->
    <!-- swiper -->

    <div class="mainContent">
      <p class="navTitle">
        <span @click.stop="$router.back(-1)"></span>
        <i class="titl"></i>
        <!-- <span @click.stop="$router.back(-1)">X</span> -->
        <!-- <i class="titl">{{propdata.title}}</i> -->
      </p>
      <h3 style="color:#fff;padding:10px">{{detailData.title}}</h3>
      <swiper v-if="initflag">
        <swiper-slide v-for="(i,index) in detailData.image_id_array " :key="index">
          <div>
            <div class="img">
              <img :src="i.url" alt>
              <!-- <img :src="tu" alt=""> -->
            </div>
            <p class="tit">
              <i class="page">{{index+1}}/{{detailData.image_id_array.length}}</i>
              <span class="titname">{{i.alt}}</span>
            </p>
            <div class="comm-footer clearfix">
              <p class="comm-keyword"></p>
            </div>
          </div>
        </swiper-slide>
      </swiper>
      <mt-popup v-model="popupVisible" position="bottom">
        <div class="tanchu" v-if="!detaildata">
          <div class="close" @click="propShow()">
            <p class="swiperTitle">
              <span class="c-title-vline"></span> 全部回复
            </p>
            <p style="font-size:30px;padding-right:10px;">×</p>
          </div>
          <div class="nodata" v-if="comment.length ==0">--- 还没有人留下评论 ---</div>
          <div class="commMain" v-else>
            <div class="childcomme">
              <li v-for="(i,t) in comment" :key="t" class="por" @click.stop="gocommedetail(i)">
                <div class="commentbox">
                  <img
                    :src="i.avatar?i.avatar:'../../../static/imgs/activity.png'"
                    @click.stop="privateinfo(i.user_id)"
                  >
                  <img :src="bigv" class="bigv" v-if="i.is_vip==1">
                  <!-- @click.stop="privateinfo(i.user_id)" -->
                  <div class="commentcon">
                    <div>
                      <p class="review-nick">
                        {{i.user_name}}
                        <span class="color">
                          <span class="hh-fab-value">{{i.flowers}}</span>
                          <i
                            v-if="i.is_flower==1"
                            class="iconfont icon-bigGood"
                            @click.stop="removelike(i,i.comment_id)"
                          ></i>
                          <i
                            v-else
                            class="iconfont icon-good"
                            @click.stop="addlike(i,i.comment_id)"
                          ></i>
                        </span>
                      </p>
                      <p class="review-area">{{i.text}}</p>
                    </div>

                    <div class="review-button">
                      <p :data-time="i.timestamp" class="review-time hh-time">
                        {{i.timestamp?i.timestamp:"刚刚"}} ·
                        <span class="replyBtn">&nbsp;回复</span>
                      </p>
                      <div v-if="i.user_id==useridaz" class="del" @click.stop="del(i,t)">删除</div>
                    </div>
                  </div>
                </div>
                <!-- v-if="i.secondComment.length!=0" -->
                <div class="son">
                  <div class="sonbox">
                    <div
                      v-for="(s,h) in i.secondComment"
                      :key="h"
                      class="sonitem"
                      @click.stop="gocommedetail(s,'isself')"
                    >
                      <div class="imgbox" style="width:40px">
                        <img
                          :src="s.avatar?s.avatar:touxiang"
                          alt
                          @click.stop="privateinfo(s.user_id)"
                        >
                        <img :src="bigv" class="bigv" v-if="s.is_vip=='1'">
                      </div>

                      <div>
                        <p class="sonname">
                          <span class="namebox">
                            {{s.nickname}}
                            <span class="tobrige">回复</span>
                            {{s.tu_nickname}}
                          </span>
                          <span class="color">
                            <span class="hh-fab-value">{{s.flowers}}</span>
                            <i
                              v-if="s.is_flower==1 && useridaz"
                              class="iconfont icon-bigGood"
                              @click.stop="removelike(s,s.id)"
                            ></i>
                            <i v-else class="iconfont icon-good" @click.stop="addlike(s,s.id)"></i>
                          </span>
                        </p>
                        <!-- <span class="sonReply" v-if="s.secondComment.length>0">{{s.secondComment.length}}回复</span> -->
                        <p class="sontext">{{s.text}}</p>
                        <div class="review-button">
                          <p :data-time="s.timestamp" class="review-time hh-time">
                            {{s.timestamp?s.timestamp:"刚刚"}} ·
                            <span class="replyBtn">&nbsp;回复</span>
                          </p>
                          <div
                            v-if="s.user_id==useridaz"
                            class="sondel"
                            @click.stop="delson(i,s,h)"
                          >删除</div>
                        </div>
                      </div>
                    </div>
                  </div>
                  <p
                    v-if="i.comments>2"
                    @click.stop="detailShow(i.comment_id)"
                    class="comlength"
                  >全部 {{i.comments}} 条评论>>></p>
                </div>
              </li>
              <div @click="more()" class="more" v-if="iddata.more==1">点击加载更多</div>
            </div>
          </div>
          <p class="inpcomm" v-if="isShow">
            <input
              type="text"
              :placeholder="placehoderInfo?'我 回复 '+ placehoderInfo: '我来讲讲....'"
              class="inp1"
              v-model="commentvalue"
              ref="sendcomment"
              id="sendcomment"
              @blur="inpblur"
            >
            <span @click="sendcomments(soncommentid)">发布</span>
          </p>
        </div>
        <div v-show="detaildata" class="tanchu">
          <div class="close" @click="propShow()">
            <p class="swiperTitle">
              <span class="c-title-vline"></span> 全部回复
            </p>
            <p style="font-size:30px;padding-right:10px;">×</p>
          </div>
          <commedetail :propid="commid" ref="comme" :loginid="useridaz"></commedetail>
        </div>
      </mt-popup>
      <div class="bottom" v-show="isShow ">
        <div class="detailbottom">
          <input
            type="text"
            placeholder="我来讲讲...."
            class="inp"
            v-model="commentvalue"
            @keyup.13="sendcomments()"
            @blur="inpblur()"
            @focus="inputfouse()"
          > &nbsp;
          <i
            class="iconcolor iconfont icon-commue"
            slot="icon"
            @click.stop="propShow()"
            v-show="commShow"
          >
            <span class="hh-fab-value">{{ num}}</span>
          </i>
          <collectOrlike
            :targetid="propdata.id?propdata.id:propdata.target_id"
            :pro="propdata"
            :actType="'图集'"
            ref="child"
            :loginid="useridaz"
            @fatherMethod="gocommedetail"
            v-show="commShow"
          ></collectOrlike>
        </div>
        <div class="sendbtn" @touchstart.stop="sendcomments()">发送</div>
      </div>
    </div>
  </div>
</template>

<script>
import moment from "moment";
import { Popup } from "mint-ui";
import HeaderBar from "com/Header-bar.vue";
import collectOrlike from "com/collectOrlike.vue";
import { MessageBox } from "mint-ui";
// import pictwite from "pages/home/pictwite.vue";
import { ComUtils } from "common/utils";
import { Toast } from "mint-ui";
import commedetail from "pages/home/commedetail.vue";
export default {
  filters: {
    changeTime(val) {
      return moment(val)
        .startOf("minute")
        .fromNow();
    }
  },
  name: "swiperf",
  props: ["itemf"],
  data() {
    return {
      bigv: require("images/bigv.png"),
      comment: [],
      commeval: "",
      ed: "",
      resdata: [],
      write: [],
      fid: "",
      iddata: [],
      touxiang: require("images/morentu.jpg"),
      commid: "",
      tit: "图集信息",
      swipesList: [],
      propdata: [],
      tu: require(`@/assets/imgs/1.png`),
      initflag: true,
      isShow: false,
      popupVisible: false,
      clickfalse: true,
      commentvalue: "",
      num: 0,
      likeNum: 1,
      soncommentid: "",
      detaildata: false,
      detailData: [],
      placehoderInfo: "",
      useridaz: "",
      keycode: true,
      commShow: true
    };
  },
  activated() {
    this.brige();

    moment.locale("zh-cn");
    this.initflag = false;
    this.$nextTick(function() {
      this.initflag = true;
      this.getParams();
      this.getcomment();
      this.getdetaildate(
        this.propdata.id ? this.propdata.id : this.propdata.target_id
      );
    });
  },
  mounted() {},
  watch: {
    // 监测路由变化,只要变化了就调用获取路由参数方法将数据存储本组件即可
    // $route: "getParams",
    commentvalue: {
      deep: true,
      handler(newVal, oldVal) {
        if (newVal.charAt(newVal.length - 1) == "@") {
          console.log(newVal);
          this.$router.push({
            path: "/friendList",
            name: "friendList"
          });
        }
      }
    }
  },
  methods: {
    getParams() {
      // let user = {
      //   id: "319430217930703154",
      //   token:
      //     "5cec187gbd89e56deb4ff3aa102fd4ad128d7ae45d8c3c6546ed8173e4901323840"
      // };
      // ComUtils.saveParams(user);
      if (window.hasOwnProperty("webkit")) {
        this.isShow = false;
      } else {
        this.isShow = true;
        var routerdata = localStorage.getItem("homedata");
        var type = localStorage.getItem("atype");
        let data = JSON.parse(routerdata);
        this.propdata = data;
        //  this.useridaz='319430217930703154'
        this.fid = localStorage.getItem("fd");
        if (
          localStorage.getItem("fn") &&
          this.commentvalue.charAt(this.commentvalue.length - 1) == "@"
        ) {
          this.commentvalue =
            this.commentvalue + localStorage.getItem("fn") + "   ";
        }
      }
    },
    async getdetaildate(activeId) {
      this.comment = [];
      try {
        let res = await this.$api.all.GETfn(
          {},
          "detail",
          this.propdata.id ? this.propdata.id : this.propdata.target_id
        );
        if (app != undefined && app.hasOwnProperty("sendClick")) {
          app.sendClick(res.id, res.clicks);
        } else {
        }
        this.detailData = res;
      } catch (e) {}
      this.getcommentdate(
        this.propdata.id ? this.propdata.id : this.propdata.target_id
      );
      this.getcomment();
      // this.onetuisong(a)
    },

    // @好友
    async atfriend() {
      try {
        let params = {
          notice_id: this.fid,
          project_id: this.propdata.id
            ? this.propdata.id
            : this.propdata.target_id,
          type: "1"
        };
        let res = await this.$api.all.POSTfn(params, "atfriend");
        console.log("@" + JSON.stringify(res));
      } catch (e) {
        console.log(e);
      }
    },
    brige() {
      let that = this;
      window.getArticleInfo = function(data, getuserid) {
        that.propdata = data;
        that.useridaz = getuserid;
        that.getdetaildate(data.id ? data.id : data.target_id);
      };
      window.popupComment = function() {
        that.propShow();
      };
    },
    gocommedetail(item, isself) {
      localStorage.setItem("commid", item);
      if (item.user_id == this.useridaz) {
        return;
      }
      document.getElementById("sendcomment").focus();
      this.soncommentid = item.id;
      this.placehoderInfo = item.nickname ? item.nickname : item.user_name;
    },
    inputfouse() {
      this.commShow = false;

      document.querySelector(".sendbtn").style.display = "block";
    },
    inpblur() {
      this.commShow = true;
      this.commentid = "";
      this.placehoderInfo = "";

      document.querySelector(".sendbtn").style.display = "none";
    },
    detailShow(pro) {
      if (pro) {
        this.commid = pro;
        this.soncommentid = pro;
        this.detaildata = true;
        let that = this;
        setTimeout(function() {
          that.$refs.comme.getcommedetail();
        });
      }
    },

    // 获取评论数据
    async getcomment() {
      try {
        let params = {
          target: this.propdata.id ? this.propdata.id : this.propdata.target_id,
          userid: this.useridaz ? this.useridaz : 2,
          user_id: this.useridaz ? this.useridaz : 2
        };
        let res = await this.$api.all.GETfn(params, "comment");
        this.num = res.result.total;
        this.$refs.child.iscollect();
        this.$refs.child.islike();
        // this.$refs.child.getcomment();
        this.$refs.child.likenum();
      } catch (e) {
        console.log(e);
      }
    },
    //发表评论
    async sendcomments(commentid) {
      if (!this.useridaz) {
        app.goLogin();
      }
      if (this.commentvalue.trim() == "") {
        return;
      }
      console.log(this.soncommentid);
      try {
        if (this.keycode == true) {
          this.keycode = false;
          let params = {
            target: this.soncommentid
              ? this.soncommentid
              : this.propdata.id
              ? this.propdata.id
              : this.propdata.target_id,
            title: this.propdata.title,
            text: this.commentvalue,
            userid: this.useridaz,
            user_id: this.useridaz,
            _: "RFs0"
          };
          // ;

          let res = await this.$api.all.POSTfn(params, "send_comment");

          if (res.result == "ok") {
            Toast("发布成功");
          } else {
            Toast(res.result);
          }
          this.soncommentid = "";
          this.getcommentdate(
            this.propdata.id ? this.propdata.id : this.propdata.target_id
          );
          this.getcomment();
          this.keycode = true;
        }
        if (app != undefined && app.hasOwnProperty("backList")) {
          app.backList();
        } else {
        }
      } catch (e) {
        console.log(e);
      }
      if (this.commentvalue.indexOf("@" + localStorage.getItem("fn")) != -1) {
        this.atfriend();
      }
      this.commentvalue = "";
      this.placehoderInfo = "";
    },
    propShow(pro) {
      this.getcommentdate(
        this.propdata.id ? this.propdata.id : this.propdata.target_id
      );
      if (this.detaildata == true) {
        this.detaildata = !this.detaildata;
      }
      this.popupVisible = !this.popupVisible;

      // this.$refs.child.getcomment();
    },
    jumpcommdetail(item, type) {
      localStorage.setItem("homedata", JSON.stringify(item));
      this.$router.push({
        path: "/writecomment",
        name: "writecomment"
      });
    },
    //去点赞
    async addlike(data, id) {
      if (!this.useridaz) {
        app.goLogin();
      }
      let params = {
        target: id,
        userid: this.useridaz,
        user_id: this.useridaz
      };
      try {
        if (this.keycode == true) {
          this.keycode = false;
          let res = await this.$api.all.POSTfn(params, "good");
          if (res.result == "ok") {
            this.$set(data, `is_flower`, 1);
            let num = Number(data.flowers);
            num = num += 1;
            this.$set(data, `flowers`, num);
            console.log("点赞数量" + num);
            if (app != undefined && app.hasOwnProperty("backList")) {
              app.backList();
            } else {
            }
          }
          console.log(res);
          this.keycode = true;
        }
      } catch (e) {
        console.log(e);
      }
    },
    // 取消点赞
    async removelike(data, id) {
      let params = {
        target: id,
        userid: this.useridaz,
        user_id: this.useridaz
      };
      try {
        let res = await this.$api.all.POSTfn(params, "good_delete");
        if (res.result == "ok") {
          this.$set(data, `is_flower`, 0);
          let num = Number(data.flowers);
          num = num -= 1;
          this.$set(data, `flowers`, num);
          console.log("点赞数量" + num);
          if (app != undefined && app.hasOwnProperty("backList")) {
            app.backList();
          } else {
          }
        }
      } catch (e) {
        console.log(e);
      }
    },

    // 获取评论数据
    async getcommentdate(id, direct, hot) {
      try {
        let params = {
          target: id,
          target_id: hot,
          direction: direct,
          count: 45,
          userid: this.useridaz ? this.useridaz : 2,
          user_id: this.useridaz ? this.useridaz : 2
        };

        let res = await this.$api.all.GETfn(params, "comment");

        this.comment = [];
        for (let i in res.result.data) {
          res.result.data[i].timestamp = this.timeago(
            res.result.data[i].timestamp * 1000
          );
          if (res.result.data[i].avatar) {
            if (res.result.data[i].avatar.endsWith(".com/")) {
              res.result.data[i].avatar = "../../../static/imgs/morentu.jpg";
            }
          }
          for (let t in res.result.data[i].secondComment) {
            res.result.data[i].secondComment[t].timestamp = this.timeago(
              res.result.data[i].secondComment[t].timestamp * 1000
            );
            if (res.result.data[i].secondComment[t].avatar) {
              if (
                res.result.data[i].secondComment[t].avatar.endsWith(".com/")
              ) {
                res.result.data[i].secondComment[t].avatar =
                  "../../../static/imgs/morentu.jpg";
              }
            }
          }
        }
        // this.comment=res.result.data

        this.comment = this.comment.concat(res.result.data);
        this.iddata = res.result;

        console.log(res);
      } catch (e) {
        console.log(e);
      }
    },

    more() {
      let dictir = "litter";
      let hot = this.iddata.min_id;
      if (this.iddata.more == 0) {
        Toast("无更多数据");
      } else {
        this.getcommentdate(
          this.propdata.id ? this.propdata.id : this.propdata.target_id,
          dictir,
          hot
        );
      }
    },

    //删除评论
    del(item, index) {
      MessageBox.confirm("确定删除此条评论?").then(async action => {
        try {
          let params = {
            // userid: "327195464540160441",
            comment_id: item.comment_id,
            userid: this.useridaz,
            user_id: this.useridaz
          };
          let res = await this.$api.all.POSTfn(params, "delcomme");
          Toast(res.result);
          if (res.result == "ok") {
            this.comment.splice(index, 1);
             this.getcomment();
          }
        } catch (e) {
          console.log(e);
        }
      });
    },
    //删除评论
    delson(s, item, index) {
      MessageBox.confirm("确定删除此条评论?").then(async action => {
        try {
          let params = {
            // userid: "327195464540160441",
            comment_id: item.id,
            userid: this.useridaz,
            user_id: this.useridaz
          };
          let res = await this.$api.all.POSTfn(params, "delcomme");
          console.log(res);
          if (res.result == "ok") {
            s.secondComment.splice(index, 1);
            this.$refs.child.getcomment();
          }
        } catch (e) {
          console.log(e);
        }
      });
    },
    //去个人资料页
    privateinfo(v) {
      if (!this.useridaz) {
        app.goLogin();
      } else {
        app.gouserInfo(v);
      }
    },

    timeago(dateTimeStamp) {
      //dateTimeStamp是一个时间毫秒，注意时间戳是秒的形式，在这个毫秒的基础上除以1000，就是十位数的时间戳。13位数的都是时间毫秒。
      var result;
      var minute = 1000 * 60; //把分，时，天，周，半个月，一个月用毫秒表示
      var hour = minute * 60;
      var day = hour * 24;
      var week = day * 7;
      var halfamonth = day * 15;
      var month = day * 30;
      var now = new Date().getTime(); //获取当前时间毫秒
      var diffValue = now - dateTimeStamp; //时间差

      if (diffValue < 0) {
        return;
      }
      var minC = diffValue / minute; //计算时间差的分，时，天，周，月
      var hourC = diffValue / hour;
      var dayC = diffValue / day;
      // var weekC = diffValue / week;
      // var monthC = diffValue / month;
      // if (monthC >= 1 && monthC <= 3) {
      // 	result = " " + parseInt(monthC) + "月前";
      // } else if (weekC >= 1 && weekC <= 3) {
      // 	result = " " + parseInt(weekC) + "周前";
      // } else
      if (dayC >= 1 && dayC <= 3) {
        result = " " + parseInt(dayC) + "天前";
      } else if (hourC >= 1 && hourC <= 23) {
        result = " " + parseInt(hourC) + "小时前";
      } else if (minC >= 1 && minC <= 59) {
        result = " " + parseInt(minC) + "分钟前";
      } else if (diffValue >= 0 && diffValue <= minute) {
        result = "刚刚";
      } else {
        var datetime = new Date();
        datetime.setTime(dateTimeStamp);
        var Nyear = datetime.getFullYear();
        var Nmonth =
          datetime.getMonth() + 1 < 10
            ? "0" + (datetime.getMonth() + 1)
            : datetime.getMonth() + 1;
        var Ndate =
          datetime.getDate() < 10
            ? "0" + datetime.getDate()
            : datetime.getDate();
        var Nhour =
          datetime.getHours() < 10
            ? "0" + datetime.getHours()
            : datetime.getHours();
        var Nminute =
          datetime.getMinutes() < 10
            ? "0" + datetime.getMinutes()
            : datetime.getMinutes();
        var Nsecond =
          datetime.getSeconds() < 10
            ? "0" + datetime.getSeconds()
            : datetime.getSeconds();
        result = Nyear + "-" + Nmonth + "-" + Ndate;
      }
      return result;
    }
  },
  components: { HeaderBar, collectOrlike, commedetail }
};
</script>

<style scoped lang="css">
.namebox {
  display: inline-block;
  width: 220px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.main {
  background: #000;
  height: 375px;
}
* {
  margin: 0;
  padding: 0;
}
.swiperTitle {
  height: 50px;
  text-align: left;
  box-sizing: border-box;
  font-weight: 700;
  display: block;
  width: 100%;
  padding: 10px 12px 0px 12px;
  font-size: 18px;
}
>>> .mint-header {
  color: #3966a7 !important;
}
.hh-fab-value {
  font-size: 12px;
  position: relative;
  top: 1px;
  right: -2px;
}
.tanchu {
  height: 500px;
  width: 101%;
  overflow-y: scroll;
  margin-top: 10px;
  padding: 0 0 20px 0;
}
.tanchu::-webkit-scrollbar {
  display: none;
}
.nodata {
  color: #666;
  text-align: center;
}
.iconcolor {
  font-size: 15px;
}
.iconcolor * {
