<template>
  <div class="mainCover">
    <HeaderBar :needb="needb" :tit="titleName"></HeaderBar>
    <div class="mainContent">
      <div class="top-box">
        <div class="login-box">
          <span class="login-text" @click="gotoLoagin">登录</span>
          <span class="sign">&nbsp;</span>
        </div>
        <div class="login-box">
          <span class="login-text">注册</span>
          <span class="sign">
            <span class="nbsp">&nbsp;</span>
          </span>
        </div>
      </div>

      <div>
        <p class="message">欢迎加入</p>
        <div class="input-box">
          <span class="iconfont icon-tel" slot="icon"></span>

          <input
            placeholder="请输入手机号码"
            type="tel"
            maxlength="13"
            pattern="\d*"
            v-model.number="Telphone"
          />
        </div>
        <div class="input-box">
          <span class="iconfont icon-account" slot="icon"></span>
          <input placeholder="请设置账号，此账号将作为登录账号" type="text" maxlength="13" v-model="Account" />
        </div>

        <div class="input-box">
          <span class="iconfont icon-vcode" slot="icon"></span>
          <input
            placeholder="请输入图形验证码"
            type="tel"
            maxlength="6"
            pattern="\d*"
            v-model="GVCodeValue"
            oninput="value=value.replace(/[^\d]/g,'')"
          />
          <img :src="imgyanzen" alt class="imageyanz" />
        </div>

        <div class="input-box">
          <span class="iconfont icon-ivcode" slot="icon"></span>
          <input
            placeholder="请输入验证码"
            type="tel"
            maxlength="6"
            pattern="\d*"
            oninput="value=value.replace(/[^\d]/g,'')"
            v-model="VCodevalue"
          />
          <span class="sendMsg" @click.prevent="sendMsg">{{codeText}}</span>
        </div>
        <div class="input-box input-box-last">
          <span class="iconfont icon-pass" slot="icon"></span>
          <input
            placeholder="请设置密码"
            type="password"
            maxlength="12"
            v-model="PasswordValue"
            oninput="value=value.replace(/[^\d]/g,'')"
          />
        </div>
        <div class="button-login">
          <input type="button" value="注册" class="input-button-login" @click="Registration" />
        </div>

        <div class="button-other">
          <mt-switch v-model="agreementvalue"></mt-switch>
          <label for="consent">我已阅读并同意</label>
          <span>
            <a href="javascript:void(0);" @click.prevent="agreementcall">《{{AgreementContent}}》</a>
          </span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "registration",
  props: {},
  data() {
    return {
      titleName: "注册",
      needb: true,
      imgyanzen: "",
      Telphone: "",
      Account: "",
      GVCodeValue: "",
      VCodevalue: "",
      PasswordValue: "",
      codeText: "发送验证码",
      agreementvalue: false,
      AgreementContent: "点击查看协议书",
      sendMsgFlag: true,
      hassendMsg: false
    };
  },
  computed: {},
  created() {
    this.imga = require("images/phone.png");
    this.imgb = require("images/password.png");
    this.getGVCode();
    // this.getAgreementContent();
  },
  mounted() {},
  watch: {},
  methods: {
    async getAgreementContent() {
      try {
        let param = {
          tel: mobile,
          uuid: "123",
          image_code: gvcodevalue,
          type: "reg"
        };
        let res = await this.$api.all.GETfn(param, "send_code");
        this.getGVCode();
        console.log(res);
      } catch (error) {
        this.getGVCode();
        console.log(error);
      }
    },
    gotoLoagin() {
      window.localStorage.clear();
      this.$router.push({
        path: "login"
      });
    },
    checkFn() {
      let mobile = this.Telphone;
      let gvcodevalue = this.GVCodeValue;
      let accountValue = this.Account;
      if (mobile == "" || mobile == null || mobile == 0) {
        this.$toast("您还未输入手机号码");
        return false;
      }

      if (accountValue == "" || accountValue == null || accountValue == 0) {
        this.$toast("您还未输入账号");
        return false;
      }

      let regExp = /^(86)?((13\d{9})|(15[0,1,2,3,5,6,7,8,9]\d{8})|(18[0,5,6,7,8,9]\d{8}))$/;
      if (!regExp.test(mobile)) {
        this.$toast("您输入的手机号码有误");
        return false;
      }

      if (mobile == "" || mobile == null || mobile == 0) {
        this.$toast("您还未输入手机号码");
        return false;
      }

      if (gvcodevalue == "" || gvcodevalue == null || gvcodevalue == 0) {
        this.$toast("您还未输入图形验证码");
        return false;
      }

      if (gvcodevalue.length < 4) {
        this.$toast("您输入图形验证码最少输入4位数");
        return false;
      }

      return true;
    },
    sendMsg() {
      if (!this.sendMsgFlag) {
        return;
      }

      if (!this.checkFn()) {
        return;
      }

      this.hassendMsg = true;
      this.SendTelMsg();
    },
    async SendTelMsg() {
      try {
        let param = {
          tel: this.Telphone,
          uuid: "111",
          image_code: this.GVCodeValue,
          type: "reg"
        };

        let res = await this.$api.all.GETfn(param, "send_code");
        res = res.result;
        if (res == "ok") {
          this.sendMsgFlag = false;
          let times = 60;
          let setMsgInterval = setInterval(() => {
            this.codeText = times + "S";
            times--;
            if (times == -1) {
              this.codeText = "发送验证码";
              this.sendMsgFlag = true;
              clearInterval(setMsgInterval);
            }
          }, 1000);
        } else {
          this.$toast(res);
        }
        this.getGVCode();
        console.log(res);
      } catch (error) {
        this.getGVCode();
        console.log(error);
      }
    },
    async getGVCode() {
      let timestamp = new Date().getTime();
      this.imgyanzen =
        "http://api2.ceh.com.cn/captcha/image/get?size=18&len=6&_=AWMT&uuid=111&r" +
        timestamp;
    },
    async Registration() {
      try {
        if (!this.checkFn()) {
          return;
        }

        if (!this.hassendMsg) {
          this.$toast("您还未发送验证码");
          return;
        }

        if (!this.agreementvalue) {
          this.$toast("您还未同意阅读协议");
          return;
        }

        let param = {
          account: this.Account,
          password: this.PasswordValue,
          tel: this.Telphone,
          code: this.VCodevalue,
          uuid: "111"
        };
        let res = await this.$api.all.POSTfn(param, "reg");
        if (res.result == "ok") {
          this.$toast("注册成功");

          setTimeout(() => {
            this.$router.push({
              name: "login"
            });
          }, 1500);
        } else {
          this.$toast(res.result);
        }
        this.getGVCode();
        console.log(res);
      } catch (error) {
        this.getGVCode();
        console.log(error);
      }
    },
    agreementcall() {
      this.$toast("暂无数据");
    }
  },
  components: {}
};
</script>

<style scoped lang="css">
.top-box {
  min-height: 110px;
  background-color: #4a8dff;
  display: flex;
  width: 100%;
  align-items: flex-end;
  text-align: center;
  color: #fff;
}

.login-box {
  flex: 1;
  min-height: 60px;
  line-height: 60px;
  text-align: center;
  clear: both;
  overflow: hidden;
}

.login-box span {
  display: block;
}

.login-text {
  height: 40px;
  line-height: 40px;
  font-size: 17px;
  padding: 5px 0px 0px 0px;
}

.sign {
  min-height: 22px;
  line-height: 22px;
  min-width: 22px;
  position: relative;
  left: 50%;
  margin-left: -10px;
}

.nbsp {
  width: 0px;
  height: 0px;
  border-left: 12px solid transparent;
  border-right: 12px solid transparent;
  border-bottom: 12px solid #fff;
  border-top: 12px solid transparent;
}

.message {
  height: 44px;
  line-height: 44px;
  text-align: center;
  color: #4a8dff;
  font-size: 16px;
  font-weight: 500;
}

.input-box {
  height: 48px;
  line-height: 48px;
  display: flex;
}

.input-box span {
  display: inline-block;
  width: 22px;
  height: 22px;
  margin: 0px 5px 0px 15px;
  color: #4a8dff;
}

.input-box img.imageyanz {
  height: 30px;
  line-height: 30px;
  margin: 7px 10px;
  width: 80px;
}

.input-box input {
  height: 30px;
  line-height: 30px;
  font-size: 15px;
  flex: 1;
  margin-left: 10px;
  margin-top: 9px;
}

.input-box .sendMsg {
  min-width: 100px;
  display: inline-block;
  height: 30px;
  margin: 5px;
  line-height: 30px;
  font-size: 14px;
  text-align: center;
  color: #fff;
  background: #4a8dff;
  border-radius: 3px;
}

.button-login {
  /* margin-top: 20px; */
  height: 40px;
  line-height: 40px;
  width: 100%;
  font-size: 16px;
  color: #fff;
  text-align: center;
  padding: 0px 12px;
}

.input-button-login {
  width: 100%;
  height: 100%;
  background-color: #4a8dff;
  color: #fff;
  font-size: 16px;
  border-radius: 3px;
}

.button-other {
  height: 44px;
  line-height: 44px;
  display: flex;
  text-align: center;
  width: 100%;
  font-size: 14px;
  color: #4a8dff !important;
}

.button-other input {
  height: 44px;
  line-height: 44px;
}

.button-other span a {
  text-decoration: none;
  height: 44px;
  line-height: 44px;
  display: inline-block;
}

.input-box-last {
  margin-bottom: 20px;
}
</style>



