<template>
  <div class="mainCover">
      <HeaderBar :needm="needm" :needb="needb" :tit="titleName"></HeaderBar>
    <div class="mainContent">
      <div class="personal_th">
        <div class="th-info">
          <img
            :src="teaminfo.avatar?teaminfo.avatar:'../../../static/imgs/morentu.jpg'"
            alt
            @click.stop="privateinfo(teaminfo.team_user_id)"
          />
          <span>{{teaminfo.captain_name}}</span>
        </div>
        <div class="th-button">
          <span
            v-if="parseInt(teaminfo.user_count) >= parseInt(teamdetail.num)"
            style="color: red;"
          >(满)</span>
          <p class="joining" v-else-if="teaminfo.is_join ==0" @click.stop="isjoint()">加入</p>
          <p class="jointeam" v-else-if="teaminfo.is_join==1" @click.stop="isjoint()">离开</p>
        </div>
      </div>
      <div class="personal_tm">
        <span>{{teaminfo.team_name}}</span>
        <p>{{teaminfo.team_content}}</p>
        <img :src="teaminfo.image_url?teaminfo.image_url:'../../../static/imgs/ditie.jpg'" alt />
      </div>
      <div class="personal_tp">
        <div class="personal_qw" v-for="(i,t) in teaminfo.user_list" :key="t">
          <div>
            <img
              :src="i.avatar?i.avatar:'../../../static/imgs/ditie.jpg'"
              alt
              @click.stop="privateinfo(i.id)"
            />
            <span>{{i.name}}</span>
          </div>
          <img src="../../../static/imgs/right_arrow.png" alt />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { Toast } from "mint-ui";
export default {
  name: "commune_th",
  props: {},
  data() {
    return {
      titleName: "组队详情",
      needb: true,
      needm: false,
      search: "",
      rdata: [],
      tdata: [],
      teaminfo: [],
      is_join: ""
    };
  },
  computed: {},
  activated() {
    this.search = require("images/search.png");
    this.getParams();
    this.teamdetail();
  },
  mounted() {},
  watch: {
    //  $route: {
    //  handler(route) {
    //     console.log("路由" + route.name);
    //     // const that = this;
    //     if (route.name !="commune_meeting") {
    //       this.asklist=[]
    //     }
    //   }},
    //  '$route':["getParams","teamdetail"]
  },
  methods: {
    getParams() {
      let teamdata = window.sessionStorage.getItem("teamdetail");
      this.tdata = JSON.parse(teamdata);
    },
    //去个人资料页
    privateinfo(v) {
      window.sessionStorage.setItem("S-person", v);
      this.$router.push({
        name: "commune_information"
      });
    },
    //
    async teamdetail() {
      try {
        let params = {
          team_id:this.tdata.team_id,
          ask_id:this.tdata.ask_id
        };
        let res = await this.$api.all.GETfn(params, "team_member_detail");

        console.log(JSON.stringify(res));
        this.teaminfo = res.result;
        this.is_join = res.is_join;
      } catch (e) {
        console.log(e);
      }
    },
    //去个人资料页
    privateinfo(v) {
      window.sessionStorage.setItem("S-person", v);

      this.$router.push({
        name: "commune_information"
      });
    },

    // 设置加入组队
    isjoint(i, e) {
      console.log(JSON.stringify(i));
      let params = {
        teamid: this.tdata.team_id,
        askid: this.tdata.ask_id
      };
      if (this.teaminfo.is_join == 0) {
        this.addjoint(params);
        console.log("加入");
       
      } else {
        this.deljoint(params);
      
      }
    },
    // 加入
    async addjoint(params) {
      try {
        let res = await this.$api.all.POSTfn(params, "join_team");
        console.log(res);
        if (res.result == true) {
          console.log("加入");
        } else {
          Toast(res.result);
        }
      } catch (e) {
        console.log(e);
      }
        this.teamdetail();
    },
    // 离开
    async deljoint(params) {
      try {
        let res = await this.$api.all.POSTfn(params, "quit_team");
        console.log(res);
        if (res.result == "ok") {
          console.log("离开");
        }
      } catch (e) {
        console.log(e);
      }
        this.teamdetail();
    }
  },
  components: {}
};
</script>
<style scoped lang="css">
.mainContent {
  padding: 12px;
}

.personal_th {
  padding-bottom: 10px;
  width: 100%;
  border-bottom: 1px solid #eee;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.th-info {
  display: flex;
  justify-content: center;
  align-items: center;
}

.th-info img {
  height: 30px;
  width: 30px;
  border-radius: 50%;
}
.th-info span {
  font-size: 14px;
  color: #333;
  margin-left: 10px;
}
.th-button p {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-size: 12px;
  background: #4a8dff;
  border-radius: 3px;
  height: 26px;
  width: 60px;
}
.personal_tm {
  /* height: 260px; */
  width: 100%;
  /* border-bottom: 1px solid #eee; */
}
.personal_tm span {
  font-size: 14px;
  color: #333;
  float: left;
  width: 100%;
  text-align: left;
  /* margin-top: 3%; */
  /* margin-bottom: 3%; */
  padding: 5px 0px;
}
.personal_tm p {
  font-size: 12px;
  color: #666;
  float: left;
  width: 100%;
  text-align: left;
  padding-bottom: 10px;
}
.personal_tm img {
  /* height: 180px; */
  width: 100%;
  border-radius: 3px;
}
.personal_tp {
  width: 100%;
  /* margin-left: 2%; */
}
.personal_tp .personal_qe {
  width: 100%;
  height: 25px;
}
.personal_tp .personal_qe span {
  font-size: 12px;
  color: #585858;
  float: left;
  width: 100%;
  text-align: left;
  margin-top: 3%;
}
.personal_tp .personal_qw {
  height: 45px;
  width: 100%;
  border: 1px solid #eee;
  border-radius: 3px;
  box-sizing: border-box;
  margin: 10px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* box-shadow: 0 1px 4px #eee; */
}
.personal_tp .personal_qw div {
  display: flex;
  align-items: center;
  padding-left: 10px;
}

.personal_tp .personal_qw img {
  height: 30px;
  width: 30px;
  border-radius: 50%;
}
.personal_tp .personal_qw span {
  font-size: 14px;
  color: #333;
  padding-left: 10px;
}
.personal_tp .personal_qw img:last-child {
  float: right;
}
</style>



