const TEST = function () {
	let _BARCODE = 'zoom',
		B = window.plus.bridge;
	let zoom =
	{
		PluginTestFunction: function (Argus1, Argus2, Argus3, Argus4, successCallback, errorCallback) {
			var success = typeof successCallback !== 'function' ? null : function (args) {
				successCallback(args);
			},
				fail = typeof errorCallback !== 'function' ? null : function (code) {
					errorCallback(code);
				};
			callbackID = B.callbackId(success, fail);

			return B.exec(_BARCODE, "PluginTestFunction", [callbackID, Argus1, Argus2, Argus3, Argus4]);
		},
		PluginTestFunctionArrayArgu: function (Argus, successCallback, errorCallback) {
			var success = typeof successCallback !== 'function' ? null : function (args) {
				successCallback(args);
			},
				fail = typeof errorCallback !== 'function' ? null : function (code) {
					errorCallback(code);
				};
			callbackID = B.callbackId(success, fail);
			return B.exec(_BARCODE, "PluginTestFunctionArrayArgu", [callbackID, Argus]);
		},
		PluginLogin: function (userInfo, successCallback, errorCallback) {
			return B.exec(_BARCODE, "PluginLogin", [userInfo]);
		},
		joinMeeting: function (params, successCallback, errorCallback) {
			console.log('i am in joinMetting')
			return B.exec(_BARCODE, "joinMeeting", [params]);
		},
		startMeeting: function (params, successCallback, errorCallback) {
			return B.exec(_BARCODE, "startMeeting", [params]);
		}
	};

	window.plus.zoom = zoom;
}

export { TEST };


