<template>
  <div class="mainCover">
    <!-- <HeaderBar :needb="true" :tit="tit"></HeaderBar> -->
    <div class="bottom" >
      <div class="detailbottom"  v-show="isShow">
        <input type="text" :placeholder="placehoderInfo?'我 回复 '+ placehoderInfo: '我来讲讲....'" ref="inputVal" class="inp" v-model="commeval" @blur="inpblur()" />
        <p @click="sendhuifu(commentid)">发布</p>
      </div>
    </div>
    <!-- 评论 -->
    <div class="mainContent">

      <div class="childcomme" v-if="commentdetail">
        <li v-for="(i,t) in commentdetail" :key="t" class="por" @click.stop="gocommedetail(i,'isself')">
          <div class="commentbox">
            <div class="imgbox">
                <img
              :src="i.avatar?i.avatar:'../../../static/imgs/morentu.jpg'"
              @click.stop="privateinfo(i.user_id)"
            />
             <img :src="bigv" class="bigv" v-if="i.is_vip==1"/>
            </div>
          
            <div class="commentcon">
              <p class="review-nick"> <span class="namebox">{{i.nickname}}
                        <span class="tobrige">回复</span>
                        {{i.tu_nickname}}</span><span class="color"><span class="hh-fab-value">{{i.flowers}}</span>  <i v-if="i.is_flower==1" class="iconfont icon-bigGood" @click.stop="removelike(i)">
                    </i><i v-else class="iconfont icon-good" @click.stop="addlike(i)">
                    </i></span></p>
              <p class="review-area">{{i.text}}</p>
              <p :data-time="i.timestamp" class="review-time hh-time">
                {{i.timestamp?i.timestamp:"刚刚"}} · <span class="replyBtn">回复</span>
                <span v-if="i.user_id==loginid" class="del" @click.stop="del(i,t)">删除</span>
                <!-- <span @click.stop="searchIn($event,i)" class="zi">回复他</span> -->
              </p>
            </div>
          </div>
        </li>
         <div @click="more()" class="more" v-if="iddata.more==1">点击加载更多</div>
      </div>
      <!-- <p v-else-if="commentdetail.secondComment=[]" class="hh-comment">-----暂无评论------</p> -->
    </div>
  </div>
</template>

<script>
import { MessageBox } from 'mint-ui';
import { Toast } from "mint-ui";
export default {
  props: ["propid","loginid"],
  data() {
    return {
       bigv:require("images/bigv.png"),
      tit: "评论详情",
      isShow:true,
      commentdetail: [],
      commeval: "",
      isshow: false,
      ed: "",
      resdata: [],
      write: [],
      fid: "",
        like: "icon-bigGood",
      likeNum: 1,
      commentid: "",
      iddata:"",
      placehoderInfo:"",
      keycode:true
    };
  },
  computed: {},
  activated() {
    this.getParams();
    this.getcommedetail();
  },
  mounted() {},
  watch: {
    commeval: {
      deep: true,
      handler(newVal, oldVal) {
        if (newVal.charAt(newVal.length - 1) == "@") {
          console.log(newVal);
          this.$router.push({
            path: "/friendList",
            name: "friendList"
          });
        }
      }
    }
  },
  methods: {
    getParams() {
      var routerdata = localStorage.getItem("homedata");
      // this.propid = localStorage.getItem("commid");
      var iss = this.commeval.search("@");
      this.fid = localStorage.getItem("fd");
      this.write = JSON.parse(routerdata);
      if (
        localStorage.getItem("fn") &&
        this.commeval.charAt(this.commeval.length - 1) == "@"
      ) {
        this.commeval = this.commeval + localStorage.getItem("fn") + "   ";
      }
    },
    // @好友
    async atfriend() {
      try {
        let params = {
          notice_id: this.fid,
          project_id: this.write.id,
          type: "1"
        };
        let res = await this.$api.all.POSTfn(params, "atfriend");
        console.log("@" + JSON.stringify(res));
      } catch (e) {
        console.log(e);
      }
    },
    //        //是否点赞
    // async islike(id) {
    //   try {
    //     let params = {
    //       target: id
    //     };
    //     let res = await this.$api.all.GETfn(params, "good_has");
    //     if (res.result == "ok") {
    //       if (res.data == 1) {
    //         this.like = "icon-bigGood";
    //       } else {
    //         this.like = "icon-good";
    //       }
    //     }
    //   } catch (e) {
    //     console.log(e);
    //   }
    // },
    //去点赞
    async addlike(data) {
      if (!this.loginid) {
        app.goLogin()
      }
       let params = {
        target: data.id,
        user_id:this.loginid,
        userid:this.loginid
      };
      try {
        if(this.keycode==true){
          this.keycode=false
        let res = await this.$api.all.POSTfn(params, "good");
        if (res.result == "ok") {
          this.$set(data, `is_flower`, 1)
           let num=Number(data.flowers)
           num=num+=1
          this.$set(data, `flowers`,num)
          console.log("点赞数量"+num)
        }
        console.log(res);
        this.keycode=true
        }
      } catch (e) {
        console.log(e);
      }
    },
     // 取消点赞
    async removelike(data) {
       let params = {
        target: data.id,
         user_id:this.loginid,
        userid:this.loginid
      };
      try {
        let res = await this.$api.all.POSTfn(params, "good_delete");
        if (res.result == "ok") {
        this.$set(data, `is_flower`, 0)
       let num=Number(data.flowers)
       num=num-=1
          this.$set(data, `flowers`,num)
          console.log("点赞数量"+num)
        }
      } catch (e) {
        console.log(e);
      }
    },
    // 获取评论数据
    async getcommedetail(direct, hot) {
      if (window.hasOwnProperty("webkit")) {
        this.isShow = false;
      } else {
        this.isShow = true;
      }
      console.log(hot)
      if (!hot) {
        this.commentdetail = [];
      }
      try {
        let params = {
          target: this.propid,
           target_id: hot,
          direction: direct,
          count:6,
           userid:this.loginid?this.loginid:2,
          user_id: this.loginid?this.loginid:2 
        };
        let res = await this.$api.all.GETfn(params, "commdetail");
       
        for(let i in res.result.data){
           res.result.data[i].timestamp = this.timeago(res.result.data[i].timestamp*1000);
        }
        this.commentdetail = this.commentdetail.concat(res.result.data);
         this.iddata = res.result;
      } catch (e) {
        console.log(e);
      }
    },
      more() {
      let dictir = "litter";
      let hot = this.iddata.min_id;
      if (this.iddata.more == 0) {
        Toast("无更多数据");
      } else {
        this.getcommedetail(
          dictir,
          hot
        );
      }
    },
    // searchIn(e, item) {
    //   // this.isshow = !this.isshow;
    //   if (e.target.className == "zi") {
    //     this.resdata = item;
    //     this.ed = "zi";
    //   } else {
    //     this.ed = "fu";
    //   }

    //   this.$refs.inputVal.focus();
    // },
      gocommedetail(item,isself) {
      localStorage.setItem("commid", item);
      if (window.hasOwnProperty("webkit")) {
        let params = {
          id: item.id
        };
        window.webkit.messageHandlers.replyCommentByID.postMessage(params);
      } else {
         if (isself && item.user_id == this.loginid) {
          Toast("不能自己回复自己");
          return;
        }
       this.$refs.inputVal.focus();
        this.commentid = item.id;
          this.placehoderInfo=item.nickname
      }
    },
    inpblur(){
       this.commentid='';
      this.placehoderInfo=''
    },
    //发表评论
    async sendhuifu(comment) {
     if (!this.loginid) {
        
        app.goLogin()
      }
      console.log("字评论的id"+ comment)
      if (this.commeval == "") {
        Toast("请输入评论信息");
      }
      let params;
      params = {
        target:comment?comment:this.propid,
        text: this.commeval,
         user_id:this.loginid,
        userid:this.loginid,
        _: "RFs0",
      };
      try {
        if(this.keycode==true){
          this.keycode=false
        let res = await this.$api.all.POSTfn(params, "send_comment");
        console.log("hhifudenayiji:" + JSON.stringify(params));
        this.getcommedetail();
         this.commentid = "";
          Toast("发布成功");
          this.commeval==""
          this.keycode=true;
        }
      } catch (e) {
        console.log(e);
      }
      if (this.commeval.indexOf("@" + localStorage.getItem("fn")) != -1) {
        this.atfriend();
      }
      this.commeval = "";
      this.placehoderInfo=''
    },
    //删除评论
    async del(item, index) {
       MessageBox.confirm('确定删除此条评论?').then(async action => {
      
      try {
        let params = {
          // userid: "327195464540160441",
          comment_id: item.id,
           user_id:this.loginid,
        userid:this.loginid
        };
        let res = await this.$api.all.POSTfn(params, "delcomme");
        if (res.result== "ok") {
           this.commentdetail.splice(index, 1);
        }
      } catch (e) {
        console.log(e);
      }
       })
    },
    //去个人资料页
    privateinfo(v) {
      app.gouserInfo(v)
    },

    timeago(dateTimeStamp) {
      //dateTimeStamp是一个时间毫秒，注意时间戳是秒的形式，在这个毫秒的基础上除以1000，就是十位数的时间戳。13位数的都是时间毫秒。
      var result;
      var minute = 1000 * 60; //把分，时，天，周，半个月，一个月用毫秒表示
      var hour = minute * 60;
      var day = hour * 24;
      var week = day * 7;
      var halfamonth = day * 15;
      var month = day * 30;
      var now = new Date().getTime(); //获取当前时间毫秒
      var diffValue = now - dateTimeStamp; //时间差

      if (diffValue < 0) {
        return;
      }
      var minC = diffValue / minute; //计算时间差的分，时，天，周，月
      var hourC = diffValue / hour;
      var dayC = diffValue / day;
      var weekC = diffValue / week;
      var monthC = diffValue / month;
      if (monthC >= 1 && monthC <= 3) {
        result = " " + parseInt(monthC) + "月前";
      } else if (weekC >= 1 && weekC <= 3) {
        result = " " + parseInt(weekC) + "周前";
      } else if (dayC >= 1 && dayC <= 6) {
        result = " " + parseInt(dayC) + "天前";
      } else if (hourC >= 1 && hourC <= 23) {
        result = " " + parseInt(hourC) + "小时前";
      } else if (minC >= 1 && minC <= 59) {
        result = " " + parseInt(minC) + "分钟前";
      } else if (diffValue >= 0 && diffValue <= minute) {
        result = "刚刚";
      } else {
        var datetime = new Date();
        datetime.setTime(dateTimeStamp);
        var Nyear = datetime.getFullYear();
        var Nmonth =
          datetime.getMonth() + 1 < 10
            ? "0" + (datetime.getMonth() + 1)
            : datetime.getMonth() + 1;
        var Ndate =
          datetime.getDate() < 10
            ? "0" + datetime.getDate()
            : datetime.getDate();
        var Nhour =
          datetime.getHours() < 10
            ? "0" + datetime.getHours()
            : datetime.getHours();
        var Nminute =
          datetime.getMinutes() < 10
            ? "0" + datetime.getMinutes()
            : datetime.getMinutes();
        var Nsecond =
          datetime.getSeconds() < 10
            ? "0" + datetime.getSeconds()
            : datetime.getSeconds();
        result = Nyear + "-" + Nmonth + "-" + Ndate;
      }
      return result;
    }
  },
  components: {}
};
</script>

<style scoped lang="css">
.mainContent {
  padding-bottom:70px;
}
.del {
  float: right;
}
.review-time {
  color: #999;
  font-size: 12px;
  height: 30px;
}
/* .allbox {
  background: #f6f7f9;
  font-size: 14px;
} */
.review-nick {
 

  color: #3966a7;
  font-size: 14px;
  /* line-height: 30px; */
  font-weight: 700;
  padding-bottom: 4px;
}
.namebox{
  display: inline-block;
  width: 220px;
  overflow: hidden;
text-overflow:ellipsis;
white-space: nowrap;
}

.zi {
  color: #333;
  /* font-weight: 600; */
  padding-left: 10px;
}
.fu {
  color: #333;
  /* font-weight: 600; */
  padding-left: 10px;
}
.review-area {
  color: #333;
  font-size: 14px;
  line-height: 16px;
  color: #333;
  width: 300px;
  word-break: break-all;
  text-align: justify;
  /* word-wrap: break-word;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical; */
}
.commentbox {
  display: flex;
  align-items: inherit;
  width: 100%;
  padding: 10px 12px;
  position: relative
}
.reply-all {
  height: 36px;
  line-height: 36px;
  text-align: left;
  box-sizing: border-box;
  font-size: 14px;
  font-weight: 700;
  display: block;
  min-width: 120px;
  padding-left: 12px;
}

.por {
  line-height: 20px;
  /* padding: 5px; */
}
.commentbox img {
  /* margin-top: 5px; */
  display: inline-block;
  height: 44px;
  width: 44px;
  border-radius: 100%;
}

.commentcon {
  text-align: start;
  width: 100%;
  /* padding: 3px 0; */
  margin-left: 10px;
}

.hot-reviews {
  width: 100%;
}
.hh-comment {
  height: 20px;
  line-height: 20px;
  text-align: center;
  font-size: 17px;
  padding-bottom: 30px
}
/* 子评论的样式*/
.childcomme {
  padding: 20px 5px 4 0px;
  margin-top: 5px;
  background: #ffffff;
  /* padding: 5px; */
}
.childitem {
  display: flex;
  justify-content: space-between;
}

.detailbottom {
  height: 44px;
  background: #ffff;
  padding-left: 6px;
  display: flex;
  justify-content: start;
  align-items: center;
  box-sizing: border-box;
  border-top: 1px solid #eee;
}
.detailbottom * {
  vertical-align: middle;
}
.detailbottom p {
  height: 30px;
  line-height: 30px;
  padding-left: 20px;
}
.inp {
  width: 80%;
  height: 30px;
  line-height: 30px;
  font-size: 14px;
  padding: 10px;
  border-radius: 3px;
  background: #f6f7f9;
  border-radius: 20px;
}

input:focus {
  background-color: #fff;
  border: 1px solid #666;
}

.bottom {
  width: 100%;
  position: fixed;
  bottom: 0;
  left: 0;
  background: #fff;
  z-index: 10000
}
/* 评论新增 */
.color{
  font-size: 14px;
  float: right;
  color: #666
}
.iconfont{
  font-size: 20px;
  
}
.sonReply{
  display: inline-block;
  background: #eee;
  color:#333;
  padding: 2px 10px;
  margin-left: 10px;
  border-radius: 20px
}
.replyBtn{
  color: #999
}
.more {
  padding: 20px 0 0 0;
  text-align: center;
  font-size: 14px;
  color: #333;
}
.tobrige{
  color: #666;
  font-size: 14px
}
.commentbox .bigv{
  height: 16px;
  width: 16px;
  position:absolute;
   top: 10.333vw;
    left: 10.333vw;
}
.sonbox .bigv{
  height: 16px;
  width: 16px;
  position:absolute;
      top: 10.333vw;
    left: 10.333vw;
}
</style>



