<template>
  <div>
    <ul class="personalinfo-ul">
      <!-- <div > -->
      <vue-scroll
        ref="scroll"
        :ops="opsconfig"
        @refresh-start="handleRS"
        @load-before-deactivate="handleLBD"
      >
        <li class="personalinfo-li">
          <ul class="detailsinfo-ul">
            <li class="detailsinfo-left">
              <span class="detailsinfo-span">
                <span class="c-font-name">{{"创建科技群"}}({{'123'}})</span>
                <span class="c-font-content">{{'mingzi'}}</span>
              </span>
            </li>
            <li class="detailsinfo-right">
              <!-- <mt-button type="default" size="small" @click="goToChatRoom(v.id)">加入</mt-button> -->
              <span class="btn">加入</span>
            </li>
          </ul>
        </li>
      </vue-scroll>

      <!-- </div> -->
    </ul>
  </div>
</template>

<script>
export default {
  props: {},
  data() {
    return {
      opsconfig: ScrollConfig.opsconfig,
    };
  },
  computed: {},
  created() {},
  mounted() {},
  watch: {},
  methods: {},
  components: {}
};
</script>

<style scoped lang="css">
.personalinfo-ul {
  display: block;
  width: 100%;
  line-height: 60px;
  height: calc(100vh-132px) !important;
  min-height: calc(100vh-132px) !important;
}

.personalinfo-li {
  display: block;
  width: 100%;
  height: 60px;
  /* margin: 6px 0px; */
}

.detailsinfo-ul {
  width: 100%;
  display: flex;
  height: 60px;
  list-style: none;
}

.detailsinfo-left {
  width: 280px;
  display: flex;
  align-items: center;
  height: 60px;
}

.imgFace {
  display: inline-block;
  background: black;
  width: 40px;
  height: 40px;
  margin: 8px 0px 8px 12px;
  border-radius: 50%;
}

.detailsinfo-left span.detailsinfo-span {
  flex: 1;
  margin-left: 8px;
}

span.detailsinfo-span span {
  display: block;
  height: 22px;
  line-height: 22px;
  /* margin: 5px; */
}

.c-font-name {
 font-size: 18px;
}
.c-font-content {
  font-size: 15px;
}

</style>



