<template>
  <div class="mainCover">
    <HeaderBar :tit="titleName"></HeaderBar>
    <div class="mainContent">
      <div class="top-box">
        <div class="list">
          <router-link :to="{name: 'news_friends'}" class="list-item">
            <div class="item-logo-box">
              <img :src="liebiao" alt class="item-logo" />
            </div>
            <div class="item-detail">
              <p class="item-detail-title">好友列表</p>
            </div>
          </router-link>

          <router-link :to="{name: 'news_groups', params: { teamType: 'advanced'}}" class="list-item">
            <div class="item-logo-box">
              <img :src="qunliao" alt class="item-logo" />
            </div>
            <div class="item-detail">
              <p class="item-detail-title">群聊</p>
            </div>
          </router-link>

          <router-link to="/news_add" class="list-item">
            <div class="item-logo-box">
              <img :src="tianjia" alt class="item-logo" />
            </div>
            <div class="item-detail">
              <p class="item-detail-title">添加好友</p>
            </div>
          </router-link>

          <router-link to="/news_apply" class="list-item">
            <div class="item-logo-box">
              <img :src="shenqing" alt class="item-logo" />
            </div>
            <div class="item-detail">
              <p class="item-detail-title">好友申请</p>
            </div>
          </router-link>
        </div>
      </div>

      <!-- <div class="line"></div> -->
      <ul>
        <li class="friend-list" v-for="session in sessionlist" @click="handleChatJump(session)">
          <div class="head-portrait-box">
            <img :src="debug" alt class="head-portrait" />
          </div>
          <div class="name-box">
            <p class="name">{{session.name}}</p>
            <p class="date">{{session.updateTimeShow}}</p>
            <p class="preview">{{session.lastMsgShow}}</p>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import YXSDKUtil from 'store/YXSDK/utils';
import HeaderBar from "com/Header-bar.vue";
export default {
  name: "news",
  props: {},
  data() {
    return {
      titleName: "消息",
      liebiao: "",
      shenqing: "",
      tianjia: "",
      qunliao: "",
      next: ""
    };
  },
  computed: {
     sessionlist () {
      let sessionlist = this.$store.state.sessionlist.filter(item => {
        item.name = ''
        item.avatar = ''
        if (item.scene === 'p2p') {
          let userInfo = null
          if (item.to !== this.myPhoneId) {
            userInfo = this.userInfos[item.to]
          } else {
            // userInfo = this.myInfo
            // userInfo.alias = '我的手机'
            // userInfo.avatar = `${config.myPhoneIcon}`
            return false
          }
          if (userInfo) {
            item.name = YXSDKUtil.getFriendAlias(userInfo)
            item.avatar = userInfo.avatar
          }
        } else if (item.scene === 'team') {
          let teamInfo = null
          teamInfo = this.$store.state.teamlist.find(team => {
            return team.teamId === item.to
          })
          if (teamInfo) {
            item.name = teamInfo.name
            item.avatar = teamInfo.avatar || (teamInfo.type === 'normal' ? this.myGroupIcon : this.myAdvancedIcon)
          } else {
            item.name = `群${item.to}`
            item.avatar = item.avatar || this.myGroupIcon
          }
        }
        let lastMsg = item.lastMsg || {}
        if (lastMsg.type === 'text') {
          item.lastMsgShow = lastMsg.text || ''
        } else if (lastMsg.type === 'custom') {
          item.lastMsgShow = YXSDKUtil.parseCustomMsg(lastMsg)
        } else if (lastMsg.scene === 'team' && lastMsg.type === 'notification') {
          item.lastMsgShow = YXSDKUtil.generateTeamSysmMsg(lastMsg)
        } else if (YXSDKUtil.mapMsgType(lastMsg)) {
          item.lastMsgShow = `[${YXSDKUtil.mapMsgType(lastMsg)}]`
        } else {
          item.lastMsgShow = ''
        }
        if (item.updateTime) {
          item.updateTimeShow = YXSDKUtil.formatDate(item.updateTime, true)
        }
        return item
      })
      return sessionlist
    },
    userInfos () {
      return this.$store.state.userInfos
    },
    myPhoneId () {
      return `${this.$store.state.userUID}`
    },
  },
  created() {
    this.liebiao = require("images/liebiao.png");
    this.shenqing = require("images/shenqing.png");
    this.tianjia = require("images/tianjia.png");
    this.qunliao = require("images/qunliao.png");
    this.next = require("images/next.png");
    this.debug = require("images/debug.png");
  },
  mounted() {},
  watch: {},
  methods: {
    // 这里的id 就是 sessionId（p2p-xxxx）
    handleChatJump({ id }){
      this.$router.push({
        name: 'chat',
        params: {
          sessionId: id
        }  
      });
      this.$store.dispatch('setCurrSession', id)
    }
  },
  components: {
    HeaderBar
  }
};
</script>

<style scoped lang="css">
a {
  text-decoration: none;
}

.mainContent {
  width: 100%;
  overflow: hidden;
  background: #fff
}

.top-box {
  height: 120px;
  display: flex;
  justify-content: center;
  align-items: center;
  /* background: url("../../assets/imgs/bg.jpg") 100% 100%; */
  margin-top: -9px
}

.list {
  width: 90%;
  height: 90px;
  border-radius: 10px;
  display: flex;
  justify-content: space-around;
  box-shadow: 0 5px 14px 0px #eee;
  padding-top: 15px;
  background-color: #fff;
  /* margin-top: 50px; */
 z-index: 1000000
}

.list-item {
  width: 20%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  align-items: center;
  height: 40px;
  line-height: 40px;
}

.item-logo-box {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.item-logo {
  width: 40px;
  height: 40px;
}

.item-detail {
  width: 100%;
  height: 60%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
  color: #333;
}

.line {
  width: 100%;
  height: 10px;
  background: #f6f7f9;
}

ul {
  padding-top: 20px;
  list-style-type: none;
  /* margin-top: -20px */
}

li { 
  width: 100%;
  overflow: hidden;
}

.friend-list {
  width: 100%;
  display: flex;
  height: 60px;
  padding-right: 12px;
}

.head-portrait-box {
  width: 15%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.head-portrait {
  width: 36px;
  height: 36px;
  border-radius: 50%;
}

.name-box {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  border-bottom: 1px solid #eee;
  padding: 6px 0px;
}

.name {
  display: block;
  width: 50%;
  font-size: 14px;
  color: #222;
}

.date {
  display: block;
  width: 50%;
  font-size: 12px;
  color: #999;
  text-align: right;
}

.preview {
  font-size: 12px;
  color: #666;
}
</style>



