<template>
  <div class="mainCover">
    <HeaderBar :needb="needb" :needch="needch" @fatherchange="Release" :tit="titleName"></HeaderBar>
    <div class="mainContent">
      <textarea
        name="内容"
        cols="24"
        rows="2"
        maxlength="40"
        class="textarea"
        :placeholder="PersonSign"
        v-model="textareaValue"
      ></textarea>
    </div>
  </div>
</template>

<script>
import { ComUtils } from "common/utils";

export default {
  name: "personal_autograph",
  props: {},
  data() {
    return {
      titleName: "更改签名",
      needb: true,
      PersonSign: "只要站在风口,猪也可以飞起来",
      textareaValue: this.$getUser.sign,
      needch: true,
      UserInfo: {}
    };
  },
  computed: {},
  created() {
    this.initParams();
  },
  mounted() {},
  watch: {},
  methods: {
    initParams() {
      this.UserInfo = this.$getUser;
    },
    async Release() {
      try {
        if (this.textareaValue == "" || this.textareaValue == null) {
          this.$toast("您未输入签名信息");
          return;
        }
        if (this.textareaValue.length < 5) {
          this.$toast("您输入得签名信息不得少于5个字符");
          return;
        }
        let params = {
          sign: this.textareaValue
        };
        let res = await this.$api.all.GETfn(params, "set_user");
        if (res.result == "ok") {
          this.$toast("发布成功");
          this.UserInfo.sign = this.textareaValue;
          ComUtils.saveParams(this.UserInfo);
          this.$router.go(-1); //返回上一页
        } else {
          this.$toast(res.result);
        }
      } catch (e) {
        this.$toast(e);
      }
    }
  },
  components: {}
};
</script>

<style scoped lang="css">
.mainContent{
    padding: 12px;
}
.textarea {
  width: 100%;
  min-height: 100px;
  font-size: 14px;
  border-radius: 3px;
  border: 1px solid #ddd;
  padding: 5px;
  text-align: justify;
}
</style>



