<template>
  <div class="mainCover">
    <!-- <HeaderBar :needb="needb" :tit="titName"></HeaderBar> -->
    <div class="bottom">
      <!-- <div class="detailbottom">
        <input type="text" placeholder="我来讲讲...." class="inp" @click.stop="jumpcommd(detaildata)" />
        <collectOrlike
          :targetid="detaildata.id?detaildata.id:detaildata.target_id"
          :pro="detaildata"
          ref="child"
        ></collectOrlike>
      </div>-->
      <div class="bottom">
        <div class="detailbottom">
          <input
            type="text"
             :placeholder="placehoderInfo?'我 回复 '+ placehoderInfo: '我来讲讲....'"
            class="inp"
            id="sendcomment"
            v-model="commentvalue"
            @keyup.13="sendcomment()"
              @blur="inpblur()"
                @focus="inputfouse()"
          />
          <collectOrlike
            :targetid="detaildata.id?detaildata.id:detaildata.target_id"
            :pro="propdata"
            ref="child"
            :loginid="useridaz"
             @fatherMethod="fatherfouse"
             v-show="commShow"
          ></collectOrlike>
           
        </div>
        <div class="sendbtn"  @touchstart.stop="sendcomment()">发送</div>
      </div>
    </div>
    <div class="mainContent">
      <div class="content" id="article">
        <div>
          <div class="titBottom">
            <img :src="detaildata.cover" alt />
            <h4 style="font-size:22px;">{{detaildata.title}}</h4>
            <p>
              <span>阅读量:{{detaildata.clicks}}</span>
              <span>{{timeago(detaildata.published*1000)}}</span>
            </p>
          </div>
          <div class="zhuantit">
            <p>{{detaildata.abstract}}</p>
          </div>
        </div>
        <div>
          <p>
            <span class="c-title-vline"></span> 专题文章
          </p>
          <div>
            <div class="huati" v-for="(i,d) in subscrnew" :key="d" @click="jumpnewdetail(i)">
              <div class="huatit">
                <div class="tit">
                  <p class="titname">{{i.title}}</p>
                  <!-- <p class="content">{{i.content}}</p> -->
                </div>
                <div class="imgbox">
                   <img v-if="i.cover_id!='0'" :src="i.cover_url?i.cover_url:''" alt />
                </div>
               
              </div>
              <!-- <div class="huatib">
                <span>发布时间:{{i.published}}</span>
              </div> -->
            </div>
            <div class="nodata" v-if="subscrnew==null">----- 暂无专题文章 -----</div>
          </div>
        </div>
      </div>
      <div class="commMain" v-if="!comment.length<=0">
        <p class="swiperTitle">
          <span class="c-title-vline"></span> 热门评论
        </p>
        <div class="childcomme">
          <li v-for="(i,t) in comment" :key="t" class="por" @click.stop="gocommedetail(i)">
            <div class="commentbox">
             
              <img :src="i.avatar?i.avatar:'../../../static/imgs/activity.png'"  @click.stop="privateinfo(i.user_id)" />
              <img :src="bigv" class="bigv1" v-if="i.is_vip==1"/>
              <!-- @click.stop="privateinfo(i.user_id)" -->
              <div class="commentcon">
                <div>
                  <p class="review-nick">
                    {{i.user_name}}
                    <span class="color">
                      <span class="hh-fab-value">{{i.flowers}}</span>
                      <i
                        v-if="i.is_flower==1"
                        class="iconfont icon-bigGood"
                        @click.stop="removelike(i,i.comment_id)"
                      ></i>
                      <i v-else class="iconfont icon-good" @click.stop="addlike(i,i.comment_id)" ></i>
                    </span>
                  </p>
                  <p class="review-area">{{i.text}}</p>
                </div>

                <div class="review-button">
                  <p :data-time="i.timestamp" class="review-time hh-time">
                    {{i.timestamp?i.timestamp:"刚刚"}} ·
                    <span class="replyBtn">回复</span>
                  </p>
                  <div v-if="i.user_id==useridaz" class="del" @click.stop="del(i,t)">删除</div>
                </div>
              </div>
            </div>
            <!-- v-if="i.secondComment.length!=0" -->
            <div class="son">
              <div class="sonbox">
                <div
                  v-for="(s,h) in i.secondComment"
                  :key="h"
                  class="sonitem"
                  @click.stop="gocommedetail(s,'isself')"
                >
                   <div class="imgbox" style="width:40px">
                      <img :src="s.avatar?s.avatar:touxiang" alt @click.stop="privateinfo(s.user_id)" />
                    <img :src="bigv" class="bigv" v-if="s.is_vip=='1'"/>  
                  </div>
                  <div>
                    <p class="sonname">
                      {{s.nickname}}
                      <span class="color">
                        <span class="hh-fab-value">{{s.flowers}}</span>
                        <i
                          v-if="s.is_flower==1"
                          class="iconfont icon-bigGood"
                          @click.stop="removelike(s,s.id)"
                        ></i>
                        <i v-else class="iconfont icon-good" @click.stop="addlike(s,s.id)" ></i>
                      </span>
                    </p>
                    <!-- <span class="sonReply" v-if="s.secondComment.length>0">{{s.secondComment.length}}回复</span> -->
                    <p class="sontext">{{s.text}}</p>
                     <div class="review-button">
                        <p :data-time="s.timestamp" class="review-time hh-time">
                          {{s.timestamp?s.timestamp:"刚刚"}} ·
                          <span class="replyBtn">&nbsp;回复</span>
                        </p>
                        <div
                          v-if="s.user_id==useridaz"
                          class="sondel"
                          @click.stop="delson(i,s,h)"
                        >删除</div>
                      </div>
                  </div>
                 
                </div>
              </div>
              <p
                v-if="i.comments>2"
                @click.stop="propShow(i.comment_id)"
                class="comlength"
              >全部 {{i.comments}} 条评论>>></p>
            </div>
          </li>
          <div @click="more()" class="more" v-if="iddata.more==1">点击加载更多</div>
        </div>
      </div>
      <!-- <pictwite :vadiodata="vadioid" ref="comme"></pictwite> -->
    </div>
    <mt-popup v-model="popupVisible" position="bottom">
      <div class="tanchu">
        <div class="close" @click="propShow()">
          <p class="swiperTitle">
            <span class="c-title-vline"></span> 全部回复
          </p>
          <p style="font-size:30px;padding-right:16px;">×</p>
        </div>
        <commedetail :propid="commid" ref="comme" :loginid="useridaz"></commedetail>
      </div>
    </mt-popup>
  </div>
</template>

<script>
import collectOrlike from "com/collectOrlike.vue";
import commedetail from "pages/home/commedetail.vue";
import { ComUtils } from "common/utils";
import { Toast } from "mint-ui";
import { MessageBox } from 'mint-ui';
export default {
  props: {},
  data() {
    return {
       bigv:require("images/bigv.png"),
      comment: [],
      iddata: [],
      propdata: [],
      needb: true,
      titName: "专题新闻",
      subscrnew: [],
      detaildata: [],
      isShow: false,
      commentvalue: "",
      commentid: "",
      popupVisible: false,
      commid: "",
      useridaz: "",
      placehoderInfo:"",
      keycode:true,
      commShow:true
    };
  },
  computed: {},
  activated() {
    this.brige();
    this.getParams();
    this.gettujiandate(
      this.propdata.id ? this.propdata.id : this.propdata.target
    );
    this.getdetaildate(
      this.propdata.id ? this.propdata.id : this.propdata.target
    );
    
  },
  mounted() {},
  watch: {},
  methods: {
    getParams() {
      var routerdata = localStorage.getItem("zhuantidata");
      // var type = window.sessionStorage.getItem("atype");
      this.propdata = JSON.parse(routerdata);
      // this.useridaz='319430217930703154'
    },
    jumpcommd(item, type) {
      localStorage.setItem("homedata", JSON.stringify(this.detaildata));
      this.$router.push({
        path: "/writecomment",
        name: "writecomment"
      });
    },
    brige() {
      let that = this;
      window.getArticleInfo = function(data, getuserid) {
        that.propdata = data;
        that.useridaz=getuserid;
        that.gettujiandate(data.id);
        that.getdetaildate(data.id);
      };
      window.iosGetcommune = function() {
        that.getcommentdate(
          that.propdata.id ? that.propdata.id : that.propdata.target_id
        );
      };
    },
    // jumpcommdetail(item, type) {
    //   localStorage.setItem("homedata", JSON.stringify(item));
    //   this.$router.push({
    //     path: "/pictwite",
    //     name: "pictwite"
    //   });
    // },
       fatherfouse(){
          var articleheight=document.getElementById("article").offsetHeight;
   document.querySelector('.mainContent').scrollTo(0,articleheight+20);
    
    },
    // 获取推荐数据
    async gettujiandate(id) {
      try {
        let params = {
          //  target:"315419982328496582",
          target: id,
          id: id
        };
        let res = await this.$api.all.GETfn(params, "subject");

        this.subscrnew = res.result;
      } catch (e) {
        console.log(e);
      }
    },
    //删除评论
   del(item, index) {
       MessageBox.confirm('确定删除此条评论?').then(async action => {
      try {
        let params = {
          // userid: "327195464540160441",
          comment_id: item.comment_id,
          userid: this.useridaz,
          user_id: this.useridaz
        };
        let res = await this.$api.all.POSTfn(params, "delcomme");
        if (res.result == "ok") {
          console.log(index);
          this.comment.splice(index, 1);
          this.$refs.child.getcomment();
        }
      } catch (e) {
        console.log(e);
      }
       })
    },
      //删除评论
   delson(s, item, index) {
       MessageBox.confirm('确定删除此条评论?').then(async action => {
      try {
        let params = {
          // userid: "327195464540160441",
          comment_id: item.id,
           userid: this.useridaz,
          user_id: this.useridaz
        };
        let res = await this.$api.all.POSTfn(params, "delcomme");
        console.log(res);
        if (res.result == "ok") {
          s.secondComment.splice(index, 1);
          this.$refs.child.getcomment();
        }
      } catch (e) {
        console.log(e);
      }
      });
    },
    // 获取详情页数据
    async getdetaildate(id) {
      try {
        let res = await this.$api.all.GETfn({}, "detail", id);
        this.detaildata = res;
         if(app !=undefined && app.hasOwnProperty('sendClick')){
          app.sendClick(res.id,res.clicks)
        }else{

        }
        // console.log("专题新闻的数据"+JSON.stringify(res))
        let that = this;
        setTimeout(function() {
          that.$refs.child.iscollect();
          that.$refs.child.islike();
          that.$refs.child.getcomment();
          that.getcommentdate(res.id);
        });
      } catch (e) {
        console.log(e);
      }
    },
    //ios获取评论
    propShow(pro) {
      if (pro) {
        this.commid = pro;
        console.log("pinglunid" + this.commid);
        let that = this;
        setTimeout(function() {
          that.$refs.comme.getcommedetail();
        });
      } else {
        // this.comment = [];
        // this.getcommentdate(
        //   this.vadioid.id ? this.vadioid.id : this.vadioid.target_id
        // );
      }
      this.popupVisible = !this.popupVisible;
    },
    gocommedetail(item,isself) {
      localStorage.setItem("commid", item);
      if (window.hasOwnProperty("webkit")) {
        let params = {
          id: item.id
        };
        window.webkit.messageHandlers.replyCommentByID.postMessage(params);
      } else {
          if (this.isself && item.user_id == this.useridaz) {
          Toast("不能自己回复自己");
          return;
        }
        document.getElementById("sendcomment").focus();
          if(!item){
          return;
        }
        this.commentid = item.comment_id;
         this.placehoderInfo = item.nickname
          ? item.nickname
          : item.user_name;
        
      }
    },
    //发表评论
    async sendcomment() {
      if(!this.useridaz){
        app.goLogin()
      }
    
      if (this.commentvalue.trim() == "") {
        Toast("评论信息不能为空");
        return;
      }
      try {
        if(this.keycode==true){
          this.keycode=false
        let params = {
          userid: this.useridaz,
          user_id: this.useridaz,
          target: this.commentid
            ? this.commentid
            : this.detaildata.id
            ? this.detaildata.id
            : this.detaildata.target_id,
          text: this.commentvalue,
          _: "RFs0",
          type: "article"
        };
        // ;
        let res = await this.$api.all.POSTfn(params, "send_comment");
        if(res.result=="ok"){
             Toast("发布成功");
          }else{
             Toast(res.result);
          }
        this.getcommentdate(
          this.detaildata.id ? this.detaildata.id : this.detaildata.target_id
        );
        this.$refs.child.getcomment();
        this.commentid = "";
        this.placehoderInfo = "";
        // this.getcomment()
        this.keycode=true
          if(app !=undefined && app.hasOwnProperty('backList')){
          app.backList()
        }else{

        }
        }
      } catch (e) {
        console.log(e);
      }
      if (this.commentvalue.indexOf("@" + localStorage.getItem("fn")) != -1) {
        this.atfriend();
      }
      this.commentvalue = "";
      this.commentid = "";
      this.placehoderInfo = "";
    },
       inputfouse(){
            this.commShow=false
      document.querySelector(".sendbtn").style.display="block"
    },
    inpblur() {
       this.commShow=true
      this.commentid = "";
      this.placehoderInfo = "";
       document.querySelector(".sendbtn").style.display="none"
    },
    //去点赞
    async addlike(data,id) {
       if(!this.useridaz){
        app.goLogin()
      }
      let params = {
        target: data.id,
        userid: this.useridaz,
          user_id: this.useridaz
      };
      try {
        let res = await this.$api.all.POSTfn(params, "good");
        if (res.result == "ok") {
          this.$set(data, `is_flower`, 1);
          let num = Number(data.flowers);
          num = num += 1;
          this.$set(data, `flowers`, num);
          console.log("点赞数量" + num);
            if(app !=undefined && app.hasOwnProperty('backList')){
          app.backList()
        }else{

        }
        }
        console.log(res);
      } catch (e) {
        console.log(e);
      }
    },
    // 取消点赞
    async removelike(data,id) {
      let params = {
        target: data.id,
        userid: this.useridaz,
          user_id: this.useridaz
      };
      try {
        let res = await this.$api.all.POSTfn(params, "good_delete");
        if (res.result == "ok") {
          this.$set(data, `is_flower`, 0);
          let num = Number(data.flowers);
          num = num -= 1;
          this.$set(data, `flowers`, num);
          console.log("点赞数量" + num);
            if(app !=undefined && app.hasOwnProperty('backList')){
          app.backList()
        }else{

        }
        }
      } catch (e) {
        console.log(e);
      }
    },
    // 获取评论数据
    async getcommentdate(id, direct, hot) {
      if (!hot) {
        this.comment = [];
      }
      try {
        let params = {
          target: id,
          target_id: hot,
          direction: direct,
          count: 5,   userid: this.useridaz?this.useridaz:2,
          user_id: this.useridaz?this.useridaz:2
        };
        let res = await this.$api.all.GETfn(params, "comment");
        for (let i in res.result.data) {
          let now = new Date().getTime();
          res.result.data[i].timestamp = this.timeago(
            res.result.data[i].timestamp * 1000
          );
           if (res.result.data[i].avatar) {
            if (res.result.data[i].avatar.endsWith(".com/")) {
              res.result.data[i].avatar = "../../../static/imgs/morentu.jpg";
            }
          }
          for (let t in res.result.data[i].secondComment) {
            res.result.data[i].secondComment[t].timestamp = this.timeago(
              res.result.data[i].secondComment[t].timestamp * 1000
            );
            if (res.result.data[i].secondComment[t].avatar) {
              if (
                res.result.data[i].secondComment[t].avatar.endsWith(".com/")
              ) {
                res.result.data[i].secondComment[t].avatar =
                  "../../../static/imgs/morentu.jpg";
              }
            }
          }
        }
        // this.comment=res.result.data
        this.comment = this.comment.concat(res.result.data);
        this.iddata = res.result;
        console.log(res);
      } catch (e) {
        console.log(e);
      }
    },

    more() {
      let dictir = "litter";
      let hot = this.iddata.min_id;
      if (this.iddata.more == 0) {
        Toast("无更多数据");
      } else {
        this.getcommentdate(
          this.vadioid.id ? this.vadioid.id : this.vadioid.target_id,
          dictir,
          hot
        );
      }
    },
    timeago(dateTimeStamp) {
      //dateTimeStamp是一个时间毫秒，注意时间戳是秒的形式，在这个毫秒的基础上除以1000，就是十位数的时间戳。13位数的都是时间毫秒。
      var result;
      var minute = 1000 * 60; //把分，时，天，周，半个月，一个月用毫秒表示
      var hour = minute * 60;
      var day = hour * 24;
      var week = day * 7;
      var halfamonth = day * 15;
      var month = day * 30;
      var now = new Date().getTime(); //获取当前时间毫秒
      var diffValue = now - dateTimeStamp; //时间差

      if (diffValue < 0) {
        return;
      }
      var minC = diffValue / minute; //计算时间差的分，时，天，周，月
      var hourC = diffValue / hour;
      var dayC = diffValue / day;
      // var weekC = diffValue / week;
      // var monthC = diffValue / month;
      // if (monthC >= 1 && monthC <= 3) {
      // 	result = " " + parseInt(monthC) + "月前";
      // } else if (weekC >= 1 && weekC <= 3) {
      // 	result = " " + parseInt(weekC) + "周前";
      // } else
      if (dayC >= 1 && dayC <= 3) {
        result = " " + parseInt(dayC) + "天前";
      } else if (hourC >= 1 && hourC <= 23) {
        result = " " + parseInt(hourC) + "小时前";
      } else if (minC >= 1 && minC <= 59) {
        result = " " + parseInt(minC) + "分钟前";
      } else if (diffValue >= 0 && diffValue <= minute) {
        result = "刚刚";
      } else {
        var datetime = new Date();
        datetime.setTime(dateTimeStamp);
        var Nyear = datetime.getFullYear();
        var Nmonth =
          datetime.getMonth() + 1 < 10
            ? "0" + (datetime.getMonth() + 1)
            : datetime.getMonth() + 1;
        var Ndate =
          datetime.getDate() < 10
            ? "0" + datetime.getDate()
            : datetime.getDate();
        var Nhour =
          datetime.getHours() < 10
            ? "0" + datetime.getHours()
            : datetime.getHours();
        var Nminute =
          datetime.getMinutes() < 10
            ? "0" + datetime.getMinutes()
            : datetime.getMinutes();
        var Nsecond =
          datetime.getSeconds() < 10
            ? "0" + datetime.getSeconds()
            : datetime.getSeconds();
        result = Nyear + "-" + Nmonth + "-" + Ndate;
      }
      return result;
    },
    jumpnewdetail(item, type) {
       app.gotoNext(item.type_id,item.id)
      // localStorage.setItem("homedata", JSON.stringify(item));
      // localStorage.setItem("atype", item.category_id);
      // this.$router.push({
      //   path: "/homeDetail",
      //   name: "homeDetail"
      // });
    },
     //去个人资料页
    privateinfo(v) {
       if (!this.useridaz) {
        app.goLogin()
      }else{
          app.gouserInfo(v)
      }
    }
  },
  components: {
    collectOrlike,
    commedetail
  }
};
</script>

<style scoped lang="css">
.mainContent {
  height: 100%;
  width: 100%;
  padding-bottom: 40px;
}
.mainContent img {
  width: 100%;
  height: 200px;
}
.content {
  padding: 10px;
}
.zhuantit {
  color: #333333;
  padding: 10px 0;
}
.zhuantit p {
  font-size: 18px;
  color: #666666;
  margin-top: 10px;
}
.huati {
  border-bottom: 1px solid #eee;
  padding: 8px 0px;
}
.huatit {
  display: flex;
  justify-content: space-between;
  min-height: 30px;
}
.huatit img {
  height: 70px;
  width: 100% !important;
  border-radius: 3px;
  /* background: transparent; */
  border: none;
}
.imgbox{
  position: relative;
   width: 100px !important;
}

.titname {
  font-size: 14px;
  color: #333333;
  /* height: 58px; */
  display: -webkit-box;
  display: -moz-box;
  white-space: pre-wrap;
  word-wrap: break-word;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
}
.huatib {
  color: #999999;
  font-size: 14px;
}
.tit {
  width: 70%;
}
.bottom {
  width: 100%;
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 1000000;
}
.detailbottom {
  height: 40px;
  /* background: #4a8dff; */
  padding-left: 6px;
  display: flex;
  justify-content: start;
  align-items: center;
  box-sizing: border-box;
  background: #fff;
  border-top: 1px solid #eee;
}

.detailbottom * {
  vertical-align: middle;
}
.inp {
  width: 70%;
  height: 30px;
  border: 1px solid #999;
  padding-left: 10px;
  font-size: 14px;
  border-radius: 20px;
}
.inp:focus {
  border:none;
}
.nodata {
  text-align: center;
  padding: 10px;
}
>>> .mint-header {
  color: #3966a7 !important;
}
.titBottom {
  color: #666666;
  font-size: 14px;
}

/* pinglun */
.tanchu {
  height: 500px;
  width: 101%;
  overflow-y: scroll;
}
.close {
  font-size: 20px;
  /* padding:10px; */
  color: #333;
  text-align: right;
  position: fixed;
  bottom: 500px;
  background: #fff;
  width: 100%;
  display: flex;
  justify-content: space-between;
  border-radius: 6px 6px 0px 0px;
}
.commMain .swiperTitle {
