<template>
  <div class="mainCover">
    <HeaderBar :needb="needb" :tit="titleName"></HeaderBar>
    <div class="mainContent">
      <vue-scroll
        ref="scroll"
        :ops="opsconfig"
        @refresh-start="handleRS"
        @load-before-deactivate="handleLBD"
        @refresh-before-deactivate="handleRBD"
        @load-start="handleLoadStart"
      >
        <div class="commune" v-for="(i,d) in asklist" :key="d" @click="goaskdetail(i)">
            <div class="tit">
                <p class="titname">{{i.title}}</p>
                <p class="content">{{i.content}}</p>
                <div class="commune_b">
                    <span>来自:{{i.commune_name}}</span>
                    <!-- <span>浏览量:{{i.countData.click.count}}</span>
                    <span>评论:{{i.countData.comment.count}}</span> -->
                    <span>{{i.create_time}}</span>
                </div>
            </div>
            <img :src="i.image_url" alt />
        </div>
        <div class="nodata" v-if="asklist.length == 0">----- 暂无数据 -----</div>
      </vue-scroll>
    </div>
  </div>
</template>

<script>
import { ScrollConfig } from "common/data.js";
import { Toast } from "mint-ui";
export default {
  name: "examination",
  props: {},
  data() {
    return {
      opsconfig: ScrollConfig.opsconfig,
      rdata: [],
      asklist: [],
      titleName: "关注公告",
      needb: true,
      iddata: []
    };
  },
  computed: {},
  activated() {
    this.getParams();
    this.exlist();
  },
  mounted() {},
  watch: {},
  methods: {
    getParams() {
      var routerdata = window.sessionStorage.getItem("commitydata");
      this.rdata = JSON.parse(routerdata);
    },
    //
    async setbrowsing(item) {
      try {
        let params = {
          ask_id: item.id
        };
        let res = await this.$api.all.POSTfn(params, "browsing");
        console.log("设置浏览量" + JSON.stringify(res));
      } catch (e) {
        console.log(e);
      }
    },
    goaskdetail(item) {
      window.sessionStorage.setItem("askdata", JSON.stringify(item));

      this.setbrowsing(item);
      this.$router.push({
        name: "commune_f"
        // params: {
        //   data: item
        // }
      });
      this.asklist == [];
    },
    async exlist(direct, hot) {
      let params = {
       liststype :'askfav',
        target_id:hot,
        count: 10,
        direction: direct
      };

      try {
        let res = await this.$api.all.GETfn(params, "my_commune");
        console.log(res);
        if (direct == "litter") {
          this.asklist = this.asklist.concat(res.result.data);
        } else {
          this.asklist = res.result.data.concat(this.asklist);
        }
        this.iddata = res.result;
      } catch (e) {
        console.log(e);
      }
    },
    handleRS(vsInstance, refreshDom, done) {
      // alert("这是执行下拉刷新开始做啥事");
      done();
    },
    handleLoadStart(vm, dom, done) {
      setTimeout(() => {
        // alert("这是执行下拉加载之前需要执行什么操作");

        done();
      }, 600);
    },
    handleLBD(vm, loadDom, done) {
      let direction = "litter";
      let hot = this.iddata.min_id;
      if (this.iddata.more == 0 ) {
        //  this.noSscrollData = true;
        Toast("无更多数据");
        done();
      } else {
        this.exlist(direction, hot);
        done();
      }
    },
    handleRBD(vm, loadDom, done) {
      setTimeout(() => {
        // alert("这是执行下拉加载完毕需要执行的事件");
        done();
      }, 500);
    }
  },
  components: {}
};
</script>

<style scoped lang="css">
>>> .__vuescroll{
    padding: 0px 12px !important;
}

.commune {
  border-bottom: 1px solid #eee;
  min-height: 86px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.tit{
    width: 251px;
    padding: 5px 5px 5px 0px;
}

.titname {
  color: #333333;
  font-size: 14px;
  font-weight: 600;
  width: 100%;
  height: 22px;
overflow: hidden;
text-overflow: ellipsis;
white-space: nowrap;
}

.content {
  font-size: 12px;
  color: #666;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  display: -webkit-box !important;
  color: #666666;
  width: 100%;
  height: 34px;
}

.commune_b {
  font-size: 12px;
  color: #bbbbbb;
  min-height: 20px;
  line-height: 20px;
}

.commune_b span {
  margin-right: 5px;
}

.commune img {
  /* height: 70px; */
  height: 80px;
  min-height: 60px;
  width: 100px;
  border-radius: 3px;
  border: none;
}

.nodata {
  text-align: center;
  padding-top: 30px;
  color: #666666;
}
</style>



