<template>
  <div class="mainCover">
    <HeaderBar @fatherbackcall="backIntegral" :needcsb="needb" :tit="titleName"></HeaderBar>
    <div class="mainContent">
      <div class="fill_th">
        <div class="fill_th_1" @click.prevent="go_integral_address" v-if="!addParam">
          <span>+</span>
          新建地址
        </div>

        <div class="payment_th_1" v-if="addParam">
          <div class="payment_th_1_1">
            <img :src="address" />
          </div>
          <div class="payment_th_1_2">
            <p>
              <span>{{addParam.Receiver}}</span>
              <span>{{addParam.Telphone}}</span>
              <br />
              <span>{{addParam.Address}}</span>
            </p>
          </div>
          <div class="clearfix"></div>
        </div>

        <div class="fill_th_2">
          <div class="fill_th_2_1">
            <img :src="allList.image_url" />
          </div>
          <div class="fill_th_2_2">
            <div>{{allList.name}}</div>
            <p class="integral-p">
              <span class="integral">{{allList.integral}}积分</span>
              <span class="unshiftshop" @click.prevent="unshiftshop">-</span>
              <span class="num-input">{{nums}}</span>
              <span class="addshop" @click="addshop">+</span>
            </p>
          </div>
        </div>
      </div>
    </div>
    <div class="fill_th_3">
      <div class="fill_th_3_1">
        合计：
        <span class="jifen">{{allgoods}}积分</span>
      </div>
      <div :class="'fill_th_3_2 ' + disable " @click="submitShop">立即支付</div>
    </div>
  </div>
</template>

<script>
import { MessageBox } from "mint-ui";
export default {
  name: "intergral_fill",
  props: {},
  data() {
    return {
      titleName: "订单填写",
      needb: true,
      smallimage: "",
      allList: {},
      UserInfo: this.$getUser,
      disable: "",
      nums: 0,
      addParam:''
    };
  },
  computed: {
    allgoods() {
      let allnums = this.nums * this.allList.integral;
      if (allnums == 0) {
        this.disable = "disable";
      } else {
        this.disable = "";
      }
      return allnums;
    }
  },
  created() {
    this.address = require("images/address.png");
    this.smallimage = require("images/small.jpg");
    this.allList = JSON.parse(window.sessionStorage.getItem("shopitem"));
    console.log(this.allList);
    if(this.addParam){
      window.sessionStorage.removeItem('addParam');
    }else{
      this.addParam = JSON.parse(window.sessionStorage.getItem("addParam"));
    }
  },
  mounted() {},
  watch: {},
  methods: {
    backIntegral(){
      window.sessionStorage.removeItem('addParam');
      this.$router.push('me_integral');
    },
    go_integral_address() {
      this.$router.push({
        name: "integral_address"
      });
    },
    unshiftshop() {
      if (this.nums >= 1) {
        this.nums--;
      }
    },
    addshop() {
      if (this.nums < this.allList.number) {
        this.nums++;
      } else {
        this.$toast("没有更多库存了");
      }
    },
    async addEvent(param) {
      try {
        let res = await this.$api.all.GETfn(param, "integral_order_add");
        res = res.result;
        if(res === 'ok'){
          this.$router.push("/integral_list"); //要跳转的界面
        }else{
          this.$toast(res);
        }
        
      } catch (error) {}
    },
    submitShop() {
      if(!this.addParam){
        this.$toast("请新建订单");
        return false;
      }else if((this.disable === "disable")){
        this.$toast("请选择订单数量");
        return false;
      }

      MessageBox.confirm("", {
        message: "您确定要兑换吗？",
        title: "提示",
        confirmButtonText: "确认",
        cancelButtonText: "取消"
      })
        .then(action => {
          if (action == "confirm") {
            //确认的回调
            let param = {
              shop_id:this.allList.shop_id,
              address:this.addParam.Address,
              number:this.nums,
              phone:this.addParam.Telphone,
              name:this.addParam.Receiver
            }
            this.addEvent(param);
          }
        })
        .catch(err => {
          if (err == "cancel") {
            //取消的回调
          }
        });
    }
  },
  components: {}
};
</script>

<style scoped lang="css">
.payment_th_1 {
  margin: 10px 10px 0 10px;
  color: #023249;
  background: white;
  font-size: 14px;
}
.payment_th_1_1 {
  width: 12%;
  float: left;
  padding: 20px 10px 0 10px;
}
.payment_th_1_2 {
  width: 88%;
  float: left;
  padding: 10px;
  color: #000;
}
.payment_th_1_1 img {
  width: 100%;
  height: 100%;
  display: block;
}

.payment_th_1 p {
  font-size: 14px;
  text-align: left;
}
/* 重置 */
ul {
  list-style: none;
  margin: 0;
  padding-inline-start: 0;
}
li {
  margin: 0;
}
img {
  display: block;
  width: 100%;
  height: 100%;
}

.fill_th {
  background-color: #f5f6f9;
  position: relative;
  height: 620px;
}
.fill_th_1 {
  width: 100%;
  color: black;
  background: white;
  font-size: 18px;
  text-align: center;
  line-height: 44px;
  height: 44px;
}
.fill_th_1 span {
  font-size: 20px;
}
.fill_th_2 {
  width: 355px;
  margin: 10px;
  color: black;
  background: white;
  float: left;
  border-radius: 5px;
}

.integral-p {
  display: block;
  width: 100%;
  display: flex;
  height: 35px;
  line-height: 35px;
}

.integral-p span {
  display: inline-block;
  height: 35px;
  line-height: 35px;
  text-align: center;
  font-size: 18px;
}

span.integral {
  width: 150px;
  text-align: left;
  font-size: 16px;
}

span.unshiftshop {
  flex: 1;
}

span.num-input {
  width: 60px;
}

span.addshop {
  flex: 1;
}

.fill_th_2_1 {
  width: 80px;
  height: 72px;
  padding: 10px;
  float: left;
}
.fill_th_2_2 {
  text-align: left;
  padding: 10px 10px 0 10px;
  float: left;
  width: 275px;
}

.fill_th_3 {
  top: 580px;
  width: 375px;
  display: flex;
}
.fill_th_3_1 {
  width: 78%;
  background-color: white;
  height: 36px;
  float: left;
  padding-left: 18px;
  line-height: 36px;
  /* text-align: center; */
}
.fill_th_3_2 {
  flex: 1;
  background-color: #1b81cb;
  height: 36px;
  float: left;
  line-height: 36px;
  color: white;
  text-align: center;
}
.fill_th_3_1 .jifen {
  color: red;
}
.clearfix {
  clear: both;
}

div.disable {
  background: rgb(114, 112, 112);
}
</style>



