<template>
  <div class="mainCover">
    <HeaderBar :needm="needm" :needb="needb" :tit="titleName"></HeaderBar>
    <div class="headersearch">
      <mt-search placeholder="请输入项目名关键字" v-model="searchparam"></mt-search>
    </div>
    <div class="mainContent">
      <vue-scroll
        ref="scroll"
        :ops="opsconfig"
        @refresh-start="handleRS"
        @load-before-deactivate="handleLBD"
      >
        <ul class="project-details-ul">
          <li class="project-details-li" v-for="(v,i) in allList" :key="i">
            <img :src="v.image_url" alt="icon" />
            <span class="project-info-span">
              <span class="project-info-span-span project-info-span-title">{{v.title}}</span>
              <span class="project-info-span-span project-info-span-info">{{v.content}}</span>
            </span>
            <span class="project-icon-span">
              <button @click.prevent="goAskData(v)">查看</button>
            </span>
          </li>
          <div slot="load-beforeDeactive" v-if="noSscrollData">暂无数据</div>
          <div class="placeholder">&nbsp;</div>
        </ul>
      </vue-scroll>
    </div>
  </div>
</template>

<script>
import { ScrollConfig } from "common/data.js";

export default {
  name: "humansearch",
  props: {},
  data() {
    return {
      titleName: "人找项目搜索",
      needb: true,
      needm: false,
      opsconfig: ScrollConfig.opsconfig,
      refresh: 1,
      noSscrollData: false,
      triggerType: "load",
      params: "",
      searchparam: "",
      allList: ""
    };
  },
  computed: {},
  created() {
    // this.params = JSON.parse(sessionStorage.getItem("morekey"));
    // if (this.params != "" && this.params != null) {
    //   this.getDate("bigger", "0");
    // }
  },
  activated() {
    this.getDatafn();
  },
  mounted() {},
  watch: {
    searchparam(newv, oldv) {
      this.getDate("bigger", "0");
    }
  },
  methods: {
    getDatafn() {
      this.params = JSON.parse(sessionStorage.getItem("morekey"));
      if (this.params != "" && this.params != null) {
        this.getDate("bigger", "0");
      }
    },
    goAskData(v) {
      window.sessionStorage.setItem("askdata", JSON.stringify(v));
      this.$router.push("commune_f");
    },
    async getDate(type, numsid) {
      try {
        let timestamp = new Date().getTime();
        let params = {
          count: 10,
          timestamp: timestamp,
          direction: type,
          target_id: numsid,
          s: this.searchparam,
          status: "2",
          tagid: this.params
        };
        let res = await this.$api.all.GETfn(params, "ask_list");
        res = res.result;
        this.allList = res.data;
        console.log(this.allList);
        this.max_id = res.max_id;
        this.min_id = res.min_id;
      } catch (error) {}
    },
    async handleRS(vsInstance, refreshDom, done) {
      // alert("这是执行下拉刷新开始做啥事");
      try {
        let timestamp = new Date().getTime();
        let param = {
          count: 10,
          timestamp: timestamp,
          direction: "bigger",
          target_id: this.max_id,
          s: this.searchparam,
          status: "2",
          tagid: this.params
        };
        let res = await this.$api.all.GETfn(param, "ask_list");
        /************************************* */

        res = res.result;
        if (res.more == "0" && res.data.length == 0) {
          this.$toast("暂无最新数据");
          done();
          return;
        }
        if (res.data.length > 0) {
          this.max_id = res.max_id;
          this.min_id = res.min_id;
          res.data.forEach((item, index, arr) => {
            this.allList.unshift(item);
          });
          this.$forceUpdate();
        }
        /************************************* */
        done();
      } catch (error) {}
    },
    async handleLBD(vm, loadDom, done) {
      // alert("这是执行下拉加载之后需要执行什么操作");

      try {
        let timestamp = new Date().getTime();
        let param = {
          count: 10,
          timestamp: timestamp,
          direction: "litter",
          target_id: this.min_id,
          s: this.searchparam,
          status: "2",
          tagid: this.params
        };
        let res = await this.$api.all.GETfn(param, "ask_list");
        /************************************* */
        res = res.result;
        if (res.more == "0" && res.data.length == 0) {
          this.$toast("没有更多数据");
          done();
          return;
        }
        if (res.data.length > 0) {
          this.max_id = res.max_id;
          this.min_id = res.min_id;
          res.data.forEach((item, index, arr) => {
            this.allList.push(item);
          });
          this.$forceUpdate();
        }
        /************************************* */

        console.log(this.allList);
      } catch (error) {}
    }
  },
  components: {},
  destroyed() {
    sessionStorage.removeItem("morekey");
    this.$destory();
  },
  beforeRouteLeave(to, from, next) {
    sessionStorage.removeItem("morekey");
    this.searchparam = "";
    this.allList = "";
    next();
  }
};
</script>

<style scoped lang="css">
.placeholder {
  width: 100%;
  height: 2px;
  line-height: 2px;
}

.headersearch {
  height: 46px;
  line-height: 46px;
  font-size: 15px;
}

.project-details-ul {
  width: 100%;
  display: block;
  padding: 0px 12px;
}

.project-details-li {
  display: block;
  width: 100%;
  height: 60px;
  line-height: 55px;
  display: flex;
  border-bottom: 1px solid #eee;
  justify-content: space-between;
  align-items: center;
}

.project-details-li img {
  width: 40px;
  height: 40px;
  /* margin: 10px 10px 5px 0px; */
  border-radius: 50%;
}

.project-info-span {
  width: 220px;
}
.project-icon-span {
  text-align: center;
  flex: 1;
}

.project-icon-span button {
  height: 28px;
  line-height: 28px;
  width: 70%;
  margin-left: 5%;
  background: transparent;
  border-radius: 3px;
  font-size: 14px;
  color: #fff;
  background: #4a8dff;
}

.project-info-span-span {
  display: block;
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  height: 30px;
  line-height: 30px;
  padding: 0 5px;
}

.project-info-span-title {
  font-size: 14px;
  padding: 5px 0px 0px 10px;
}

.project-info-span-info {
  font-size: 12px;
  padding: 0px 0px 0px 10px;
}
</style>



