<template>
  <div class="mainCover">
    <HeaderBar :tit="titleName"></HeaderBar>
    <div class="mainContent">
      <router-link to="/me_personal" class="content-head">
        <div class="head-photo">
          <img v-lazy="UserInfo.avatar" class="page-default-image" />
        </div>
        <div class="head-text">
          <span class="head-text-name">{{UserInfo.name}}</span>
          <span class="head-text-phone">手机号:{{UserInfo.mobile}}</span>
        </div>
        <div class="content_th_1_right_arrow content_th_1_right_arrow_top">
          <img :src="backimage" />
        </div>
        <div class="clearfix"></div>
      </router-link>
      <div class="c-part-block"></div>
      <div class="content_th_2">
        <ul>
          <router-link to="/me_comment">
            <li>
              <div>
                <img :src="pinglunimg" />
              </div>
              <span class="contetn_qw">评论</span>
            </li>
          </router-link>
          <router-link to="/me_collection">
            <li>
              <img :src="shoucangimg" />
              <span class="contetn_qw">收藏</span>
            </li>
          </router-link>
          <router-link to="/me_thumbs_up">
            <li>
              <img :src="dianzanimg" />
              <span class="contetn_qw">点赞</span>
            </li>
          </router-link>
          <router-link to="/me_questions">
            <li>
              <img :src="tiwen1img" />
              <span class="contetn_qw">提问</span>
            </li>
          </router-link>
          <router-link to="/me_dynamic">
            <li>
              <img :src="dongtaiimg" />
              <span class="contetn_qw">动态</span>
            </li>
          </router-link>
          <router-link to="/me_notice">
            <li>
              <img :src="gonggaoimg" />
              <span class="contetn_qw">公告</span>
            </li>
          </router-link>
          <router-link to="/me_team">
            <li>
              <img :src="zuduiimg" />
              <span class="contetn_qw">组队</span>
            </li>
          </router-link>
          <router-link to="/me_set_up">
            <li>
              <img :src="shezhiimg" />
              <span class="contetn_qw">设置</span>
            </li>
          </router-link>
          <div class="clearfix"></div>
        </ul>
      </div>
      <div class="c-part-block"></div>
      <div class="content_th_3">
        <ul>
          <router-link to="/mynotice">
            <li>
              <span>我的通知</span>
              <div class="content_th_1_right_arrow push_nbsp">
                <span class="spot mespot" v-show="mespot">&nbsp;</span>
                <img :src="backimage" />
              </div>
            </li>
          </router-link>
          <!-- <router-link to="/me_integral">
            <li>
              <span>我的积分</span>
              <span class="integral">{{accquireInte}}积分</span>
              <div class="content_th_1_right_arrow">
                <img :src="backimage" />
              </div>
            </li>
          </router-link>-->
          <router-link to="/me_privilege">
            <li>
              <span>我的公社</span>
              <div class="content_th_1_right_arrow">
                <img :src="backimage" />
              </div>
            </li>
          </router-link>
          <router-link to="/me_follow">
            <li>
              <span>我的关注</span>
              <div class="content_th_1_right_arrow">
                <img :src="backimage" />
              </div>
            </li>
          </router-link>
          <router-link to="/me_interest">
            <li>
              <span>兴趣爱好</span>
              <div class="content_th_1_right_arrow">
                <img :src="backimage" />
              </div>
            </li>
          </router-link>
          <router-link to="/me_history">
            <li>
              <span>浏览历史</span>
              <div class="content_th_1_right_arrow">
                <img :src="backimage" />
              </div>
            </li>
          </router-link>
          <router-link to="/me_activity">
            <li class="xianxia">
              <span>线下活动</span>
              <div class="content_th_1_right_arrow">
                <img :src="backimage" />
              </div>
            </li>
          </router-link>
          <router-link to="/me_opinion">
            <li class="yijian">
              <span>意见反馈</span>
              <div class="content_th_1_right_arrow">
                <img :src="backimage" />
              </div>
            </li>
          </router-link>
           <router-link to="/me_integral">
            <li class="yijian">
              <span>我的积分</span>
              <div class="content_th_1_right_arrow">
                <img :src="backimage" />
              </div>
            </li>
          </router-link>

          <div class="c-part-block"></div>
          <router-link to="/me_contact">
            <li class="lianxi">
              <span>商务合作</span>
              <div class="content_th_1_right_arrow">
                <img :src="backimage" />
              </div>
            </li>
          </router-link>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "me",
  props: {},
  data() {
    return {
      titleName: "我的",
      UserInfo: {},
      backimage: "",
      pinglunimg: "",
      shoucangimg: "",
      dianzanimg: "",
      tiwen1img: "",
      dongtaiimg: "",
      gonggaoimg: "",
      zuduiimg: "",
      shezhiimg: "",
      accquireInte: "0"
      //  pinglun.png shoucang.png dianzan.png tiwen1.png dongtai.png gonggao.png zudui.png  shezhi.png
    };
  },
  computed: {},
  created() {
    this.initParams();
    this.backimage = require("images/right_arrow.png");
    this.pinglunimg = require("images/pinglun.png");
    this.shoucangimg = require("images/shoucang.png");
    this.dianzanimg = require("images/dianzan.png");
    this.tiwen1img = require("images/tiwen1.png");
    this.dongtaiimg = require("images/dongtai.png");
    this.gonggaoimg = require("images/gonggao.png");
    this.zuduiimg = require("images/zudui.png");
    this.shezhiimg = require("images/shezhi.png");
    this.getAcquire();
  },
  mounted() {},
  watch: {
    $route(to, from) {
      this.initParams();
      this.notmessages();
    }
  },
  methods: {
    initParams() {
      this.$nextTick(() => {
        this.UserInfo = this.$getUser;
      });
    },
    async getAcquire() {
      try {
        let params = {};
        let res = await this.$api.all.GETfn(params, "integral_user_get");
        if (res.result != "" && res.result != null) {
          window.localStorage.setItem(
            "accquireInte",
            JSON.stringify(res.result)
          );
          this.accquireInte = res.result.integral_number;
        }
        console.log(res); //accquireInte
      } catch (e) {
        console.log(e);
      }
    },
    async notmessages() {
      try {
        let params = {};
        let res = await this.$api.all.POSTfn(params, "notice_have");

        if (res.result) {
          if (res.result == "ok") {
            this.$store.dispatch("pullmespot", true);
            this.$forceUpdate();
          } else {
            this.$store.dispatch("pullmespot", false);
            this.$forceUpdate();
          }
        }
      } catch (e) {
        console.log(e);
      }
    }
  },
  computed: {
    mespot() {
      return this.$store.state.meSpot;
    }
  },
  components: {}
};
</script>

<style scoped lang="css">
.contetn_qw {
  display: block;
  text-align: center;
  font-size: 12px;
}

ul {
  list-style: none;
  margin: 0;
  padding-inline-start: 0;
}
li {
  margin: 0;
}
img {
  display: block;
}
a {
  color: #333333 !important;
  text-decoration: none;
}

.mainContent {
  height: calc(100vh-94px) !important;
  min-height: calc(100vh-94px) !important;
}

.content-head {
  height: 70px;
  background: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
}

.head-photo {
  height: 100%;
  width: 20%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.head-photo img {
  height: 44px;
  width: 44px;
  border-radius: 22px;
}

.head-text {
  font-size: 14px;
  text-align: left;
  height: 60px;
  /* line-height: 20px; */
  padding: 10px 0px;
  width: 70%;
}
.head-text span {
  display: block;
  width: 100%;
  height: 20px;
  line-height: 20px;
}
.head-text-name {
  color: #222;
  font-size: 16px;
  word-break: break-all;
  display: block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.head-text-phone {
  margin-top: 2px;
  color: #676767;
}

.content_th_1_right_arrow {
  height: 24px;
  width: 24px;
  float: right;
  /* margin: 28px 10px; */
}

.push_nbsp {
  height: 24px;
  width: 40px !important;
  text-align: right;
}
.push_nbsp img {
  width: 24px !important;
  height: 24px !important;
}

.content_th_1_right_arrow_top {
  flex: 1;
  height: 64px;
  padding: 20px 0px;
}
.content_th_1_right_arrow_top img {
  width: 20px;
  height: 20px;
}

.content_th_1_right_arrow span.spot {
  display: inline-block;
  width: 6px;
  height: 6px;
  background: rgb(216, 28, 28);
  border-radius: 50%;
}
.content_th_1_right_arrow img {
  width: 24px;
  height: 24px;
  display: inline-block;
}

.content_th .content_th_2 {
  min-height: 130px;
  height: 110px;
  background: #fff;
}

.content_th_2 {
  margin: 10px 0px;
}

.content_th_2 ul li {
  text-align: center;
  margin: 0 auto;
  width: 25%;
  float: left;
  padding: 3px 28px;
  font-size: 14px;
}
.content_th_2 ul li img {
  width: 86%;
  margin-left: 7%;
}

.content_th_3 {
  background: #fff;
  min-height: 320px;
  margin-top: 4px;
  text-align: left;
}

.content_th_3 ul li:nth-child(4) ul {
  display: none;
}
.content_th_3 ul li:nth-child(4):hover ul {
  display: block;
}

.content_th_3 ul li {
  border-bottom: 1px solid #eee !important;
  /* padding: 8px 0px 8px 20px; */
  margin: 0px 12px;
  padding: 9px 0px 9px 5px;
  font-size: 12px;
  height: 44px;
}
.content_th_3 .content_th_1_right_arrow {
  height: 24px;
  width: 24px;
  float: right;
  margin-right: 4px;
}

.content_th_3 ul li.yijian {
  border-bottom: none !important;
}

.content_th_3 ul li.lianxi {
  border-bottom: none !important;
}

.content_th_3 ul li ul li:nth-child(5) {
  border-bottom: none;
}
.content_th_3 ul li:nth-child(1) {
  position: relative;
}

.content_th_3 ul li:nth-child(1) span:nth-child(2) {
  color: #4a8dff;
  position: absolute;
  right: 40px;
}

.content_th_3 ul li span {
  display: inline-block;
  height: 24px;
  line-height: 24px;
  font-size: 14px;
}

.clearfix {
  clear: both;
}
</style>




