<template>
  <div class="mainCover">
    <!-- <HeaderBar :needb="true" :tit="titleName" v-show="isShow"></HeaderBar> -->
    <div class="mainContent">
      <div class="shipinbox">
        <video-player
          @click.stop
          class="video-player"
          :playsinline="playsinline"
          :options="playerOptions"
          :headers="headers"
          @play="onPlayerPlay($event)"
          @pause="onPlayerPause($event)"
          @ended="onPlayerEnded($event)"
          @loadeddata="onPlayerLoadeddata($event)"
          @waiting="onPlayerWaiting($event)"
          @playing="onPlayerPlaying($event)"
          @timeupdate="onPlayerTimeupdate($event)"
          @canplay="onPlayerCanplay($event)"
          @canplaythrough="onPlayerCanplaythrough($event)"
          @ready="playerReadied"
          @statechanged="playerStateChanged($event)"
        ></video-player>
        <div class="topvi">
          <h4 class="tit">{{detaildata.title}}</h4>
          <p class="titbottom">
            浏览量:{{detaildata.clicks}}
            <span style="margin-left:15px">{{detaildata.startTime}}</span>~<span>{{detaildata.endTime}}</span>
          </p>
          <p class="titinfo">{{detaildata.introduce}}</p>
        </div>
      </div>
      <div>
        <!-- 评论 -->
        <!-- v-if="comment.length!=0" -->
        <div class="commMain" v-if="comment.length!=0">
          <p class="swiperTitle">
            <span class="c-title-vline"></span> 热门评论
          </p>
          <div class="childcomme">
            <li v-for="(i,t) in comment" :key="t" class="por" @click.stop="gocommedetail(i)">
              <div class="commentbox">
                <div class="imgbox">

                   <img
                  :src="i.avatar?i.avatar:'../../../static/imgs/activity.png'"
                  @click.stop="privateinfo(i.user_id)"
                />
                <img :src="bigv" class="bigv1" v-if="i.is_vip==1" />
                </div>
               
                <!-- @click.stop="privateinfo(i.user_id)" -->
                <div class="commentcon">
                  <div>
                    <p class="review-nick">
                      {{i.user_name}}
                      <span class="color">
                        <span class="hh-fab-value">{{i.flowers}}</span>
                        <i
                          v-if="i.is_flower==1"
                          class="iconfont icon-bigGood"
                          @click.stop="removelike(i)"
                        ></i>
                        <i v-else class="iconfont icon-good" @click.stop="addlike(i)"></i>
                      </span>
                    </p>
                    <p class="review-area">{{i.text}}</p>
                  </div>
                  <div class="review-button">
                    <p :data-time="i.timestamp" class="review-time hh-time">
                      {{i.timestamp?i.timestamp:"刚刚"}} ·
                      <span class="replyBtn">&nbsp;回复</span>
                    </p>
                    <div v-if="i.user_id==$getUser.id" class="del" @click.stop="del(i,t)">删除</div>
                  </div>
                </div>
              </div>
              <!-- v-if="i.secondComment.length!=0" -->
              <div class="son">
                <div class="sonbox">
                  <div
                    v-for="(s,h) in i.secondComment"
                    :key="h"
                    class="sonitem"
                    @click.stop="gocommedetail(s,true)"
                  >
                     <div class="imgbox" style="width:40px">
                      <img :src="s.avatar?s.avatar:touxiang" alt @click.stop="privateinfo(s.user_id)" />
                    <img :src="bigv" class="bigv" v-if="s.is_vip=='1'"/>  
                  </div>
                    <div>
                      <p class="sonname">
                        {{s.nickname}}
                        <span class="color">
                          <span class="hh-fab-value">{{s.flowers}}</span>
                          <i
                            v-if="s.is_flower==1"
                            class="iconfont icon-bigGood"
                            @click.stop="removelike(s)"
                          ></i>
                          <i v-else class="iconfont icon-good" @click.stop="addlike(s)"></i>
                        </span>
                      </p>
                      <!-- <span class="sonReply" v-if="s.secondComment.length>0">{{s.secondComment.length}}回复</span> -->
                      <p class="sontext">{{s.text}}</p>

                      <div class="review-button">
                        <p :data-time="s.timestamp" class="review-time hh-time">
                          {{s.timestamp?s.timestamp:"刚刚"}} ·
                          <span class="replyBtn">&nbsp;回复</span>
                        </p>
                        <div
                          v-if="s.user_id==$getUser.id"
                          class="sondel"
                          @click.stop="delson(i,s,t)"
                        >删除</div>
                      </div>
                    </div>
                  </div>
                </div>
                <p
                  v-if="i.comments>2"
                  @click.stop="propShow(i.comment_id)"
                  class="comlength"
                >全部 {{i.comments}} 条评论>>></p>
              </div>
            </li>
            <div @click="more()" class="more" v-if="iddata.more==1">点击加载更多</div>
          </div>
        </div>
         <p v-else class="hh-comment">-----暂无评论------</p>
      </div>
    </div>
    <div class="bottom" v-if="isShow">
      <div class="detailbottom">
        <input
          type="text"
          :placeholder="placehoderInfo?'我 回复 '+ placehoderInfo: '我来讲讲....'"
          class="inp"
          id="sendcomment"
          v-model="commentvalue"
          @keyup.13="sendcomment()"
          @blur="inpblur()"
           @focus="inputfouse()"
        />
        <collectOrlike
          :targetid="vadioid.id?vadioid.id:vadioid.target_id"
          :pro="vadioid"
          ref="child"
          :loginid="useridaz"
          @fatherMethod="fatherfouse"
         
        v-show="commShow"
        ></collectOrlike>
      </div>
       <div class="sendbtn"  @touchstart.stop="sendcomment()">发送</div>
    </div>
    <mt-popup v-model="popupVisible" position="bottom">
      <div class="tanchu">
        <div class="close" @click="propShow()">
          <p class="swiperTitle">
            <span class="c-title-vline"></span> 全部回复
          </p>
          <p style="font-size:30px;padding-right:16px;">×</p>
        </div>
        <commedetail :propid="commid" ref="comme" :loginid="useridaz"></commedetail>
      </div>
    </mt-popup>
  </div>
</template>

<script>
import { MessageBox } from 'mint-ui';
import commedetail from "pages/home/commedetail.vue";
import HeaderBar from "com/Header-bar.vue";
import collectOrlike from "com/collectOrlike.vue";
import { videoPlayer } from "vue-video-player";
import pictwite from "pages/home/pictwite.vue";
import { ComUtils } from "common/utils";
import { Toast } from "mint-ui";
import moment from "moment";
import "video.js/dist/video-js.css";
import "vue-video-player/src/custom-theme.css";
export default {
  name: "video-play",
  props: {
    videoUrl: {
      type: String,
      defalut: ""
    },
    loadImgUrl: {
      type: String,
      defalut: ""
    },
    palyId: {
      type: String,
      defalut: ""
    }
  },
  filters: {
    changeTime(val) {
      return moment(val)
        .startOf("minute")
        .fromNow();
    }
  },
  data() {
    return {
      commShow:true,
      detaildata: [],
      bigv: require("images/bigv.png"),
      comment: [],
      commeval: "",
      isshow: false,
      ed: "",
      resdata: [],
      write: [],
      fid: "",
      iddata: [],
      touxiang: require("images/morentu.jpg"),
      playerOptionsarr: [],
      vadioid: "",
      isShow: false,
      commentvalue: "",
      commentid: "",
      popupVisible: false,
      commid: "",
      placehoderInfo: "",
      useridaz: "",
      keycode: true,
      playsinline: true,
      playerOptions: {
        events: [],
        playbackRates: [0.7, 1.0, 1.5, 2.0], // 播放速度
        autoplay: true, // 如果true,浏览器准备好时开始回放。
        controls: true, //  控制条
        preload: "auto", // 视频预加载
        muted: false, //  默认情况下将会消除任何音频。
        loop: false, // 导致视频一结束就重新开始。
        language: "zh-CN",
        controlBar: {
          timeDivider: true,
          durationDisplay: true
        },
        aspectRatio: "16:9", // 将播放器置于流畅模式，并在计算播放器的动态大小时使用该值。值应该代表一个比例 - 用冒号分隔的两个数字（例如"16:9"或"4:3"）
        fluid: true, // 当true时，Video.js player将拥有流体大小。换句话说，它将按比例缩放以适应其容器。
        sources: [
          {
            type: "application/x-mpegURL",
            src: ""
          }
        ],
        hls: true, // 启用hls？
        poster: "", // 你的封面地址
        width: document.documentElement.clientWidth,
        notSupportedMessage: "此视频暂无法播放，请稍后再试" //  允许覆盖Video.js无法播放媒体源时显示的默认信息。
      }
    };
  },
  beforeRouteLeave(to, from, next) {
    this.$destroy();
    next();
  },

  activated() {
    moment.locale("zh-cn");
    this.brige();
    this.getdetaildate(
      this.vadioid.id ? this.vadioid.id : this.vadioid.target_id
    );
    this.getvediosrc();
  },
  mounted() {},
  methods: {
    getvediosrc() {
      // this.useridaz = "319430217930703154";

      if (window.hasOwnProperty("webkit")) {
        this.isShow = false;
      } else {
        this.isShow = true;
        this.vadioid = JSON.parse(localStorage.getItem("vadiodata"));
        let that = this;
        setTimeout(function() {
          that.getcommentdate(
            that.vadioid.id ? that.vadioid.id : that.vadioid.target_id
          );
        }, 0);
      }
      // console.log("视频信息" + JSON.stringify(this.vadioid));
    },
    async getdetaildate(activeId) {
      this.comment = [];
      try {
        let res = await this.$api.all.GETfn(
          {},
          "detail",
            // "382201303146693113"
          this.vadioid.id ? this.vadioid.id : this.vadioid.target_id
        );
             if(app !=undefined && app.hasOwnProperty('sendClick')){
          app.sendClick(res.id,res.clicks)
        }else{

        }
        // Toast(res)
        res.endTime=res.endTime.substring(11)
        this.detaildata = res;
        this.playerOptions.sources[0].src = res.path;
        this.playerOptions.poster = res.cover;
      } catch (e) {}
      // this.onetuisong(a)
    },
    fatherfouse() {
      var articleheight = document.getElementById("article").offsetHeight;
      document.querySelector(".mainContent").scrollTo(0, articleheight + 20);
      //  document.getElementById("sendcomment").focus();
    },
    goFrom(el) {
      //  this.$router.push({
      //   name: "newFrom"
      // });
      let goUrl = "newFrom";
      var str = location.href.slice(0, 20);

      if (window.hasOwnProperty("webkit")) {
        window.webkit.messageHandlers.openSourceArticles.postMessage(
          str + "/#/" + goUrl
        );
      } else {
        app.gotoDetail(str, goUrl, el.target.innerHTML);
      }
    },
    //ios获取评论
    propShow(pro) {
      if (pro) {
        this.commid = pro;
        let that = this;
        setTimeout(function() {
          that.$refs.comme.getcommedetail();
        });
      } else {
        // this.comment = [];
        // this.getcommentdate(
        //   this.vadioid.id ? this.vadioid.id : this.vadioid.target_id
        // );
      }
      this.popupVisible = !this.popupVisible;
    },
    gocommedetail(item, isself) {
      localStorage.setItem("commid", item);
      if (window.hasOwnProperty("webkit")) {
        let params = {
          id: item.id
        };
        window.webkit.messageHandlers.replyCommentByID.postMessage(params);
      } else {
        if (item.user_id == this.useridaz) {
          Toast("不能自己回复自己");
          return;
        }

        document.getElementById("sendcomment").focus();
        if (!item) {
          return;
        }
        this.commentid = item.id;
        this.placehoderInfo = item.nickname ? item.nickname : item.user_name;
      }
    },
      inputfouse(){
            this.commShow=false
      document.querySelector(".sendbtn").style.display="block"
    },
    inpblur() {
       this.commShow=true
      this.commentid = "";
      this.placehoderInfo = "";
       document.querySelector(".sendbtn").style.display="none"
    },
    //发表评论
    async sendcomment() {
      if (!this.useridaz) {
        app.goLogin();
      }
      if (this.commentvalue.trim() == "") {
        Toast("评论信息不能为空");
        return;
      }
      try {
        if (this.keycode == true) {
          this.keycode = false;
          let params = {
            target: this.commentid
              ? this.commentid
              : this.vadioid.id
              ? this.vadioid.id
              : this.vadioid.target_id,
            text: this.commentvalue,
            userid: this.useridaz,
            user_id: this.useridaz,
            _: "RFs0",
            type: "article"
          };
          // ;
          let res = await this.$api.all.POSTfn(params, "send_comment");
          Toast("发布成功");
          this.commentvalue = "";
          this.placehoderInfo = "";
          this.getcommentdate(
            this.vadioid.id ? this.vadioid.id : this.vadioid.target_id
          );
          this.$refs.child.getcomment();
          this.commentid = "";
          // this.getcomment()
          this.keycode = true;
            if(app !=undefined && app.hasOwnProperty('backList')){
          app.backList()
        }else{

        }
        }
      } catch (e) {
        console.log(e);
      }
      if (this.commentvalue.indexOf("@" + localStorage.getItem("fn")) != -1) {
        this.atfriend();
      }
    },
    brige() {
      let that = this;
      window.getArticleInfo = function(data, getuserid) {
        that.useridaz = getuserid;
        that.vadioid = data;
        that.getdetaildate(data.id ? data.id : data.target_id);
        that.getcommentdate(
          that.vadioid.id ? that.vadioid.id : that.vadioid.target_id
        );
        that.getcommentdate(
          that.vadioid.id ? that.vadioid.id : that.vadioid.target_id
        );
      };
      window.iosGetcommune = function() {
        that.getcommentdate(data.id);
      };
    },
    //去点赞
    async addlike(data) {
      if (!this.useridaz) {
        app.goLogin();
      }
      let params = {
        target: data.comment_id,
        userid: this.useridaz,
        user_id: this.useridaz
      };
      try {
        let res = await this.$api.all.POSTfn(params, "good");
        if (res.result == "ok") {
          this.$set(data, `is_flower`, 1);
          let num = Number(data.flowers);
          num = num += 1;
          this.$set(data, `flowers`, num);
          console.log("点赞数量" + num);
            if(app !=undefined && app.hasOwnProperty('backList')){
          app.backList()
        }else{

        }
        }
        console.log(res);
      } catch (e) {
        console.log(e);
      }
    },
    // 取消点赞
    async removelike(data) {
      let params = {
        target: data.comment_id,
        userid: this.useridaz,
        user_id: this.useridaz
      };
      try {
        let res = await this.$api.all.POSTfn(params, "good_delete");
        if (res.result == "ok") {
          this.$set(data, `is_flower`, 0);
          let num = Number(data.flowers);
          num = num -= 1;
          this.$set(data, `flowers`, num);
          console.log("点赞数量" + num);
            if(app !=undefined && app.hasOwnProperty('backList')){
          app.backList()
        }else{

        }
        }
      } catch (e) {
        console.log(e);
      }
    },
    // 获取评论数据
    async getcommentdate(id, direct, hot) {
      if (!hot) {
        this.comment = [];
      }
      try {
        let params = {
          target: id,
          target_id: hot,
          direction: direct,
          count: 5,
          userid: this.useridaz ? this.useridaz : 2,
          user_id: this.useridaz ? this.useridaz : 2
        };
        let res = await this.$api.all.GETfn(params, "comment");
        for (let i in res.result.data) {
          let now = new Date().getTime();
          res.result.data[i].timestamp = this.timeago(
            res.result.data[i].timestamp * 1000
          );
           if(res.result.data[i].avatar){
           if( res.result.data[i].avatar.endsWith(".com/")){
            res.result.data[i].avatar='../../../static/imgs/morentu.jpg'
          }
           }
          for (let t in res.result.data[i].secondComment) {
            res.result.data[i].secondComment[t].timestamp = this.timeago(
              res.result.data[i].secondComment[t].timestamp * 1000
            );
             if(res.result.data[i].secondComment[t].avatar){
             if( res.result.data[i].secondComment[t].avatar.endsWith(".com/")){
            res.result.data[i].secondComment[t].avatar='../../../static/imgs/morentu.jpg'
          }
             }
          }
        }
        // this.comment=res.result.data
        this.comment = this.comment.concat(res.result.data);
        this.iddata = res.result;
        this.$refs.child.iscollect();
        this.$refs.child.islike();
        this.$refs.child.likenum();
        this.$refs.child.getcomment();
        console.log(res);
      } catch (e) {
        console.log(e);
      }
    },

    more() {
      let dictir = "litter";
      let hot = this.iddata.min_id;
      if (this.iddata.more == 0) {
        Toast("无更多数据");
      } else {
        this.getcommentdate(
          this.vadioid.id ? this.vadioid.id : this.vadioid.target_id,
          dictir,
          hot
        );
      }
    },
    timeago(dateTimeStamp) {
      //dateTimeStamp是一个时间毫秒，注意时间戳是秒的形式，在这个毫秒的基础上除以1000，就是十位数的时间戳。13位数的都是时间毫秒。
      var result;
      var minute = 1000 * 60; //把分，时，天，周，半个月，一个月用毫秒表示
      var hour = minute * 60;
      var day = hour * 24;
      var week = day * 7;
      var halfamonth = day * 15;
      var month = day * 30;
      var now = new Date().getTime(); //获取当前时间毫秒
      var diffValue = now - dateTimeStamp; //时间差

      if (diffValue < 0) {
        return;
      }
      var minC = diffValue / minute; //计算时间差的分，时，天，周，月
      var hourC = diffValue / hour;
      var dayC = diffValue / day;
      // var weekC = diffValue / week;
      // var monthC = diffValue / month;
      // if (monthC >= 1 && monthC <= 3) {
      // 	result = " " + parseInt(monthC) + "月前";
      // } else if (weekC >= 1 && weekC <= 3) {
      // 	result = " " + parseInt(weekC) + "周前";
      // } else
      if (dayC >= 1 && dayC <= 3) {
        result = " " + parseInt(dayC) + "天前";
      } else if (hourC >= 1 && hourC <= 23) {
        result = " " + parseInt(hourC) + "小时前";
      } else if (minC >= 1 && minC <= 59) {
        result = " " + parseInt(minC) + "分钟前";
      } else if (diffValue >= 0 && diffValue <= minute) {
        result = "刚刚";
      } else {
        var datetime = new Date();
        datetime.setTime(dateTimeStamp);
        var Nyear = datetime.getFullYear();
        var Nmonth =
          datetime.getMonth() + 1 < 10
            ? "0" + (datetime.getMonth() + 1)
            : datetime.getMonth() + 1;
        var Ndate =
          datetime.getDate() < 10
            ? "0" + datetime.getDate()
            : datetime.getDate();
        var Nhour =
          datetime.getHours() < 10
            ? "0" + datetime.getHours()
            : datetime.getHours();
        var Nminute =
          datetime.getMinutes() < 10
            ? "0" + datetime.getMinutes()
            : datetime.getMinutes();
        var Nsecond =
          datetime.getSeconds() < 10
            ? "0" + datetime.getSeconds()
            : datetime.getSeconds();
        result = Nyear + "-" + Nmonth + "-" + Ndate;
      }
      return result;
    },

    //删除评论
    async del(item, index) {
       MessageBox.confirm('确定删除此条评论?').then(async action => {

      try {
        let params = {
          // userid: "327195464540160441",
          comment_id: item.id
        };
        let res = await this.$api.all.POSTfn(params, "delcomme");
        console.log(res);
        if (res.result == "ok") {
          console;
          this.comment.splice(index, 1);
          this.$refs.child.getcomment();
        }
      } catch (e) {
        console.log(e);
      }
       })
    },
    //删除评论
    async delson(s, item, index) {
       MessageBox.confirm('确定删除此条评论?').then(async action => {

      try {
        let params = {
          // userid: "327195464540160441",
          comment_id: item.id
        };
        let res = await this.$api.all.POSTfn(params, "delcomme");
        console.log(res);
        if (res.result == "ok") {
          s.secondComment.splice(index, 1);
          this.$refs.child.getcomment();
        }
      } catch (e) {
        console.log(e);
      }
       })
    },
    //去个人资料页
    privateinfo(v) {
      if (!this.useridaz) {
        app.goLogin();
      } else {
        app.gouserInfo(v);
      }
    
    }
    
  },
  components: { HeaderBar, collectOrlike, videoPlayer, pictwite, commedetail },
  computed: {
    player() {
      return this.$refs.videoPlayer.player;
    }
  }
};
function onConnectionChange() {
   if(navigator.onLine==false){
     setTimeout(function() {
       Toast("网络错误,请检查网络连接")
     }, 2000);
     
   }else{
     window.location.reload();
   }
    // console.log('是否联网:');
    // console.log(navigator.onLine);
    // const { rtt, downlink, effectiveType, saveData } = navigator.connection;
    // console.log(`有效网络连接类型: ${effectiveType}`);
    // console.log(`估算的下行速度/带宽: ${downlink}Mb/s`);
    // console.log(`估算的往返时间: ${rtt}ms`);
    // console.log(`打开/请求数据保护模式: ${saveData}`)
};
 
navigator.connection.addEventListener('change', onConnectionChange);
</script>
<style scoped lang="css">
.swiperTitle {
  height: 50px;
  text-align: left;
  box-sizing: border-box;
  font-weight: 700;
  display: block;
  width: 100%;
  padding: 10px 12px 0px 12px;
  font-size: 18px;
}
.tanchu {
  height: 500px;
  width: 101%;
  overflow-y: scroll;
}
.swiper-container {
  width: 375px !important;
}
.close {
  font-size: 20px;
  color: #333;
  text-align: right;
  position: fixed;
