<template>
  <div class="Cover">
    <p v-show="allList.length==0" class="nothing">——— Ta还没有参与的项目 ———</p>
    <ul class="scroll-ul">
      <vue-scroll
        ref="scroll"
        :ops="opsconfig"
        @refresh-start="handleRS"
        @load-before-deactivate="handleLBD"
      >
        <li v-for="(v,i) in allList" :key="i" class="scroll-li" @click="gocommune(v)">
          <div class="content">
            <div class="content-left">
              <p class="title">{{v.title}}</p>
              <p class="word">{{v.content}}</p>
              <p class="from">来自：{{v.commune_name}}</p>
            </div>
            <div class="content-right">
              <img v-lazy="v.image_url" class="page-lazyload-image1" />
            </div>
          </div>
        </li>
      </vue-scroll>
    </ul>
  </div>
</template>

<script>
import { ScrollConfig } from "common/data.js";
import moment from "moment";
export default {
  name: "addInProjects",
  props: {},
  data() {
    return {
      opsconfig: ScrollConfig.opsconfig,
      titleName: "参与项目",
      needb: true,
      UserParam: {},
      allList: [],
      UserInfo: this.$getUser,
      max_id: "",
      min_id: ""
    };
  },
  computed: {},
  created() {
    this.UserParam = JSON.parse(window.sessionStorage.getItem("S-itemv"));
    this.getDate("bigger", "0");
  },
  mounted() {
    this.$nextTick(() => {
      document.querySelector(".scroll-ul").click();
    });
  },
  watch: {
    $route(to, from) {
      this.UserParam = JSON.parse(window.sessionStorage.getItem("S-itemv"));
      this.getDate("bigger", "0");
      this.$nextTick(() => {
        document.querySelector(".scroll-ul").click();
      });
    }
  },
  methods: {
    gocommune(v) {
      console.log(v);
      window.sessionStorage.setItem("askdata", JSON.stringify(v));
      this.$router.push("commune_f");
    },
    async getDate(type, numsid) {
      try {
       
        let param = {
          count: 10,
          direction: type,
          target_id: numsid,
          vuserid: this.UserParam.user_id,
          userid: this.UserParam.user_id,
          user_id: this.UserParam.user_id,
          token: ""
        };
        let res = await this.$api.all.GETfn(param, "vip_join_team"); //likeimg
        res = res.result;
        this.allList = res.data;
        this.max_id = res.max_id;
        this.min_id = res.min_id;
      } catch (error) {
        console.log(error);
      }
    },
    async handleRS(vsInstance, refreshDom, done) {
      // alert("这是执行下拉刷新开始做啥事");
      try {
        let param = {
          count: 10,
          direction: "bigger",
          target_id: this.max_id,
          vuserid: this.UserParam.user_id,
          userid: this.UserParam.user_id,
          user_id: this.UserParam.user_id,
          token: ""
        };
        let res = await this.$api.all.GETfn(param, "vip_join_team");
        /************************************* */

        res = res.result;
        if (res.more == "0" && res.data.length == 0) {
          this.$toast("暂无最新数据");
          done();
          return;
        }
        if (res.data.length > 0) {
          this.max_id = res.max_id;
          // this.min_id = res.min_id;
          res.data.forEach((item, index, arr) => {
            this.allList.unshift(item);
          });
          this.$forceUpdate();
        }
        /************************************* */
        done();
      } catch (error) {}
    },
    async handleLBD(vm, loadDom, done) {
      // alert("这是执行下拉加载之后需要执行什么操作");
      try {
        let param = {
          direction: "litter",
          target_id: this.min_id,
          count: 10,
          vuserid: this.UserParam.user_id,
          userid: this.UserParam.user_id,
          user_id: this.UserParam.user_id,
          token: ""
        };
        let res = await this.$api.all.GETfn(param, "vip_join_team");
        /************************************* */
        res = res.result;
        if (res.more == "0" && res.data.length == 0) {
          this.$toast("没有更多数据");
          done();
          return;
        }
        if (res.data.length > 0) {
          // this.max_id = res.max_id;
          this.min_id = res.min_id;
          res.data.forEach((item, index, arr) => {
            this.allList.push(item);
          });
          this.$forceUpdate();
        }
        /************************************* */

        done();

        console.log(this.allList);
      } catch (error) {}
    }
  },
  components: {}
};
</script>

<style scoped lang="css">
.Cover {
  width: 100%;
  height: calc(100vh-204px);
  min-height: calc(100vh-204px);
}

.scroll-ul {
  width: 100%;
  height: calc(100vh-204px);
  min-height: calc(100vh-204px);
  padding: 0px 12px;
}

.scroll-li {
  width: 100%;
  height: 86px;
  min-height: 86px;
}

.content {
  width: 100%;
  height: 86px;
  min-height: 86px;
  padding: 8px 0px;
  display: flex;
  border-bottom: 1px solid #eaeaea;
}

.content-left {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.content-left p {
  height: 20px;
  line-height: 20px;
}

.state1 {
  color: rgb(240, 164, 0);
}
.state2 {
  color: rgb(0, 160, 35);
}
.state3 {
  color: rgb(200, 0, 0);
}

.title {
  font-size: 12px;
  min-height: 20px;
  line-height: 20px;
  color: #333;
  text-align: justify;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  display: -webkit-box !important;
}

.word {
  font-size: 12px;
  min-height: 20px;
  height: 20px;
  line-height: 20px;
  color: #666;
  text-align: justify;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  display: -webkit-box !important;
}

.from {
  min-height: 20px;
  font-size: 10px;
  color: #999;
  display: inherit;
  align-items: center;
  text-align: justify;
}

.content-right {
  width: 35%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row-reverse;
  margin-left: 10px;
  padding: 5px;
}

.content-right img {
  width: 100%;
  max-height: 98%;
  display: inline-block;
}

.nothing {
  margin-top: 15px;
  font-size: 12px;
  color: #999;
  height: 40px;
  line-height: 40px;
  width: 100%;
  text-align: center;
}
</style>



