<template>
    <div class="mainCover">
        <HeaderBar :needm="needm" :needb="needb" :tit="titleName"></HeaderBar>

        <div class="add">
            <div class="search-logo" > <img :src="search" alt="" class="search-img" > </div>
            <input type="tel" v-model="phone" placeholder="请输入您的手机号"/>
            <button type="button" @click="findFriend">查找</button>
        </div>
        <ul>
            <li class="friend-list" v-if="friend.account">
                <div class="head-portrait-box">
                    <img :src="friend.avatar" alt class="head-portrait" />
                </div>
                <div class="name-box">
                    <div class="info">
                    <p class="name">{{friend.name}}</p>
                    </div>
                    <div class="add-button" @click="addFriend">{{isFriend ? '已添加' : '加为好友'}}</div>
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
import { Toast } from 'mint-ui';
import HeaderBar from "com/Header-bar.vue";
export default {
    name: "news_friends",
    props: {},
    data() {
        return {
            titleName: "添加好友",
            needb: true,
            needm: false,
            phone: '',
            search: "",
            friend: {}
        };
    },
    computed: {
        isFriend(id){
            return this.friendslist.some(({account})=>account === this.friend.id)
        },
        friendslist(){
            return this.$store.state.friendslist;
        },
    },
    created() {
        this.search = require("images/search.png");
    },
    mounted() {},
    watch: {},
    methods: {
        async findFriend(){
            try {
                let params = {
                    s: this.phone
                };
                let res = await this.$api.all.GETfn(params, "xiaoxi_find");
                if (res.result == "ok") {
                    this.friend = Object.assign({}, this.friend, res.user);
                }
            } catch (e) {
                console.log(e);
            }
        },
        addFriend(){
            // if(this.isFriend) return Toast('对方已经是您的好友！');
            nim.applyFriend({
                account: this.friend.id,
                done: applyFriendDone
            });

            function applyFriendDone(error, obj) {
                console.log(error);
                console.log(JSON.stringify(obj));
                if (error) {
                    return;
                }
                Toast('申请加为好友' + (!error ? '成功' : '失败'));
                console.log('申请加为好友' + (!error ? '成功' : '失败'));
                return;
            }
        }
    },
    components: { 
        HeaderBar
    }
};
</script>

<style scoped lang="css">
.add{
    height:50px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #f6f7f9;
}

button{
	width:21%;
	height:34px;
	background:#4a8dff;
	color:#fff;
	font-size: 10px;
    border-radius: 0px 3px 3px 0px;
}

input {
    width: 63%;
    height: 34px;
    font-size: 12px;
}

.search-logo {
    width: 9%;
    height: 34px;
    background-color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 3px 0px 0px 3px;
}

.search-img {
    width: 15px;
    height: 15px;
}
ul {
  list-style-type: none;
}

li {
  width: 100%;
  overflow: hidden;
}

.friend-list {
  width: 100%;
  display: flex;
  height: 60px;
  padding-right: 12px;
}

.head-portrait-box {
  width: 15%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.head-portrait {
  width: 36px;
  height: 36px;
  border-radius: 50%;
}

.info {
  width: 70%;
}

.name-box {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  border-bottom: 1px solid #eee;
}

.name {
  display: block;
  font-size: 14px;
  color: #222;
}

.add-button {
  width: 26%;
  height: 30px;
  font-size: 12px;
  background-color: #4a8dff;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 3px;
}

.preview {
  font-size: 12px;
  color: #666;
}
</style>



