<template>
  <div class="mainCover">
    <HeaderBar :needb="needb" :needs="needs" @fathersubmit="fathersubmit" :tit="titleName"></HeaderBar>
    <div class="mainContent">
      <div class="head_th">
        <div class="head_th_1">
          <div class="head_th_1_1">
            <img :src="imagenbsp" ref="imagenbsp" id="imagenbsp" />
          </div>
        </div>
        <mt-palette-button
          content="+"
          direction="t"
          class="pb"
          :radius="80"
          ref="target_2"
          mainButtonStyle="color:yellow;background-color:#26a2ff;"
          :offset="Math.PI / 12"
          style="left:calc(50% - 30px);"
        >
          <div class="my-icon-button indexicon icon-popup" @touchstart="sub_log(1)"></div>
          <div class="my-icon-button indexicon icon-popup" @touchstart="sub_log(2)"></div>
          <div class="my-icon-button indexicon icon-popup" @touchstart="sub_log(3)"></div>
          <div class="my-icon-button indexicon icon-popup" @touchstart="sub_log(4)"></div>
          <div class="my-icon-button indexicon icon-popup" @touchstart="sub_log(5)"></div>
          <div class="my-icon-button indexicon icon-popup" @touchstart="sub_log(6)"></div>
        </mt-palette-button>
        <div class="photoaddress">
          <Photoclipcom
            viewdom="imagenbsp"
            outputSizeWidth="480"
            outputSizeHeight="480"
            :inSize="INSIZE"
            @fatherfromcall="getchilfromdata"
            @fatherimgcall="getchildimgcall"
          ></Photoclipcom>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ComUtils } from "common/utils.js";
export default {
  name: "personal_head",
  props: {},
  data() {
    return {
      titleName: "个人头像",
      needb: true,
      UserInfo: this.$getUser,
      needs: true,
      imagenbsp: "",
      photoflag: false,
      formData: new FormData(),
      hasfromdata: false,
      INSIZE: [240, 240]
    };
  },
  computed: {},
  created() {
    this.imagenbsp = this.UserInfo.avatar + "/big";
  },
  mounted() {},
  watch: {},
  methods: {
    getchildimgcall(data) {
      this.hasfromdata = true;
      this.imagenbsp = data;
    },
    getchilfromdata(data) {
      this.hasfromdata = true;
      this.formData = data;
    },
    async fathersubmit() {
      if (!this.hasfromdata) {
        this.$toast("您还未做选择");
        return;
      }
      try {
        let params = this.formData;
        params.append("image_data", this.imagenbsp);

        let res = await this.$api.all.POSTfn(params, "user_set_newlogo");
        this.$toast("修改成功");
        this.imagenbsp = res.result + "/big";
        this.UserInfo.avatar = res.result;
        ComUtils.saveParams(this.UserInfo);
        this.$router.go(-1);
      } catch (e) {
        this.$toast(e);
      }
    }
  },
  components: {},
  beforeRouteLeave(to, from, next) {
    this.hasfromdata = false;
    next();
  }
};
</script>

<style scoped lang="css">
.head_th {
  width: 100%;
  min-height: 160px;
}
.head_th_1 {
  padding: 20px;
}
.head_th_1_1 {
  /* border: 2px white dashed; */
  padding: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.head_th_1_1 img {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  border: 1px solid #eee;
  /* box-shadow: 0 1px 4px #eee; */
}

.head_th_2 {
  width: 100%;
  height: 40px;
  line-height: 40px;
  display: flex;
}

.head_th_2_div {
  line-height: 40px;
  height: 40px;
  flex: 1;
  text-align: center;
}

.head_th_2 button {
  width: 100px;
  height: 40px;
  line-height: 40px;
  background: #4a8dff;
  color: white;
  font-size: 14px;
  border-radius: 5px;
}

.photoaddress {
  width: 58px;
  height: 58px;
  display: block;
  position: relative;
  left: 50%;
  margin-left: -29px;
  top: -60px;
}
</style>



