<template>
  <div class="mainCover">
    <HeaderBar :needb="needb" :tit="titleName"></HeaderBar>
    <div class="mainContent">
      <ul class="activity_th">
        <p v-show="allList.length==0" class="nothing">——— 暂无活动 ———</p>
        <vue-scroll
          ref="scroll"
          :ops="opsconfig"
          @refresh-start="handleRS"
          @load-before-deactivate="handleLBD"
        >
          <li class="activity-li" @click.prevent="gotoactdeta(v)" v-for="(v,i) in allList" :key="i">
            <div class="activity-left">
              <p class="titlea">{{v.title}}</p>
              <!-- <h6>{{v.content}}</h6> -->
              <span class="data">{{v.publish_time | changeTime}}</span>
            </div>
            <div class="activity-right">
              <img v-lazy="v.cover" class="page-lazyload-image1" />
            </div>
          </li>
        </vue-scroll>
      </ul>
    </div>
  </div>
</template>

<script>
import { ScrollConfig } from "common/data.js";
import moment from "moment";
import { ComUtils } from "common/utils.js";

export default {
  name: "me_activity",
  props: {},
  data() {
    return {
      opsconfig: ScrollConfig.opsconfig,
      titleName: "线下活动",
      needb: true,
      allList: [],
      UserInfo: this.$getUser,
      max_id: "",
      min_id: ""
    };
  },
  computed: {},
  created() {
    moment.locale("zh-cn");
    this.getDate("bigger", "0");
  },
  mounted() {},
  watch: {},
  methods: {
    gotoactdeta(v) {
      window.sessionStorage.setItem("homedata", JSON.stringify(v));
      // this.$router.push({
      //   name: "homeDetail"
      // });
      let that = this;
      ComUtils.skipPage(v, that);
    },
    async getDate(type, numsid) {
      try {
        debugger;
        let timestamp = new Date().getTime();
        let param = {
          count: 10,
          timestamp: timestamp,
          direction: type,
          target_id: numsid
        };
        let res = await this.$api.all.GETfn(param, "activity_list");
        res = res.result;
        this.max_id = res.max_id;
        this.min_id = res.min_id;
        this.allList = res.data;

        console.log("aaaaaaaaaa:" + this.allList);
      } catch (error) {
        console.log(error);
      }
    },
    async handleRS(vsInstance, refreshDom, done) {
      // alert("这是执行下拉刷新开始做啥事");
      try {
        let timestamp = new Date().getTime();
        let param = {
          count: 10,
          timestamp: timestamp,
          direction: "bigger",
          target_id: this.max_id
        };
        let res = await this.$api.all.GETfn(param, "activity_list");
        res = res.result;

        if (res.data.length == 0) {
          this.$toast("暂无最新数据");
          done();
          return;
        }
        if (res.data.length > 0) {
          this.max_id = res.max_id;
          this.min_id = res.min_id;
          res.data.forEach((item, index, arr) => {
            this.allList.unshift(item);
          });
          this.$forceUpdate();
        }
        console.log(this.allList);
        done();
      } catch (error) {}
    },
    async handleLBD(vm, loadDom, done) {
      // alert("这是执行下拉加载之后需要执行什么操作");
      try {
        let timestamp = new Date().getTime();
        let param = {
          count: 10,
          timestamp: timestamp,
          direction: "litter",
          target_id: this.min_id
        };
        let res = await this.$api.all.GETfn(param, "activity_list");
        res = res.result;
        if (res.data.length == 0) {
          this.$toast("没有更多数据");
          done();
          return;
        }
        if (res.data.length > 0) {
          this.max_id = res.max_id;
          this.min_id = res.min_id;
          res.data.forEach((item, index, arr) => {
            this.allList.push(item);
          });
          this.$forceUpdate();
        }
        done();

        console.log(this.allList);
      } catch (error) {}
    }
  },
  components: {},
  filters: {
    changeTime(val) {
      return moment(val)
        .startOf("minute")
        .fromNow();
    }
  }
};
</script>

<style scoped lang="css">
.mainContent {
  min-height: calc(100vh- 44px) !important;
  height: calc(100vh- 44px) !important;
}

.activity_th {
  text-align: left;
  font-size: 14px;
  width: 100%;
  min-height: calc(100vh- 44px) !important;
  height: calc(100vh- 44px) !important;
  padding: 0px 12px;
}

.activity-li {
  height: 110px;
  min-height: 110px;
  width: 100%;
  /* padding: 5px 0px; */
  display: flex;
  border-bottom: 1px solid #eee;
}

.activity-left {
  width: 62%;
  padding: 12px 0px;
  /* position: relative; */
  /* height: 100px; */
  display: flex;
  flex-wrap: wrap;
  align-content: space-between;
}
p.titlea {
  width: 217px;
  color: #333;
  line-height: 20px;
  min-height: 20px;
  font-size: 14px;
  width: 200px;
  word-break: break-all;
  display: -webkit-box;
  -webkit-line-clamp: 3; /*限制在一个块元素显示的文本的行数*/
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.activity-left h6 {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;
  overflow: hidden;
  height: 50%;
  width: 100%;
  /* line-height: 20px; */
  font-size: 12px;
  color: #666;
}
span.data {
  /* display: block; */
  width: 100%;
  height: 20px;
  line-height: 20px;
  font-size: 12px;
  color: #999;
  /* padding-right: 5px; */
}
.activity-right {
  flex: 1;
  height: 100%;
  padding: 10px 0px 10px 10px;
}

.activity-right img {
  width: 100%;
  height: 96%;
  display: block;
}

.nothing {
  margin-top: 15px;
  font-size: 12px;
  color: #999;
  height: 40px;
  line-height: 40px;
  width: 100%;
  text-align: center;
}
</style>



