<template>
  <div class="mainCover">
    <HeaderBar :needb="needb" :needch="needch" @fatherchange="Release" :tit="titleName"></HeaderBar>
    <div class="mainContent">
      <input type="text" placeholder="请您输入新的昵称" v-model="submitName" maxlength="30" />
    </div>
  </div>
</template>

<script>
import { ComUtils } from "common/utils";
export default {
  name: "personal_name",
  props: {},
  data() {
    return {
      titleName: "更改昵称",
      needb: true,
      UserInfo: this.$getUser,
      submitName: "",
      needch: true
    };
  },
  computed: {},
  created() {
    this.submitName = this.UserInfo.name;
  },
  mounted() {},
  watch: {},
  methods: {
    async Release() {
      try {
        if (this.submitName == "" || this.submitName == null) {
          this.$toast("您未输入新的昵称");
          return;
        }
        if (this.submitName.length < 2) {
          this.$toast("您输入得昵称信息不得少于2个字符");
          return;
        }
        let params = {
          name: this.submitName,
          userid: this.UserInfo.id,
          user_id: this.UserInfo.id,
          token: this.UserInfo._token
        };
        let res = await this.$api.all.GETfn(params, "set_user");
        if (res.result == "ok") {
          this.$toast("修改成功");
          this.UserInfo.name = this.submitName;
          ComUtils.saveParams(this.UserInfo);
          this.$router.go(-1); //返回上一页
        } else {
          this.$toast(res.result);
        }
      } catch (e) {
        this.$toast(e);
      }
    }
  },
  components: {}
};
</script>

<style scoped lang="css">
input {
  display: block;
  width: 96%;
  height: 40px;
  line-height: 40px;
  border: 1px solid #ddd;
  margin: 15px 2%;
  text-indent: 1em;
  font-size: 14px;
  border-radius: 3px;
}

input:focus {
}

.button {
  width: 100%;
  text-align: right;
  padding-right: 20px;
}

button {
  width: 75px;
  height: 38px;
  line-height: 38px;
  color: #fff;
  background: #023249;
  border-radius: 5px;
  font-size: 16px;
}
</style>



