<template>
  <div class="mainCover">
    <HeaderBar :needm="needm" :needb="needb" :tit="titleName"></HeaderBar>
    <div class="mainContent">
      <div class="club-name">
        <input
          type="text"
          placeholder="#在这里输入你想要发布的话题#"
          v-model="commname"
          name="name"
          maxlength="10"
        />
      </div>
      <div class="createclubs_tq" id="describe">
        <!-- <label>*描述</label> -->
        <textarea
          rows="3"
          maxlength="14"
          placeholder="请输入话题描述....."
          style="height:200px;width:100%;color:#333"
          id="question"
          name="info"
          v-model="introduction"
        ></textarea>
        <div class="num">{{introduction.length}}/14</div>
      </div>
      <div class="btn">
        <button type="button" class="c-ok-btn" id="yBtn" @click="commune_cj()">发布</button>
      </div>
    </div>
  </div>
</template>

<script>
import Thumbnail from "com/thumbnail.vue";
import { Toast } from "mint-ui";
export default {
  name: "community",
  props: {},
  data() {
    return {
      commname: "",
      introduction: "",
      titleName: "发布话题",
      needb: true,
      needm: false,
      bgImage: "",
      communecj: [],
      sheetVisible: false,
      type: "",
      rdata: [],
      INSIZE: [300, 150]
    };
  },
  computed: {},
  activated() {
    this.bgImage = require("images/lvse.jpg");
    this.getParams();
  },
  mounted() {},
  watch: {},
  methods: {
    getParams() {
      var routerdata = window.sessionStorage.getItem("commitydata");
      // console.log(routerdata);
      this.rdata = JSON.parse(routerdata);
    },
    getchildimgcall(data) {
      this.formItem.imgi = data;
    },
    getchilfromdata(data) {
      this.param.append("file", data.get("file"));
    },
    //发布社问
    async commune_cj() {
      var ast = window.sessionStorage.getItem("asktype");
      if (this.commname == "" || this.introduction == "") {
        return;
      }
      nim.createTeam({
        type: "advanced",
        name: "高级群",
        avatar: "avatar",
        // accounts: ["a1", "a2"],
        intro: "群简介",
        announcement: "群公告",
        // joinMode: 'needVerify',
        // beInviteMode: 'needVerify',
        // inviteMode: 'manager',
        // updateTeamMode: 'manager',
        // updateCustomMode: 'manager',
        level: 50,
        ps: "我建了一个高级群",
        custom: "群扩展字段, 建议封装成JSON格式字符串",
        done: createTeamDone
      });
      function createTeamDone(error, obj) {
        console.log(error);
        console.log(obj);
        console.log("创建" + obj.team.type + "群" + (!error ? "成功" : "失败"));
        if (!error) {
          onCreateTeam(obj.team, obj.owner);
        }
      }
      this.formItem.commname = "";
      this.formItem.introduction = "";
      Toast("发布成功,等待社长审核");
    }
  },
  components: { Thumbnail }
};
</script>

<style scoped lang="css">
.content {
  padding: 0px 12px;
}
/* 
.club-name {
  height: 36px;
  display: flex;
  align-items: center;
  border: 1px solid #eee;
  border-radius: 3px;
  margin-top: 10px;
} */
.club-name input {
  width: 100%;
  background: #f6f7f9;
  height: 40px;
  font-size: 14px;
}

input {
  font-size: 12px;
  line-height: 30px;
  padding: 0 10px;
}

#question {
  width: 100%;
  margin-top: 10px;
  font-size: 14px;
  background: #f6f7f9;
}
.weixuanze {
  margin-left: 10px;
}
textarea {
  border: 0px;
  padding: 10px;
}
.btn {
  text-align: center;
  margin-top: 10px;
}
.c-ok-btn {
  width: 80%;
  color: #ffffff;
  background: #3966a7;
  height: 40px;
  border-radius: 20px;
  margin-top: 150px;
}

.tag-btn[data-v-0f4b0c0e] {
  margin-right: 210px;
}
.mint-cell {
  width: 370px;
  height: 40px;
}
#hh-commune {
  color: #333;
}
.num {
  position: relative;
  bottom: 25px;
  left: 330px;
  color: #999;
}
</style>




