<template>
  <div class="mainCover">
    <HeaderBar :needb="needb" :needs="needs" :tit="titleName" @fathersubmit="commune_cj"></HeaderBar>
    <div class="th_box">
      <div class="team_th">
        <span>*队伍名称</span>
        <input type="text" placeholder="请输入30字以内的队伍名称" name="name" v-model="teamname" />
      </div>
      <div class="team_th">
        <span>*所需人数</span>
        <input
          type="text"
          placeholder="请输入两位数字，不超过99人"
          name="num"
          v-model="teamnum"
          @change="check_price()"
        />
      </div>
      <div class="team_th" @click="openPicker($event)">
        <span>*开始时间</span>
        <input type="text" placeholder="开始时间" v-model="time1" name="begintime" />
      </div>
      <div class="team_th" @click="openPicker($event)">
        <span>*结束时间</span>
        <input type="text" placeholder="结束时间" v-model="time2" name="endtime" />
      </div>
      <mt-datetime-picker ref="picker" type="date" v-model="pickerValue" @confirm="handleConfirm"></mt-datetime-picker>
      <mt-datetime-picker
        ref="pickerd"
        type="date"
        v-model="pickerValued"
        @confirm="handleConfirm1"
      ></mt-datetime-picker>
      <div class="team_q">
        <textarea
          name="content"
          id
          cols="50"
          rows="5"
          v-model="teamcontent"
          placeholder="*请输入您要发布的内容"
          class="conten"
        ></textarea>
      </div>
    </div>
    <div class="createclubs_tm">
      <div class="logo-bo">
        <Photoclipcom
          viewdom="fasImg"
          outputSizeWidth="350"
          outputSizeHeight="175"
          :inSize="insizes"
          @fatherfromcall="getchilfromdata"
          @fatherimgcall="getchildimgcall"
        ></Photoclipcom>
        <div class="imgBorder">
          <img :src="imgi" alt class="face-img" id="fasImg" />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { Toast } from "mint-ui";
import { DatetimePicker } from "mint-ui";

export default {
  name: "commune_th",
  props: {},
  data() {
    return {
      titleName: "编辑队伍",
      needb: true,
      needm: false,
      search: "",
      pickerValue: "",
      pickerValued: "",
      imgi: require("images/upload.jpg"),
      teamname: "",
      teamnum: "",
      teamcontent: "",
      file: "",
      needs: "true",
      param: new FormData(),
      rdata: [],
      time1: "",
      time2: "",
      insizes:[200,140]
    };
  },
  computed: {},
  activated() {
    this.search = "../../../static/imgs/upload.jpg"? "../../../static/imgs/upload.jpg":require("images/upload.jpg");
    this.getParams();
  },
  mounted() {},
  watch: {
    $route: {
      handler(route) {
        // console.log("路由" + route.name);
        // const that = this;
        if (route.name == "commune_f") {
          this.teamname = "";
          this.imgi ="../../../static/imgs/upload.jpg"? "../../../static/imgs/upload.jpg":require("images/upload.jpg");
          this.teamnum = "";
          this.teamcontent = "";
          this.time1 = "";
          this.time2 = "";
        }
      }
    }
  },
  methods: {
    getParams() {
      var routerdata = window.sessionStorage.getItem("askdata");
      // console.log("s设问deid" + routerdata);
      this.rdata = JSON.parse(routerdata);
    },
    getchildimgcall(data) {
      this.imgi = data;
    },
    getchilfromdata(data) {
      this.param.append("file", data.get("file"));
    },
    openPicker(e) {
      if (e.target.placeholder == "开始时间") {
        this.$refs.picker.open();
      } else {
        this.$refs.pickerd.open();
      }
    },
    handleConfirm() {
      if (this.$refs.picker.value) {
        this.time1 = this.formatDate(this.$refs.picker.value);
      } else {
        Toast("未选择");
      }
    },
    handleConfirm1() {
      if (this.$refs.pickerd.value) {
        this.time2 = this.formatDate(this.$refs.pickerd.value);
      } else {
        Toast("未选择");
      }
    },
    check_price: function() {
      var price = /^([1-9]\d|\d)$/;
      if (!price.test(this.teamnum)) {
        Toast("最多99人");
        return;
      }
    },
    // 格式化获取的时间
    formatDate(date) {
      const y = date.getFullYear();
      let m = date.getMonth() + 1;
      m = m < 10 ? "0" + m : m;
      let d = date.getDate();
      d = d < 10 ? "0" + d : d;
      return y + "-" + m + "-" + d;
    },
    // 创建组队
    async commune_cj() {
      if (
        this.teamname == "" ||
        this.imgi == "../../../static/imgs/upload.jpg"? "../../../static/imgs/upload.jpg":require("images/upload.jpg")||
        this.teamnum == "" ||
        this.time1 == "" ||
        this.time2 == "" ||
        this.teamcontent == ""
      ) {
        return;
      }
      this.check_price();
      let params = this.param;
      params.append("name", this.teamname);
      params.append("num", this.teamnum);
      params.append("begintime", this.time1);
      params.append("endtime", this.time2);
      params.append("content", this.teamcontent);
      params.append("askid", this.rdata.id);
      try {
        let res = await this.$api.all.POSTfn(params, "team_creat");
        console.log(res);
        if (res.result == true) {
          this.$router.push({
            name: "commune_f"
          });
        } else if (res.result == false) {
          Toast("您在此项目中有队伍啦");
        } else {
          Toast(res.result);
        }
        this.teamname = "";
        this.imgi = "../../../static/imgs/upload.jpg"? "../../../static/imgs/upload.jpg":require("images/upload.jpg");
        this.teamnum = "";
        this.time1 = "";
        this.time2 = "";
        this.teamcontent = "";
      } catch (e) {
        console.log(e);
      }
    }
  },
  components: {}
};
</script>
<style scoped lang="css">
.face-img {
  height: 100px;
  width: 100px;
}

.th_box {
  padding: 0px 12px;
}
.createclubs_tm {
  width: 100%;
  padding: 10px 10px;
  height: 200px;
}
.createclubs_tm .logo-bo >>> #imagefile {
  width: 100px;
  height: 100px;
  position: absolute !important;
}
.c-ok-btn,
.c-cancel-btn {
  background: #023249;
  color: #fff;
  font-size: 18px;
  padding: 3px 10px;
  border-radius: 3px;
  margin: 20px 10px;
}
.c-cancel-btn {
  background: #ccc;
}
.logo-bo{
  position: relative;
  top:50px
} 
.conten {
  width: 100%;
  border: 0px;
  font-size: 14px;
  padding: 5px;
  text-align: justify;
  border: 1px solid #eee;
  border-radius: 3px;
  margin-top: 10px;
  color: #666;
}
.team_th {
  width: 100%;
  /* height: 44px; */
  /* line-height: 44px; */
  display: flex;
  align-items: center;
  height: 36px;
  font-size: 14px;
  color: #333;
  border: 1px solid #eee;
  border-radius: 3px;
  margin-top: 10px;
  padding-left: 10px;
}
.team_th span {
  width: 100px;
}
.team_th input {
  width: 100%;
  color: #666;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.team_q input {
  width: 100%;
}

.face-img,
.imgInput {
  height: 100px;
  width: 100px;
  position: absolute;
}

.imgBorder {
  height: 115px;
  width: 115px;
  padding-top: 10px;
  margin-left: 12px;
}

.btn {
  text-align: center;
}
</style>



