<template>
  <div class="content">
    <ul class="comment-ul">
      <!-- <vue-scroll
        ref="scroll"
        :ops="opsconfig"
        @refresh-start="handleRS"
        @load-before-deactivate="handleLBD"
        @refresh-before-deactivate="handleRBD"
        @load-start="handleLoadStart"
      > -->
        <li @click="jumppicture(i)" v-for="(i,v) in vadioList" :key="v">
          <div class="comm-body">
            <div class="hh-face-img">
              <img class="bg" :src="i.cover" alt />
              <img class="bofang" src="../../../static/imgs/bo1.png" alt />
              <p class="clickNum">
                {{i.clicks}}次播放
                <span>51:20</span>
              </p>
              <div class="masking">
                <p>{{i.title}}</p>
              </div>
            </div>
            <div class="ciomm-footer clearfix">
              <!-- <p>{{i.title}}</p> -->
              <div class="comm-keyword">
                <span>{{i.date}}</span>
                <div class="iconbox">
                  <i class="iconfont icon-good">
                    <span>{{i.flowers}}</span>
                  </i>
                  <i class="commueicon com-icon iconfont icon-commue">
                    <span class="hh-fab-value">{{i.comments}}</span>
                  </i>
                  <i class="shareicon com-icon iconfont icon-share">
                    <span>{{i.shares}}</span>
                  </i>
                </div>
              </div>
            </div>
          </div>
        </li>
      <!-- </vue-scroll> -->
    </ul>
  </div>
</template>

<script>
import { videoPlayer } from "vue-video-player";

import { ScrollConfig } from "common/data.js";
import { Toast } from "mint-ui";
export default {
  name: "swiperf",
  props: ["itemf"],
  data() {
    return {
      vadioList: [],
      opsconfig: ScrollConfig.opsconfig,
      refresh: 1,
      noSscrollData: false,
      triggerType: "load",
      videoSrcarr: [],
      playerOptionsarr: [],
      bgimage: []
    };
  },
  created() {
    // for (let i = 1; i < 6; i++) {
    //   let imgparam = require(`@/assets/imgs/0${i}.png`);
    //   this.bgimage.push(imgparam);
    // }
    this.getvadioList();
    // this.getvediosrc();
  },
  watch: {
    vadioList: function() {
      this.$nextTick(function() {
        document.querySelector(".comment-ul").click();
      });
    }
  },
  components: { 
    videoPlayer
  },
  mounted() {},
  methods: {
    // 视频列表
    async getvadioList(direct, hot) {
      try {
        let params = {
          s: "",
          count: 10,
          target_id: hot,
          direction: direct,
          category: "13"
        };
        let res = await this.$api.all.GETfn(params, "list");
        console.log("视频列表" + JSON.stringify(res));
        for (let t in res.data) {
          let now = new Date().getTime();
          res.data[t].date = this.timeago(res.data[t].date * 1000);
        }
        if (direct == "bigger") {
          this.vadioList = res.data.concat(this.vadioList);
        } else {
          this.vadioList = this.vadioList.concat(res.data);
        }
        // this.newList = res.data;
        this.iddata = res.result;
      } catch (e) {
        console.log(e);
      }
    },
    timeago(dateTimeStamp) {
      //dateTimeStamp是一个时间毫秒，注意时间戳是秒的形式，在这个毫秒的基础上除以1000，就是十位数的时间戳。13位数的都是时间毫秒。
      var result;
      var minute = 1000 * 60; //把分，时，天，周，半个月，一个月用毫秒表示
      var hour = minute * 60;
      var day = hour * 24;
      var week = day * 7;
      var halfamonth = day * 15;
      var month = day * 30;
      var now = new Date().getTime(); //获取当前时间毫秒
      var diffValue = now - dateTimeStamp; //时间差

      if (diffValue < 0) {
        return;
      }
      var minC = diffValue / minute; //计算时间差的分，时，天，周，月
      var hourC = diffValue / hour;
      var dayC = diffValue / day;
      // var weekC = diffValue / week;
      // var monthC = diffValue / month;
      // if (monthC >= 1 && monthC <= 3) {
      // 	result = " " + parseInt(monthC) + "月前";
      // } else if (weekC >= 1 && weekC <= 3) {
      // 	result = " " + parseInt(weekC) + "周前";
      // } else
      if (dayC >= 1 && dayC <= 3) {
        result = " " + parseInt(dayC) + "天前";
      } else if (hourC >= 1 && hourC <= 23) {
        result = " " + parseInt(hourC) + "小时前";
      } else if (minC >= 1 && minC <= 59) {
        result = " " + parseInt(minC) + "分钟前";
      } else if (diffValue >= 0 && diffValue <= minute) {
        result = "刚刚";
      } else {
        var datetime = new Date();
        datetime.setTime(dateTimeStamp);
        var Nyear = datetime.getFullYear();
        var Nmonth =
          datetime.getMonth() + 1 < 10
            ? "0" + (datetime.getMonth() + 1)
            : datetime.getMonth() + 1;
        var Ndate =
          datetime.getDate() < 10
            ? "0" + datetime.getDate()
            : datetime.getDate();
        var Nhour =
          datetime.getHours() < 10
            ? "0" + datetime.getHours()
            : datetime.getHours();
        var Nminute =
          datetime.getMinutes() < 10
            ? "0" + datetime.getMinutes()
            : datetime.getMinutes();
        var Nsecond =
          datetime.getSeconds() < 10
            ? "0" + datetime.getSeconds()
            : datetime.getSeconds();
        result = Nyear + "-" + Nmonth + "-" + Ndate;
      }
      return result;
    },
    handleRS(vsInstance, refreshDom, done) {
      // alert("这是执行下拉刷新开始做啥事");
      done();
    },
    handleLoadStart(vm, dom, done) {
      setTimeout(() => {
        // alert("这是执行下拉加载之前需要执行什么操作");
        done();
      }, 600);
    },
    handleLBD(vm, loadDom, done) {
      done();
    },
    handleRBD(vm, loadDom, done) {
      done();
    },
    //点击浏览
    async setclick(item) {
      try {
        let params = {
          target: item.id,
          action: 1,
          value: 1
        };
        let res = await this.$api.all.GETfn(params, "setclick");
        console.log(res);
        if (res.result.result == "ok") {
        }
      } catch (e) {
        console.log(e);
      }
    },
    getIos(item) {
      let that = this;
      let isLogin = window.localStorage.getItem("isLogin");
      let loginstauts;
      if (isLogin == true || isLogin == "true") {
        loginstauts = "1";
      } else {
        loginstauts = "0";
      }
      var str = location.href;
       str = str.match(/(\S*)\#/)[1];
      let localUrl =str+"#/" ;
      let param = {
        articleId: item.id + "",
        articleUrl: localUrl + "vadiodetail",
        // 'articleType':type.category+"",
        loginstating: loginstauts,
        userId: this.$getUser.id + "",
        userToken: this.$getUser.token + "",
        route: localUrl + "login"
      };
      if (window.hasOwnProperty("webkit")) {
        window.webkit.messageHandlers.openWebView.postMessage(param);
      } else {
      
      }
    },
    jumppicture(item) {
      if (window.hasOwnProperty("webkit")) {
        this.getIos(item);
      } else {
        localStorage.setItem("vadiodata", JSON.stringify(item));
        this.$router.push({
          path: "/vadiodetail",
          name: "vadiodetail"
        });
      }
      // 创建UIAlertView类的实例对象
      //  let param = ["articleId:articleType:articleUrl:userId:userToken:" , item.id+'','','vadiodetail',this.$getUser.id+"",this.$getUser.token+''];
      // //保存到缓存
      // localStorage.setItem("plusData", JSON.stringify(param));

      // this.$router.push({
      //   path: "/vadiodetail?plusJump",
      //   name: 'vadiodetail'
      // });
      this.setclick(item);
    }
  }
};
</script>

<style scoped lang="css">
* {
  margin: 0;
  padding: 0;
}
.iconbox i,
.iconbox i span {
  font-size: 16px;
}
.iconbox i {
  display: inline-block;
  width: 35px;
}
.content {
  /* min-height: calc(100vh- 40px) !important;
  height: calc(100vh- 40px) !important; */
  text-align: start;
  height: 100%;
}
.comm-body .bg {
  width: 100%;
  height: 200px;
  position: relative;
}
.bofang {
  height: 40px;
  width: 40px;
  position: absolute;
  top: 40%;
  left: 160px;
  /* margin-top: -124px; */
}
.hh-face-img {
  position: relative;
}
.masking {
  font-size: 14px;
  width: 100%;
  font-weight: 700;
  position: absolute;
  top: 0;
  /* line-height: 22px; */
  background: linear-gradient(
    rgba(0, 0, 0, 0.6) 5%,
    rgba(0, 0, 0, 0.5) 15%,
     rgba(0, 0, 0, 0.3) 25%,
    rgba(0, 0, 0, 0.1) 35%,
    rgba(0, 0, 0, 0)
  );
  box-sizing: border-box;
  padding: 10px 12px;
  height: 100%;
  z-index: 1010000000;
  /* border-radius: 0px 0px 5px 5px; */
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  overflow: hidden;
  /*! autoprefixer: off */
  -webkit-box-orient: vertical;
}
.masking p {
  width: 100%;
  font-weight: 600;
  color: #fff;
  overflow: hidden;
  text-overflow: ellipsis;
  text-align: justify;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  overflow: hidden;
  /*! autoprefixer: off */
  -webkit-box-orient: vertical;
}
.clickNum {
  color: #fff;
  position: absolute;
  bottom: 10px;
  font-size: 14px;
  right: 15px;
}
.clickNum span {
  margin-left: 8px;
}
.comment-ul {
  min-height: calc(100vh- 40px) !important;
  height: calc(100vh- 40px) !important;
  padding: 0px 12px;
}

.comment-ul li {
  padding-bottom: 10px;
}

.comm-keyword {
  display: flex;
  justify-content: space-between;
  color: #999;
  font-size: 14px;
  line-height: 24px;
}
.comm-footer {
  /* height: 60px; */
  line-height: 30px;
  box-sizing: border-box;
  text-align: justify;
  line-height: 18px;
  font-size: 14px;
}
</style>


