<template>
  <div class="mainCover">
    <!-- <HeaderBar :needb="needb" :tit="titleName"></HeaderBar> -->
    <div class="mainContent">
      <p v-show="allList.length==0" class="nothing">——— 您还没有评论过 ———</p>

      <ul class="comment-ul">
        <vue-scroll
          ref="scroll"
          :ops="opsconfig"
          @refresh-start="handleRS"
          @load-before-deactivate="handleLBD"
        >
          <li v-for="(v,i) in allList" :key="i" class="comment-li" @click="gotoAsk(v)">
            <div class="infocontent">
              <div class="infocontent-left">
                <img v-lazy="v.avatar_path" class="page-lazyload-image1" />
                <div>
                  <p class="name">
                    {{v.userName}}
                    <span v-show="v.pu_name">
                      <span style="color:#666;font-size:14px">回复</span>
                      {{v.pu_name}}
                    </span>
                  </p>
                  <p class="date">{{v.created}}</p>
                </div>
              </div>
              <i class="iconfont icon-good">
                <span>{{v.flowers}}</span>
              </i>
            </div>
            <div class="comments">{{v.pu_text}}</div>
            <div class="content">
              <div class="content-left">
                <p class="title">{{v.pr_title?v.pr_title : "文章已被删除" }}</p>
              </div>
            </div>
          </li>
        </vue-scroll>
      </ul>
    </div>
  </div>
</template>

<script>
import { ScrollConfig } from "common/data.js";
import moment from "moment";
import { ComUtils } from "common/utils.js";
import { Toast } from "mint-ui";
export default {
  name: "me_comment",
  props: {},
  data() {
    return {
      opsconfig: ScrollConfig.opsconfig,
      titleName: "评论",
      needb: true,
      allList: [],
      UserInfo: this.$getUser,
      max_id: "",
      min_id: "",
      dataxiaflag: false
    };
  },
  computed: {},
  created() {
    moment.locale("zh-cn");
    this.getDate("bigger", "0");
  },
  mounted() {
    this.$nextTick(function() {
      document.querySelector(".comment-ul").click();
    });
  },
  watch: {
    $route() {
      this.$nextTick(function() {
        document.querySelector(".comment-ul").click();
      });
    }
  },
  filters: {
    changeTime(val) {
      return moment(val)
        .startOf("minute")
        .fromNow();
    }
  },
  activated() {
    this.getDate("bigger", "0");
  },
  methods: {
    gotoAsk(item) {
      if (item.target_type == "2003") {
        window.sessionStorage.setItem("askdata", JSON.stringify(v));
        this.$router.push({
          name: "commune_f"
        });
      } else  {
        let gourl = "";
        if (item.type_id == "2") {
          localStorage.setItem("homedata", JSON.stringify(item));
          gourl = "pictureDetail";
        } else if (item.type_id == "4") {
          localStorage.setItem("zhuantidata", JSON.stringify(item));
          gourl = "subjectDetail";
        } else if (item.type_id == "3") {
          localStorage.setItem("vadiodata", JSON.stringify(item));
          gourl = "vadiodetail";
        } else {
          localStorage.setItem("homedata", JSON.stringify(item));
          // console.log(item);
          localStorage.setItem("atype", item.category_id);
          gourl = "homeDetail";
        }
         this.$router.push({
          
          name: gourl
        });
        let that = this;
        // ComUtils.skipPage(item, that);
      }
    },
    async getDate(type, numsid) {
      try {
        let timestamp = new Date().getTime();
        let param = {
          count: 10,
          timestamp: timestamp,
          direction: type,
          target_id: this.$route.query.id
        };
        let res = await this.$api.all.GETfn(param, "user_comment");
        res = res;
        this.max_id = res.max_id;
        this.min_id = res.min_id;
        this.allList = res.data;

        console.log(this.allList);
      } catch (error) {
        console.log(error);
      }
    },
    async handleRS(vsInstance, refreshDom, done) {
      // alert("这是执行下拉刷新开始做啥事");
      try {
        let timestamp = new Date().getTime();
        let param = {
          count: 10,
          timestamp: timestamp,
          direction: "bigger",
          target_id: this.max_id
        };
        let res = await this.$api.all.GETfn(param, "user_comment");
        res = res.result;

        if (res.data.length > 0) {
          this.max_id = res.max_id;
          // this.min_id = res.min_id;
          res.data.forEach((item, index, arr) => {
            this.allList.unshift(item);
          });
          this.$forceUpdate();
        } else if (res.more == "0" && res.data.length == 0) {
          this.$toast("暂无最新数据");
          done();
          return;
        }
        console.log(this.allList);
        done();
      } catch (error) {}
    },
    async handleLBD(vm, loadDom, done) {
      // alert("这是执行下拉加载之后需要执行什么操作");
      try {
        let timestamp = new Date().getTime();
        let param = {
          count: 10,
          timestamp: timestamp,
          direction: "litter",
          target_id: this.min_id
        };
        let res = await this.$api.all.GETfn(param, "user_comment");
        res = res.result;
        if (res.data.length > 0) {
          this.dataxiaflag = false;
          // this.max_id = res.max_id;
          this.min_id = res.min_id;
          res.data.forEach((item, index, arr) => {
            this.allList.push(item);
          });
          this.$forceUpdate();
        } else if (res.more == "0" && res.data.length == 0) {
          this.$toast("没有更多数据");
          this.dataxiaflag = true;
          done();
          return;
        }
        done();

        console.log(this.allList);
      } catch (error) {}
    }
  },
  components: {}
};
</script>

<style scoped lang="css">
.mainContent {
  height: 623px;
  min-height: 623px;
}

.comment-ul {
  width: 100%;
  min-height: calc(100vh-1px);
  height: calc(100vh-1px);
  padding: 0px 12px;
}

.comment-li {
  border-bottom: 1px solid #eee;
  /* height: 120px; */
  min-height: 120px;
  display: block;
  padding: 10px 0px;
}

.comments {
  color: #333;
  /* text-align: justify; */
  line-height: 30px;
  display: flex;
  width: 100%;
  word-wrap: break-word;
  text-overflow: ellipsis;
  overflow: hidden;
}

.content {
  /* height: 80px; */
  min-height: 40px;
  /* padding: 5px 0px; */
  display: flex;
  background-color: #f6f7f9;
  border-radius: 3px;
}

.content-left {
  flex: 1;
  display: flex;
  align-content: space-between;
  flex-wrap: wrap;
  /* height: 80px; */
  line-height: 24px;
  padding: 6px;
}

.title {
  width: 100%;
  color: #333;
  text-align: justify;
  white-space: normal;
  overflow: auto;
  table-layout: fixed;
  word-break: break-all;
  word-wrap: break-word;
  /* display: inline-block !important; */
}
.name {
  color: #3966a7;
}
.date {
  /* height: 30px; */
  line-height: 30px;
  /* padding-left: 4% 0%; */
  font-size: 14px;
  color: #999;
  display: inherit;
  align-items: center;
}
.iconfont {
  font-size: 18px;
  color: #666;
}
.infocontent {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.infocontent-left {
  min-width: 25%;
  display: flex;
  align-items: initial;
  justify-content: flex-start;
}
.infocontent-left div {
  margin-left: 10px;
}
img {
  height: 40px;
  width: 40px;
  border-radius: 50%;
}

.nothing {
  margin-top: 15px;
  font-size: 12px;
  color: #999;
  height: 40px;
  line-height: 40px;
  width: 100%;
  text-align: center;
}
</style>



