<template>
  <div class="mainCover">
    <HeaderBar :needb="needb" :tit="titleName"></HeaderBar>

    <div class="mainContent">
      <router-link to="/changepassword">
        <div class="list-item">
          <p class="item-detail-title">修改密码</p>
          <div class="item-detail-content">
            <img :src="next" alt class="next-img" />
          </div>
        </div>
      </router-link>

      <div class="list-item list-item-last">
        <p class="item-detail-title">版本号</p>
        <div class="item-detail-content">版本1.0.0</div>
      </div>

      <div class="line"></div>
      <div class="list-item" @click.prevent="sheetVisible1 = true">
        <p class="item-detail-title">
          行业类型
          <span>[{{listdatas[0].name}}]</span>
        </p>
        <div class="item-detail-content">
          <img :src="next" alt class="next-img" />
        </div>
      </div>

      <div class="list-item" @click.prevent="sheetVisible2 = true">
        <p class="item-detail-title">
          所在区域
          <span>[{{listdatas[1].name}}]</span>
        </p>
        <div class="item-detail-content">
          <img :src="next" alt class="next-img" />
        </div>
      </div>

      <div class="list-item" @click.prevent="sheetVisible3 = true">
        <p class="item-detail-title">
          回报机制
          <span>[{{listdatas[2].name}}]</span>
        </p>
        <div class="item-detail-content">
          <img :src="next" alt class="next-img" />
        </div>
      </div>

      <div class="list-item" @click.prevent="sheetVisible4 = true">
        <p class="item-detail-title">
          项目金额
          <span>[{{listdatas[3].name}}]</span>
        </p>
        <div class="item-detail-content">
          <img :src="next" alt class="next-img" />
        </div>
      </div>

      <div class="list-item" @click.prevent="sheetVisible5 = true">
        <p class="item-detail-title">
          通过以上条件找到我
          <span>[{{isthroughme}}]</span>
        </p>
        <div class="item-detail-content">
          <img :src="next" alt class="next-img" />
        </div>
      </div>

      <!-- <div class="btn">
        <b>PPPing封面</b>
      </div>
      <div class="line"></div>-->
      <div class="btn-box">
        <div class="btn" @click="openConfirm">
            <b>退出登录</b>
        </div>
      </div>
    </div>
    <mt-actionsheet :actions="actions1" v-model="sheetVisible1"></mt-actionsheet>
    <mt-actionsheet :actions="actions2" v-model="sheetVisible2"></mt-actionsheet>
    <mt-actionsheet :actions="actions3" v-model="sheetVisible3"></mt-actionsheet>
    <mt-actionsheet :actions="actions4" v-model="sheetVisible4"></mt-actionsheet>
    <mt-actionsheet :actions="actions5" v-model="sheetVisible5"></mt-actionsheet>
  </div>
</template>

<script>
export default {
  name: "me_set_up",
  props: {},
  data() {
    return {
      titleName: "设置",
      needb: true,
      next: "",
      sheetVisible1: false,
      sheetVisible2: false,
      sheetVisible3: false,
      sheetVisible4: false,
      sheetVisible5: false,
      actions1: [],
      actions2: [],
      actions3: [],
      actions4: [],
      actions5: [],
      listdatas: [
        { name: "未选择" },
        { name: "未选择" },
        { name: "未选择" },
        { name: "未选择" }
      ],
      isthroughme: "",
      UserInfo: this.$getUser,
      itemarrary: [
        "occupation_id[]",
        "address_id[]",
        "mechanism_id[]",
        "investment_amount_id[]"
      ]
    };
  },
  computed: {},
  created() {
    this.next = require("images/next.png");
    this.getDataList();
    this.getactions1();
    this.getactions2();
    this.getactions3();
    this.getactions4();
    this.getactions5();
  },
  mounted() {},
  watch: {},
  methods: {
    selectactions1(e, index) {
      let param = {
        name: e.name,
        type: e.type,
        id: e.id
      };
      this.listdatas[0] = param;
      this.pullactionstate(param.id, 0);
    },
    selectactions2(e, index) {
      let param = {
        name: e.name,
        type: e.type,
        id: e.id
      };
      this.listdatas[1] = param;
      this.pullactionstate(param.id, 1);
    },
    selectactions3(e, index) {
      let param = {
        name: e.name,
        type: e.type,
        id: e.id
      };
      this.listdatas[2] = param;
      this.pullactionstate(param.id, 2);
    },
    selectactions4(e, index) {
      let param = {
        name: e.name,
        type: e.type,
        id: e.id
      };
      this.listdatas[3] = param;
      this.pullactionstate(param.id, 3);
    },
    selectactions5(e, index) {
      this.pullfind_mefn(e.type, e.name);
    },
    async pullfind_mefn(v, name) {
      try {
        let that = this;
        let params = {
          para: v
        };
        this.isthroughme = name;

        let res = await this.$api.all.GETfn(params, "find_me");
        console.log(res);
        if (res.result != "ok") {
          // this.$toast(res.result);
          return;
        }
      } catch (error) {
        console.log(error);
      }
    },
    async pullactionstate(v, i) {
      try {
        let that = this;
        let params = {};
        params[this.itemarrary[i]] = v;
        let res = await this.$api.all.GETfn(params, "set_person");
        console.log(res);
      } catch (error) {
        console.log(error);
      }
    },
    openConfirm() {
      this.$message.confirm("确定退出系统?", "提示").then(action => {
        if (action == "confirm") {
          window.localStorage.clear();
          this.$store.dispatch("pullUserinfo", {});
          this.$router.push({
            name: "login"
          });
        } else {
          console.log("取消操作");
        }
      });
    },
    async getDataList() {
      try {
        let that = this;
        let params = {};
        let res = await this.$api.all.POSTfn(params, "user_resume");
        res = res.result;
        res.forEach((v, i, arr) => {
          let param = {
            name: v.name ? v.name : "未选择",
            id: v.id,
            type: v.type
          };
          that.listdatas[i] = param;
        });
        console.log(this.actions1);
      } catch (error) {
        console.log(error);
      }
    },
    async getactions1() {
      try {
        let that = this;
        let params = {};
        let res = await this.$api.all.POSTfn(params, "person_type");
        res = res.result;
        res.forEach((v, i, arr) => {
          let param = {
            name: v.name,
            id: v.id,
            type: v.type,
            method: that.selectactions1
          };
          this.actions1.push(param);
        });
        console.log(this.actions1);
      } catch (error) {
        console.log(error);
      }
    },
    async getactions2() {
      try {
        let that = this;
        let params = {};
        let res = await this.$api.all.POSTfn(params, "person_area");
        res = res.result;
        res.forEach((v, i, arr) => {
          let param = {
            name: v.name,
            id: v.id,
            type: v.type,
            method: that.selectactions2
          };
          this.actions2.push(param);
        });
        console.log(this.actions2);
      } catch (error) {
        console.log(error);
      }
    },
    async getactions3() {
      try {
        let that = this;
        let params = {};
        let res = await this.$api.all.POSTfn(params, "person_reward");
        res = res.result;
        res.forEach((v, i, arr) => {
          let param = {
            name: v.name,
            id: v.id,
            type: v.type,
            method: that.selectactions3
          };
          this.actions3.push(param);
        });
        console.log(this.actions3);
      } catch (error) {
        console.log(error);
      }
    },
    async getactions4() {
      try {
        let that = this;
        let params = {};
        let res = await this.$api.all.POSTfn(params, "person_money");
        res = res.result;
        res.forEach((v, i, arr) => {
          let param = {
            name: v.name,
            id: v.id,
            type: v.type,
            method: that.selectactions4
          };
          this.actions4.push(param);
        });
        console.log(this.actions4);
      } catch (error) {
        console.log(error);
      }
    },
    async getactions5() {
      try {
        let that = this;
        let params = {};
        let res = await this.$api.all.GETfn(params, "see_find");
        if (res.result == "ok") {
          this.isthroughme = "是";
        } else {
          this.isthroughme = "否";
        }

        let paramarr = [];
        paramarr.push({
          name: "是",
          type: "yes",
          method: that.selectactions5
        });
        paramarr.push({
          name: "否",
          type: "no",
          method: that.selectactions5
        });
        this.actions5 = paramarr;
      } catch (error) {
        console.log(error);
      }
    }
  },
  components: {}
};
</script>

<style scoped lang="css">
a {
  text-decoration: none;
}

div.btn b {
  font-size: 15px;
}

.list-item {
  width: 92%;
  height: 50px;
  margin: 0% 4%;
  display: flex;
  font-size: 15px;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid #eee;
}

.list-item-last{
  border-bottom: none;
}

.item-detail-title {
  margin-left: 5px;
  font-size: 14px;
  color: #333;
}

.item-detail-title span {
  display: inline-block;
  padding: 0px 3px;
  font-size: 14px;
  font-weight: 500;
  color: #4a8dff;
}

.item-detail-content {
  font-size: 14px;
  color: #999;
}

.next-img {
  width: 12px;
  height: 12px;
}

.btn-box{
    padding-top: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.btn {
  width: 80%;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  color: #fff;
  background-color: #4a8dff;
  font-weight: 600;
  border-radius: 3px;
}

.line {
  width: 100%;
  height: 10px;
  background: #f6f7f9;
}
</style>



