<template>
  <div class="mainCover">
    <mt-search cancel-text="取消" placeholder="请输入搜索信息" v-model="searchValue" ref="findcancel"></mt-search>
    <div class="canceldiv" @click.prevent="$router.back(-1)">&nbsp;</div>
    <div class="mainContent">
      <vue-scroll
        ref="scroll"
        :ops="opsconfig"
        @refresh-start="handleRS"
        @load-before-deactivate="handleLBD"
        @refresh-before-deactivate="handleRBD"
        @load-start="handleLoadStart"
      >
        <!-- :topMethod="down" :bottomMethod="up" -->

        <div id="v-article" class="ull" v-cloak ref="text">
          <div class="commune" v-for="(i,d) in asklist" :key="d" @click="goaskdetail(i)">
            <div class="commune_t">
              <div class="tit">
                <p class="titname" v-html="i.title"></p>
                <p class="content" v-html="i.content"></p>
              </div>
              <img :src="i.image_url" alt />
            </div>
             <div class="commune_b">
                  <span>来自:{{i.commune_name}}</span>
                  <span>浏览量:{{i.browse_number}}</span>
                  <span>评论:{{i.comment_number}}</span>
                  <span>{{i.create_time}}</span>
                </div>
          </div>
          <!-- <div class="nodata" v-if="asklist.length == 0">暂无数据</div> -->
        </div>
      </vue-scroll>
    </div>
  </div>
</template>

<script>
import { ScrollConfig } from "common/data.js";
import { Toast } from "mint-ui";
export default {
  name: "search",
  props: {},
  data() {
    return {
      searchValue: "",
      routerPage: "",
      asklist: [],
      opsconfig: ScrollConfig.opsconfig,
      refresh: 1,
      noSscrollData: false,
      triggerType: "load",
      iddata: [],
      rdata: []
    };
  },
  computed: {},
  activated() {
    this.routerPage = this.$route.params.pathname;
    // this.getnewdate();
  },
  mounted() {
    // console.log(this.$refs.findcancel.$el.children[0].childNodes[0]) //this.$refs.dom.addEventListener('click',function(){});
    // this.$refs.findcancel
  },
  watch: {
    searchValue: function() {
      this.focus();
    },
     $route: function() {
      this.$nextTick(function() {
        this.$refs.text.click();
      });
    }
  },
  methods: {
    focus: function() {
      if (document.querySelector("input") == document.activeElement) {
        this.fuzzy_search(this.searchValue.trim());
      }
    },
    fuzzy_search: function(value) {
      var that = this;
      if (!value) {
        that.asklist = [];
        return;
      }
      this.getaskdate();
    },
    getParams() {
      var routerdata = window.sessionStorage.getItem("commitydata");
      this.rdata = JSON.parse(routerdata);
    },

    //设问数据
    async getaskdate(direct, hot) {
      try {
        let params = {
          userid: "327195464540160441",
          token:
            "6g475hc3c2ce55cab1209998c8ecf04a0e4125a05d3806c448a6e8a9c0901b93840",
          communeid: this.rdata.id,
          count: 5,
          s: this.searchValue
        };
        let res = await this.$api.all.GETfn(params, "ask_list");
        console.log(JSON.stringify(res));
        if (direct == "bigger") {
          res.result.data = res.result.data.concat(this.asklist);
        } else {
          res.result.data = this.asklist.concat(res.result.data);
        }
        this.iddata = res.result;
        this.changeColor(res.result.data);
      } catch (e) {
        console.log(e);
      }
    },
    changeColor(resultsList) {
      resultsList.map((item, index) => {
        if (this.searchValue && this.searchValue.length > 0) {
          // 匹配关键字正则
          let replaceReg = new RegExp(this.searchValue, "g");
          // 高亮替换v-html值
          // let replaceString ='<span style="color:red">' + this.searchValue + "</span>";
          // resultsList[index].title = item.title.replace(
          //   replaceReg,
          //   replaceString
          // );
          //   resultsList[index].content = item.content.replace(
          //   replaceReg,
          //   replaceString
          // );
        }
      });
      this.asklist = [];
      this.asklist = resultsList;
    },
    handleRS(vsInstance, refreshDom, done) {
      // alert("这是执行下拉刷新开始做啥事");
      done();
    },
    handleLoadStart(vm, dom, done) {
      setTimeout(() => {
        // alert("这是执行下拉加载之前需要执行什么操作");
        done();
      }, 600);
    },
    handleLBD(vm, loadDom, done) {
      let dictir = "litter";
      let hot = this.iddata.minId;
      console.log(this.iddata.has_more);
      if (this.iddata.more == 0) {
        this.noSscrollData = true;
        Toast("无搜索内容");
        done();
        return;
      }
      this.getaskdate(dictir, hot);
      done();
    },
    handleRBD(vm, loadDom, done) {
      let dictir = "bigger";
      let hot = this.iddata.maxId;
      console.log(this.iddata.has_more);
      if (this.iddata.more == 0) {
        this.noSscrollData = true;
        Toast("无搜索内容");
        done();
        return;
      }
      this.getaskdate(dictir, hot);
      done();
    },
    async setbrowsing(item) {
      try {
        let params = {
          ask_id: item.id
        };
        let res = await this.$api.all.POSTfn(params, "browsing");
        console.log("设置浏览量" + JSON.stringify(res));
      } catch (e) {
        console.log(e);
      }
    },
    goaskdetail(item) {
      window.sessionStorage.setItem("askdata", JSON.stringify(item));
      this.setbrowsing(item);
      this.$router.push({
        name: "commune_f"
        // params: {
        //   data: item
        // }
      });
    }
  },
  components: {}
};
</script>

<style scoped lang="css">
.mint-search {
  height: 40px;
  width: 100%;
}

.mint-searchbar {
  padding: 5px 10px;
}
.project-img[data-v-aef1e666] {
  width: 30% !important;
  height: 60px !important;

  /* margin-left: -40px; */
}

.hot-keyword span {
  font-size: 0.24rem;
  /*margin-right:0.1rem;*/

  color: #ccc;
}
.hot-keyword {
  text-align: left;
}
.hot-name p {
  /*padding:0.1rem 0;*/
  font-size: 0.3rem;
  /* font-weight: 700; */
  padding: 0.2rem 0 0.1rem;
  line-height: 1.5;
  color: #000;
}
.itemlist {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-around;
  border-bottom: 1px solid #ddd;
  padding: 10px 5px;
  box-sizing: border-box;
  /* justify-content:space-around; */
}
.iteml {
  width: 60%;
  text-align: left;
}

.itemlist img {
  width: 30% !important;
  height: 60px;
  /* margin-left: -40px; */
}

.canceldiv {
  height: 44px;
  line-height: 44px;
  width: 50px;
  background: transparent;
  position: absolute;
  right: 0;
  z-index: 999;
}
/* tob内容 */
.ul {
  padding: 0px 12px;
}
.tit .content {
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  display: -webkit-box !important;
  color: #666666;
}
.titname {
  color: #333333;
  font-size: 16px;
  font-weight: 700;
}
.commune {
  border-bottom: 1px solid #eee;
  padding: 8px 5px;
  min-height: 105px;
}
.commune_t {
  display: flex;
  justify-content: space-between;
  min-height: 70px;
}
.commune_t img {
  height: 70px;
  width: 100px;
  border-radius: 3px;
  /* background: transparent; */
  border: none;
}
.commune_t .titname {
  font-size: 14px;
  padding: 5px 0;
}

.commune_t .content {
  font-size: 12px;
  /* padding: 5px 0; */
}

.commune_b {
  font-size: 12px;
  padding-top: 5px;
  color: #bbbbbb;
  min-height: 20px;
  line-height: 20px;
}

.commune_b span {
  margin-right: 5px;
}
</style>





