<template>
  <div class="mainCover">
    <HeaderBar :needb="needb" :tit="titleName"></HeaderBar>
    <div class="mainContent">
      <!-- 账号输入框 -->
      <div class="input-box">
        <span class="iconfont icon-pass" slot="icon"></span>

        <input
          type="text"
          class="input-default"
          placeholder="请输入原密码"
          v-model="oldpassword"
          maxlength="12"
          pattern="\d*"
          onkeyup="this.value=this.value.replace(/^[\u4e00-\u9fa5]+$/,'');"
        />
      </div>
      <!-- 账号输入框 -->
      <div class="input-box">
        <span class="iconfont icon-pass" slot="icon"></span>
        <input
          type="text"
          class="input-default"
          placeholder="请输入新密码"
          v-model="newpassword"
          maxlength="12"
          pattern="\d*"
          onkeyup="this.value=this.value.replace(/^[\u4e00-\u9fa5]+$/,'g')"
        />
      </div>
      <!-- 账号输入框 -->
      <div class="input-box">
        <span class="iconfont icon-pass" slot="icon"></span>
        <input
          type="text"
          class="input-default"
          placeholder="请您在确认密码"
          v-model="rednewpassword"
          maxlength="12"
          pattern="\d*"
          onkeyup="this.value=this.value.replace(/^[\u4e00-\u9fa5]+$/,'');"
        />
      </div>

      <div class="button-login">
        <input type="button" value="提交" class="input-button-login" @click.prevent="loginConfirm" />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "changepassword",
  props: {},
  data() {
    return {
      titleName: "修改密码",
      needb: true,
      oldpassword: "",
      newpassword: "",
      rednewpassword: "",
      imga: "",
      imgb: "",
      UserInfo: this.$getUser
    };
  },
  computed: {},
  created() {
    this.imga = require("images/phone.png");
    this.imgb = require("images/password.png");
  },
  mounted() {},
  watch: {},
  methods: {
    async loginConfirm() {
      try {
        let params = {
          account: this.UserInfo.account,
          oldpwd: this.oldpassword,
          newpwd: this.newpassword,
          userid: this.UserInfo.id,
          token: this.UserInfo._token
        };
        let res = await this.$api.home.GETfn(params, "change_pwd");
        if (res.result != "ok") {
          this.$toast(res.result);
        }
        window.localStorage.clear();
        window.sessionStorage.clear();
        this.$router.push({
          name: "login"
        });
        console.log(res);
      } catch (e) {
        console.log(e);
      }
    }
  },
  components: {}
};
</script>

<style scoped lang="css">
.mainContent {
  padding: 0px 12px;
}

.input-box-all {
  margin: 12px 0px;
}

.top-box {
  min-height: 110px;
  background-color: #1b81cb;
  display: flex;
  width: 100%;
  align-items: flex-end;
  text-align: center;
  color: #fff;
}

.login-box {
  flex: 1;
  min-height: 60px;
  line-height: 60px;
  text-align: center;
  clear: both;
  overflow: hidden;
}

.login-box span {
  display: block;
}

.login-text {
  height: 40px;
  line-height: 40px;
  font-size: 17px;
  padding: 5px 0px 0px 0px;
}

.sign {
  min-height: 22px;
  line-height: 22px;
  min-width: 22px;
  position: relative;
  left: 50%;
  margin-left: -10px;
}

.nbsp {
  width: 0px;
  height: 0px;
  border-left: 11px solid transparent;
  border-right: 11px solid transparent;
  border-bottom: 11px solid #fff;
  border-top: 11px solid transparent;
}

.message {
  height: 44px;
  line-height: 44px;
  text-align: center;
  color: #023249;
  font-size: 17px;
  font-weight: 500;
}

.input-box {
  height: 40px;
  line-height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 10px;
  border: 1px solid #eee;
  border-radius: 3px;
  color: #333;
}

.input-box span {
  width: 30px;
  height: 30px;
  margin-left: 10px;
  margin-bottom: 5px;
  color: #1b81cb;
}

.input-box img.imageyanz {
  height: 30px;
  line-height: 30px;
  margin: 7px 10px;
  width: 80px;
}

.input-box input {
  margin-left: 10px;
  height: 30px;
  line-height: 30px;
  font-size: 14px;
  color: #333;
  flex: 1;
}

.input-box .sendMsg {
  min-width: 100px;
  display: inline-block;
  height: 30px;
  margin: 5px;
  line-height: 30px;
  font-size: 15px;
  text-align: center;
  color: #fff;
  background: #1b81cb;
  border-radius: 5px;
}

.button-login {
  margin-top: 20px;
  height: 40px;
  line-height: 40px;
  width: 100%;
  font-size: 14px;
  text-align: center;
}

.input-button-login {
  width: 90%;
  height: 100%;
  background: #4a8dff;
  color: #fff;
  font-size: 14px;
  border-radius: 3px;
}

.button-other {
  height: 44px;
  line-height: 44px;
  display: flex;
  text-align: center;
  padding-left: 10%;
}

.button-other input {
  height: 44px;
  line-height: 44px;
  width: 16px;
}
</style>



