<template>
  <div class="mainCover">
   <!-- <HeaderBar :needb="true" :tit="tit" v-show="isShow"></HeaderBar> -->
   
    <div class="mainContent"> 
       <vue-scroll
        ref="scroll"
        :ops="opsconfig"
        @refresh-start="handleRS"
        @load-before-deactivate="handleLBD"
        @refresh-before-deactivate="handleRBD"
        @load-start="handleLoadStart"
      >
      <ul id="v-article" class="news-ul">
          <li v-for="(item, key) in newList" :key="key"  @click="godetail(item)">
            <!-- 单文详情 -->
            <div class="com-text" v-if="item.type_id!='3' && parseInt(item.coverType)==4">
              <ul class="com-ul">
                <ul class="com-last-ul">
                  <li class="sign-li-title">{{item.title}}</li>
                </ul>
                <li class="com-li-info">
                  <span class="com-span-title">{{item.sourceName?item.sourceName:"导报社"}}</span>
                  <span class="com-span-pre">浏览量:{{item.clicks}}</span>
                  <span class="com-span-time">{{item.date}}</span>
                </li>
              </ul>
            </div>
            <!-- 图文 -->
            <div class="com-text" v-if="item.type_id!='3' && (parseInt(item.coverType)==1 || parseInt(item.coverType)==2)">
                <ul class="com-last-ul">
                  <li class="com-left">
                    <div class="graph-li-title">{{item.title}}</div>
                    <div class="com-li-info">
                      <span class="com-span-title">{{item.sourceName?item.sourceName:"导报社"}}</span>
                      <span class="com-span-pre">浏览量:{{item.clicks}}</span>
                      <span class="com-span-time">{{item.date}}</span>
                    </div>
                  </li>
                  <li class="graph-li-img">
                    <img    v-lazy="item.image_ids[0].url" class="page-lazyload-image1"/>
                  </li>
                </ul>
            </div>

            <!-- 多图详情-->
            <div class="com-text" v-if="parseInt(item.coverType)==3">
              <ul class="com-ul">
                <li class="sign-li-title">{{item.title}}</li>
                <ul class="com-last-ul">
                  <li class="more-li-img" v-for="(s,y) in item.image_ids" :key="y">
                    <img  v-lazy="s.url" class="page-lazyload-image1"/>
                  </li>
                </ul>
                <li class="com-li-info">
                  <span class="com-span-title">{{item.sourceName?item.sourceName:"导报社"}}</span>
                  <span class="com-span-pre">浏览量:{{item.clicks}}</span>
                  <span class="com-span-time">{{item.date}}</span>
                </li>
              </ul>
            </div>
            <!-- 视频新闻 -->
            <!-- <div v-if="item.type_id=='3'"> -->
              <div class="comm-body" v-if="item.type_id=='3'">
                <div class="hh-face-img">
                  <img class="page-lazyload-image1 bg"  v-lazy="item.cover"  alt />
                  <img class="bofang" src="../../../static/imgs/bo1.png" alt />
                  <p class="clickNum">
                    {{item.clicks}}次播放
                    <span>51:20</span>
                  </p>
                  <div class="masking">
                    <p>{{item.title}}</p>
                  </div>
                </div>
                <div class="ciomm-footer clearfix">
                
                  <div class="comm-keyword">
                    <span>{{item.date}}</span>
                    <div class="iconbox">
                      <i class="iconfont icon-good">
                        <span>{{item.flowers}}</span>
                      </i>
                      <i class="commueicon com-icon iconfont icon-commue">
                        <span class="hh-fab-value">{{item.comments}}</span>
                      </i>
                      <i class="shareicon com-icon iconfont icon-share">
                        <span>{{item.shares}}</span>
                      </i>
                    </div>
                  </div>
                </div>
              </div>
            <!-- </div> -->
          </li>
      
   </ul>
        <!-- <div slot="load-beforeDeactive" v-if="noSscrollData">暂无更多</div> -->
      </vue-scroll>
 
    </div>
  </div>
</template>

<script>
import { ScrollConfig } from "common/data.js";
import { Toast } from "mint-ui";
import { ComUtils } from "common/utils";
export default {
  name: "community",
  props: {},
  data() {
    return {
      newList: [],
      opsconfig: ScrollConfig.opsconfig,
      refresh: 1,
      noSscrollData: false,
      triggerType: "load",
      bigdata: [],
      smalldata: [],
      atype: "",
       tit: "导报社",
        isShow: true,
    };
  },
  computed: {},
 activated() {
    this.getnewdate();
    // this.stopTouchend();
  },
  created() {
    this.getnewdate();
    // this.stopTouchend();
  },
  mounted() {},
  watch: {
    newList: function() {
      this.$nextTick(function() {
        document.querySelector(".news-ul").click();
      });
    },
  },
  methods: {
    isLogin() {
      let that = this;
      let isLogin = window.localStorage.getItem("isLogin");
      let loginstauts;
      if (isLogin == true || isLogin == "true") {
        loginstauts = "2";
      } else {
        loginstauts = "3";
      }
      var str = location.href;
      str = str.match(/(\S*)\#/)[1];
      let localUrl = str + "#/";
      let param = {
        userId: this.$getUser.id + "",
        userToken: this.$getUser.token + "",
        route: localUrl + "login",
        loginstating: loginstauts
      };
      if (window.hasOwnProperty("webkit")) {
        window.webkit.messageHandlers.openWebView.postMessage(param);
      } else {
      }
    },
     getIos(item, gourl) {
      
      let that = this;
      let isLogin = window.localStorage.getItem("isLogin");
      let loginstauts;
      if (isLogin == true || isLogin == "true") {
        loginstauts = "1";
      } else {
        loginstauts = "0";
      }
      var str = location.href;
      app.gotoDetail(str,gourl)
      // if (window.hasOwnProperty("webkit")) {
      //   window.webkit.messageHandlers.openWebView.postMessage(param);
      // } else {
      // }
    },
  
    // 新闻数据
    async getnewdate(direct, hot) {
       if (window.hasOwnProperty("webkit")) {
        this.isShow = false;
      } else {
        this.isShow = true;
      }
      try {
        let self = this;
        let params = {
          sourceId:'0',
          count: 10,
          target_id: hot,
          direction: direct,
          disable_cache: 0
        };

        let res = await this.$api.all.GETfn(params, "lkk");
        for (let i in res.result.data) {
          if (res.result.data[i].image_ids.length > 3) {
            res.result.data[i].image_ids.length = 3;
          }
          res.result.data[i].date = this.timeago(res.result.data[i].date * 1000);
        }
        if (direct == "bigger") {
          this.newList = res.result.data.concat(this.newList);
          this.bigdata = res;
        } else if (direct == "litter") {
          this.newList = this.newList.concat(res.result.data);
          this.smalldata = res;
        } else {
          this.newList = res.result.data;
          this.bigdata = res.result;
          this.smalldata = res.result;
        }
        console.log(res);
      } catch (e) {
        console.log(e);
      }
    },
    handleRS(vsInstance, refreshDom, done) {
      // alert("这是执行下拉刷新开始做啥事");
      done();
    },
    handleLoadStart(vm, dom, done) {
      setTimeout(() => {
        // alert("这是执行下拉加载之前需要执行什么操作");
        done();
      }, 600);
    },
    handleLBD(vm, loadDom, done) {
      let dictir = "litter";
      let hot = this.smalldata.min_id;
      if (
        this.smalldata.more == false 
      ) {
        //  this.noSscrollData = true;
        Toast("无更新数据");
        done();
      } else {
        this.getnewdate(dictir, hot);
        done();
      }
    },
    handleRBD(vm, loadDom, done) {
      console.log("下拉");
      let dictir = "bigger";
      let hot = this.bigdata.max_id;
      if (
        this.bigdata.more == false 
      ) {
        //  this.noSscrollData = true;
        Toast("无更多数据");
        done();
      } else {
        this.getnewdate(dictir, hot);
        done();
      }
      done();
    },
    //点击浏览
    async setclick(item) {
      try {
        let params = {
          // userid: "327195464540160441",
          target: item.id,
          action: 1,
          value: 1
        };
        let res = await this.$api.all.GETfn(params, "setclick");
        console.log(res);
        if (res.result.result == "ok") {
        }
      } catch (e) {
        console.log(e);
      }
    },
     // 跳转到详情页
    godetail(item) {
      let isLogin = window.localStorage.setItem("isLogin", "true");
      let data = {
        id: "319430217930703154",
       
        token:
          "5cec187gbd89e56deb4ff3aa102fd4ad128d7ae45d8c3c6546ed8173e4901323840",
      };
      ComUtils.saveParams(data);
      console.log("要跳转的id" + item.type_id);
      let gourl = "";
      if (item.type_id == "2") {
        localStorage.setItem("homedata", JSON.stringify(item));
        gourl = "pictureDetail";
      } else if (item.type_id == "4") {
        localStorage.setItem("zhuantidata", JSON.stringify(item));
        gourl = "subjectDetail";
      } else if (item.type_id == "3") {
        localStorage.setItem("vadiodata", JSON.stringify(item));
        gourl = "vadiodetail";
      } else {
        localStorage.setItem("homedata", JSON.stringify(item));
        // console.log(item);
        localStorage.setItem("atype", item.category_id);
        gourl = "homeDetail";
      }
      Toast("hahhahaha")
      // if (window.hasOwnProperty("webkit")) {
        this.getIos(item, gourl);
      // } else {
      //   this.$router.push({
      //     // path: "/homeDetail",
      //     name: gourl
      //   });
      // }
      this.setclick(item);
    },
    timeago(dateTimeStamp) {
      //dateTimeStamp是一个时间毫秒，注意时间戳是秒的形式，在这个毫秒的基础上除以1000，就是十位数的时间戳。13位数的都是时间毫秒。
      var result;
      var minute = 1000 * 60; //把分，时，天，周，半个月，一个月用毫秒表示
      var hour = minute * 60;
      var day = hour * 24;
      var week = day * 7;
      var halfamonth = day * 15;
      var month = day * 30;
      var now = new Date().getTime(); //获取当前时间毫秒
      var diffValue = now - dateTimeStamp; //时间差

      if (diffValue < 0) {
        return;
      }
      var minC = diffValue / minute; //计算时间差的分，时，天，周，月
      var hourC = diffValue / hour;
      var dayC = diffValue / day;
      // var weekC = diffValue / week;
      // var monthC = diffValue / month;
      // if (monthC >= 1 && monthC <= 3) {
      // 	result = " " + parseInt(monthC) + "月前";
      // } else if (weekC >= 1 && weekC <= 3) {
      // 	result = " " + parseInt(weekC) + "周前";
      // } else
      if (dayC >= 1 && dayC <= 3) {
        result = " " + parseInt(dayC) + "天前";
      } else if (hourC >= 1 && hourC <= 23) {
        result = " " + parseInt(hourC) + "小时前";
      } else if (minC >= 1 && minC <= 59) {
        result = " " + parseInt(minC) + "分钟前";
      } else if (diffValue >= 0 && diffValue <= minute) {
        result = "刚刚";
      } else {
        var datetime = new Date();
        datetime.setTime(dateTimeStamp);
        var Nyear = datetime.getFullYear();
        var Nmonth =
          datetime.getMonth() + 1 < 10
            ? "0" + (datetime.getMonth() + 1)
            : datetime.getMonth() + 1;
        var Ndate =
          datetime.getDate() < 10
            ? "0" + datetime.getDate()
            : datetime.getDate();
        var Nhour =
          datetime.getHours() < 10
            ? "0" + datetime.getHours()
            : datetime.getHours();
        var Nminute =
          datetime.getMinutes() < 10
            ? "0" + datetime.getMinutes()
            : datetime.getMinutes();
        var Nsecond =
          datetime.getSeconds() < 10
            ? "0" + datetime.getSeconds()
            : datetime.getSeconds();
        result = Nyear + "-" + Nmonth + "-" + Ndate;
      }
      return result;
    },
   
  },
  components: {}
};
</script>

<style scoped lang="css">
.hot-date {
  margin-left: 10px;
}

.project-img {
  width: 30% !important;
  height: 70px;
  /* margin-left: -40px; */
  /* border-radius: 3px; */
}

.hot-keyword span {
  font-size: 12px;
  color: rgb(158, 156, 156);
}
.hot-keyword {
  text-align: left;
}

.hot-name {
  width: 100%;
}

.hot-name p {
  font-size: 14px;
  /* padding: 5px 5px; */
  line-height: 1.5;
  color: #000;
  word-wrap: break-word;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
}

.itemlist {
  display: flex;
  /* flex-direction: row-reverse; */
  justify-content: space-between;

  margin: 0px 12px;
  box-sizing: border-box;
}
.iteml {
  flex: 1;
  text-align: left;
  display: flex;
  flex-wrap: wrap;
  align-content: space-between;
  padding-right: 5px;
}
/* 视频播放 */
* {
  margin: 0;
  padding: 0;
}
.iconbox i,
.iconbox i span {
  font-size: 16px;
}
.iconbox i {
  display: inline-block;
  width: 35px;
}
.content {
  /* min-height: calc(100vh- 40px) !important;
  height: calc(100vh- 40px) !important; */
  text-align: start;
  height: 100%;
}
.comm-body .bg {
  width:355px;
  height: 200px;
  position: relative;
}
.comm-body{
   padding:10px
}
.bofang {
  height: 40px;
  width: 40px;
  position: absolute;
  top: 40%;
  left: 160px;
  /* margin-top: -124px; */
}
.hh-face-img {
  position: relative;
}
.masking {
  font-size: 14px;
  width: 100%;
  font-weight: 700;
  position: absolute;
  top: 0;
  /* line-height: 22px; */
  background: linear-gradient(
    rgba(0, 0, 0, 0.6) 5%,
    rgba(0, 0, 0, 0.5) 15%,
    rgba(0, 0, 0, 0.3) 25%,
    rgba(0, 0, 0, 0.1) 35%,
    rgba(0, 0, 0, 0)
  );
  box-sizing: border-box;
  padding: 10px 12px;
  height: 100%;
  z-index: 1010000000;
  /* border-radius: 0px 0px 5px 5px; */
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  overflow: hidden;
  /*! autoprefixer: off */
  -webkit-box-orient: vertical;
}
.masking p {
  width: 100%;
  font-weight: 600;
  color: #fff;
  overflow: hidden;
  text-overflow: ellipsis;
  text-align: justify;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  overflow: hidden;
  /*! autoprefixer: off */
  -webkit-box-orient: vertical;
}
.clickNum {
  color: #fff;
  position: absolute;
  bottom: 10px;
  font-size: 14px;
  right: 15px;
}
.clickNum span {
  margin-left: 8px;
}
.comment-ul {
  min-height: calc(100vh- 40px) !important;
  height: calc(100vh- 40px) !important;
  padding: 0px 12px;
}

.comment-ul li {
  padding-bottom: 10px;
}

.comm-keyword {
  display: flex;
  justify-content: space-between;
  color: #999;
  font-size: 14px;
  line-height: 24px;
}
.comm-footer {
  /* height: 60px; */
  line-height: 30px;
  box-sizing: border-box;
  text-align: justify;
  line-height: 18px;
  font-size: 14px;
}
/* 多图 */
.com-text {
  width: 100%;
  padding: 10px 12px;
   border-bottom: 1px solid #eee;
  /* margin-top: 30px; */
}
>>> .mint-header {
  color: #3966a7 !important;
}
.com-ul {
  width: 100%;
  min-height: 50px;
}

.com-last-ul {
  width: 100%;
  /* min-height: 80px; */
  display: flex;
  justify-content: space-between;
}
.content {
  min-height: calc(100vh- 40px) !important;
  height: calc(100vh- 40px) !important;
}
.com-left {
  width: 80%;
  padding-right: 10px;
  padding-bottom: 4px;
  display: flex;
  flex-wrap: wrap;
  align-content: space-between;
}

.more-li-img {
  width: 33%;
}

.more-li-img img {
  width: 100%;
  padding: 0px 2px;
  height: 70px;
  border-radius: 3px;
}

.sign-li-title {
  font-size: 14px;
  color: #333;
  min-height: 22px;
  max-height: 44px;
  line-height: 22px;
  margin-bottom: 5px;
  width: 100%;
  flex: 1;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
}
/* graph-li-title */
.graph-li-title {
  /* flex: 1; */
  font-size: 14px;
  color: #333;
  min-height: 22px;
  max-height: 44px;
  line-height: 22px;
  width: 100%;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
}

.sign-li-img {
  width: 100%;
}

.sign-li-img img {
  width: 100%;
  max-height: 180px;
  border-radius: 3px;
}

.graph-li-img {
  width: 40%;
}

.graph-li-img img {
  width: 100%;
  height: 75px;
  max-height: 180px;
  border-radius: 3px;
}

.com-li-info {
  width: 100%;
  display: flex;
  height: 16px;
  line-height: 16px;
  font-size: 12px;
}

.com-li-info span {
  display: inline-block;
  height: 16px;
  line-height: 16px;
}

.com-span-title {
  min-width: 70px;
  color: #3966a7;
}

.com-span-pre {
  flex: 1;
  color: #999;
}

.com-span-time {
  min-width: 40px;
  color: #999;
}

/****
    新增文章详情
  **/

.comment-ul {
  display: flex;
  /* flex-wrap: nowrap; */
  justify-content: space-around;
}
</style>




