<template>
  <div class="content">
    <!-- 公社 -->
    <vue-scroll
      ref="scroll"
      :ops="opsconfig"
      @refresh-start="handleRS"
      @load-before-deactivate="handleLBD"
      @refresh-before-deactivate="handleRBD"
      @load-start="handleLoadStart"
    >
      <div>
        <div class="newItem" v-for="(i,t) in newList" :key="t" @click="goFrom">
          <!-- 多图详情 -->
          <div class="com-text" v-if="i.type_id=='2'">
            <ul class="com-ul">
              <li class="sign-li-title">{{i.title}}</li>
              <ul class="com-last-ul">
                <li class="more-li-img"  v-for="(z,x) in i.image_id_array" :key="x">
                  <!-- <img :src="z.url"/> -->
                </li>
              </ul>
              <li class="com-li-info">
                <span class="com-span-title">桥公社</span>
                <span class="com-span-pre">评论:10</span>
                <span class="com-span-time">{{i.date}}</span>
              </li>
            </ul>
          </div>
          <!-- 大图详情 -->
          <div class="com-text" v-if="i.cover_id=='0' && (i.type_id=='1' || i.type_id=='0')">
            <ul class="com-ul">
              <li class="sign-li-title">{{i.title}}</li>
              <ul class="com-last-ul">
                <li class="sign-li-img">
                  <img :src="i.cover_id=='0'?resetimg:i.cover" />
                </li>
              </ul>
              <li class="com-li-info">
                <span class="com-span-title">桥公社</span>
                <span class="com-span-pre">评论:10</span>
                <span class="com-span-time">{{i.date}}</span>
              </li>
            </ul>
          </div>
           <!-- 单图详情 -->
           <div class="com-text" v-if="(i.type_id=='1' || i.type_id=='0' )&& i.cover_id!='0' ">
            <ul class="com-ul">
              <ul class="com-last-ul">
                <li class="com-left">
                  <div class="graph-li-title">{{i.title}}</div>
                  <div class="com-li-info">
                    <span class="com-span-title">桥公社</span>
                    <span class="com-span-pre">评论:10</span>
                    <span class="com-span-time">{{i.date}}</span>
                  </div>
                </li>
                <li class="graph-li-img">
                  <img :src="i.cover" />
                </li>
              </ul>
            </ul>
          </div>
        </div>
      </div>
    </vue-scroll>
  </div>
</template>

<script>
import { ScrollConfig } from "common/data.js";
import { Toast } from "mint-ui";
export default {
  name: "swiperf",
  props: ["itemf"],
  data() {
    return {
      opsconfig: ScrollConfig.opsconfig,
      refresh: 1,
      noSscrollData: false,
      triggerType: "load",
      resetimg: require("images/1.png"),
      bigdata: [],
      smalldata: [],
      atype: "",
      newList: []
    };
  },
  activated() {
    this.getnewdate();
  },
  created() {
    this.getnewdate();
  },
  mounted() {},
  methods: {
    getIos(item, gourl) {
      let that = this;
      let isLogin = window.localStorage.getItem("isLogin");
      let loginstauts;
      if (isLogin == true || isLogin == "true") {
        loginstauts = "1";
      } else {
        loginstauts = "0";
      }
     var str = location.href;
       str = str.match(/(\S*)\#/)[1];
      let localUrl =str+"#/" ;
      let param = {
        articleId: item.id + "",
        articleUrl: localUrl + gourl,
        // 'articleType':type.category+"",
        loginstating: loginstauts,
        userId: this.$getUser.id + "",
        userToken: this.$getUser.token + "",
        route: localUrl + "login"
      };
      if (window.hasOwnProperty("webkit")) {
        // Toast("hhaha");
        window.webkit.messageHandlers.openWebView.postMessage(param);
      } else {
       
      }
    },
    goFrom(){
       this.$router.push({
        name: "newFrom"
      })
    },
    // 新闻数据
    async getnewdate(direct, hot) {
      try {
        let self = this;
        let params = {
          s: "",
          category: "1001",
          count: 10,
          hot_id: hot,
          direction: direct,
          disable_cache: 0
        };
        let res = await this.$api.all.GETfn(params, "list");
        for(let i in res.data){
          if(res.data[i].image_id_array){
            res.data[i].image_id_array.length=3;
             res.data[i].date=this.timeago( res.data[i].date*1000)
          }
        }
        this.atype = res.category;
        if (direct == "bigger") {
          this.newList = res.data.concat(this.newList);
          this.bigdata = res;
        } else if (direct == "litter") {
          this.newList = this.newList.concat(res.data);
          this.smalldata = res;
        } else {
          this.newList = res.data;
          this.bigdata = res;
          this.smalldata = res;
        }
        this.newList.forEach((item, index, arr) => {
          if (item.cover_id == "0") {
            self.showflag = false;
          } else {
            self.showflag = true;
          }
        });

        console.log;
      } catch (e) {
        console.log(e);
      }
    },
    handleRS(vsInstance, refreshDom, done) {
      // alert("这是执行下拉刷新开始做啥事");
      done();
    },
    handleLoadStart(vm, dom, done) {
      setTimeout(() => {
        // alert("这是执行下拉加载之前需要执行什么操作");
        done();
      }, 600);
    },
    handleLBD(vm, loadDom, done) {
      let dictir = "litter";
      let hot = this.smalldata.min_id;
      if (
        this.smalldata.has_more == false &&
        this.smalldata.direction == "litter"
      ) {
        //  this.noSscrollData = true;
        Toast("无更新数据");
        done();
      } else {
        this.getnewdate(dictir, hot);
        done();
      }
    },
    handleRBD(vm, loadDom, done) {
      console.log("下拉");
      let dictir = "bigger";
      let hot = this.bigdata.max_id;
      if (
        this.bigdata.has_more == false &&
        this.bigdata.direction == "bigger"
      ) {
        //  this.noSscrollData = true;
        Toast("无更多数据");
        done();
      } else {
        this.getnewdate(dictir, hot);
        done();
      }
      done();
    },
    //点击浏览
    async setclick(item) {
      try {
        let params = {
          target: item.id,
          action: 1,
          value: 1
        };
        let res = await this.$api.all.GETfn(params, "setclick");
        console.log(res);
        if (res.result.result == "ok") {
        }
      } catch (e) {
        console.log(e);
      }
    },
    // 跳转到详情页
    godetail(item) {
      console.log("要跳转的id" + item.type_id);
      let gourl = "";
      if (item.type_id == "2") {
        localStorage.setItem("homedata", JSON.stringify(item));
        gourl = "pictureDetail";
      } else if (item.type_id == "4") {
        localStorage.setItem("zhuantidata", JSON.stringify(item));
        gourl = "subjectDetail";
      } else {
        localStorage.setItem("homedata", JSON.stringify(item));
        // console.log(item);
        localStorage.setItem("atype", item.category_id);
        gourl = "homeDetail";
      }
      this.getIos(item, gourl);
      this.setclick(item);
    },
     timeago(dateTimeStamp) {
      //dateTimeStamp是一个时间毫秒，注意时间戳是秒的形式，在这个毫秒的基础上除以1000，就是十位数的时间戳。13位数的都是时间毫秒。
      var result;
      var minute = 1000 * 60; //把分，时，天，周，半个月，一个月用毫秒表示
      var hour = minute * 60;
      var day = hour * 24;
      var week = day * 7;
      var halfamonth = day * 15;
      var month = day * 30;
      var now = new Date().getTime(); //获取当前时间毫秒
      var diffValue = now - dateTimeStamp; //时间差

      if (diffValue < 0) {
        return;
      }
      var minC = diffValue / minute; //计算时间差的分，时，天，周，月
      var hourC = diffValue / hour;
      var dayC = diffValue / day;
      // var weekC = diffValue / week;
      // var monthC = diffValue / month;
      // if (monthC >= 1 && monthC <= 3) {
      // 	result = " " + parseInt(monthC) + "月前";
      // } else if (weekC >= 1 && weekC <= 3) {
      // 	result = " " + parseInt(weekC) + "周前";
      // } else
      if (dayC >= 1 && dayC <= 3) {
        result = " " + parseInt(dayC) + "天前";
      } else if (hourC >= 1 && hourC <= 23) {
        result = " " + parseInt(hourC) + "小时前";
      } else if (minC >= 1 && minC <= 59) {
        result = " " + parseInt(minC) + "分钟前";
      } else if (diffValue >= 0 && diffValue <= minute) {
        result = "刚刚";
      } else {
        var datetime = new Date();
        datetime.setTime(dateTimeStamp);
        var Nyear = datetime.getFullYear();
        var Nmonth =
          datetime.getMonth() + 1 < 10
            ? "0" + (datetime.getMonth() + 1)
            : datetime.getMonth() + 1;
        var Ndate =
          datetime.getDate() < 10
            ? "0" + datetime.getDate()
            : datetime.getDate();
        var Nhour =
          datetime.getHours() < 10
            ? "0" + datetime.getHours()
            : datetime.getHours();
        var Nminute =
          datetime.getMinutes() < 10
            ? "0" + datetime.getMinutes()
            : datetime.getMinutes();
        var Nsecond =
          datetime.getSeconds() < 10
            ? "0" + datetime.getSeconds()
            : datetime.getSeconds();
        result = Nyear + "-" + Nmonth + "-" + Ndate;
      }
      return result;
    }
  }
};
</script>

<style scoped lang="css">
* {
  margin: 0;
  padding: 0;
}
.com-text {
  width: 100%;
  padding: 0px 12px;
  /* margin-top: 30px; */
}
>>>.mint-header{
  color: #3966a7 !important;
}
.com-ul {
  width: 100%;
  min-height: 80px;
  padding: 10px 0px;
  border-bottom: 1px solid #eee;
}

.com-last-ul {
  width: 100%;
  /* min-height: 80px; */
  display: flex;justify-content: space-between
}
.content{
   min-height: calc(100vh- 40px) !important;
  height: calc(100vh- 40px) !important;
}
.com-left {
  width: 80%;
  padding-right: 10px;
  padding-bottom: 4px;
  display: flex;
  flex-wrap: wrap;
  align-content: space-between;
}

.more-li-img {
 width: 33%;
}

.more-li-img img {
 width: 100%;
  padding: 0px 2px;
  height: 70px;
  border-radius: 3px;
}

.sign-li-title {
  font-size: 14px;
  color: #333;
  min-height: 22px;
  max-height: 44px;
  line-height: 22px;
  margin-bottom: 5px;
  width: 100%;
  flex: 1;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
}
/* graph-li-title */
.graph-li-title {
  /* flex: 1; */
  font-size: 14px;
  color: #333;
  min-height: 22px;
  max-height: 44px;
  line-height: 22px;
  width: 100%;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
}

.sign-li-img {
  width: 100%;
}

.sign-li-img img {
  width: 100%;
  max-height: 180px;
  border-radius: 3px;
}

.graph-li-img {
  width: 40%;
}

.graph-li-img img {
  width: 100%;
  max-height: 180px;
  border-radius: 3px;
}

.com-li-info {
  width: 100%;
  display: flex;
  height: 16px;
  line-height: 16px;
  font-size: 12px;
}

.com-li-info span {
  display: inline-block;
  height: 16px;
  line-height: 16px;
}

.com-span-title {
  min-width: 70px;
  color: #3966a7;
}

.com-span-pre {
  flex: 1;
  color: #999;
}

.com-span-time {
  min-width: 40px;
  color: #999;
}

/****
    新增文章详情
  **/

.comment-ul {
  display: flex;
  /* flex-wrap: nowrap; */
  justify-content: space-around;
}
</style>


