<template>
  <div class="mainCover">
    <HeaderBar :needb="needb" :tit="titleName"></HeaderBar>
    <div class="mainContent">
      <ul class="follow1-ul">
        <vue-scroll
          ref="scroll"
          :ops="opsconfig"
          @refresh-start="handleRS"
          @load-before-deactivate="handleLBD"
        >
          <li class="follow1-li" v-for="(v,i) in allList" :key="i" @click.prevent="gotoAsk(v)">
            <div class="content1-left">
              <span class="span-title">{{(v.target_caption)}}</span>
              <!-- <span class="span-content">来自: {{v.target_caption}}</span> -->
              <span class="span-data">{{v.time | changeTime}}</span>
            </div>
            <div class="content1-right">
              <img :src="v.cover" />
            </div>
          </li>
        </vue-scroll>
      </ul>
    </div>
  </div>
</template>

<script>
import { ScrollConfig } from "common/data.js";
import JSONP from "common/json.js";
import moment from "moment";
import { ComUtils } from "common/utils.js";

export default {
  name: "me_follow",
  props: {},
  data() {
    return {
      opsconfig: ScrollConfig.opsconfig,
      titleName: "我的关注",
      needb: true,
      smallimage: "",
      max_id: "",
      min_id: "",
      allList: []
    };
  },
  computed: {},
  created() {
    this.smallimage = require("images/small.jpg");
    moment.locale("zh-cn");
    this.getDate("bigger", "0");
  },
  mounted() {},
  watch: {},
  methods: {
    gotoAsk(v) {
      console.log(v);
      if (v.target_type == "2003") {
        window.sessionStorage.setItem("askdata", JSON.stringify(v));
        this.$router.push({
          name: "commune_f"
        });
      } else if (v.target_type == "2001") {
        window.sessionStorage.setItem("homedata", JSON.stringify(v));
        // this.$router.push({
        //   name: "homeDetail"
        // });
        let that = this;
        ComUtils.skipPage(v, that);
      } else {
        this.$toast("数据异常,请联系管理员");
      }
    },
    async getDate(type, numsid) {
      try {
        debugger;
        let timestamp = new Date().getTime();
        console.log(timestamp);
        let params = {
          count: 10,
          timestamp: timestamp,
          direction: type,
          target_id: numsid
        };
        let res = await this.$api.all.GETfn(params, "user_collect");
        res = res.result;
        this.allList = res.data;
        this.max_id = res.max_id;
        this.min_id = res.min_id;
        console.log(this.allList);
      } catch (error) {}
    },
    async handleRS(vsInstance, refreshDom, done) {
      // alert("这是执行下拉刷新开始做啥事");
      try {
        let timestamp = new Date().getTime();
        let param = {
          count: 10,
          timestamp: timestamp,
          direction: "bigger",
          target_id: this.max_id
        };
        let res = await this.$api.all.GETfn(param, "user_collect");
        res = res.result;
        if (res.more == "0" && res.data.length == 0) {
          this.$toast("暂无最新数据");
          done();
          return;
        } else if (res.data.length > 0) {
          this.max_id = res.max_id;
          // this.min_id = res.min_id;
          res.data.forEach((item, index, arr) => {
            this.allList.unshift(item);
          });
          this.$forceUpdate();
          console.log(this.allList);
          done();
        }
        done();
      } catch (error) {}
    },
    async handleLBD(vm, loadDom, done) {
      // alert("这是执行下拉加载之后需要执行什么操作");
      try {
        let timestamp = new Date().getTime();
        let param = {
          count: 10,
          timestamp: timestamp,
          direction: "litter",
          target_id: this.min_id
        };
        let res = await this.$api.all.GETfn(param, "user_collect");
        res = res.result;
        if (res.more == "0" && res.data.length == 0) {
          this.$toast("没有更多数据");
          done();
          return;
        } else if (res.data.length > 0) {
          // this.max_id = res.max_id;
          this.min_id = res.min_id;
          res.data.forEach((item, index, arr) => {
            this.allList.push(item);
          });
          this.$forceUpdate();
          done();
          console.log(this.allList);
        }
        done();
      } catch (error) {}
    }
  },
  components: {},
  filters: {
    changeTime(val) {
      return moment(val)
        .startOf("minute")
        .fromNow();
    }
  }
};
</script>

<style scoped lang="css">
.mainContent {
  min-height: calc(100vh- 44px) !important;
  height: calc(100vh- 44px) !important;
}

.follow1-ul {
  min-height: calc(100vh- 44px) !important;
  height: calc(100vh- 44px) !important;
}

/* min-height: calc(100vh- 44px) !important;
  height: calc(100vh- 44px) !important; */
.follow2-button {
  flex: 1;
  height: 30px;
}

.follow2-content {
  height: 90px;
  line-height: 25px;
  width: 50%;
  padding: 10px 10px;
}

.follow2-content span {
  display: block;
  font-size: 14px;
}

.follow2-image {
  width: 30%;
  height: 90px;
  padding: 10px 0px;
}
.follow2-image img {
  width: 100%;
  height: auto;
}

.follow2-li div,
p,
button {
  display: inline-block;
}

.follow2-li {
  font-size: 14px;
  text-align: left;
  line-height: 20px;
  height: 90px;
  min-height: 90px;
  width: 100%;
  padding: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.follow1-ul,
.follow2-ul {
  height: 623px;
  min-height: 623px;
  width: 100%;
}

.follow1-ul {
  padding: 0px 12px;
}

.follow1-li {
  font-size: 14px;
  text-align: left;
  line-height: 20px;
  height: 90px;
  min-height: 90px;
  width: 100%;
  padding: 10px 0px;
  border-bottom: 1px solid #eee;
  display: flex;
}

.content1-left {
  width: 65%;
  line-height: 35px;
}

.content1-left span {
  display: block;
  width: 100%;
  height: 35px;
  line-height: 35px;
  /* padding: 2px 0px; */
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  color: #333;
}

.span-title {
  font-size: 15px;
  font-weight: 600;
}

.content1-left .span-content {
  font-size: 13px;
  color: #666;
}

.content1-left .span-data {
  font-size: 12px;
  color: #999;
}

.content1-right {
  flex: 1;
  padding-left: 10px;
}

.content1-right img {
  width: 100%;
  height: auto;
}
</style>



