<template>
  <div class="mainCover">
    <HeaderBar :needm="needm" :needb="needb" :tit="titleName"></HeaderBar>
    <div class="mainContent">
      <div class="fathercoverdiv">
        <div class="childcoverdiv">
          <li class="childcoverdiv-li" v-for="(item,index) in memberlist" :key="index">
            <img :src="item.avatar?item.avatar:'../../../static/imgs/activity.png'" alt="icon" @click.stop="privateinfo(item.id)" />
            <span>{{item.name}}</span>
          </li>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "communesmembers",
  props: {},
  data() {
    return {
      needm:true,
      titleName: "成员信息",
      needb: true,
      smallimage: "",
       rdata:[],
       memberlist:[]
    };
  },
  computed: {},
  activated() {
    this.smallimage = require("images/small.jpg");
     this.getParams();
     this.getmember()
  },
  mounted() {},
  watch: {},
  methods: {
      getParams() {
      var routerdata = window.sessionStorage.getItem("commitydata");
      console.log("luyou"+routerdata)
      this.rdata = JSON.parse(routerdata);
    },
    async getmember() {
      try {
        let params = {
          communeid:this.rdata.id,
         userid: "327195464540160441",
        token:"6g475hc3c2ce55cab1209998c8ecf04a0e4125a05d3806c448a6e8a9c0901b93840",
          count:100
        };
        let res = await this.$api.all.GETfn(params, "commune_user");
        this.memberlist=res.result
        console.log(res);
      } catch (e) {
        console.log(e);
      }
    },
       //去个人资料页
    privateinfo(v) {
    window.sessionStorage.setItem("S-person", v);
      this.$router.push({
        name: "commune_information"
      });
    },

  },
  components: {}
};
</script>

<style scoped lang="css">
.fathercoverdiv {
  display: flex;
  display: -webkit-flex;
  flex-direction: column;
}

.childcoverdiv {
  width: 100%;
  height: auto;
  display: flex;
  flex-wrap: wrap;
  padding: 8px;
}

.childcoverdiv-li {
  margin: 5px;
  width: 60px;
  min-height: 80px;
  border-radius: 8px;
}

.childcoverdiv-li-last {
  margin: 5px;
  width: 20%;
  height: 0px;
}

.bg_color_CB0165 {
  background: #cb0165;
}

.childcoverdiv-li img {
  display: block;
  width: 50px;
  height: 50px;
  margin: 5px;
  border-radius:50%;
}

.childcoverdiv-li span {
    width: 100%;
  display: block;
  height: 20px;
  line-height: 20px;
  font-size: 12px;
  text-align: center;
  color: #333;
  overflow: hidden;
  text-overflow: ellipsis;
white-space: nowrap;
}
</style>



