<template>
  <div class="content">
    <swiper :options="swiperOption" ref="mySwiper">
      <!-- v-show="n.type_id=='4'" -->
      <swiper-slide v-for="(n,i) in itemf" :key="i" @click.native="jumpnewdetail(n)" >
      <img v-lazy="n.imgPath?n.imgPath:n.image_url" class="page-lazyload-image1 hotb" alt="">
        <!-- </div> -->
        <p class="hot" v-if="n.type_id==4">专题</p>
        <div class="masking">
          <p>{{n.title}}</p>
          <p>
            <span>{{n.clicks?"浏览量:":''}}{{n.clicks}}</span>
            <span>&nbsp;&nbsp; {{n.date}}</span>
          </p>
        </div>
      </swiper-slide>
    </swiper>
  </div>
</template>
<script>
import { Toast } from "mint-ui";
export default {
  name: "swiperf",
  props: ["itemf", "type"],

  data() {
    return {
      swipesList: [],
      swipesListdata: [],
      swiperOption: {
        loop: true,
        pagination: {
          el: ".swiper-pagination"
        },
        observer: true, //修改swiper自己或子元素时，自动初始化swiper
        observeParents: false //修改swiper的父元素时，自动初始化swiper
      }
    };
  },
  created() {
    this.changeDate();
  },
  activated() {},
  methods: {
    changeDate() {
      // console.log("哈哈哈哈哈哈"+JSON.stringify(itemf))
      for (let i in this.itemf) {
        this.itemf[i].date = this.timeago(this.itemf[i].date * 1000);
      }
    },
    //点击浏览
    async setclick(item) {
      try {
        let params = {
          target: item.id,
          action: 1,
          value: 1
        };
        let res = await this.$api.all.GETfn(params, "setclick");
        console.log(res);
        if (res.result.result == "ok") {
        }
      } catch (e) {
        console.log(e);
      }
    },
    getIos(item) {
      let that = this;
      let isLogin = window.localStorage.getItem("isLogin");
      let loginstauts;
      if (isLogin == true || isLogin == "true") {
        loginstauts = "1";
      } else {
        loginstauts = "0";
      }
      var str = location.href;
      str = str.match(/(\S*)\#/)[1];
      let localUrl = str + "#/";
      let param = {
        articleId: item.id + "",
        articleUrl: localUrl + "homeDetail",
        // 'articleType':type.category+"",
        loginstating: loginstauts,
        userId: this.$getUser.id + "",
        userToken: this.$getUser.token + "",
        route: localUrl + "login"
      };
      if (window.hasOwnProperty("webkit")) {
        // Toast("hhaha");
        window.webkit.messageHandlers.openWebView.postMessage(param);
      } else {
      }
    },
    timeago(dateTimeStamp) {
      //dateTimeStamp是一个时间毫秒，注意时间戳是秒的形式，在这个毫秒的基础上除以1000，就是十位数的时间戳。13位数的都是时间毫秒。
      var result;
      var minute = 1000 * 60; //把分，时，天，周，半个月，一个月用毫秒表示
      var hour = minute * 60;
      var day = hour * 24;
      var week = day * 7;
      var halfamonth = day * 15;
      var month = day * 30;
      var now = new Date().getTime(); //获取当前时间毫秒
      var diffValue = now - dateTimeStamp; //时间差

      if (diffValue < 0) {
        return;
      }
      var minC = diffValue / minute; //计算时间差的分，时，天，周，月
      var hourC = diffValue / hour;
      var dayC = diffValue / day;
      // var weekC = diffValue / week;
      // var monthC = diffValue / month;
      // if (monthC >= 1 && monthC <= 3) {
      // 	result = " " + parseInt(monthC) + "月前";
      // } else if (weekC >= 1 && weekC <= 3) {
      // 	result = " " + parseInt(weekC) + "周前";
      // } else
      if (dayC >= 1 && dayC <= 3) {
        result = " " + parseInt(dayC) + "天前";
      } else if (hourC >= 1 && hourC <= 23) {
        result = " " + parseInt(hourC) + "小时前";
      } else if (minC >= 1 && minC <= 59) {
        result = " " + parseInt(minC) + "分钟前";
      } else if (diffValue >= 0 && diffValue <= minute) {
        result = "刚刚";
      } else {
        var datetime = new Date();
        datetime.setTime(dateTimeStamp);
        var Nyear = datetime.getFullYear();
        var Nmonth =
          datetime.getMonth() + 1 < 10
            ? "0" + (datetime.getMonth() + 1)
            : datetime.getMonth() + 1;
        var Ndate =
          datetime.getDate() < 10
            ? "0" + datetime.getDate()
            : datetime.getDate();
        var Nhour =
          datetime.getHours() < 10
            ? "0" + datetime.getHours()
            : datetime.getHours();
        var Nminute =
          datetime.getMinutes() < 10
            ? "0" + datetime.getMinutes()
            : datetime.getMinutes();
        var Nsecond =
          datetime.getSeconds() < 10
            ? "0" + datetime.getSeconds()
            : datetime.getSeconds();
        result = Nyear + "-" + Nmonth + "-" + Ndate;
      }
      return result;
    },
    jumpnewdetail(item, type) {
      if (window.hasOwnProperty("webkit")) {
        this.getIos(item);
      } else {
        localStorage.setItem("homedata", JSON.stringify(item));
        // localStorage.setItem("atype", type.category);
        this.$router.push({
          path: "/homeDetail",
          name: "homeDetail"
        });
      }
      this.setclick(item);
    }
  },
  computed: {
    swiper() {
      return this.$refs.mySwiper.swiper;
    }
  }
};
</script>

<style scoped>
.hotb {
  position: relative;
}
.hot {
  padding: 2px 5px;
  background: #0000007f;
  color: #fff;
  z-index: 100000000;
  position: absolute;
  left: 12px;
  bottom: 70px;
  border-radius: 3px;
  font-size: 12px;
}
* {
  margin: 0;
  padding: 0;
}
.content {
  width: 100%;
  padding: 0px;
  overflow: hidden;
}
.swiper-container {
  width: 100%;
  height: 100%;
  padding: 0px;
  /* position: absolute;
  left: 50%;
  top: 50%; */
  /* margin-left: -px; */
}
.swiper-slide {
  background-position: center;
  background-size: cover;
  position: relative;
  width: 100%;
}
.swiper-slide .hotb {
  height: 210px;
  width: 375px;
  border-radius: 0px;
}
.swiper-pagination {
  height: 20px;
}

.masking {
  font-size: 14px;
  width: 100%;
  font-weight: 700;
  position: absolute;
  bottom: 0px;
  /* line-height: 22px; */
  background: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.8) 90%);
  box-sizing: border-box;
  padding: 10px 12px;
  z-index: 1010000000;
  /* border-radius: 0px 0px 5px 5px; */
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  overflow: hidden;
  /*! autoprefixer: off */
  -webkit-box-orient: vertical;
}
.masking p {
  width: 100%;
  font-weight: 700;
  color: #fff;
  overflow: hidden;
  text-overflow: ellipsis;
  text-align: justify;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  overflow: hidden;
  /*! autoprefixer: off */
  -webkit-box-orient: vertical;
  /* margin-top: 3px; */
  font-size: 16px;
}
.masking p span {
  font-weight: 100;
  font-size: 12px;
}
.list-block /deep/ .swiper-wrapper {
  transition-timing-function: linear !important;
}

.swiper-container-horizontal > .swiper-pagination-bullets,
.swiper-pagination-custom,
.swiper-pagination-fraction {
  top: 10px !important;
}

.wrapper >>> .swiper-pagination-bullet-active {
  background: #fff;
}
</style>


