<template>
  <div class="mainCover">
    <HeaderBar :needc="needc" :tit="titleName" @fathercreate="goToCreateclubs" v-show="isShow"></HeaderBar>
    <div class="swiperbox">
      <swiperfang :itemf="topcommune"></swiperfang>
    </div>
    <!-- <div class="top_p">
      <div class="top_p_2" @click="gomeetdetail">
        <span>视频会议</span>
      </div>
    </div>-->
    <div class="mainContent">
      <ul :class="hei" ref="text">
        <vue-scroll
          ref="scroll"
          :ops="opsconfig"
          @load-before-deactivate="handleLBD"
          @refresh-before-deactivate="handleRBD"
        >
          <li
            ref="item"
            class="top_tm"
            v-for="(item,index) in commentlist"
            :key="index"
            @click="jumpcommDetail(item)"
          >
            <img v-lazy="item.image_url" class="page-lazyload-image1" />
            <div class="top_tm_1">
              <p>{{item.name}}</p>
              <p class="top_tm_2">{{item.info}}</p>
              <p class="num">
                <img :src="person" alt />
                {{item.joinCount}}人
              </p>
            </div>
          </li>
        </vue-scroll>
      </ul>
    </div>
  </div>
</template>

<script>
import { ScrollConfig } from "common/data.js";
import { Toast } from "mint-ui";
import swiperfang from "com/swiperfang.vue";
export default {
  name: "community",
  props: {},
  data() {
    return {
      opsconfig: ScrollConfig.opsconfig,
      refresh: 1,
      noSscrollData: false,
      triggerType: "load",
      titleName: "社群",
      needc: true,
      commentlist: [],
      topcommune: [],
      iddata: [],
      hei: "club-ul",
      isShow: false,
      person: require("images/peoSons.png")
    };
  },
  computed: {},
  activated() {
    this.getdate();
    this.communetop();
  },
  mounted() {
    // this.jumpcommdetail()
  },
  watch: {
    $route: {
      handler(route) {
        console.log("路由" + route.name);
        // const that = this;
        if (route.name != "community") {
          this.commentlist = [];
        } else {
          this.getdate();
        }
      }
    },
    commentlist: function() {
      this.$nextTick(function() {
        this.$refs.text.click();
      });
    }
  },
  methods: {
    goToCreateclubs() {
      this.$router.push({
        path: "/createclubs"
      });
      this.commentlist = [];
    },
    gotoSearch(pathname) {
      this.$router.push({
        name: "search",
        params: {
          pathname
        }
      });
      this.commentlist = [];
    },

    jumpcommDetail(item) {
      window.sessionStorage.setItem("commitydata", JSON.stringify(item));
      this.$router.push({
        name: "commune_meeting"
      });
      this.commentlist = [];
    },
    // zi(e){
    //   console.log(e.target)
    // },
    //
    async setbrowsing(item) {
      try {
        let params = {
          ask_id: item.ask_id
        };
        let res = await this.$api.all.POSTfn(params, "browsing");
        console.log("设置浏览量" + JSON.stringify(res));
      } catch (e) {
        console.log(e);
      }
    },
    goaskdetail(item) {
      window.sessionStorage.setItem("askdata", JSON.stringify(item));
      this.setbrowsing(item);
      this.$router.push({
        name: "commune_f"
      });
      this.commentlist = [];
    },
    //置顶广告
    async communetop() {
      try {
        let params = {};
        let qwe = await this.$api.all.POSTfn(params, "top_commune");
        if (qwe.result == "false") {
          this.hei = "club";
        } else {
          this.hei = "club-ul";
        }
        this.topcommune = qwe.result;
      } catch (e) {}
    },

    // 公社数据
    async getdate(direct, hot) {
      if (window.hasOwnProperty("webkit")) {
        this.isShow = false;
      } else {
        this.isShow = true;
      }
      try {
        let params = {
          count: 6,
          target_id: hot,
          direction: direct
          // disable_cache: 0
        };
        let res = await this.$api.home.GETfn(params, "commune_list");
        if (direct == "bigger") {
          this.commentlist = res.result.data.concat(this.commentlist);
        } else if (direct == "litter") {
          this.commentlist = this.commentlist.concat(res.result.data);
        } else {
          this.commentlist = res.result.data;
        }
        this.iddata = res.result;
        console.log(res);
      } catch (e) {}
    },
    handleLBD(vm, loadDom, done) {
      let dictir = "litter";
      let hot = this.iddata.min_id;
      if (this.iddata.more == 0) {
        //  this.noSscrollData = true;
        Toast("无更多数据");

        done();
      } else {
        this.getdate(dictir, hot);
        done();
      }
    },
    handleRBD(vm, loadDom, done) {
      let dictir = "bigger";
      let hot = this.iddata.max_id;
      if (this.iddata.more == 0) {
        Toast("无更新数据");
        done();
      } else {
        this.getdate(dictir, hot);
        done();
      }
      done();
    },
    gomeetdetail() {
      this.$router.push({
        name: "meetingDetail"
      });
      this.commentlist = [];
    }
  },
  components: {
    swiperfang
  }
};
</script>

<style scoped lang="css">
/* .hha{
  height: 100px;
  width: 100%
} */
.swiperbox{
  height: 210px;
}
.mainContent {
  min-height: calc(100vh- 240px) !important;
  height: calc(100vh- 240px) !important;
}
.content {
  height: 210px !important;
}
.top_th {
  height: 210px;
  width: 100%;
  position: relative;
}
.top_th img {
  height: 210px;
  width: 100%;
}
.top_th .top_1 {
  height: 86px;
  width: 100%;
  background: linear-gradient(transparent, rgba(0, 0, 0, 0.8) 90%);
  margin-top: -91px;
  position: absolute;
}
.num {
  color: #3966a7;
  font-size: 14px;
  font-weight: normal;
  margin-top: 30px;
}
.num * {
  vertical-align: middle;
}
.num img {
  height: 18px;
  width: 18px;
}
.top_th .top_1 .top_1_1 {
  font-size: 16px;
  font-weight: 700;
  color: #fff;
  position: relative;
  line-height: 22px;
  text-align: justify;
  width: 100%;
  padding: 28px 12px 0px 12px;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
}
.top_p {
  height: 60px;
  width: 100%;
  /* display: flex;
  justify-content: center;
  align-items: center; */
}

.top_p_2 {
  border-radius: 3px;
  width: 90%;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #3966a7;
  margin: 10px 20px;
}

.top_p_2 span {
  font-size: 14px;
  color: #fff;
}
.club-ul {
  width: 100%;
  padding: 5px 12px 0px 12px;
  min-height: calc(100vh- 240px);
  height: calc(100vh- 240px);
  margin-top: 10px;
}
.club {
  width: 100%;

  padding: 5px 12px 0px 12px;
  min-height: calc(100vh- 154px) !important;
  height: calc(100vh- 154px) !important;
}
>>> .__panel {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
ul.club:after {
  content: "";
  height: 0;
  clear: both;
  overflow: hidden;
  display: block;
  visibility: hidden;
}
ul.club-ul:after {
  content: "";
  height: 0;
  clear: both;
  overflow: hidden;
  display: block;
  visibility: hidden;
}
li.top_tm {
  min-height: 30px;
  height: 120px;
  width: 100%;
  list-style: none;
  /* position: relative; */
  overflow: hidden;
  border-radius: 3px;
  display: flex;
  justify-content: space-between;
}

ul li img {
  height: 100px;
  width: 170px;
  border-radius: 3px;
}
ul li span {
  font-size: 16px;
  color: #333;
  text-align: left;
}
ul li .top_tm_1 {
  font-size: 14px;
  min-height: 20px;
  width: 45%;
  text-overflow: ellipsis;
  font-weight: 700;
}
/* ul li .top_tm_1 .top_tm_1_1 {
  font-size: 12px;
  color: #fff;
  width: 100%;
  text-indent: 5px;
} */
ul .top_tm .top_tm_2 {
  font-size: 12px;
  color: #999;
  width: 100%;
  min-height: 30px;
  /* height: 65px; */
  overflow: hidden;
  text-align: justify;
  text-overflow: ellipsis;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  display: -webkit-box !important;
}
</style>



