<template>
  <div class="mainCover">
    <HeaderBar :needb="needb" :tit="titleName"></HeaderBar>
    <!-- <form id="hh-commune" enctype="multipart/form-data" name="fileinfo"> -->
    <div class="content">
      <div class="titlebox">
        <mt-field label="*公社名称" placeholder="请输入公社名称" v-model="formItem.commname" name="name"></mt-field>
      </div>
      <div class="createclubs_tq" id="describe">
        <!-- <label>*描述</label> -->
        <mt-field
          label="*描述"
          placeholder="请简要的描述下本公社吧"
          type="textarea"
          rows="3"
          name="info"
          v-model="formItem.introduction"
        ></mt-field>
        <input class="info-tag" type="hidden" name="tag" :value="formItem.tag" />
      </div>
      <div class="createclubs_tm">
        <label>*公社封面</label>
        <div class="logo-box">
          <Photoclipcom
            viewdom="fasImg"
            @fatherfromcall="getchilfromdata"
            @fatherimgcall="getchildimgcall"
            outputSizeWidth="340"
            outputSizeHeight="200"
            :inSize="INSIZE"
          ></Photoclipcom>
          <img
            :src="formItem.imgi"
            alt
            class="face-img"
            @click="$refs.inputFile.click()"
            id="fasImg"
          />
        </div>
      </div>
    </div>
    <div class="btn">
      <button type="button" class="c-ok-btn" id="yBtn" @click="commune_cj()">确认</button>
      <button type="button" class="c-cancel-btn nBtn" @click="$router.back(-1)">取消</button>
    </div>
  </div>
  <!-- </form> -->
</template>

<script>
import { Toast } from "mint-ui";
export default {
  name: "community",
  props: {},
  data() {
    return {
      titleName: "创建公社",
      needb: true,
      communecj: [],
      formItem: {
        commname: "",
        imgi: require("images/upload.jpg"),
        introduction: "",
        file: "",
        tag: "标签"
      },
      param: new FormData(),
      INSIZE: [300, 150]
    };
  },
  computed: {},
  activated() {
    this.datas();
  },
  mounted() {},
  watch: {
    $route: {
      handler(route) {
        // console.log("路由" + route.name);
        // const that = this;
        if (route.name == "community") {
          this.formItem.commname = "";
          this.formItem.imgi = require("images/upload.jpg");
          this.formItem.introduction = "";
        }
      }
    }
  },
  methods: {
    datas() {},
    getchildimgcall(data) {
      this.formItem.imgi = data;
    },
    getchilfromdata(data) {
      this.param = data;
    },

    // 创建公社
    async commune_cj() {
      if (
        this.formItem.commname == "" ||
        this.formItem.imgi == require("images/upload.jpg") ||
        this.formItem.introduction == ""
      ) {
        return;
      }

      try {
        let params = this.param;
        params.append("name", this.formItem.commname);
        params.append("info", this.formItem.introduction);
        params.append("tag", this.formItem.tag);
        params.append("file", this.formItem.imgi);
        let res = await this.$api.all.POSTfn(params, "create_commune");
        this.communecj = res.result;
        Toast("创建成功,等待审核");
        this.$router.push({
          path: "/community",
          name: "community"
        });
        this.formItem.commname = "";
        this.formItem.imgi = require("images/upload.jpg");
        this.formItem.introduction = "";
      } catch (e) {
        // console.log(e);
      }
    }
  },
  components: {}
};
</script>

<style scoped lang="css">
.content {
  padding: 0px 12px;
}

.titlebox,
.createclubs_tm,
.createclubs_tq >>> .mint-cell-wrapper {
  font-size: 14px;
  color: #333;
  border: 1px solid #eee;
  border-radius: 3px;
  margin-top: 10px;
  padding-left: 10px;
}

.mint-cell {
  height: 100%;
  min-height: 36px;
  color: #999;
}

.createclubs_th {
  height: 30px;
  width: 100%;
  float: left;
}
.createclubs_th >>> label {
  font-size: 14px;
  color: #999;
}

span {
  font-size: 14px;
  float: left;
  margin-top: 1%;
}
input {
  font-size: 14px;
  /* line-height: 30px; */
  /* padding: 0 40px; */
}

.info-tag {
  height: 0px;
}

.createclubs_tm {
  height: 150px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.createclubs_tq {
  height: 100px;
  width: 100%;
}
.descript-textarea textarea {
  height: 100%;
  padding: 10px;
}
.logo-box >>> #imagefile {
  position: absolute;
  opacity: 0;
  background: greenyellow;
  width: 100px;
  height: 100px;
  position: absolute !important;
}
.btn {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 20px 12px;
}

.c-ok-btn,
.c-cancel-btn {
  width: 150px;
  height: 36px;
  background: #4a8dff;
  color: #fff;
  font-size: 14px;
  padding: 5px 40px;
  border-radius: 3px;
}
.c-cancel-btn {
  background: #ccc;
}
.face-box {
  width: 65%;
  float: right;
  margin-top: 10px;
}
.face-img {
  height: 100px;
  width: 100px;
}
.logo-box {
  margin-right: 130px;
}
</style>




