<template>
  <div class="mainCover">
    <HeaderBar
      :needm="needm"
      :needb="needb"
      :tit="titleName"
      :needw="needwrite"
      @fatherwrite="fatherwritefn"
    ></HeaderBar>
    <div class="bigcover">
      <p class="subtitle c-font-special">
        <span class="c-title-vline"></span> 大V在线
      </p>

      <div class="bigcover-content">
        <swiper :options="swiperOption">
          <swiper-slide v-for="(v,i) in larvdatalist" :key="i" class="nav-swiper-slide">
            <div class="largevbar-li" @click.prevent="handleClick(v)" :data-id="v.user_id">
              <img v-lazy="v.avatar" class="page-default-image" alt />
              <span class="largevbar-span">{{v.name}}</span>
            </div>
          </swiper-slide>
        </swiper>
      </div>
    </div>

    <div class="lookforsb-cover">
      <div class="lookforsb c-font-list">
        <router-link class="card" to="/profinder">
          <div class="card-text">项目找人</div>
          <img class="card-img" :src="cardimgl" alt />
        </router-link>

        <router-link class="card" to="/lookforsb">
          <div class="card-text">人找项目</div>
          <img class="card-img" :src="cardimgr" alt />
        </router-link>
      </div>
    </div>

    <div class="mainContent">
      <vue-scroll
        ref="scroll"
        :ops="opsconfig"
        @refresh-start="handleRS"
        @load-before-deactivate="handleLBD"
      >
        <div v-for="(v,i) in askdatalists" :key="i" class="projectdynamicscover">
          <div class="projectdynamics">
            <div class="pdynamics-icon">
              <img :src="v.avatar" class="page-default-image" @click.prevent="personnal(v.user_id)" />
            </div>
            <div class="pdynamics-right">
              <ul class="pdynamics-ul">
                <li class="pdynamics-personnel c-font-name">{{v.user_name}}</li>
                <li class="pdynamics-name c-font-content">{{v.content}}</li>
                <li class="pdynamics-image">
                  <img
                    v-lazy="v.image_url"
                    class="page-lazyload-image1"
                    @click.prevent="communedetail(v)"
                  />
                </li>
                <li class="pdynamics-comments">
                  <span class="pdynamics-timer c-font-date">{{v.create_time | changeTime}}</span>
                  <span class="pdynamicsflex pdynamics-give" @click="GiveFn(v.id,i,v)">
                    <span
                      :class="['goodicon com-icon iconfont icon-good', { 'bluecolor': v.countData.flower.self=='1'}]"
                      slot="icon"
                      ref="likeimg"
                    >
                      <span
                        class="flower-count"
                        v-if="v.countData.flower.count!=0"
                      >{{v.countData.flower.count}}</span>
                    </span>
                  </span>

                  <span class="pdynamicsflex pdynamics-comment" @click="displayComment(i)">
                    <span class="commueicon com-icon iconfont icon-commue" slot="icon"></span>
                  </span>
                  <span class="pdynamicsflex pdynamics-forward" @click="fenxiang(i)">
                    <span class="shareicon com-icon iconfont icon-share" slot="icon" ref="fenxiang"></span>
                  </span>
                </li>
                <li :class="'pdynamics-input pdynamics-input-'+i" ref="pdynamicsinputref">
                  <input
                    type="text"
                    :class="'pdynamics-input_' + i"
                    v-model="commentaryData"
                    max-length="120"
                  />
                  <button @click.prevent="Comment(i,v)">评论</button>
                </li>
                <ul class="pdynamics-commentinformation-all" ref="pciall">
                  <li
                    class="pdynamics-commentinformation"
                    v-for="(child_v,child_i) in v.comment"
                    :key="child_i"
                  >
                    <span class="pdynamics-commentator c-font-name">{{child_v.name}}</span>:
                    <span class="pdynamics-detailed c-font-content">{{child_v.text}}</span>
                  </li>
                </ul>
              </ul>
            </div>
          </div>
          <div class="c-part-block"></div>
        </div>
        <div slot="load-beforeDeactive" v-if="noSscrollData">暂无更多</div>
      </vue-scroll>
    </div>
  </div>
</template>
  
<script>
import { ScrollConfig } from "common/data.js";
import { ComUtils } from "common/utils.js";
import moment from "moment";
import { mapState } from "vuex";

export default {
  name: "find",
  props: {},
  data() {
    return {
      opsconfig: ScrollConfig.opsconfig,
      refresh: 1,
      noSscrollData: false,
      triggerType: "load",
      titleName: "发现",
      needb: false,
      needm: false,
      needwrite: true,
      smallimage: "",
      pageSizes: 30,
      larvdatalist: [],
      askdatalist: [],
      askdatalists: [],
      pdynamicsinputvalue: "",
      commentaryData: "",
      styleFlag: true,
      UserInfo: this.$getUser,
      fenxiangimg: "",
      swiperOption: {
        slidesPerView: 6.5,
        // spaceBetween: 1,
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        }
      },
      max_id: "",
      min_id: ""
    };
  },
  computed: {
    ...mapState(["userinfomation"]),
    optionCustomer() {
      return {
        direction: 3,
        limitMoveNum: 20,
        step: 0.7
      };
    }
  },
  created() {
    this.fenxiangimg = require("images/fenxiang.png");
    this.cardimgl = require("images/cardimgl.png");
    this.cardimgr = require("images/cardimgr.png");

    moment.locale("zh-cn");
    this.smallimage = require("images/small.jpg");
    this.getDateList();
    // this.getDynamicData("bigger", "0");
  },
  mounted() {},
  watch: {},
  filters: {
    changeTime(val) {
      return moment(val)
        .startOf("minute")
        .fromNow();
    },
    filtercount(val) {
      return val == "0" ? null : val;
    }
  },
  activated() {
    this.getDynamicData("bigger", "0");
  },
  methods: {
    personnal(v) {
      window.sessionStorage.setItem("S-person", v);
      this.$router.push({
        name: "commune_information"
      });
    },
    communedetail(v) {
      window.sessionStorage.setItem("askdata", JSON.stringify(v));
      this.$router.push("commune_f");
    },
    handleClick(v) {
      window.sessionStorage.setItem("S-itemv", JSON.stringify(v));
      this.$router.push({
        name: "bigvonline"
      });
    },
    parentClick(e) {
      console.log(e);
    },
    fenxiang(i) {
      // if (!this.$refs.fenxiang[i].hasclick) {
      //   this.$refs.fenxiang[i].hasclick = true;
      //   this.$toast("分享成功");
      // } else if (this.$refs.fenxiang[i].hasclick) {
      //   this.$refs.fenxiang[i].hasclick = false;
      //   // this.$toast("取消分享");
      // }
      // this.$refs.fenxiang[i].hasclick
      //   ? (this.$refs.fenxiang[i].style.color = "#4a8dff")
      //   : (this.$refs.fenxiang[i].style.color = "#444");
    },
    displayComment(i) {
      i = parseInt(i);
      if (!this.$refs.pdynamicsinputref[i].hasclick) {
        this.$refs.pdynamicsinputref[i].hasclick = true;
      } else {
        this.$refs.pdynamicsinputref[i].hasclick = false;
      }
      this.$refs.pdynamicsinputref[i].hasclick
        ? (this.$refs.pdynamicsinputref[i].style.display = "block")
        : (this.$refs.pdynamicsinputref[i].style.display = "none");
    },
    Comment(i, v) {
      if ((this.commentaryData == "") | (this.commentaryData == null)) {
        this.$toast("请输入评论信息");
        return;
      }
      if (this.commentaryData.length > 120) {
        this.$toast("评论字数不能超过120个字符");
        return;
      }
      let lidom = document.createElement("li");
      lidom.setAttribute("class", "pdynamics-commentinformation");
      let _html = `<span class="pdynamics-commentator" style="color:#4a8dff;">${this.UserInfo.name}:</span><span class="pdynamics-detailed">${this.commentaryData}</span>`; //commentaryData
      lidom.innerHTML = _html;
      if (this.styleFlag) {
        let stylestr =
          ".pdynamics-commentinformation { width: 300px;margin: 5px 0px;padding: 5px 10px;min-height: 24px;line-height: 24px;background: #f6f7f9;text-align: justify;border-radius: 3px;}.pdynamics-commentator {  display: inline-block;float: left;min-height: 24px;line-height: 24px;min-width: 20px;}.pdynamics-detailed {  font-size: 13px;color: #6b6767;display: inline-block;word-wrap: break-word;max-width: 280px;min-height: 20px;line-height: 20px;}";
        ComUtils.createStyle(stylestr);
        this.styleFlag = false;
      }
      this.$refs.pciall[i].prepend(lidom);
      this.sendcomment(v);
      // this.commentaryData = "";
    },
    async GiveFn(id, i, item) {
      try {
        let param = {
          target: id
        };
        if (item.countData.flower.self == "1") {
          if (!this.$refs.likeimg[i].hasdian) {
            this.$refs.likeimg[i].hasdian = true;
            // this.$toast("点赞成功!");
            // item.countData.flower.count++;
            let addparam = {
              target: item.id,
              type: "2003"
            };
            this.removelike(addparam, item);
          } else {
            this.$refs.likeimg[i].hasdian = false;
            // item.countData.flower.count--;
            // this.$toast("取消点赞!");
            let removeparam = {
              target: item.id,
              type: "2003"
            };
            this.addlike(removeparam, item);
          }

          this.$refs.likeimg[i].hasdian
            ? (this.$refs.likeimg[i].style.color = "#444")
            : (this.$refs.likeimg[i].style.color = "#4a8dff");
        } else {
          if (!this.$refs.likeimg[i].hasdian) {
            this.$refs.likeimg[i].hasdian = true;
            // this.$toast("点赞成功!");
            // item.countData.flower.count++;
            let addparam = {
              target: item.id,
              type: "2003"
            };
            this.addlike(addparam, item);
          } else {
            this.$refs.likeimg[i].hasdian = false;
            // item.countData.flower.count--;
            // this.$toast("取消点赞!");
            let removeparam = {
              target: item.id,
              type: "2003"
            };
            this.removelike(removeparam, item);
          }
          this.$refs.likeimg[i].hasdian
            ? (this.$refs.likeimg[i].style.color = "#4a8dff")
            : (this.$refs.likeimg[i].style.color = "#444");
        }
      } catch (error) {
        console.log(error);
      }
    },
    GOTOBGVO() {
      this.$router.push({
        path: "/bigvonline"
      });
    },
    fatherwritefn() {
      this.$router.push("pubdy");
    },
    //去点赞
    async addlike(data, item) {
      try {
        let res = await this.$api.all.POSTfn(data, "good");
        if (res.result == "ok") {
          this.$toast("点赞成功!");
          item.countData.flower.count++;
        }
        console.log(res);
      } catch (e) {
        console.log(e);
      }
    },
    // 取消点赞
    async removelike(data, item) {
      try {
        let res = await this.$api.all.POSTfn(data, "good_delete");
        if (res.result == "ok") {
          item.countData.flower.count--;
          this.$toast("取消点赞!");
        }
        console.log(res);
      } catch (e) {
        console.log(e);
      }
    },
    async getDateList() {
      try {
        let param = {};
        let res = await this.$api.all.GETfn(param, "vip_list");
        this.larvdatalist = res.result;
        console.log(this.larvdatalist);
      } catch (e) {
        console.log(e);
      }
    },
    async getDynamicData(type, numid) {
      try {
        let param = {
          count: 20,
          direction: type,
          target_id: numid,
          type: "3"
        };
        let res = await this.$api.all.GETfn(param, "ask_list");
        res = res.result;
        this.askdatalists = res.data;
        this.max_id = res.max_id;
        this.min_id = res.min_id;
        console.log(res);
      } catch (e) {
        console.log(e);
      }
    },
    async handleRS(vsInstance, refreshDom, done) {
      try {
        let param = {
          count: 20,
          direction: "bigger",
          target_id: this.max_id,
          type: "3"
        };
        let res = await this.$api.all.GETfn(param, "ask_list");
        res = res.result;
        if (res.more == "0" && res.data.length == 0) {
          this.$toast("暂无最新数据");
          done();
          return;
        }
        if (res.data.length > 0) {
          this.max_id = res.max_id;
          this.min_id = res.min_id;
          res.data.forEach((item, index, arr) => {
            this.askdatalists.unshift(item);
          });
          this.$forceUpdate();
        }
        done();
      } catch (e) {
        done();
      }
    },
    async handleLBD(vm, loadDom, done) {
      try {
        let param = {
          count: 20,
          direction: "litter",
          target_id: this.min_id,
          type: "3"
        };
        let res = await this.$api.all.GETfn(param, "ask_list");
        res = res.result;
        if (res.more == "0" && res.data.length == 0) {
          this.$toast("暂无最新数据");
          done();
          return;
        }
        if (res.data.length > 0) {
          this.max_id = res.max_id;
          this.min_id = res.min_id;
          res.data.forEach((item, index, arr) => {
            this.askdatalists.push(item);
          });
          this.$forceUpdate();
        }
        done();
      } catch (e) {
        done();
      }
    },
    //发表评论
    async sendcomment(v) {
      try {
        //   console.log("接过来的id"+this.$route.params.data.id)
        let params = {
          target: v.id,
          title: "",
          text: this.commentaryData,
          _: "RFs0",
          type: "article"
        };
        // ;
        let res = await this.$api.all.POSTfn(params, "send_comment");
        this.commentaryData = "";
      } catch (e) {
        console.log(e);
      }
    }
  },
  components: {}
};
</script>

<style scoped lang="css">
a {
  color: #333 !important;
  text-decoration: none;
}

.c-font-content {
  color: #333;
  font-size: 14px;
}

.mainContent {
  min-height: calc(100vh-274px) !important;
  height: calc(100vh-274px) !important;
}
.seamless-warp {
  min-height: 50px;
  width: 375px;
  overflow: hidden;
}

.pdynamics-input {
  width: 95%;
  min-height: 26px;
  line-height: 26px;
  display: none;
}

.pdynamics-input input {
  width: 80%;
  min-height: 26px;
  line-height: 26px;
  text-indent: 5px;
  font-size: 14px;
  border: 1px solid #4a8dff;
  border-radius: 3px;
  margin: 11px 0px;
}

.bluecolor {
  color: #4a8dff;
}

.pdynamics-input input:hover {
  box-shadow: rgba(161, 159, 159, 0.7) 0px 0px 1px 1px;
}
.pdynamics-input button {
  width: 18%;
  min-height: 26px;
  line-height: 26px;
  text-align: center;
  font-size: 14px;
  border-radius: 3px;
  margin: 10px 0px;
  color: #fff;
  background: #4a8dff;
}

@component-namespace page {
  @component lazyload {
    @descendent list {
      text-align: center;
    }

    @descendent listitem {
      width: 300px;
      margin: 0 auto;
      margin-bottom: 10px;
      background-color: #ddd;
    }

    @descendent image {
      display: block;
      width: 100%;
    }

    @descendent image[lazy=loading] {
      width: 40px;
      margin: auto;
      height: 100px;
      background: url(../../assets/imgs/loging.gif) no-repeat fixed center;
    }
  }
}

.largevbar {
  min-height: 50px;
  width: 375px;
  text-align: center;
}

.bigcover {
  width: 375px;
  min-height: 115px;
  height: 115px;
}

.bigcover-content {
  width: 375px;
  height: 80px;
  line-height: 80px;
}

.swiper-container {
  height: 80px;
  line-height: 80px;
}

.largevbar-ul {
  width: 375px;
  height: 80px;
  display: block;
}

.largevbar-li {
  display: inline-block;
  width: 40px;
  height: 68px;
  margin: 8px;
}

.largevbar-li img {
  display: block;
  width: 100%;
  height: 40px;
  border-radius: 50%;
  /* border: 1px solid rgb(225, 241, 255); */
}

.largevbar-li span {
  display: block;
  width: 100%;
  height: 23px;
  line-height: 23px;
  font-size: 12px;
  color: black;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.subtitle {
  height: 40px;
  line-height: 40px;
  text-align: left;
  padding-left: 12px;
}
a.mint-cell {
  border-bottom: 1px solid #eee !important;
  height: 40px !important;
  line-height: 40px !important;
  text-align: left;
  text-indent: 10px;
  background: #ffffff;
  display: block;
  min-height: 40px !important;
}

.lookforsb-cover {
  height: 70px;
  display: flex;
  justify-content: center;
  /* padding: 10px 0px; */
  background-color: #f6f7f9;
}

.lookforsb {
  /* padding-top: 10px; */
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin: 0px 12px;
  height: 70px;
}

.card {
  width: 46%;
  height: 50px;
  margin: 0px 5px;
  background-color: #fff;
  border-radius: 5px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0px 6px 12px #eee;
}

.card-text {
  font-size: 16px;
  color: #333;
  margin-left: 20px;
}

.card-img {
  width: 60px;
  height: 60px;
  margin-right: 6px;
}

.projectdynamicscover {
  width: 100%;
  min-height: 243px;
}

.projectdynamics {
  width: 100%;
  min-height: 243px;
  font-size: 14px;
  display: flex;
  /* margin-bottom: 10px; */
}

.pdynamics-icon {
  display: inline-block;
  width: 40px;
  height: 40px;
  /* padding: 10px 5px 0px 10px; */
  margin: 15px 12px 0px 12px;
}

.pdynamics-icon img {
  width: 40px;
  height: 40px;
  border-radius: 20px;
}

.pdynamics-right {
  display: inline-block;
  height: 100%;
  flex: 1;
  /* padding: 11px 10px 11px 0px; */
  margin: 10px 12px 0px 0px;
}

.pdynamics-ul {
  height: 100%;
  list-style: none;
}

.pdynamics-personnel {
  width: 100%;
  height: 24px;
  line-height: 24px;
  vertical-align: middle;
}

.pdynamics-name {
  /* margin: 2px 0px 10px 0px; */
  margin-bottom: 10px;
  min-height: 20px;
  line-height: 20px;
  width: 100%;
  text-align: justify;
}

.pdynamics-image {
  margin: 2px 0px;
  width: 205px;
  height: 131px;
}

.pdynamics-image img {
  /* width: 100%; */
  height: 100%;
}

.pdynamics-comments {
  /* margin: 2px 0px; */
  width: 290px;
  height: 24px;
  line-height: 24px;
  /* padding-right: 10px; */
  font-size: 12px;
  display: flex;
}

.pdynamics-comments span.pdynamics-timer {
  width: 200px;
}

.pdynamicsflex {
  flex: 1;
  display: flex;
  min-width: 40px;
}

.pdynamicsflex span {
  /* width: 24px; */
  height: 24px;
  line-height: 24px;
  flex: 1;
}

.pdynamics-commentinformation {
  width: 300px;
  padding: 5px 10px;
  min-height: 24px;
  line-height: 24px;
  background: #f6f7f9;
  text-align: justify;
  border-radius: 3px;
}

.pdynamics-commentinformation-all {
  width: 300px;
  min-height: 24px;
  line-height: 24px;
}

.pdynamics-commentator {
  display: inline-block;
  float: left;
  min-height: 24px;
  line-height: 24px;
  min-width: 20px;
}

.pdynamics-detailed {
  font-size: 13px;
  color: #6b6767;
  display: inline-block;
  word-wrap: break-word;
  max-width: 280px;
  min-height: 20px;
  line-height: 20px;
}

.com-icon {
  font-size: 16px;
  display: inline-block;
  width: 24px;
  height: 24px;
  line-height: 24px;
}

.flower-count {
  display: inline-block;
  font-size: 12px;
  width: 13px !important;
  height: 24px;
  line-height: 24px;
  text-align: center;
}

.largevbar\-span {
  text-align: center;
}
</style>



