<template>
  <div class="homecomcover">
    <div>
      <div class="swiperbox">
        <swiperfang :itemf="hotPro.banner"></swiperfang>
      </div>
    </div>
    <div class="topNew">
      <div
        class="com-text"
        v-for="(item,index) in hotPro.hotInformation"
        :key="index"
        @click="godetail(item)"
      >
        <ul class="com-ul">
          <ul class="com-last-ul">
            <li class="com-left">
              <div class="graph-li-title">{{item.title}}</div>
              <div class="com-li-info">
                <!-- <span class="toptit">置顶</span> -->
                <span class="com-span-title titm" v-show="item.sourceName">{{ item.sourceName?item.sourceName:""}} &nbsp;</span>
                <span class="com-span-pre"> 浏览量:{{item.clicks}}</span>
                <span class="com-span-time">{{item.date}}</span>
              </div>
            </li>
            <li class="graph-li-img">
              <img v-lazy="item.imgPath" class="page-lazyload-image1" />
            </li>
          </ul>
        </ul>
      </div>
    </div>
    <!-- 公社 -->
    <div class="commentlist">
      <p class="swiperTitle">
        <span class="c-title-vline"></span> 公社
        <span class="c-title-right" @click="jumpcommdetail(item)">全部公社 ></span>
      </p>
      <ul>
        <swiper :options="swiperOption" ref="mySwipergongshe">
          <swiper-slide
            v-for="(item,index) in hotPro.commune"
            :key="index"
            class="comment-li commune"
          >
            <p @click="jumpcommDetail(item)">{{item.name}}</p>
          </swiper-slide>
        </swiper>
      </ul>

      <p class="swiperTitle">
        <span class="c-title-vline"></span> 大V推荐
      </p>
      <ul class="comment-ul">
        <div v-for="(c,b) in hotPro.recommendedUser" :key="b" class="comment-li-v">
          <ul class="li-v-ul" >
            <span class="del" @click="deleBigv(b)">x</span>

            <li class="li-v-img">
              <img :src="c.avatar?c.avatar:touxiang"  @click.stop="handleClick(c)"/>
            </li>
            <li class="li-v-title">{{c.name}}</li>
            <li class="li-v-text">{{c.job}}</li>
            <li class="li-v-button">
              <button v-if="c.fouse==0" @click.stop="addfouse(c,b)">关注</button>
              <button v-else @click.stop="removefouse(c,b)">已关注</button>
            </li>
          </ul>
        </div>
      </ul>
    </div>

    <!-- 文章详情 -->
    <div class="hotNew">
      <div v-for="(item,index) in hotPro.information" :key="index" @click="godetail(item)">
 <!-- 单文详情 -->
        <div class="com-text" v-if="parseInt(item.coverType)==4">
          <ul class="com-ul">
            <ul class="com-last-ul">
              <li class="sign-li-title">{{item.title}}</li>
            </ul>
            <li class="com-li-info">
              <span class="com-span-title"  v-show="item.sourceName">{{item.sourceName?item.sourceName:""}}  &nbsp;</span>
              <span class="com-span-pre">浏览量:{{item.clicks}}</span>
              <span class="com-span-time">{{item.date}}</span>
            </li>
          </ul>
        </div> 
        <div class="com-text" v-if="item.type_id!='3' && (parseInt(item.coverType)==1 || parseInt(item.coverType)==2)">
          <ul class="com-ul">
            <ul class="com-last-ul">
              <li class="com-left">
                <div class="graph-li-title">{{item.title}}</div>
                <div class="com-li-info">
                  <span class="com-span-title"  v-show="item.sourceName">{{item.sourceName?item.sourceName:""}}  &nbsp;</span>
                  <span class="com-span-pre">浏览量:{{item.clicks}}</span>
                  <span class="com-span-time">{{item.date}}</span>
                </div>
              </li>
              <li class="graph-li-img">
                <img  v-lazy="item.imgPath" class="page-lazyload-image1"  />
              </li>
            </ul>
          </ul>
        </div>

        <!-- 多图详情-->
        <div class="com-text" v-if="parseInt(item.coverType)==3">
          <ul class="com-ul">
            <li class="sign-li-title">{{item.title}}</li>
            <ul class="com-last-ul">
              <li class="more-li-img" v-for="(s,y) in item.image_ids" :key="y">
                <img v-lazy="s.url" class="page-lazyload-image1" />
              </li>
             
            </ul>
            <li class="com-li-info">
              <span class="com-span-title"  v-show="item.sourceName">{{item.sourceName?item.sourceName:""}} &nbsp;</span>
              <span class="com-span-pre">浏览量:{{item.clicks}}</span>
              <span class="com-span-time">{{item.date}}</span>
            </li>
          </ul>
        </div>
        <!-- 大图详情 -->
        <div class="com-text"  v-if="item.type_id=='3' ">
          <ul class="com-ul">
            <li class="sign-li-title">{{item.title}}</li>
            <ul class="com-last-ul">
              <li class="sign-li-img">
                <img v-lazy="item.imgPath" class="page-lazyload-image1" />
              </li>
            </ul>
            <li class="com-li-info">
              <span class="com-span-title"  v-show="item.sourceName">{{item.sourceName?item.sourceName:""}}  &nbsp;</span>
              <span class="com-span-pre">浏览量:{{item.clicks}}</span>
              <span class="com-span-time">{{item.date}}</span>
            </li>
          </ul>
        </div>
      </div>
    </div>
    <!-- 文章详情 -->
  </div>
</template>

<script>
import { ScrollConfig } from "common/data.js";
import swiperfang from "com/swiperfang.vue";
import swipergun from "com/swipergun.vue";
import moment from "moment";

export default {
  name: "homecom",
  props: ["hotPro"],
  data() {
    return {
      opsconfig: ScrollConfig.opsconfig,
      commmentlist: [],
      datalist: [],
      vip: [],
      swiperOption: {
        slidesPerView: 2,
        // spaceBetween: 1,
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        }
      },
      resetimg: require("images/1.png"),
      touxiang: require("images/morentu.jpg"),
    };
  },
  computed: {},
  activated() {
    // console.log("用户" + JSON.stringify(this.hotPro.information));
  },
  created() {
    // this.getvdate();
    // this.getdate();
    // this.getswiper();
    moment.locale("zh-cn");
  },
  mounted() {},
  watch: {},
  filters: {
    changeTime(val) {
      return moment(val)
        .startOf("minute")
        .fromNow();
    }
  },
  methods: {
    jumpcommdetail(item) {
      // window.sessionStorage.setItem("commitydata", JSON.stringify(item));
      this.$router.push({
        name: "community"
      });
    },
      jumpcommDetail(item) {
      window.sessionStorage.setItem("commitydata", JSON.stringify(item));
      this.$router.push({
        name: "commune_meeting"
      });
      this.commentlist = [];
    },
 //去关注
    async addfouse(i,k) {
      let data={
        // userid:i.user_id,
        target:i.vip_id
      }
      try {
        let res = await this.$api.all.POSTfn(data, "addFouse");
        if (res.result == "ok") {
           this.$set(this.hotPro.recommendedUser[k], `fouse`, 1)
        }
        console.log(res);
      } catch (e) {
        console.log(e);
      }
    },
    // 取消关注
    async removefouse(i,k) {
       let data={
        // userid:i.user_id,
        target:i.vip_id
      }
      try {
        let res = await this.$api.all.POSTfn(data, "removeFouse");
        if (res.result == "ok") {
           this.$set(this.hotPro.recommendedUser[k], `fouse`, 0)
        }
      } catch (e) {
        console.log(e);
      }
    },
    deleBigv(i){
     this.hotPro.recommendedUser.splice(i,1)
    },
     handleClick(v) {
      window.sessionStorage.setItem("S-itemv", JSON.stringify(v));
      this.$router.push({
        name: "bigvonline"
      });
    },
      getIos(item, gourl) {
      
      let that = this;
      let isLogin = window.localStorage.getItem("isLogin");
      let loginstauts;
      if (isLogin == true || isLogin == "true") {
        loginstauts = "1";
      } else {
        loginstauts = "0";
      }
      var str = location.href;
      app.gotoDetail(str,gourl)
      // if (window.hasOwnProperty("webkit")) {
      //   window.webkit.messageHandlers.openWebView.postMessage(param);
      // } else {
      // }
    },
    // 跳转到详情页
    godetail(item) {
      console.log("要跳转的id" + item.type_id);
      let gourl = "";
      if (item.type_id == "2") {
      localStorage.setItem("homedata", JSON.stringify(item));
        gourl = "pictureDetail";
      } else if (item.type_id == "4") {
        localStorage.setItem("zhuantidata", JSON.stringify(item));
        gourl = "subjectDetail";
      } else {
        localStorage.setItem("homedata", JSON.stringify(item));
        // console.log(item);
        localStorage.setItem("atype", item.category_id);
        gourl = "homeDetail";
      }
 this.setclick(item);
        // this.getIos(item, gourl);
      // } else {
        this.$router.push({
          // path: "/homeDetail"
          name: gourl,
        });
      // }

     
    },
  //点击浏览
    async setclick(item) {
      try {
        let params = {
          // userid: "327195464540160441",
          target: item.id,
          action: 1,
          value: 1
        };
        let res = await this.$api.all.GETfn(params, "setclick");
        console.log(res);
        if (res.result.result == "ok") {
        }
      } catch (e) {
        console.log(e);
      }
    },
  },
  components: {
    swiperfang,
    swipergun
  }
};
</script>

<style scoped lang="css">
/****
    新增文章详情
  **/
.topNew {
  margin-top: 10px;
}
.toptit {
  color: red;
}
.com-text {
  width: 100%;
  padding: 0px 12px;
  /* margin-top: 30px; */
}
.swiperbox {
  height: 210px;
}
.com-ul {
  width: 100%;
  min-height: 80px;
  padding: 10px 0px;
  border-bottom: 1px solid #eee;
}
.del {
  color: #777;
  position: absolute;
  top: -3px;
  right: -10px;
  display: inline-block;
  height: 20px;
  width: 20px;
  line-height: 20px;
  text-align: center;
}

.com-last-ul {
  width: 100%;
  /* min-height: 80px; */
  display: flex;
}
.com-left {
  width: 80%;
  padding-right: 10px;
  padding-bottom: 4px;
  display: flex;
  flex-wrap: wrap;
  align-content: space-between;
}

.more-li-img {
  flex: 1;
}

.more-li-img img {
  width: 100%;
  padding: 0px 2px;
  max-height: 120px;
  min-height: 80px;
  border-radius: 3px;
}

.sign-li-title {
  font-size: 14px;
  color: #333;
  min-height: 22px;
  max-height: 44px;
  line-height: 22px;
  margin-bottom: 5px;
  width: 100%;
  flex: 1;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
}
/* graph-li-title */
.graph-li-title {
  /* flex: 1; */
  font-size: 16px;
  color: #333;
  min-height: 22px;
  max-height: 44px;
  line-height: 22px;
  width: 100%;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
}

.sign-li-img {
  width: 100%;
}

.sign-li-img img {
  width: 100%;
  max-height: 180px;
  border-radius: 3px;
}

.graph-li-img {
  width: 40%;
}

.graph-li-img img {
  width: 100%;
  max-height: 180px;
  border-radius: 3px;
}

.com-li-info {
  width: 100%;
  display: flex;
  height: 16px;
  line-height: 16px;
  font-size: 12px;
}

.com-li-info span {
  display: inline-block;
  height: 16px;
  line-height: 16px;
}

.com-span-title {
  min-width: 50px;
  color: #3966a7;
}

.com-span-pre {
  flex: 1;
  color: #999;
}

.com-span-time {
  min-width: 40px;
  color: #999;
}

/****
    新增文章详情
  **/

.homecomcover {
  min-height: 532px !important;
  height: 532px !important;
}

.swiperTitle {
  height: 46px;
  line-height: 20px;
  text-align: left;
  box-sizing: border-box;
  font-weight: 700;
  display: block;
  width: 100%;
  padding: 15px 12px 10px 12px;
  font-size: 18px;
}
.c-title-vline {
  margin: 0px;
}

.comment-ul {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  overflow: hidden;
}

.comment-li {
  height: 40px;
  line-height: 40px;
  text-align: center;
  min-width: 85px;
  max-width: 100px;
  /* border: 1px solid #eee; */
  border-radius: 5px;
  font-size: 14px;
  font-weight: 700;
  color: #63a0f9;
  background: #deecff;
}
.commune {
  margin-left: 12px;
  margin-right: 0 !important;
}

.comment-li-v {
  text-align: center;
  /* border: 1px solid #eee ; */
  box-shadow: 0px 2px 6px 0px #0000000e;
  padding: 8px 16px;
  border-radius: 5px;
  margin-left: 18px
}

.li-v-title {
  height: 18px;
  line-height: 18px;
  font-size: 14px;
  color: #333;
}
.li-v-ul {
  position: relative;
}
.li-v-text {
  height: 14px;
  line-height: 14px;
  font-size: 12px;
  color: #999;
  margin-top:5px;
}

.li-v-button {
  width: 100%;
  margin-top: 8px;
}

.li-v-button button {
  display: inline-block;
  height: 25px;
  font-size: 12px;
  line-height: 25px;
  width: 70px;
  color: #fff;
  background: #4a8dff;
  border-radius: 3px;
}

.li-v-img {
  height: 60px !important;
  width: 100%;
  padding-top: 5px;
}

.li-v-img img {
  width: 50px;
  height: 50px;
  border: 1px solid transparent;
  border-radius: 50%;
  display: inline-block;
}

.comment-span {
  /* display: block;
  height: 90%; */
}

.c-title-right {
  display: inline-block;
  float: right;
  font-size: 14px;
  font-weight: normal;
  color: #999;
}

.comm-name {
  display: block;
  width: 100%;
  height: 30px;
  line-height: 30px;
}

.comm-name p {
  width: 100%;
  height: 30px;
  line-height: 30px;
  font-weight: 600;
  display: block;
  padding: 0px 12px;
}

.hh-face-img {
  width: 100%;
  max-height: 220px;
  min-height: 220px;
}
.hh-face-img img {
  width: 100%;
  max-height: 220px;
  min-height: 220px;
  border-radius: 3px;
}

.comm-body {
  /* width: 100%; */
  max-height: 220px;
  min-height: 220px;
  margin: 0px 12px;
}

.comm-keyword {
  width: 100%;
  max-height: 30px;
  min-height: 30px;
  line-height: 30px;
  padding: 0px 12px;
}

.comm-footer {
  width: 100%;
  max-height: 30px;
  min-height: 30px;
  line-height: 30px;
  /* padding: 0px 12px; */
}

.comm-keyword span {
  display: inline-block;
  /* margin: 0 10px; */
  margin-right: 10px;
}

.nbsp {
  height: 10px;
  width: 100%;
  background: transparent;
}
</style>



