// 会议公社


<template>
  <div class="mainCover">
    <HeaderBar
      :needb="needb"
      @fatherjoin="setjoin"
      :needj="isneedj"
      :tit="titName"
      v-if="showHeaderflag"
    >{{joinname}}</HeaderBar>
    <div class="commune_th">
      <router-link to="/searask">
        <i class="mintui mintui-search"></i>
        <input type="text" placeholder="请输入关键字" />
      </router-link>
      <!-- <div @click.stop="goToPath()">社问</div> -->
    </div>
    <mt-navbar v-model="selected">
      <mt-tab-item id="1">话题</mt-tab-item>
      <mt-tab-item id="2">推介</mt-tab-item>
      <mt-tab-item id="3">社群</mt-tab-item>
    </mt-navbar>

    <div class="mainContent">
      <mt-tab-container v-model="selected">
        <mt-tab-container-item id="1">
          <huati></huati>
        </mt-tab-container-item>

        <mt-tab-container-item id="2">
          <vue-scroll
            ref="scroll"
            :ops="opsconfig"
            @refresh-start="handleRS"
            @load-before-deactivate="handleLBD"
            @refresh-before-deactivate="handleRBD"
            @load-start="handleLoadStart"
          >
            <div class="commune_box">
              <div class="commune" v-for="(i,d) in asklist" :key="d" @click="goaskdetail(i)">
                <div class="commune_t" v-show="i.image_url">
                  <img :src="i.image_url" alt />
                  <div class="tit">
                    <p class="titname">{{i.title}}</p>
                    <p class="content">{{i.commune_name}}</p>
                  </div>
                </div>
                <div class="commune_no" v-show="!i.image_url">
                  <p class="titname">{{i.title}}</p>
                  <p class="content">{{i.commune_name}}</p>
                </div>
              </div>
              <div class="nodata" v-if="asklist.length == 0">暂无数据</div>
            </div>
          </vue-scroll>
        </mt-tab-container-item>

        <mt-tab-container-item id="3">
          <group></group>
        </mt-tab-container-item>
      </mt-tab-container>

      <div class="create" @click="goCreate">
        <img :src="create" alt srcset />
      </div>
    </div>
  </div>
</template>
<script>
import mui from "static/js/mui.min.js";
import { ScrollConfig } from "common/data.js";
import { Toast } from "mint-ui";
import huati from "com/huati.vue";
import group from "com/commune_groupList.vue";
export default {
  name: "commune_th",
  props: {},
  data() {
    return {
      showHeaderflag: true,
      opsconfig: ScrollConfig.opsconfig,
      create: require("images/sqCreate.png"),
      joinname: "加入",
      isneedj: true,
      needb: true,
      search: "",
      selected: "1",
      rdata: [],
      titName: "",
      asklist: [],
      tabitem: ["1", "2"],
      iscombig: "",
      iddata: [],
      go: [
        {
          name: "成员",
          rou: "communesmembers"
        },
        {
          name: "发公告",
          rou: "club_asked"
        },
        {
          name: "关注公告",
          rou: "announcement"
        },
        {
          name: "评论公告",
          rou: "commentbulletin"
        }
      ],
      gose: [
        {
          name: "审核公告",
          rou: "examination"
        },
        {
          name: "监督公告",
          rou: "supervise"
        }
      ]
    };
  },
  computed: {},
  activated() {
    this.search = require("images/search.png");
    this.getParams();
    this.getdate();
    this.isjoin();
    // this.stopTouchend()
    // this.iscommbig();
  },
  mounted() {},
  watch: {
    $route: {
      handler(route) {
        if (route.name != "commune_meeting") {
          this.asklist = [];
        } else {
          this.$nextTick(function() {
            document.querySelector(".mainContent").click();
          });
        }
      }
    }
  },
  methods: {
    getParams() {
      let routerdata = window.sessionStorage.getItem("commitydata");
      console.log("我的公社传的数据" + routerdata);
      let self = this;
      this.rdata = JSON.parse(routerdata);
      this.showHeaderflag = false;
      this.$nextTick(() => {
        self.titName = self.rdata.name
          ? self.rdata.name
          : self.rdata.commune_name;
        self.showHeaderflag = true;
        self.$forceUpdate();
      });
    },
    //是否加入公社
    async isjoin() {
      try {
        let params = {
          communeid: this.rdata.commune_id
        };
        let res = await this.$api.all.GETfn(params, "isjoin");
        if (res.result == 1) {
          this.joinname = "离开公社";
        } else {
          this.joinname = "加入公社";
        }
      } catch (e) {
        console.log(e);
      }
    },
    // 设置加入离开
    setjoin() {
      let data = {
        communeid: this.rdata.commune_id
      };
      if (this.joinname == "加入公社") {
        console.log("加入");
        this.join(data);
      } else {
        console.log("离开");
        this.out(data);
      }
    },
    //去加入
    async join(data) {
      try {
        let res = await this.$api.all.POSTfn(data, "join");
        if (res.result == "ok") {
          this.joinname = "离开公社";
        }
      } catch (e) {
        console.log(e);
      }
    },
    // 离开
    async out(data) {
      try {
        let res = await this.$api.all.POSTfn(data, "out");
        if (res.result == "ok") {
          this.joinname = "加入公社";
        }
      } catch (e) {
        console.log(e);
      }
    },
    // 设问列表
    async getdate(direct, hot) {
      let params = {
        communeid: this.rdata.commune_id,
        count: 6,
        status: "",
        recommendid: "",
        hotid: "",
        target_id: hot,
        direction: direct
      };
      console.log(this.selected);
      if (this.selected == "1") {
        params.status = "2";
        params.recommendid = "";
        params.hotid = "";
      } else if (this.selected == "2") {
        params.recommendid = "2005";
        params.status = "";
        params.hotid = "";
      } else if (this.selected == "3") {
        params.hotid = "2004";
        params.recommendid = "";
        params.status = "";
      } else {
      }
      try {
        let res = await this.$api.home.GETfn(params, "ask_list");
        console.log(res);
        if (direct == "litter") {
          this.asklist = this.asklist.concat(res.result.data);
        } else {
          this.asklist = res.result.data.concat(this.asklist);
        }
        this.iddata = res.result;
      } catch (e) {}
    },
    golink(i) {
      window.sessionStorage.setItem("asktype", 2);
      this.$router.push({
        path: i.rou
      });
    },
    //
    async setbrowsing(item) {
      try {
        let params = {
          ask_id: item.id
        };
        let res = await this.$api.all.POSTfn(params, "browsing");
      } catch (e) {
        console.log(e);
      }
    },
    goaskdetail(item) {
      window.sessionStorage.setItem("askdata", JSON.stringify(item));

      this.setbrowsing(item);
      this.$router.push({
        name: "commune_f"
      });
      this.asklist == [];
    },
    // goaskseatch() {
    //   this.asklist = [];
    //   this.$router.push({
    //     name: "searask"
    //   });
    // },
    goCreate() {
      window.sessionStorage.setItem("asktype", 1);
      this.$router.push({
        name: "commune_group"
      });
    },
    handleRS(vsInstance, refreshDom, done) {
      // alert("这是执行下拉刷新开始做啥事");
      done();
    },
    handleLoadStart(vm, dom, done) {
      setTimeout(() => {
        // alert("这是执行下拉加载之前需要执行什么操作");

        done();
      }, 600);
    },
    handleLBD(vm, loadDom, done) {
      let dictir = "litter";
      let hot = this.iddata.min_id;
      if (this.iddata.more == 0) {
        Toast("无更多数据");
        done();
      } else {
        this.getdate(dictir, hot);
        done();
      }
    },
    handleRBD(vm, loadDom, done) {
      let dictir = "bigger";
      let hot = this.iddata.max_id;
      if (this.iddata.more == 0) {
        Toast("无更新数据");
        done();
      } else {
        this.getdate(dictir, hot);
        done();
      }
    }
  },

  components: { huati, group }
};
</script>
<style scoped lang="css">
/* 创建按钮 */
.create {
  position: fixed;
  bottom: 0;
  left: 35%;
}
.create img {
  height: 100px;
  width: 100px;
}
/* .mainContent {
  min-height: 570px;
} */

/* 搜索关键字 */
.commune_th {
  height: 56px;
  line-height: 55px;
  text-align: center;
  width: 100%;
  background: #f6f7f9;
  position: relative;
}
.commune_th input {
  border: 1px solid #eee;
  background: #fff;
  height: 33px;
  width: 310px;
  line-height: 32px;
  border-radius: 20px;
  font-size: 14px;
  text-indent: 35px;
}
.commune_th i {
  position: absolute;
  top: 3px;
  display: inline-block;
  margin-left: 10px;
  color: #666 !important;
}
.commune_th div {
  font-size: 14px;
  width: 20%;
  height: 32px;
  line-height: 32px;
  text-align: center;
  border-radius: 0px 3px 3px 0px;
  background: #4a8dff;
  color: #fff;
}

/* tob内容 */
.tit {
  width: 67%;
}
.mint-tab-container-item[data-v-7c4b4ebe] {
  height: 100%;
}
.content {
  width: 100%;
  font-size: 12.5px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  width: 100%;
  color: #666666;
  padding-top: 5px;
}
.titname {
  color: #333333;
  font-size: 15.5px;
  min-height: 43px;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  display: -webkit-box !important;
  width: 100%;
  font-weight: 600;
}
.commune {
  border-bottom: 1px solid #eee;
  padding: 8px 0px;
}
.commune_box {
  min-height: calc(100vh- 145px);
  height: calc(100vh- 145px);
}
.commune_t {
  display: flex;
  justify-content: space-between;
  min-height: 70px;
}
.commune_t img {
  height: 70px;
  width: 100px;
  /* background: transparent; */
  border: none;
}

/* 
.commune_b {
  font-size: 12px;
  padding-top: 5px;
  color: #bbbbbb;
  min-height: 20px;
  line-height: 20px;
}

.commune_b span {
  margin-right: 5px;
} */

.mint-navbar {
  border-bottom: 1px solid #eee;
  height: 44px;
  line-height: 44px;
  padding: 0 50px;
  padding-top: 4px;
}
.mint-navbar .mint-tab-item.is-selected {
  color: #3966a7;
  font-size: 14px;
  border-bottom: 2px solid #3966a7;
  margin: 0px;
}
.mint-tab-item {
  color: #999;
  line-height: 40px;
  min-height: 40px;
}

.mint-tab-container {
  min-height: 523px;
}

.mint-tab-container-item {
  padding: 0px 12px;
  min-height: 523px;
  height: 523px;
}

.gocomm {
  padding-left: 10px;
  font-size: 14px;
  border-bottom: 1px solid #eee;
  height: 44px;
  line-height: 44px;
}

.gocomm img {
  width: 24px;
  height: 24px;
  position: absolute;
  right: 5px;
  margin: 10px 0px;
}

.gonggao-list {
  margin: 5px 0 0 0;
}

.mint-tab-item >>> .mint-tab-item-label {
  font-size: 14px !important;
  line-height: 44px !important;
  height: 44px !important;
}
.nodata {
  width: 100%;
  text-align: center;
  color: #666;
  padding: 30px 0;
  font-size: 14px;
}
</style>



