<template>
  <div class="mainCover">
    <HeaderBar :needm="needm" :needb="needb" :tit="titleName"></HeaderBar>
    <div class="mainContent">
      <ul>
        <li v-for="(friend, index) in friendslist" :key="index" @click="handleChatJump(friend)">
          <div class="friend-list s">
            <div class="head-portrait-box">
              <img :src="friend.avatar" alt class="head-portrait" />
            </div>
            <div class="name-box">
              <p class="name">{{friend.alias}}</p>
            </div>
          </div>
        </li>

      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "news_friends",
  props: {},
  data() {
    return {
      titleName: "好友列表",
      needb: true,
      needm: false,
      back: "",
      debug: ""
    };
  },
  computed: {
    friendslist () {
      return this.$store.state.friendslist.filter(item => {
        let account = item.account
        let thisAttrs = this.userInfos[account]
        let alias = thisAttrs.alias ? thisAttrs.alias.trim() : ''
        item.alias = alias || thisAttrs.nick || account
        item.link = `/namecard/${item.account}`
        if ((!thisAttrs.isFriend) || (thisAttrs.isBlack)) {
          return false
        }
        return true
      })
    },
    userInfos () {
      return this.$store.state.userInfos
    }
  },
  created() {
    this.debug = require("images/debug.png");
  },
  mounted() {},
  watch: {},
  methods: {
    handleChatJump({account}){
      const sessionId = `p2p-${account}`;
      this.$router.push({
        name: 'chat',
        params: {
          sessionId
        }  
      });
      this.$store.dispatch('setCurrSession', sessionId)
    }
  },
  components: {}
};
</script>

<style scoped lang="css">
a {
  text-decoration: none;
}

.back {
  width: 45px;
  height: 45px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
}
.back-logo {
  width: 60%;
  height: 60%;
}
.back-link {
  align-items: flex-start;
  display: flex;
}

ul {
  padding: 0px;
  list-style-type: none;
}

li {
  width: 100%;
  overflow: hidden;
}

.friend-list {
  width: 100%;
  display: flex;
  height: 50px;
}

.head-portrait-box {
  width: 15%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.head-portrait {
  width: 36px;
  height: 36px;
  border-radius: 50%;
}

.name-box {
  width: 85%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid #eaeaea;
}

.name {
  font-size: 14px;
  color: #383839;
}
</style>



