<template>
  <div class="mainCover">
   <div>输入：<input type="text"></div>
    <ul class="headertab-ul">
      <li class="headertab-li" @click.prevent="sheetVisible1 = true" size="large">
        <span class="headertab-span c-font-list">
          {{cityfield}}
          <span class="all-tab-span all-tab-left">
            <span class="all-tab-nbsp">&nbsp;</span>
          </span>
        </span>
      </li>
      <li class="headertab-li" @click.prevent="sheetVisible2 = true" size="large">
        <span class="headertab-span c-font-list">
          {{industryfield}}
          <span class="all-tab-span all-tab-left">
            <span class="all-tab-nbsp">&nbsp;</span>
          </span>
        </span>
      </li>
      <li class="headertab-li headertab-li-active" @click.prevent="sheetVisible3 = true">
        <span class="headertab-span c-font-list">
          {{huibaoValue}}
          <span class="all-tab-span all-tab-right">
            <span class="all-tab-nbsp">&nbsp;</span>
          </span>
        </span>
      </li>
    </ul>
    <div class="canceldiv" @click.prevent="$router.back(-1)">&nbsp;</div>

    <div class="mainContent">
      
    </div>
      <mt-actionsheet :actions="actions1" v-model="sheetVisible1"></mt-actionsheet>
    <mt-actionsheet :actions="actions2" v-model="sheetVisible2"></mt-actionsheet>
    <mt-actionsheet :actions="actions3" v-model="sheetVisible3"></mt-actionsheet>
  </div>
</template>

<script>
import { ScrollConfig } from "common/data.js";
// import AutoScroll from "assets/script/autoScroll";
import { Toast } from "mint-ui";
import zhuanti from "pages/home/zhuanti.vue";
import vadioNew from "pages/home/vadioNew.vue";
import person from "com/person.vue";
import newList from "pages/home/newslist.vue";
import allSearch from "com/allSearch.vue";
import huati from "com/huati.vue";
let autoScrollInstance = null;
export default {
  name: "search",
  props: {},
  data() {
    return {
      searchValue: "",
      routerPage: "",
      newList: [],
      opsconfig: ScrollConfig.opsconfig,
      refresh: 1,
      noSscrollData: false,
      iddata: [],
      isShow: false,
      allSaerchD: [],
       sheetVisible1: false,
      sheetVisible2: false,
      sheetVisible3: false,
      actions1: [],
      actions2: [],
      actions3: [],
       cityfield: "城市",
      industryfield: "行业",
      huibaoValue: "专业",
      itemarrary: {
        "area_id": "",
        "industry_id": "",
        "reward_id": ""
      },
     
    };
  },
  computed: {},
  created() {
    this.routerPage = this.$route.params.pathname;

    // this.getnewdate();
  },
  mounted() {},
  activated() {
     this.getactions1();
    this.getactions2();
    this.getactions3();
    this.newList=[]
    if (window.hasOwnProperty("webkit")) {
      this.isShow = false;
    } else {
      this.isShow = true;
    }
    this.brige();
    let that = this;
    setTimeout(function() {
      that.$refs.zonghe.allData();
    });
  },
  watch: {
    searchValue: function() {
      this.focus();
    },
    $route: function() {
      this.$nextTick(function() {
        this.$refs.text.click();
      });
    }
  },
  methods: {
   
    brige() {
      let that = this;
      //  Toast("ios传过来的参数1")
      window.searchvalue = function(data) {
        //  Toast("ios传过来的参数"+JSON.stringify(data))
        if (data != undefined && data.hasOwnProperty("text")) {
          that.getnewdate(data.text);
        } else {
        }
      };
    },
    focus: function() {
      if (document.querySelector("input") == document.activeElement) {
        this.fuzzy_search(this.searchValue.trim());
      }
    },
    fuzzy_search: function(value) {
      var that = this;
      if (!value) {
        that.newList = [];
        return;
      }
      // this.getnewdate();
    },
    async getactions1() {
      try {
        let that = this;
        let params = {};
        let res = await this.$api.all.POSTfn(params, "person_area");
        res = res.result;
        let _param = {
          name: "全部城市",
          method: that.clearParamfn
        };
        this.actions1.push(_param);
        res.forEach((v, i, arr) => {
          let param = {
            name: v.name,
            id: v.id,
            type: v.type,
            method: that.selectactions1
          };
          this.actions1.push(param);
        });

        console.log(this.actions1);
      } catch (error) {
        console.log(error);
      }
    },
    async getactions2() {
      try {
        let that = this;
        let params = {};
        let res = await this.$api.all.POSTfn(params, "person_type");
        res = res.result;
        let _param = {
          name: "全部行业",
          method: that.clearParamfn
        };
        this.actions2.push(_param);
        res.forEach((v, i, arr) => {
          let param = {
            name: v.name,
            id: v.id,
            type: v.type,
            method: that.selectactions2
          };
          this.actions2.push(param);
        });
        console.log(this.actions2);
      } catch (error) {
        console.log(error);
      }
    },
    async getactions3() {
      try {
        let that = this;
        let params = {};
        let res = await this.$api.all.POSTfn(params, "person_reward");
        res = res.result;
        let _param = {
          name: "全部职务",
          method: that.clearParamfn
        };
        this.actions3.push(_param);
        res.forEach((v, i, arr) => {
          let param = {
            name: v.name,
            id: v.id,
            type: v.type,
            method: that.selectactions3
          };
          this.actions3.push(param);
        });
        console.log(this.actions3);
      } catch (error) {
        console.log(error);
      }
    },
    selectactions1(e, index) {
      this.cityfield = e.name;
      this.itemarrary["area_id"] = e.id;
      // this.pullactionstate();
      this.getDate("bigger", "0");
    },
    selectactions2(e, index) {
      this.itemarrary["industry_id"] = e.id;
      this.industryfield = e.name;
      // this.pullactionstate();
      this.getDate("bigger", "0");
    },
    selectactions3(e, index) {
      this.huibaoValue = e.name;
      this.itemarrary["reward_id"] = e.id;
      this.getDate("bigger", "0");
    },
    clearParamfn(e) {
      if (e.name == "全部城市") {
        this.cityfield = "城市";
        this.itemarrary["area_id"] = "";
        this.getDate("bigger", "0");
      } else if (e.name == "全部行业") {
        this.industryfield = "行业";
        this.itemarrary["industry_id"] = "";
        this.getDate("bigger", "0");
      } else {
        this.huibaoValue = "职务";
        this.itemarrary["reward_id"] = "";
        this.getDate("bigger", "0");
      }
      // this.getDefaultData("bigger", "0");
    },
 
    // 新闻数据
    async getnewdate(type,text, direct, hot) {
      try {
        let params = {
          s: this.searchValue,
          // s: text,
          category: type,
          count: 10,
          hot_id: hot,
          direction: direct,
          disable_cache: 0
        };
        let res = await this.$api.home.GETfn(params, "search");
        if (res.data.length == 0) {
          Toast("无搜索内容");
        }
         for(let i in res.data){
          res.data[i].date=this.timeago(res.data[i].date*1000)
        }
        // this.newList = res.data;
        if (direct == "bigger") {
          res.data = res.data.concat(this.newList);
        } else {
          res.data = this.newList.concat(res.data);
        }
        this.iddata = res;
        this.changeColor(res.data);
      } catch (e) {
        console.log(e);
      }
    },
    changeColor(resultsList) {
      resultsList.map((item, index) => {
        if (this.searchValue && this.searchValue.length > 0) {
          // 匹配关键字正则
          let replaceReg = new RegExp(this.searchValue, "g");
          // 高亮替换v-html值
          let replaceString =
            '<span style="color:red">' + this.searchValue + "</span>";
          resultsList[index].title = item.title.replace(
            replaceReg,
            replaceString
          );
        }
      });
      this.newList = [];
      this.newList = resultsList;
    },
    handleRS(vsInstance, refreshDom, done) {
      // alert("这是执行下拉刷新开始做啥事");
      done();
    },
    handleLoadStart(vm, dom, done) {
      setTimeout(() => {
        // alert("这是执行下拉加载之前需要执行什么操作");

        done();
      }, 600);
    },
    handleLBD(vm, loadDom, done) {
      let dictir = "litter";
      let hot = this.iddata.min_id;
      console.log(this.iddata.has_more);
      if (this.iddata.has_more == false) {
        this.noSscrollData = true;
        Toast("无搜索内容");
        done();
        return;
      }
      this.getnewdate(dictir, hot);
      done();
    },
    handleRBD(vm, loadDom, done) {
      let dictir = "bigger";
      let hot = this.iddata.max_id;
      console.log(this.iddata.has_more);
      if (this.iddata.has_more == false) {
        this.noSscrollData = true;
        Toast("无搜索内容");
        done();
        return;
      }
      this.getnewdate(dictir, hot);
      done();
    },
    timeago(dateTimeStamp) {
      //dateTimeStamp是一个时间毫秒，注意时间戳是秒的形式，在这个毫秒的基础上除以1000，就是十位数的时间戳。13位数的都是时间毫秒。
      var result;
      var minute = 1000 * 60; //把分，时，天，周，半个月，一个月用毫秒表示
      var hour = minute * 60;
      var day = hour * 24;
      var week = day * 7;
      var halfamonth = day * 15;
      var month = day * 30;
      var now = new Date().getTime(); //获取当前时间毫秒
      var diffValue = now - dateTimeStamp; //时间差

      if (diffValue < 0) {
        return;
      }
      var minC = diffValue / minute; //计算时间差的分，时，天，周，月
      var hourC = diffValue / hour;
      var dayC = diffValue / day;
      // var weekC = diffValue / week;
      // var monthC = diffValue / month;
      // if (monthC >= 1 && monthC <= 3) {
      // 	result = " " + parseInt(monthC) + "月前";
      // } else if (weekC >= 1 && weekC <= 3) {
      // 	result = " " + parseInt(weekC) + "周前";
      // } else
      if (dayC >= 1 && dayC <= 3) {
        result = " " + parseInt(dayC) + "天前";
      } else if (hourC >= 1 && hourC <= 23) {
        result = " " + parseInt(hourC) + "小时前";
      } else if (minC >= 1 && minC <= 59) {
        result = " " + parseInt(minC) + "分钟前";
      } else if (diffValue >= 0 && diffValue <= minute) {
        result = "刚刚";
      } else {
        var datetime = new Date();
        datetime.setTime(dateTimeStamp);
        var Nyear = datetime.getFullYear();
        var Nmonth =
          datetime.getMonth() + 1 < 10
            ? "0" + (datetime.getMonth() + 1)
            : datetime.getMonth() + 1;
        var Ndate =
          datetime.getDate() < 10
            ? "0" + datetime.getDate()
            : datetime.getDate();
        var Nhour =
          datetime.getHours() < 10
            ? "0" + datetime.getHours()
            : datetime.getHours();
        var Nminute =
          datetime.getMinutes() < 10
            ? "0" + datetime.getMinutes()
            : datetime.getMinutes();
        var Nsecond =
          datetime.getSeconds() < 10
            ? "0" + datetime.getSeconds()
            : datetime.getSeconds();
        result = Nyear + "-" + Nmonth + "-" + Ndate;
      }
      return result;
    }
  },
  components: {
    allSearch,
    vadioNew,
    zhuanti,
    huati,
    person
  }
};
</script>

<style lang="css" scoped>
.tit {
  text-align: left;
  font-size: 16px;
  font-weight: 700;
  display: block;
}

.headersearch {
  height: 46px;
  line-height: 46px;
  font-size: 15px;
}
/* 头部切换 */
.headersearch >>> .mint-searchbar-cancel {
  color: #3966a7 !important;
}
.headertab-span {
  display: block;
  width: 100%;
  height: 20px;
}
.headertab-span * {
  vertical-align: middle;
}
.all-tab-span {
  display: inline-block;
  width: 8px;
  height: 8px;
  /* position: relative;
  left: 76%; */
  /* margin-left: -8px; */
  border-right: 6px solid transparent;
  border-left: 6px solid transparent;
  border-top: 6px solid #ddd;
}

.all-tab-nbsp {
  width: 0px;
  height: 0px;
}

.headersearch {
  height: 44px;
  line-height: 44px;
}

.headertab-ul {
  width: 100%;
}
.c-font-content{
  color: #3966a7;
  width: 100%;
  display: inline-block;
}
.c-font-content span{
  width: 80%;
   display: inline-block;
   float: right;
   text-align: right;
}
.headertab-li {
  list-style: none;
  flex: 1;
  text-align: center;
  min-height: 44px;
  line-height: 44px;
  border-bottom: 1px solid #eee;
  vertical-align: bottom;
  padding-right: 8px;
  float: left;
  min-width: 33.3%;
}

/* 列表 */
.commune {
  border-bottom: 1px solid #eee;
  padding: 8px 0px;
}
.commune_box {
  padding: 10px;
  min-height: calc(100vh- 145px);
  height: calc(100vh- 145px);
}
.commune_t {
  display: flex;
  justify-content: space-between;
  min-height: 70px;
}
.commune_t img {
  height: 70px;
  width: 100px;
  /* background: transparent; */
  border: none;
}
.tit {
  width: 67%;
}
.titname {
  margin-top: 10px;
  color: #333333;
  font-size: 15.5px;
  min-height: 43px;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  display: -webkit-box !important;
  width: 100%;
  font-weight: 600;
}
.headersearch >>> .mint-searchbar-cancel {
  color: #3966a7 !important;
}
</style>





