<template>
  <div class="mainCover">
    <!-- <HeaderBar :needm="needm" :needb="needb" :tit="titleName"></HeaderBar> -->
    <div class="headersearch">
      <input type="text" placeholder="请输入关键字" v-model="resultSet" class="searchinput"  @keyup.13="appkeyHide">
      <i class="mintui mintui-search"></i>
      <!-- <mt-search placeholder="请输入项目名关键字" v-model="resultSet" cancel-text="取消"></mt-search> -->
    </div>
    <ul class="headertab-ul">
      <li class="headertab-li" @click.prevent="sheetVisible1 = true" size="large">
        <span class="headertab-span c-font-list">
          {{cityfield}}
          <span class="all-tab-span all-tab-left">
            <span class="all-tab-nbsp">&nbsp;</span>
          </span>
        </span>
      </li>
      <li class="headertab-li" @click.prevent="sheetVisible2 = true" size="large">
        <span class="headertab-span c-font-list">
          {{industryfield}}
          <span class="all-tab-span all-tab-left">
            <span class="all-tab-nbsp">&nbsp;</span>
          </span>
        </span>
      </li>
      <li class="headertab-li headertab-li-active" @click.prevent="sheetVisible3 = true">
        <span class="headertab-span c-font-list">
          {{huibaoValue}}
          <span class="all-tab-span all-tab-right">
            <span class="all-tab-nbsp">&nbsp;</span>
          </span>
        </span>
      </li>
    </ul>
    <div class="mainContent">
      <!-- <vue-scroll
        ref="scroll"
        :ops="opsconfig"
        @refresh-start="handleRS"
        @load-before-deactivate="handleLBD"
      >-->
      <div class="commune_box">
        <div class="nodata" v-show="allDatalist.length <=0">----- 暂无数据 -----</div>
        <div class="commune" v-for="(i,d) in  allDatalist" :key="d" @click.prevent="goAskData(i)" >
          <div class="commune_t" v-show="i.imgPath">
            <img v-lazy="i.imgPath" alt />
            <div class="tit">
              <p class="titname" v-html="i.title"></p>
              <span class="c-font-content">{{i.industry_name }} <span>{{ i.reward_name}}</span></span>
            </div>
          </div>
          <div class="commune_no" v-show="!i.imgPath">
            <p class="titname">{{i.title}}</p>
            <span class="c-font-content">{{i.industry_name }} <span>{{ i.reward_name}}</span></span>
          </div>
        </div>
        <!-- <div class="nodata" v-if="asklist.length == 0">暂无数据</div> -->
      </div>
      <!-- </vue-scroll> -->
    </div>
    <mt-actionsheet :actions="actions1" v-model="sheetVisible1"></mt-actionsheet>
    <mt-actionsheet :actions="actions2" v-model="sheetVisible2"></mt-actionsheet>
    <mt-actionsheet :actions="actions3" v-model="sheetVisible3"></mt-actionsheet>
  </div>
</template>

<script>
import { ScrollConfig } from "common/data.js";
import { ComUtils } from "common/utils";
export default {
  name: "lookforsb",
  props: {},
  data() {
    return {
      userid:"",
      opsconfig: ScrollConfig.opsconfig,
      titleName: "人找项目",
      needb: true,
      needm: false,
      allDatalist: [],
      sheetVisible1: false,
      sheetVisible2: false,
      sheetVisible3: false,
      actions1: [],
      actions2: [],
      actions3: [],
      UserInfo: this.$getUser,
      max_id: "",
      min_id: "",
      cityfield: "城市",
      industryfield: "行业",
      huibaoValue: "回报机制",
      itemarrary: {
        "area_id": "",
        "industry_id": "",
        "reward_id": ""
      },
      resultSet: ""
    };
  },
  computed: {},
  created() {
    this.getactions1();
    this.getactions2();
    this.getactions3();
  },
  activated() {
    this.getDate("bigger", "0");
    //  let data = {
    //     id: "319430217930703154",
    //     token:
    //       "5cec187gbd89e56deb4ff3aa102fd4ad128d7ae45d8c3c6546ed8173e4901323840"
    //   };
    //   ComUtils.saveParams(data);
  },
  mounted() {},
  watch: {
    resultSet: function() {
      if (document.querySelector("input") == document.activeElement) {
        this.getDate( "bigger", "0");
      } else {
        this.getDate("bigger", "0");
      }
    }
  },
  methods: {
     brige() {
      let that = this;

      window.getuserId = function( getuserid) {
        if(getuserid){
           that.userid = getuserid;
        }else{
          that.userid=""
        }
       
        
      };
     
    },
    moreEvent(key) {
      sessionStorage.setItem("morekey", JSON.stringify(key));
      this.toPath();
    },
    goAskData(v) {
      //  var str = location.href;
     app.goProject(JSON.stringify(v))
      // this.$router.push("commune_f");
    },
    toPath(key) {
      if (key == 0) {
        sessionStorage.removeItem("morekey");
      }
      this.$router.push({
        path: "/humansearch"
      });
    },
     appkeyHide(){
      app.keyHide()
    },
  
    async getDate(type, numsid) {
      try {
        let params = {
          key:this.resultSet,
          count: 15,
          direction: type,
          target_id: numsid,
          // status: "2",
          userid:this.userid?this.userid:" ",
          // user_id:this.userid?this.userid:" "
        };
        Object.assign(params, this.itemarrary);
        let res = await this.$api.all.GETfn(params, "find_ask_tag_list");
        console.log(res);
        this.allDatalist = res.data;
        this.max_id = res.maxId;
        this.min_id = res.minId;
      } catch (error) {
        console.log(error);
      }
    },
    async getactions1() {
      try {
        let that = this;
        let params = {};
        let res = await this.$api.all.POSTfn(params, "person_area");
        res = res.result;
        let _param = {
          name: "全部城市",
          method: that.clearParamfn
        };
        this.actions1.push(_param);
        res.forEach((v, i, arr) => {
          let param = {
            name: v.name,
            id: v.id,
            type: v.type,
            method: that.selectactions1
          };
          this.actions1.push(param);
        });

        console.log(this.actions1);
      } catch (error) {
        console.log(error);
      }
    },
    async getactions2() {
      try {
        let that = this;
        let params = {};
        let res = await this.$api.all.POSTfn(params, "person_type");
        res = res.result;
        let _param = {
          name: "全部行业",
          method: that.clearParamfn
        };
        this.actions2.push(_param);
        res.forEach((v, i, arr) => {
          let param = {
            name: v.name,
            id: v.id,
            type: v.type,
            method: that.selectactions2
          };
          this.actions2.push(param);
        });
        console.log(this.actions2);
      } catch (error) {
        console.log(error);
      }
    },
    async getactions3() {
      try {
        let that = this;
        let params = {};
        let res = await this.$api.all.POSTfn(params, "person_reward");
        res = res.result;
        let _param = {
          name: "全部回报机制",
          method: that.clearParamfn
        };
        this.actions3.push(_param);
        res.forEach((v, i, arr) => {
          let param = {
            name: v.name,
            id: v.id,
            type: v.type,
            method: that.selectactions3
          };
          this.actions3.push(param);
        });
        console.log(this.actions3);
      } catch (error) {
        console.log(error);
      }
    },
    selectactions1(e, index) {
      this.cityfield = e.name;
      this.itemarrary["area_id"] = e.id;
      // this.pullactionstate();
      this.getDate("bigger", "0");
    },
    selectactions2(e, index) {
      this.itemarrary["industry_id"] = e.id;
      this.industryfield = e.name;
      // this.pullactionstate();
      this.getDate("bigger", "0");
    },
    selectactions3(e, index) {
      this.huibaoValue = e.name;
      this.itemarrary["reward_id"] = e.id;
      this.getDate("bigger", "0");
    },
    clearParamfn(e) {
      if (e.name == "全部城市") {
        this.cityfield = "城市";
        this.itemarrary["area_id"] = "";
        this.getDate("bigger", "0");
      } else if (e.name == "全部行业") {
        this.industryfield = "行业";
        this.itemarrary["industry_id"] = "";
        this.getDate("bigger", "0");
      } else {
        this.huibaoValue = "回报机制";
        this.itemarrary["reward_id"] = "";
        this.getDate("bigger", "0");
      }
      // this.getDefaultData("bigger", "0");
    }
    // async handleRS(vsInstance, refreshDom, done) {
    //   // alert("这是执行下拉刷新开始做啥事");
    //   try {
    //     let timestamp = new Date().getTime();
    //     let param = {
    //       timestamp: timestamp,
    //       count: 15,
    //       direction: "bigger",
    //       target_id: this.max_id,
    //       status: "2"
    //     };
    //     let res = await this.$api.all.GETfn(param, "find_ask_tag_list");
    //     /************************************* */

    //     res = res.result;
    //     if (res.more == "0" && res.data.length == 0) {
    //       this.$toast("暂无最新数据");
    //       done();
    //       return;
    //     }

    //     let that = this;
    //     if (this.$isArrayfn(res.data) && res.data.length > 0) {
    //       this.allDatalist = res.data;
    //       this.allDatalist.forEach((item, index, arr) => {
    //         if (item.type == "1") {
    //           that.lookforsblist.push(item);
    //         } else if (item.type == "2") {
    //           that.lookforreslist.push(item);
    //         } else if (item.type == "3") {
    //           that.lookforproject.push(item);
    //         }
    //       });
    //     }

    //     this.max_id = res.maxId;
    //     this.min_id = res.minId;
    //     this.$forceUpdate();
    //     /************************************* */
    //     done();
    //   } catch (error) {}
    // },
    // async handleLBD(vm, loadDom, done) {
    //   // alert("这是执行上拉加载之后需要执行什么操作");

    //   try {
    //     let timestamp = new Date().getTime();
    //     let param = {
    //       timestamp: timestamp,
    //       count: 15,
    //       direction: "litter",
    //       target_id: this.min_id,
    //       status: "2"
    //     };
    //     let res = await this.$api.all.GETfn(param, "find_ask_tag_list");
    //     /************************************* */

    //     res = res.result;
    //     if (res.more == "0" && res.data.length == 0) {
    //       this.$toast("暂无更多数据");
    //       done();
    //       return;
    //     }

    //     let that = this;
    //     if (this.$isArrayfn(res.data) && res.data.length > 0) {
    //       this.allDatalist = res.data;
    //       this.allDatalist.forEach((item, index, arr) => {
    //         if (item.type == "1") {
    //           that.lookforsblist.push(item);
    //         } else if (item.type == "2") {
    //           that.lookforreslist.push(item);
    //         } else if (item.type == "3") {
    //           that.lookforproject.push(item);
    //         }
    //       });
    //     }

    //     this.max_id = res.maxId;
    //     this.min_id = res.minId;
    //     this.$forceUpdate();
    //     /************************************* */
    //     done();
    //   } catch (error) {}
    // }
  },
  components: {}
};
</script>

<style scoped lang="css">
.tit {
  text-align: left;
  font-size: 16px;
  font-weight: 700;
  display: block;
}

.headersearch {
  height: 46px;
  line-height: 46px;
  font-size: 15px;
  background: #f6f6f7;
  padding: 0 10px;
}
/* 头部切换 */

.headertab-span {
  display: block;
  width: 100%;
  height: 20px;
}
.headertab-span * {
  vertical-align: middle;
}
.all-tab-span {
  display: inline-block;
  width: 8px;
  height: 8px;
  /* position: relative;
  left: 76%; */
  /* margin-left: -8px; */
  border-right: 6px solid transparent;
  border-left: 6px solid transparent;
  border-top: 6px solid #ddd;
}
>>> .mint-actionsheet-list{
 max-height: 400px !important;
  overflow-y: auto !important;

}

.all-tab-nbsp {
  width: 0px;
  height: 0px;
}


.headertab-ul {
  width: 100%;
   border-bottom: 1px solid #eee;
   padding-left: 2px
}
.c-font-content{
  margin-top: 10px;
  color: #999;
  width: 90%;
  display: inline-block;
}
.c-font-content span{
  width: 80%;
   display: inline-block;
   float: right;
   text-align: right;
}
.headertab-li {
  list-style: none;
  flex: 1;
  text-align: center;
  min-height: 44px;
  line-height: 44px;
 
  vertical-align: bottom;
  /* padding-right: 8px; */
  float: left;
  min-width: 30.9%;
}

/* 列表 */
.commune {
  border-bottom: 1px solid #eee;
  padding: 8px 0px;
}
.commune_box {
  padding: 10px;
  min-height: calc(100vh- 145px);
  height: calc(100vh- 145px);
}
.commune_t {
  display: flex;
  justify-content: space-between;
  min-height: 70px;
}
.commune_t img {
  height: 70px;
  width: 100px;
  /* background: transparent; */
  border: none;
}
.tit {
  width: 67%;
}
.titname {
  /* margin-top: px; */
  color: #333333;
  font-size: 15.5px;
  min-height: 43px;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  display: -webkit-box !important;
  width: 100%;
  font-weight: 600;
}

.searchinput{
 
  border-radius: 20px;
  padding: 5px 10px 5px 30px;
  width: 100%;
  height: 30px;
}
.searchinput:focus{
  background: #ffffff
}
>>>.mintui-search{
  position: absolute;
  top: 1px;
  left: 20px;
  color:  #3966a7;
}
.nodata{
  color: #666;
  text-align: center;
  margin-top: 30px
}
</style>



