<template>
  <div class="mainCover">
    <HeaderBar :needb="needb" :tit="titleName"></HeaderBar>
    <div class="mainContent">
      <p v-show="allList.length==0" class="nothing">——— 您还没有发布过公告 ———</p>
      <ul class="scroll-ul">
        <vue-scroll
          ref="scroll"
          :ops="opsconfig"
          @refresh-start="handleRS"
          @load-before-deactivate="handleLBD"
        >
          <li v-for="(v,i) in allList" :key="i" class="scroll-li" @click.prevent="communedetail(v)">
            <div class="content">
              <div class="content-left">
                <p class="title">(<span :class="{'state1':(v.status == 1),'state2':(v.status == 2),'state3':(v.status == 3)}">{{ sta[v.status-1] }}</span>)
                  <b>{{v.title}}</b>
                </p>
                <p class="word">{{v.content}}</p>
                <p class="from">来自：{{v.commune_name}}</p>
              </div>
              <div class="content-right">
                <img v-lazy="v.image_url" class="page-lazyload-image" />
              </div>
            </div>
          </li>
        </vue-scroll>
      </ul>
    </div>
  </div>
</template>

<script>
import { ScrollConfig } from "common/data.js";
import moment from "moment";
export default {
  name: "me_notice",
  props: {},
  data() {
    return {
      opsconfig: ScrollConfig.opsconfig,
      titleName: "公告",
      needb: true,
      allList: [],
      sta: ["待审核", "已通过", "未通过"],
      UserInfo: this.$getUser,
      max_id: "",
      min_id: ""
    };
  },
  computed: {},
  created() {
    moment.locale("zh-cn");
    this.getDate("bigger", "0");
  },
  mounted() {},
  watch: {},
  methods: {
    communedetail(v) {
      window.sessionStorage.setItem("askdata", JSON.stringify(v));
      this.$router.push("commune_f");
    },
    async getDate(type, numsid) {
      try {
        let timestamp = new Date().getTime();
        let params = {
          count: 10,
          timestamp: timestamp,
          direction: type,
          target_id: numsid,
          liststype: "ask",
          type: "2"
        };
        let res = await this.$api.all.GETfn(params, "my_commune");
        res = res.result;
        this.allList = res.data;
        this.max_id = res.max_id;
        this.min_id = res.min_id;
      } catch (error) {}
    },
    async handleRS(vsInstance, refreshDom, done) {
      // alert("这是执行下拉刷新开始做啥事");
      try {
        let timestamp = new Date().getTime();
        let param = {
          count: 10,
          timestamp: timestamp,
          direction: "bigger",
          target_id: this.max_id,
          liststype: "ask",
          type: "2"
        };
        let res = await this.$api.all.GETfn(param, "my_commune");
        /************************************* */

        res = res.result;
        if (res.more == "0" && res.data.length == 0) {
          this.$toast("暂无最新数据");
          done();
          return;
        }
        if (res.data.length > 0) {
          this.max_id = res.max_id;
          this.min_id = res.min_id;
          res.data.forEach((item, index, arr) => {
            this.allList.unshift(item);
          });
          this.$forceUpdate();
        }
        /************************************* */
        done();
      } catch (error) {}
    },
    async handleLBD(vm, loadDom, done) {
      // alert("这是执行下拉加载之后需要执行什么操作");

      try {
        let timestamp = new Date().getTime();
        let param = {
          count: 10,
          timestamp: timestamp,
          direction: "litter",
          target_id: this.min_id,
          liststype: "ask",
          type: "2"
        };
        let res = await this.$api.all.GETfn(param, "my_commune");
        /************************************* */
        res = res.result;
        if (res.more == "0" && res.data.length == 0) {
          this.$toast("没有更多数据");
          done();
          return;
        }
        if (res.data.length > 0) {
          this.max_id = res.max_id;
          this.min_id = res.min_id;
          res.data.forEach((item, index, arr) => {
            this.allList.push(item);
          });
          this.$forceUpdate();
        }
        /************************************* */

        console.log(this.allList);
      } catch (error) {}
    }
  },
  components: {}
};
</script>

<style scoped lang="css">
.mainContent {
  width: 100%;
  height: 623px;
  min-height: 623px;
}
.scroll-ul {
  width: 100%;
    min-height: calc(100vh-44px);
  height: calc(100vh-44px);
  padding: 0px 12px;
}

.scroll-li {
  width: 100%;
  /* height: 80px; */
  /* min-height: 80px; */
}
.content {
  height: 100px;
  display: flex;
  border-bottom: 1px solid #eaeaea;
}

.content-left {
  width: 65%;
  /* margin: 8px 0px; */
  padding: 6px 0px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.state1 {
  color: rgb(240, 164, 0);
}
.state2 {
  color: rgb(0, 160, 35);
}
.state3 {
  color: rgb(200, 0, 0);
}

.title {
  font-size: 14px;
  color: #333;
  text-align: justify;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  display: -webkit-box !important;
}

.word {
  /* height: 40%; */
  font-size: 12px;
  line-height: 15px;
  color: #666;
  text-align: justify;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  display: -webkit-box !important;
}

.from {
  height: 25%;
  font-size: 6px;
  color: #999;
  display: inherit;
  align-items: center;
  text-align: justify;
}

.content-right {
  width: 35%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.content-right img {
  width: 90%;
  height: 90%;
}

.nothing {
  margin-top: 15px;
  font-size: 12px;
  color: #999;
  height: 40px;
  line-height: 40px;
  width: 100%;
  text-align: center;
}
</style>



