<template>
  <div class="mainCover">
    <div class="changeCover">
      <div class="headerCover">
        <mt-button icon="back" @click.native="gobackfn" slot="left"></mt-button>
      </div>

      <div class="topUserInfo">
        <div class="headerPortrait">
          <ul class="headerP-ul">
            <li class="headerP-li-left">
              <div class="img-border">
                <img v-lazy="UserParam.avatar" class="page-default-image" />
              </div>
              <div class="accountInfo">
                <ul class="account-ul">
                  <li>
                    <span class="name-class">{{UserParam.user_name}}</span>
                  </li>
                  <li class="account-meta-li">{{alldata.licontent}}</li>
                </ul>
              </div>
            </li>
            <li class="headerP-li-right">
              <div class="contactbutton">
                <button @click.prevent="toPageChatRoom">急速联系</button>
              </div>
              <div class="contactbutton">
                <button @click.prevent="toPageChatRoom" class="add">加好友</button>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </div>
    <div class="mainContent">
      <div class="page-navbar">
        <mt-navbar class="page-part" v-model="selected">
          <mt-tab-item id="0">大V问题</mt-tab-item>
          <mt-tab-item id="1">大V评论</mt-tab-item>
          <mt-tab-item id="2">大V关注</mt-tab-item>
          <mt-tab-item id="3">参与项目</mt-tab-item>
        </mt-navbar>
        <mt-tab-container class="page-content" v-model="selected">
          <components :is="currcomponent"></components>
        </mt-tab-container>
      </div>
    </div>
  </div>
</template>

<script>
const AddInProjects = () =>
  import(
    /* webpackChunkName: 'ImportFuncDemo' */ "pages/find/addInProjects.vue"
  ); //参与项目
const BigVAttention = () =>
  import(
    /* webpackChunkName: 'ImportFuncDemo' */ "pages/find/bigVAttention.vue"
  ); //大V关注
const BigVreview = () =>
  import(/* webpackChunkName: 'ImportFuncDemo' */ "pages/find/bigVreview.vue"); //大V评论
const BigVProblem = () =>
  import(/* webpackChunkName: 'ImportFuncDemo' */ "pages/find/bigVProblem.vue"); //大V问题

export default {
  name: "bigvonline",
  props: {},
  data() {
    return {
      titleName: "",
      needb: true,
      needm: false,
      UserInfo: this.$getUser,
      alldata: [],
      UserParam: {},
      selected: "0",
      currcomponent: "BigVProblem",
      componentarr: [
        "BigVProblem",
        "BigVreview",
        "BigVAttention",
        "AddInProjects"
      ]
    };
  },
  computed: {},
  activated() {
    console.log(window.sessionStorage.getItem("S-itemv"))
    this.UserParam = JSON.parse(window.sessionStorage.getItem("S-itemv"));
    this.getData();
  },
  created() {
    this.UserParam = JSON.parse(window.sessionStorage.getItem("S-itemv"));
    this.getData();
  },
  mounted() {},
  watch: {
    selected(newv, oldv) {
      this.currcomponent = this.componentarr[newv];
    }
  },
  methods: {
    toPageChatRoom() {
      this.$toast("功能暂未开放,敬请期待");
    },
    gotoPath(pathname) {
      this.$router.push({
        path: pathname
      });
    },
    async getData() {
      try {
        let param = {
          userid: this.UserParam.vip_id,
          user_id: this.UserParam.vip_id,
          token: "",
          target: this.UserParam.vip_id
        };
        let res = await this.$api.all.GETfn(param, "user_resume"); //likeimg
        this.alldata = res.result;
      } catch (error) {
        console.log(error);
      }
    },
    gobackfn() {
      this.$router.go(-1); //返回上一页
    }
  },
  components: {
    AddInProjects,
    BigVProblem,
    BigVreview,
    BigVAttention
  },
  destroyed() {
    this.$destory();
  }
};
</script>

<style scoped lang="css">
.mint-header {
  background-color: #fff !important;
  color: #4a8dff !important;
}

.mint-tab-item-label {
  height: 44px;
  line-height: 44px;
  font-size: 16px;
  color: #333;
}

.mint-tab-item {
  padding: 0px !important;
  height: 99%;
  color: #333;
  display: flex;
  align-items: center;
  justify-content: center;
}

.mint-navbar .mint-tab-item.is-selected {
  border-bottom: 2px solid #4a8dff;
  color: #4a8dff !important;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 600;
}

.is-selected .mint-tab-item-label {
  color: #4a8dff !important;
}

.page-navbar {
  width: 100%;
  height: 100%;
}
.page-part {
  height: 44px;
  line-height: 44px;
  border-bottom: 1px solid #eee;
}

.topUserInfo {
  width: 100%;
  min-height: 120px;
  /* background: -webkit-linear-gradient(#4a8dff, #fff); Safari 5.1 - 6.0 */
  /* background: -o-linear-gradient(#4a8dff, #fff); Opera 11.1 - 12.0 */
  /* background: -moz-linear-gradient(#4a8dff, #fff); Firefox 3.6 - 15 */
  /* background: linear-gradient(#4a8dff, #fff); 标准的语法 */
}

.headerPortrait {
  width: 100%;
  height: 120px;
}

.accountInfo {
  width: 100%;
  height: 50px;
}

.headerP-ul {
  width: 100%;
  height: 100%;
  display: flex;
}

.headerP-ul li {
  height: 100%;
}

.headerP-li-left {
  width: 60%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  align-items: center;
}
.headerP-li-left img {
  width: 60px;
  height: 60px;
  border-radius: 50%;
}
.headerP-li-left .img-border {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  border: 1px solid #4a8cff50;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
}

.name-class {
  display: block;
  width: 100%;
  /* height: 35px; */
  /* line-height: 35px; */
  text-align: center;
  color: #4a8dff;
  font-weight: 600;
  padding-bottom: 10px;
}

.headerP-li-right {
  width: 40%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  align-items: center;
  /* padding-left: 40px; */
  padding: 10px 0px 26px;
}

.contactbutton {
  width: 100%;
  height: 30px;
  line-height: 30px;
  display: flex;
  justify-content: start;
  flex-wrap: wrap;
  align-items: center;
}

.contactbutton button {
  height: 30px;
  line-height: 30px;
  width: 100px;
  background: #4a8dff;
  color: #fff;
  font-size: 14px;
  border-radius: 50px;
}

button.add {
  background: #fff;
  color: #4a8dff;
  border: 1px solid #4a8dff;
}

.account-ul {
  width: 100%;
  height: 60px;
}

.account-ul li {
  height: 35px;
  line-height: 35px;
  list-style: none;
}

.account-li {
  font-size: 16px;
  font-weight: 300;
}

.account-meta-li {
  text-indent: 2em;
  font-size: 16px;
  font-weight: 500;
  height: 20px !important;
  line-height: 20px !important;
  list-style: none;
}

ul.bottom-but-ul {
  width: 100%;
  height: 60px;
  line-height: 60px;
  display: flex;
  /* background: #eee */
}

li.bottom-li {
  list-style: none;
  flex: 1;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.mint-button--normal,
.mint-button--small {
  padding: 0px 10px;
  font-size: 15px;
}

.selectbtn {
  border-bottom: 1px solid #eee;
}

.selectbtn .mint-button--normal,
.selectbtn .mint-button--small {
  padding: 0px 10px;
}

.largeV-option-ul {
  width: 90%;
  height: 80px;
  display: flex;
  margin: 20px 5%;
}
li.largeV-option-li {
  list-style: none;
  flex: 1;
  text-align: center;
  border: 1px solid #eeeeee;
  border-radius: 50%;
  border-left: none;
  border-bottom: none;
  border-right: none;
  list-style: none;
}

li.largeV-option-li span {
  margin: 5px 0px 2px 0px;
  display: block;
  text-align: center;
  font-size: 15px;
}
li.largeV-option-li span img {
  width: 25px;
  height: 25px;
  margin: 0 auto;
}

.page-content {
  height: 441px;
  min-height: 441px;
}

.mint-tab-container {
  height: calc(100vh-208px) !important;
  min-height: calc(100vh-208px) !important;
}

.mint-tab-container-wrap {
  height: calc(100vh-208px) !important;
  min-height: calc(100vh-208px) !important;
}

.changeCover {
  background: linear-gradient(#ace0f9 0%, #fff1eb 100%);
  z-index: 100000000;
}

.mint-button--default,
.mint-button--small {
  background: transparent;
  color: #4a8dff;
}
</style>



