<template>
  <div class="mainCover">
    <HeaderBar :needb="needb" :tit="titleName"></HeaderBar>
    <div class="mainContent">
      <ul class="privilege_th">
        <vue-scroll
          ref="scroll"
          :ops="opsconfig"
          @refresh-start="handleRS"
          @load-before-deactivate="handleLBD"
        >
          <li class="privilege_th_1" v-for="(v,i) in allList" :key="i" @click.prevent="gotoAsk(v)">
            <div>
              <p style="padding-bottom:5px">{{v.name}}</p>
              <p class="name">
               {{v.info}}
              </p>
              <p class="jointime">已加入公社{{v.create_timestamp}}天</p>
            </div>
            <div>
              <img v-lazy="v.cover" class="page-lazyload-image1" />
            </div>
          </li>
        </vue-scroll>
      </ul>
    </div>
  </div>
</template>

<script>
import { ScrollConfig } from "common/data.js";
import moment from "moment";

export default {
  name: "me_privilege",
  props: {},
  data() {
    return {
      opsconfig: ScrollConfig.opsconfig,
      titleName: "我的公社",
      needb: true,
      needm: false,
      search: "",
      smallimage: "",
      allList: [],
      UserInfo: this.$getUser,
      max_id: "",
      min_id: "",
    };
  },
  computed: {},
  created() {
    this.search = require("images/search.png");
    this.smallimage = require("images/small.jpg");
    moment.locale("zh-cn");
    this.getDate("bigger", "0");
  },
  filters: {
    changeTime(val) {
      return moment(val)
        .startOf("minute")
        .fromNow();
    }
  },
  mounted() {},
  watch: {},
  methods: {
    gotoAsk(v) {
      window.sessionStorage.setItem("commitydata", JSON.stringify(v));
      this.$router.push("commune_meeting");
    },
    async getDate(type, numsid) {
      try {
        let params = {
          count: 10,
          direction: type,
          target_id: numsid,
          liststype: "joincomment"
        };
        let res = await this.$api.all.GETfn(params, "commune_my");
        for(let i in res.data){
          var timestamp1 = Date.parse( new Date());
          var utc=timestamp1 - res.data[i].create_timestamp*1000;
          res.data[i].create_timestamp=Math.round(utc/(24*60*60*1000)) 
        }
        this.allList = res.data;
        this.max_id = res.max_id;
        this.min_id = res.min_id;
      } catch (error) {}
    },
    async handleRS(vsInstance, refreshDom, done) {
      // alert("这是执行下拉刷新开始做啥事");
      try {
        let param = {
          count: 10,
          direction: "bigger",
          target_id: this.max_id,
          liststype: "joincomment"
        };
        let res = await this.$api.all.GETfn(param, "commune_my");
        /************************************* */
       
        if (res.more == "0" && res.data.length == 0) {
          this.$toast("暂无最新数据");
          done();
          return;
        }
        if (res.data.length > 0) {
          this.max_id = res.max_id;
          this.min_id = res.min_id;
          res.data.forEach((item, index, arr) => {
            this.allList.unshift(item);
          });
          this.$forceUpdate();
        }
        /************************************* */
        done();
      } catch (error) {}
    },
    async handleLBD(vm, loadDom, done) {
      // alert("这是执行下拉加载之后需要执行什么操作");
      try {
        debugger;
        let param = {
          count: 10,
          direction: "litter",
          target_id: this.min_id,
          liststype: "joincomment"
        };
        let res = await this.$api.all.GETfn(param, "commune_my");

        /************************************* */
        if (res.more == "0" && res.data.length == 0) {
          this.$toast("没有更多数据");
          done();
          return;
        }
        if (res.data.length > 0) {
          this.max_id = res.max_id;
          this.min_id = res.min_id;
          res.data.forEach((item, index, arr) => {
            this.allList.push(item);
          });
          this.$forceUpdate();
        }
        /************************************* */
        done();
        console.log(this.allList);
      } catch (error) {}
    },
        timeago(dateTimeStamp) {
      //dateTimeStamp是一个时间毫秒，注意时间戳是秒的形式，在这个毫秒的基础上除以1000，就是十位数的时间戳。13位数的都是时间毫秒。
      var result;
      var minute = 1000 * 60; //把分，时，天，周，半个月，一个月用毫秒表示
      var hour = minute * 60;
      var day = hour * 24;
      var week = day * 7;
      var halfamonth = day * 15;
      var month = day * 30;
      var now = new Date().getTime(); //获取当前时间毫秒
      var diffValue = now - dateTimeStamp; //时间差

      if (diffValue < 0) {
        return;
      }
      var minC = diffValue / minute; //计算时间差的分，时，天，周，月
      var hourC = diffValue / hour;
      var dayC = diffValue / day;
      var weekC = diffValue / week;
      var monthC = diffValue / month;
      if (dayC >= 1 && dayC <= 6) {
        result = " " + parseInt(dayC) + "天";
      } else if (hourC >= 1 && hourC <= 23) {
        result = "1天";
      } else if (minC >= 1 && minC <= 59) {
        result = "1天";
      } else if (diffValue >= 0 && diffValue <= minute) {
        result = "1天";
      } else {
        var datetime = new Date();
        datetime.setTime(dateTimeStamp);
        var Nyear = datetime.getFullYear();
        var Nmonth =
          datetime.getMonth() + 1 < 10
            ? "0" + (datetime.getMonth() + 1)
            : datetime.getMonth() + 1;
        var Ndate =
          datetime.getDate() < 10
            ? "0" + datetime.getDate()
            : datetime.getDate();
        var Nhour =
          datetime.getHours() < 10
            ? "0" + datetime.getHours()
            : datetime.getHours();
        var Nminute =
          datetime.getMinutes() < 10
            ? "0" + datetime.getMinutes()
            : datetime.getMinutes();
        var Nsecond =
          datetime.getSeconds() < 10
            ? "0" + datetime.getSeconds()
            : datetime.getSeconds();
        result = Nyear + "-" + Nmonth + "-" + Ndate;
      }
      return result;
    }
  },
  components: {}
};
</script>

<style scoped lang="css">
.mainContent {
  width: 100%;
  min-height: calc(100vh- 44px) !important;
  height: calc(100vh- 44px) !important;
}

.privilege_th {
  width: 100%;
  padding: 10px;
  min-height: calc(100vh- 44px) !important;
  height: calc(100vh- 44px) !important;
}
.privilege_th_1 {
  display: flex;
  justify-content: space-between;
  min-height: 80px;
  padding: 10px 0;
  border-bottom: 1px solid #eee;
}
.privilege_th_1 p{
  padding-bottom:10px 0
}
.jointime{
  color: #999;
  font-size: 14px
}
.name {
  height: 40px;
  color: #444;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
  font-size: 14px;
  padding: 0
}
.privilege_th_1 div:nth-child(1) {
  width: 65%;
 
}
.privilege_th_1 div:nth-child(2) {
  width: 33%;
}

.privilege_th_1 div:nth-child(2) img {
  width: 100%;
  height: 80px;
  border-radius: 5px
}
</style>



