<template>
  <div class="mainCover">
    <HeaderBar :needb="needb" :tit="titleName"></HeaderBar>
    <div class="mainContent">
      <p v-show="allList.length==0" class="nothing">——— 您还没有过收藏 ———</p>
      <ul class="collection-ul">
        <vue-scroll
          ref="scroll"
          :ops="opsconfig"
          @refresh-start="handleRS"
          @load-before-deactivate="handleLBD"
        >
          <li
            v-for="(v,i) in allList"
            :key="i"
            @click.prevent="gotoAsk(v)"
            class="collection-li"
            ref="lidom"
          >
            <div class="com-text" v-if="(v.type_id==0||v.type==1) && v.cover_id=='0'">
              <ul class="com-ul">
                <ul class="com-last-ul">
                  <li class="sign-li-title">{{v.target_caption}}</li>
                </ul>
                <li class="com-li-info">
                  <!-- <span
                    class="com-span-title"
                    v-show="item.sourceName"
                  >{{item.sourceName?item.sourceName:""}} &nbsp;</span>-->
                  <span class="com-span-pre">浏览量:{{v.clicks}}</span>
                  <span class="com-span-time">{{v.time}}</span>
                </li>
              </ul>
            </div>
            <!-- 多图详情-->
            <div class="com-text" v-if="v.type_id=='2'">
              <ul class="com-ul">
                <li class="sign-li-title">{{v.target_caption}}</li>
                <ul class="com-last-ul">
                  <li class="more-li-img" v-for="(s,y) in v.image_id_array" :key="y">
                    <img v-lazy="s.url" class="page-lazyload-image1" />
                  </li>
                </ul>
                <li class="com-li-info">
                  <!-- <span
                    class="com-span-title"
                    v-show="item.sourceName"
                  >{{item.sourceName?item.sourceName:""}} &nbsp;</span>-->
                  <span class="com-span-pre">浏览量:{{v.clicks}}</span>
                  <span class="com-span-time">{{v.time}}</span>
                </li>
              </ul>
            </div>
            <!-- 大图详情 -->
            <div class="comm-body" v-if="v.type_id=='4'">
              <div class="hh-face-img">
                <img class="bg" :src="v.cover" alt />
                <img class="bofang" src="../../../static/imgs/bo1.png" alt />
                <p class="clickNum">
                  {{v.clicks}}次播放
                  <span>51:20</span>
                </p>
                <div class="masking">
                  <p>{{v.target_caption}}</p>
                </div>
              </div>
              <div class="ciomm-footer clearfix">
                <!-- <p>{{i.title}}</p> -->
                <div class="comm-keyword">
                  <span>{{v.time}}</span>
                  <div class="iconbox">
                    <i class="iconfont icon-good">
                      <span>{{v.flowers}}</span>
                    </i>
                    <i class="commueicon com-icon iconfont icon-commue">
                      <span class="hh-fab-value">{{v.comments}}</span>
                    </i>
                    <i class="shareicon com-icon iconfont icon-share">
                      <span>{{v.shares}}</span>
                    </i>
                  </div>
                </div>
              </div>
            </div>
            <div class="com-text" v-if="(v.type_id==0||v.type==1 || v.type==4) && v.cover_id!='0'">
              <ul class="com-ul">
                <ul class="com-last-ul">
                  <li class="com-left">
                    <div class="graph-li-title">{{v.target_caption}}</div>
                    <div class="com-li-info">
                      <!-- <span
                        class="com-span-title"
                        v-show="item.sourceName"
                      >{{item.sourceName?item.sourceName:""}} &nbsp;</span>-->
                      <span class="com-span-pre">浏览量:{{v.clicks}}</span>
                      <span class="com-span-time">{{v.time}}</span>
                    </div>
                  </li>
                  <li class="graph-li-img">
                    <img v-lazy="v.cover" class="page-lazyload-image1" />
                  </li>
                </ul>
              </ul>
            </div>
          </li>
        </vue-scroll>
      </ul>
    </div>
  </div>
</template>

<script>
import { ScrollConfig } from "common/data.js";
import moment from "moment";
import { ComUtils } from "common/utils.js";

export default {
  name: "me_collection",
  props: {},
  data() {
    return {
      opsconfig: ScrollConfig.opsconfig,
      titleName: "我的收藏",
      needb: true,
      allList: [],
      UserInfo: this.$getUser,
      max_id: "",
      min_id: ""
    };
  },
  computed: {},
  created() {
    moment.locale("zh-cn");
    this.like = require("images/like.png");
    this.com = require("images/comment.png");
    this.getDate("bigger", "0");
  },
  mounted() {
    this.$nextTick(function() {
      document.querySelector(".collection-ul").click();
    });
  },
  activated () {
     this.getDate("bigger", "0");
  },
  watch: {
    $route() {
      this.$nextTick(function() {
        document.querySelector(".collection-ul").click();
      });
    }
  },
  methods: {
    gotoAsk(item) {
      // if (v.target_type == "2003") {
      //   window.sessionStorage.setItem("askdata", JSON.stringify(v));
      //   this.$router.push({
      //     name: "commune_f"
      //   });
      // } else if (v.target_type == "2001") {
      //   window.sessionStorage.setItem("homedata", JSON.stringify(v));
      //   // this.$router.push({
      //   //   name: "homeDetail"
      //   // });
      //   let that = this;
      //   ComUtils.skipPage(v, that);
      // } else {
      //   this.$toast("数据异常,请联系管理员");
      // }

       let gourl = "";
      if (item.type_id == "2") {
        localStorage.setItem("homedata", JSON.stringify(item));
        gourl = "pictureDetail";
      } else if (item.type_id == "4") {
        localStorage.setItem("zhuantidata", JSON.stringify(item));
        gourl = "subjectDetail";
      } else if (item.type_id == "3") {
        localStorage.setItem("vadiodata", JSON.stringify(item));
        gourl = "vadiodetail";
      } else {
        localStorage.setItem("homedata", JSON.stringify(item));
        // console.log(item);
        localStorage.setItem("atype", item.category_id);
        gourl = "homeDetail";
      }
      this.$router.push({
          
          name: gourl
        });
          // app.gotoDetail(str,gourl);
    },
    async getDate(type, numsid) {
      try {
        let timestamp = new Date().getTime();
        let param = {
          count: 10,
          timestamp: timestamp,
          direction: type,
          target_id: numsid
        };
        let res = await this.$api.all.GETfn(param, "user_collect");
          
        res = res.result;
         for (let i in res.data) {
           if(res.data[i].type_id=="2"){

               if (res.data[i].image_id_array.length > 3) {
            res.data[i].image_id_array.length = 3;
          }
          res.data[i].image_ids=res.data[i].image_id_array
           }
         
          //  res.data[i].image_ids.length
          // var myDate = new Date();
          // res.data[i].date = this.timeago(res.data[i].date * 1000);
        }
        console.log(res);
        this.allList = res.data;
        this.max_id = res.max_id;
        this.min_id = res.min_id;
      } catch (error) {}
    },
    async handleRS(vsInstance, refreshDom, done) {
      try {
        let timestamp = new Date().getTime();
        let param = {
          count: 10,
          timestamp: timestamp,
          direction: "bigger",
          target_id: this.max_id
        };
        let res = await this.$api.all.GETfn(param, "user_collect");
        /************************************* */
        res = res.result;
        if (res.more == "0" && res.data.length == 0) {
          this.$toast("暂无最新数据");
          done();
          return;
        } else if (res.data.length > 0) {
          this.max_id = res.max_id;
          // this.min_id = res.min_id;
          res.data.forEach((item, index, arr) => {
            this.allList.unshift(item);
          });
          this.$forceUpdate();
        }
        /************************************* */
        done();
      } catch (error) {}
    },
    async handleLBD(vm, loadDom, done) {
      try {
        let timestamp = new Date().getTime();
        let param = {
          count: 10,
          timestamp: timestamp,
          direction: "litter",
          target_id: this.min_id
        };
        let res = await this.$api.all.GETfn(param, "user_collect");

        /************************************* */
        res = res.result;
        if (res.more == "0" && res.data.length == 0) {
          this.$toast("没有更多数据");
          done();
          return;
        }
        if (res.data.length > 0) {
          // this.max_id = res.max_id;
          this.min_id = res.min_id;
          res.data.forEach((item, index, arr) => {
            this.allList.push(item);
          });
          this.$forceUpdate();
        }
        /************************************* */
        done();
      } catch (error) {}
    }
  },
  components: {}
};
</script>

<style scoped lang="css">
.mainContent {
  width: 100%;
  height: 623px;
  min-height: 623px;
}
.collection-ul {
  width: 100%;
  min-height: calc(100vh-44px);
  height: calc(100vh-44px);
}

.iconbox i,
.iconbox i span {
  font-size: 16px;
}
.iconbox i {
  display: inline-block;
  width: 35px;
}
.content {
  /* min-height: calc(100vh- 40px) !important;
  height: calc(100vh- 40px) !important; */
  text-align: start;
  height: 100%;
}
.comm-body .bg {
  width: 355px;
  height: 200px;
  position: relative;
}
.comm-body {
  padding: 10px;
}
.bofang {
  height: 40px;
  width: 40px;
  position: absolute;
  top: 40%;
  left: 160px;
  /* margin-top: -124px; */
}
.hh-face-img {
  position: relative;
}
.masking {
  font-size: 14px;
  width: 100%;
  font-weight: 700;
  position: absolute;
  top: 0;
  /* line-height: 22px; */
  background: linear-gradient(
    rgba(0, 0, 0, 0.6) 5%,
    rgba(0, 0, 0, 0.5) 15%,
    rgba(0, 0, 0, 0.3) 25%,
    rgba(0, 0, 0, 0.1) 35%,
    rgba(0, 0, 0, 0)
  );
  box-sizing: border-box;
  padding: 10px 12px;
  height: 100%;
  z-index: 1010000000;
  /* border-radius: 0px 0px 5px 5px; */
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  overflow: hidden;
  /*! autoprefixer: off */
  -webkit-box-orient: vertical;
}
.masking p {
  width: 100%;
  font-size: 16px;
  font-weight: 600;
  color: #fff;
  overflow: hidden;
  text-overflow: ellipsis;
  text-align: justify;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  overflow: hidden;
  /*! autoprefixer: off */
  -webkit-box-orient: vertical;
}
.clickNum {
  color: #fff;
  position: absolute;
  bottom: 10px;
  font-size: 14px;
  right: 15px;
}
.clickNum span {
  margin-left: 8px;
}
.comment-ul {
  min-height: calc(100vh- 40px) !important;
  height: calc(100vh- 40px) !important;
  padding: 0px 12px;
}

.comment-ul li {
  padding-bottom: 10px;
}

.comm-keyword {
  display: flex;
  justify-content: space-between;
  color: #999;
  font-size: 14px;
  line-height: 24px;
}
.comm-footer {
  /* height: 60px; */
  line-height: 30px;
  box-sizing: border-box;
  text-align: justify;
  line-height: 18px;
  font-size: 14px;
}
/* 多图 */
.com-text {
  width: 100%;
  padding: 10px 12px;
  border-bottom: 1px solid #eee;
  /* margin-top: 30px; */
}
>>> .mint-header {
  color: #3966a7 !important;
}
.com-ul {
  width: 100%;
  min-height: 50px;
}

.com-last-ul {
  width: 100%;
  /* min-height: 80px; */
  display: flex;
  justify-content: space-between;
}
.content {
  min-height: calc(100vh- 80px) !important;
  height: calc(100vh- 80px) !important;
}
.com-left {
  width: 80%;
  padding-right: 10px;
  padding-bottom: 4px;
  display: flex;
  flex-wrap: wrap;
  align-content: space-between;
}

.more-li-img {
  width: 33%;
}

.more-li-img img {
  width: 100%;
  padding: 0px 2px;
  height: 70px;
  border-radius: 5px;
}

.sign-li-title {
  font-size: 16px;
  color: #333;
  min-height: 22px;
  max-height: 44px;
  line-height: 22px;
  margin-bottom: 5px;
  width: 100%;
  flex: 1;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
}
/* graph-li-title */
.graph-li-title {
  /* flex: 1; */
  font-size: 16px;
  color: #333;
  min-height: 22px;
  max-height: 44px;
  line-height: 22px;
  width: 100%;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
}

.graph-li-img {
  width: 40%;
}

.graph-li-img img {
  width: 100%;
  height: 75px;
  max-height: 180px;
  border-radius: 3px;
}

.com-li-info {
  width: 100%;
  display: flex;
  height: 16px;
  line-height: 16px;
  font-size: 12px;
}

.com-li-info span {
  display: inline-block;
  height: 16px;
  line-height: 16px;
}

.com-span-title {
  min-width: 70px;
  color: #3966a7;
}

.com-span-pre {
  flex: 1;
  color: #999;
}

.com-span-time {
  min-width: 40px;
  color: #999;
}

/****
    新增文章详情
  **/

.comment-ul {
  display: flex;
  /* flex-wrap: nowrap; */
  justify-content: space-around;
}
</style>



