const opsconfig = {
    vuescroll: {
        mode: "slide",
        pullRefresh: {
            enable: true
        },
        // sizeStrategy: 'number',
        pushLoad: {
            enable: true,
            auto: true,
            autoLoadDistance: 10
        },
        bar: {
            background: '#023249',
            opacity: 0,
            size: '2px',
            onlyShowBarOnScroll: false,
            disable: true
        },
        rail: {
            background: '#023249',
            opacity: 0,
            size: '2px',
            onlyShowBarOnScroll: false,
            disable: true
        }
    }
};
opsconfig.vuescroll.pullRefresh.tips = {
    deactive: "下拉刷新",
    active: "释放刷新",
    start: "刷新中...",
    beforeDeactive: "刷新成功!"
};
opsconfig.vuescroll.pushLoad.tips = {
    deactive: "上拉加载",
    active: "释放加载",
    start: "加载中...",
    beforeDeactive: "加载成功!"
};

export const ScrollConfig = {
    opsconfig: opsconfig
}


