<template>
  <div class="mainContent">
    <div class="titbox">
      <p class="titlename">其实海与洋还是有些差别的。 海和洋的区分： 广阔的海洋，从蔚蓝到碧绿，美丽而又壮观。海，在洋的边缘，是大洋的附属部分。</p>
      <p class="footBottom">浏览量：123 · 1909-10-11 17:50</p>
      <p class="info">
        <span>所属区域：北京</span>
        <span>回报机制：使用者付费</span>
      </p>
    </div>
    <div class="content">
      一、海洋是怎样形成的？海水是从哪里来的？海其实是在地球史上第一次火山爆发时由于水蒸气太多而形成了云，之后又下了几千年的暴雨，汇成了大海。
      二、研究证明，大约在50亿年前，从太阳星云中分离出一些大大小小的星云团块。它们一边绕太阳旋转，一边自转。在运动过程中，互相碰撞，有些团块彼此结合，由小变大，逐渐成为原始的地球。星云团块碰撞过程中，在引力作用下急剧收缩，加之内部放射性元素蜕变，使原始地球不断受到加热增温；当内部温度达到足够高时，地内的物质包括铁、镍等开始熔解。在重力作用下，重的下沉并趋向地心集中，形成地核；轻者上浮，形成地壳和地幔。在高温下，内部的水分汽化与气体一起冲出来，飞升入空中。但是由于地心的引力，它们不会跑掉，只在地球周围，成为气水合一的圈层。
      三、位于地表的一层地壳，在冷却凝结过程中，不断地受到地球内部剧烈运动的冲击和挤压，
      大海图片
      大海图片
      因而变得褶皱不平，有时还会被挤破，形成地震与火山爆发，喷出岩浆与热气。开始，这种情况发生频繁，后来渐渐变少，慢慢稳定下来。这种轻重物质分化，产生大动荡、大改组的过程，大概是在45亿年前完成了。
      四、地壳经过冷却定形之后，地球就像个久放而风干了的苹果，表面皱纹密布，凹凸不平。高山、平原、河床、海盆，各种地形一应俱全了。
      五、在很长一个时期内，天空中水气与大气共存于一体；浓云密布。天昏地暗，随着地壳逐渐冷却，大气的温度也慢慢地降低，水气以尘埃与火山灰为凝结核，变成水滴，越积越多。由于冷却不均，空气对流剧烈，形成雷电狂风，暴雨浊流，雨越下越大，一直下了很久很久。滔滔的洪水，通过千川万壑，汇集成巨大的水体，这就是原始的海洋。
      六、原始的海洋，海水不是咸的，而是带酸性、又是缺氧的。水分不断蒸发，反复地形云致雨，重又落回地面，把陆地和海底岩石中的盐分溶解，不断地汇集于海水中。经过亿万年的积累融合，才变成了大体匀的咸水。同时，由于大气中当时没有氧气，也没有臭氧层，紫外线可以直达地面，靠海水的保护，生物首先在海洋里诞生。大约在38亿年前，即在海洋里产生了有机物，先有低等的单细胞生物。在6亿年前的古生代，有了海藻类，在阳光下进行光合作用，产生了氧气，慢慢积累的结果，形成了臭氧层。此时，
      周智慧先生大海作品
      周智慧先生大海作品
      生物才开始登上陆地。
      七、总之，经过水量和盐分的逐渐增加，及地质历史上的沧桑巨变，原始海洋逐渐演变成今天的海洋。
      海水为什么是咸的？它会不会随着时间的推移变得越来越咸？多少年来，人们一直没有找到最终一致的答案。
      八、海水所以咸，是因为海水中有3.5%左右的盐。其中大部分是氯化钠，还有少量的氯化镁、硫酸钾、碳酸钙等；正是这些盐类使海水变得又苦又涩，难以入口。那么这些盐类究竟来自何方？有的科学家认为，地球在漫长的地质时期，刚开始形成的地表水（包括海水）都是淡水。后来由于水流冲刷侵蚀了地表岩石，岩石中的盐分不断地溶于水中。这些水流又不断地汇成大河奔腾入海，使大海成了盐类的最后归宿。随着水分不断蒸发，盐分逐渐沉积，天长日久，盐类越积越多，于是海水就变成咸的了。这是一种“后天说”。按此说推论，随着时间的流逝，海水将会越来越咸。
      周智慧大海作品
      周智慧大海作品
      九、有的科学家不同意上述看法。他们认为，海水一开始就是咸的。是先天就形成的。根据他们的观测研究发现，海水并没有越来越咸，海水中的盐分并没有显著的增加，只是在地球的各个地质历史时期，海水中含盐分的比例是不同的。
      十、还有一些科学家认为，海水之所以是咸的，即有先天的原因，也有后天的原因；不仅有大陆上的盐类不断加入到海洋中去，而且在大洋底部随着海底火山喷发，海底岩浆溢出，也会不断地给海洋增加盐类。这种说法得到了大多数学者的赞同。
      十一、也还有一些科学家以死海为例指出，尽管海洋中的盐类会越来越多，但“物极必反”，随着海水中可溶性盐类不断增加，它们之间会变成不可溶的化合物沉入海底，久而久之，被海底吸收，海洋中的盐度则可能保持平衡。
      海洋药库
      据有关医学专家预测，人类将在21世纪制服癌症。那么，人类靠的是何种灵丹妙药？科学家们研究后发现，海洋将成为21世纪的药库。
